import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Draggable } from 'react-beautiful-dnd';
import { Constants } from '@pwc/pwc-deals-web-common';
import { sanitize } from '../../../../common/services/sanitizeService';

import LibraryItemCard from '../../common/LibraryItemCard/LibraryItemCard';
import RichTextLibraryItemCard from '../../common/LibraryItemCard/RichTextLibraryItemCard';
import { useSections } from '../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { DRAG_AND_DROP_STATE_ACTION } from '../FreeformAnalysisForm/FreeformAnalysisFormUtil/dragAndDropReducer';
import DROPPABLE_LOCATION_IDS from './droppableLocationIds';
import {
  setLibraryItemFinishPosition,
  setLibraryItemStartPosition
} from '../../../../common/redux/reportFormat/reportFormatSlice';
import './DraggableLibraryItemCard.scss';

// disable all react-beautiful-dnd development warnings
window['__react-beautiful-dnd-disable-dev-warnings'] = true;

const DraggableLibraryItemCard = ({
  libraryItem,
  index,
  droppableLocationId,
  droppableLocations
}) => {
  const [
    {
      draggableBetweenSectionsItemId,
      currentDroppableSectionId,
      currentPosition,
      currentSections
    },
    dispatch
  ] = useSections();
  const reduxDispatch = useDispatch();
  const [isDragDisabled, setIsDragDisabled] = useState(false);
  const [isInSourceTray, setIsInSourceTray] = useState(false);
  const [wrapperElement, setWrapperElement] = useState(null);
  const [mouseClick, setMouseClick] = useState(false);

  const isAtTheBottomOfCurrentColumn = () => {
    const { libraryItemIds } = droppableLocations[currentPosition.droppableId];

    const indexOfLastItem = libraryItemIds.includes(libraryItem.id)
      ? libraryItemIds.length - 1
      : libraryItemIds.length;

    return currentPosition.index === indexOfLastItem;
  };

  const canMoveUpToPrevSection = (initialIndex, dndPosition) => {
    if (dndPosition) {
      return !isInSourceTray && dndPosition.index === 0;
    }
    return initialIndex === 0;
  };

  const canMoveDownToNextSection = dndPosition => {
    if (dndPosition && isAtTheBottomOfCurrentColumn()) {
      const isLastSection =
        Number(dndPosition.droppableId.slice(-1)) === currentSections.length;

      return !isLastSection;
    }
    return false;
  };

  const setDraggableBetweenSectionsItemId = useCallback(
    libraryItemId => {
      dispatch({
        type: DRAG_AND_DROP_STATE_ACTION.ITEM_ID_UPDATED,
        payload: { draggableBetweenSectionsItemId: libraryItemId }
      });
    },
    [dispatch]
  );
  const moveItemToAdjacentSection = type => {
    setDraggableBetweenSectionsItemId(libraryItem.id);
    wrapperElement.dispatchEvent(
      new KeyboardEvent('keydown', { keyCode: Constants.space, bubbles: false })
    );

    const currentSection = currentSections.find(s =>
      s.items.map(i => i.id).includes(libraryItem.id)
    );

    dispatch({
      type,
      payload: {
        libraryItemId: libraryItem.id,
        currentSectionOrder: currentSection ? currentSection.order : 1
      }
    });
  };

  const canMoveToAdjacentArea = (position, keyCode) => {
    // check whether draggable can move to area above or below (within same section)
    const currentDroppableLocation = droppableLocations[position.droppableId];
    if (keyCode === Constants.arrowUp && currentDroppableLocation.areaAbove) {
      // return area above
      return currentDroppableLocation.areaAbove;
    }
    if (keyCode === Constants.arrowDown && currentDroppableLocation.areaBelow) {
      // return area below
      return currentDroppableLocation.areaBelow;
    }
    return false;
  };

  const updateCurrentPosition = (position, itemId) => {
    dispatch({
      type: DRAG_AND_DROP_STATE_ACTION.ITEM_MOVED_WITHIN_SECTION,
      payload: {
        currentPosition: position,
        draggableWithinSectionsItemId: itemId
      }
    });
    wrapperElement.dispatchEvent(
      new KeyboardEvent('keydown', { keyCode: Constants.space, bubbles: false })
    );
  };

  const handleOnKeyDown = keyCode => {
    if (currentPosition) {
      // TODO: Revisit to improve
      // check if arrowUp or arrowDown
      if (keyCode === Constants.arrowUp || keyCode === Constants.arrowDown) {
        reduxDispatch(
          setLibraryItemStartPosition({
            start: currentPosition,
            draggableId: libraryItem.id
          })
        );
        // check if droppableArea above or droppableArea below
        const adjacentArea = canMoveToAdjacentArea(currentPosition, keyCode);
        if (adjacentArea) {
          // execute code to update position with new currentPosition
          const newPosition = {
            droppableId: adjacentArea,
            index: 0
          };
          reduxDispatch(setLibraryItemFinishPosition({ finish: newPosition }));
          updateCurrentPosition(newPosition, libraryItem.id);
        } else if (
          // if no adjacent droppableArea above or below, then check for adjacent section above or below
          keyCode === Constants.arrowUp &&
          canMoveUpToPrevSection(index, currentPosition)
        ) {
          moveItemToAdjacentSection(
            DRAG_AND_DROP_STATE_ACTION.ITEM_MOVED_UP_TO_NEW_SECTION
          );
        } else if (
          keyCode === Constants.arrowDown &&
          !isInSourceTray &&
          canMoveDownToNextSection(currentPosition)
        ) {
          moveItemToAdjacentSection(
            DRAG_AND_DROP_STATE_ACTION.ITEM_MOVED_DOWN_TO_NEW_SECTION
          );
        }
      }
    }
  };

  useEffect(() => {
    const getSectionOrder = () => {
      if (droppableLocationId === DROPPABLE_LOCATION_IDS.SOURCE_TRAY_ID)
        return 0;
      return Number(droppableLocationId.slice(-1));
    };

    if (
      wrapperElement &&
      draggableBetweenSectionsItemId === libraryItem.id &&
      currentDroppableSectionId === getSectionOrder()
    ) {
      wrapperElement.focus();
      wrapperElement.dispatchEvent(
        new KeyboardEvent('keydown', {
          keyCode: Constants.space,
          bubbles: false
        })
      );
      setDraggableBetweenSectionsItemId(null);
    }
  }, [
    currentDroppableSectionId,
    draggableBetweenSectionsItemId,
    droppableLocationId,
    libraryItem.id,
    setDraggableBetweenSectionsItemId,
    wrapperElement
  ]);

  useEffect(() => {
    const currentLocationId = currentPosition
      ? currentPosition.droppableId
      : droppableLocationId;

    setIsInSourceTray(
      currentLocationId === DROPPABLE_LOCATION_IDS.SOURCE_TRAY_ID
    );
  }, [currentPosition, droppableLocationId]);
  const fieldRef = useRef();

  return (
    <Draggable
      draggableId={libraryItem.id.toString()}
      index={index}
      isDragDisabled={isDragDisabled}
    >
      {(draggableProvided, draggableSnapshot) => {
        return (
          // eslint-disable-next-line jsx-a11y/no-static-element-interactions
          <div
            className="draggable-library-item-card2"
            data-testid="draggable-library-item-card"
            {...draggableProvided.draggableProps}
            {...draggableProvided.dragHandleProps}
            ref={myRef => {
              draggableProvided.innerRef(myRef);
              setWrapperElement(myRef);
            }}
            onKeyDown={e =>
              handleOnKeyDown(e.keyCode, draggableProvided.innerRef)
            }
            onMouseDown={() => {
              if (fieldRef && fieldRef.current) {
                const position = fieldRef.current.getBoundingClientRect();
                // scrolls to 64px above element to allow drag and drop movement
                window.scrollTo(
                  position.left,
                  position.top + window.scrollY - 64
                );
                setMouseClick(true);
              }
            }}
            onMouseUp={() => setMouseClick(false)}
            data-draggable-mode={!!currentPosition}
          >
            {libraryItem.type ===
            Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value ? (
              <RichTextLibraryItemCard
                libraryItem={libraryItem}
                isInSourceTray={isInSourceTray}
                setIsDragDisabled={setIsDragDisabled}
                isDragging={draggableSnapshot.isDragging}
              >
                {libraryItem.data}
              </RichTextLibraryItemCard>
            ) : (
              <LibraryItemCard
                libraryItem={libraryItem}
                isDragging={draggableSnapshot.isDragging || mouseClick}
              >
                {!process.env.REACT_APP_FF20_ENABLED ? (
                  <div
                    ref={fieldRef}
                    dangerouslySetInnerHTML={{
                      __html: sanitize(libraryItem.data)
                    }}
                  />
                ) : null}
              </LibraryItemCard>
            )}
          </div>
        );
      }}
    </Draggable>
  );
};

export default DraggableLibraryItemCard;
