import { createSlice } from '@reduxjs/toolkit';
import _get from 'lodash/get';
import { clearAllProjectData } from '../actions/clearAllProjectData';
import { getLongDescriptionByDocumentAndAdjustment } from './longDescription';
import { getRichTextLongDescription } from '../../services/richTextLongDescriptionService';
import setRichText from '../services/setRichText';

export const initialState = {
  loading: false,
  libraryItemLinks: {},
  basisOfPresentation: '',
  selectedAdjustment: '',
  longDescription: {
    header: '',
    subheader: '',
    childrenData: [],
    content: {
      plainText: '',
      richText: ''
    }
  },
  adjustmentMetadata: {
    libraryItemId: '',
    relatedTextKey: '',
    adjustmentGroup: '',
    adjustmentKey: '',
    documentCode: '',
    projectId: '',
    contentId: '',
    contentVersionNumber: ''
  }
};

const structuredDocumentDescriptionSlice = createSlice({
  name: 'structuredDocumentDescription',
  initialState,
  reducers: {
    loadDescriptionBegin: (state, action) => {
      const {
        header,
        subheader,
        adjustmentGroup,
        adjustmentKey,
        documentCode,
        projectId,
        contentId,
        contentVersionNumber
      } = action.payload;

      state.loading = true;

      state.selectedAdjustment = adjustmentKey;
      state.longDescription.header = header;
      state.longDescription.subheader = subheader;

      state.adjustmentMetadata = {
        libraryItemId: undefined,
        relatedTextKey: '',
        adjustmentGroup,
        adjustmentKey,
        documentCode,
        projectId,
        contentId,
        contentVersionNumber
      };
    },
    loadDescriptionComplete: (state, action) => {
      const {
        content,
        childrenData,
        libraryItemId,
        relatedTextKey
      } = action.payload;

      state.loading = false;

      state.longDescription.content = content;
      state.longDescription.childrenData = childrenData;

      state.adjustmentMetadata.relatedTextKey = relatedTextKey;
      state.adjustmentMetadata.libraryItemId = libraryItemId;
    },
    descriptionResetToBasisOfPresentation: state => {
      state.longDescription.header = 'Basis of Presentation';
      state.longDescription.subheader = '';
      state.longDescription.content = {
        plainText: state.basisOfPresentation,
        richText: ''
      };
      state.longDescription.childrenData = [];
    },
    basisOfPresentationSet: (state, action) => {
      state.basisOfPresentation = action.payload;
    },
    libraryItemLinksSet: (state, action) => {
      state.libraryItemLinks[action.payload.documentCode] =
        action.payload.libraryItemLinks;
    }
  },
  extraReducers: builder => {
    builder.addCase(clearAllProjectData, () => initialState);
  }
});

export default structuredDocumentDescriptionSlice.reducer;

// Actions
export const {
  loadDescriptionBegin,
  loadDescriptionComplete,
  basisOfPresentationSet,
  libraryItemLinksSet,
  descriptionResetToBasisOfPresentation
} = structuredDocumentDescriptionSlice.actions;

// Selectors
export const getLibraryItemLinks = (state, code) =>
  _get(state, `structuredDocumentDescription.libraryItemLinks[${code}]`, []);

export const getAnalysisDescription = state => ({
  loading: _get(state, `structuredDocumentDescription.loading`, false),
  selectedAdjustment: _get(
    state,
    `structuredDocumentDescription.selectedAdjustment`,
    ''
  ),
  header: _get(
    state,
    `structuredDocumentDescription.longDescription.header`,
    ''
  ),
  subheader: _get(
    state,
    `structuredDocumentDescription.longDescription.subheader`,
    ''
  ),
  content: _get(
    state,
    `structuredDocumentDescription.longDescription.content`,
    {}
  ),
  childrenData: _get(
    state,
    `structuredDocumentDescription.longDescription.childrenData`,
    []
  ),
  relatedTextKey: _get(
    state,
    `structuredDocumentDescription.adjustmentMetadata.relatedTextKey`,
    ''
  ),
  libraryItemId: _get(
    state,
    `structuredDocumentDescription.adjustmentMetadata.libraryItemId`,
    undefined
  )
});

export const getAdjustmentMetadata = state =>
  state.structuredDocumentDescription.adjustmentMetadata;

// Thunks
export const setDescriptorForAdjustmentThunk = (
  adjustmentGroup,
  adjustmentKey,
  documentCode,
  projectId,
  contentId,
  contentVersionNumber
) => async (dispatch, getState) => {
  await dispatch(
    loadDescriptionBegin({
      header: adjustmentGroup,
      subheader: adjustmentKey,
      adjustmentGroup,
      adjustmentKey,
      documentCode,
      projectId,
      contentId,
      contentVersionNumber
    })
  );

  const defaultLongDescription = getLongDescriptionByDocumentAndAdjustment(
    documentCode,
    adjustmentKey
  )(getState());

  const libraryItemLinks = getLibraryItemLinks(getState(), documentCode);
  const dynamicLongDescription = await getRichTextLongDescription(
    projectId,
    documentCode,
    contentId,
    contentVersionNumber,
    defaultLongDescription.adjustmentRowId,
    libraryItemLinks
  );

  const descriptionPayload = {
    content: {
      plainText: _get(defaultLongDescription, 'data', '')
    },
    childrenData: _get(defaultLongDescription, 'childrenData', [])
  };

  if (dynamicLongDescription) {
    descriptionPayload.relatedTextKey = dynamicLongDescription.relatedTextKey;
    descriptionPayload.libraryItemId = dynamicLongDescription.libraryItemId;
    descriptionPayload.content = { richText: dynamicLongDescription.data };
    descriptionPayload.childrenData = dynamicLongDescription.childrenData;
  }

  dispatch(loadDescriptionComplete(descriptionPayload));
};

export const saveDocumentDescriptionThunk = richTextData => async (
  dispatch,
  getState
) => {
  // find embedded child item ids
  const root = document.createElement('html');
  root.innerHTML = richTextData;

  const childItems = root.querySelectorAll('.placeholder.ck-widget');
  const childItemIds = [];
  childItems.forEach(x =>
    childItemIds.push(parseInt(x.getAttribute('id'), 10))
  );

  const { relatedTextKey, libraryItemId } = getAnalysisDescription(getState());

  const adjustmentMeta = getAdjustmentMetadata(getState());

  const payload = {
    projectId: adjustmentMeta.projectId,
    libraryItemId,
    name: relatedTextKey,
    richTextData,
    childItemIds
  };

  try {
    await setRichText(payload);
  } catch (error) {
    /** error handling? */
  }

  // reload things
  await setDescriptorForAdjustmentThunk(
    adjustmentMeta.adjustmentGroup,
    adjustmentMeta.adjustmentKey,
    adjustmentMeta.documentCode,
    adjustmentMeta.projectId,
    adjustmentMeta.contentId,
    adjustmentMeta.contentVersionNumber
  )(dispatch, getState);
};
