import locationService from '../../../services/locationService';
import {
  invalidateTokens,
  getSignoutUrl
} from '../../../services/security/userAuthService';
import projectTokenCache from '../../../services/security/projectTokenCache';

export const logOutThunk = () => async () => {
  await invalidateTokens();
  const signoutUrl = await getSignoutUrl();
  projectTokenCache.reset();
  locationService.redirect(signoutUrl);
};
