import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'connected-react-router';
import cssVars from 'css-vars-ponyfill';
import App from './app/App';
import * as serviceWorker from './serviceWorker';
import { history, createStore } from './app/reduxStore';
import '@pwc/pwc-deals-rich-text-editor/build/ckEditorStyles.css';
import 'bootstrap/scss/bootstrap.scss';
import './styles/main.scss';
import '@pwc/pwc-deals-ui/build/styles/main.css';
import 'focus-visible/dist/focus-visible.min';
import jobRunner from './common/jobs/job.runner';

jobRunner.start();

const cssPonyfill = () =>
  cssVars({
    onlyLegacy: true,
    include: 'style,link[rel="stylesheet"]:not([href*="//"])',
    // eslint-disable-next-line no-console
    onComplete: () => console.log('CSS Variables updated')
  });

cssPonyfill();

ReactDOM.render(
  <Provider store={createStore()}>
    <ConnectedRouter history={history}>
      <App />
    </ConnectedRouter>
  </Provider>,
  document.getElementById('root')
);

serviceWorker.unregister();
