import React from 'react';
import { Icon, Button } from '@pwc/pwc-deals-ui';
import { getProjectType } from '../project/common/projectOptions';
import StatusPill from '../../common/components/Pills/StatusPill';
import locationService from '../../common/services/locationService';
import ProjectLoader from './ProjectLoader';
import './ProjectStatus.scss';
import { PROJECT_STATUS_CODES } from '../../common/constants/ProjectStatus';
import ProjectStatePill from '../projectStatePill/ProjectStatePill';

const { REACT_APP_SUPPORT_EMAIL } = process.env;

const ProjectStatus = ({ project }) => {
  const handleEmail = () => {
    if (REACT_APP_SUPPORT_EMAIL) {
      locationService.redirect(`mailto:${REACT_APP_SUPPORT_EMAIL}`);
    }
  };

  if (project.status === PROJECT_STATUS_CODES.COMPLETED) {
    return (
      <span className="project-status align-self-md-center mt-0">
        <StatusPill
          dataTestId={`project-${project.id}-type`}
          title={getProjectType(project.type)}
        />
      </span>
    );
  }
  if (project.status === PROJECT_STATUS_CODES.ON_HOLD) {
    return (
      <span className="project-status" data-testid="project-on-hold">
        <span className="project-status__icon">
          <Icon name="alert" width={24} className="fill icon-yellow" />
        </span>
        <span>
          Project initiation on hold as the team investigate an issue. The
          process will retry automatically.
        </span>
      </span>
    );
  }
  if (project.status === PROJECT_STATUS_CODES.FAILED) {
    return (
      <span className="project-status" data-testid="project-failed">
        <span className="project-status__icon">
          <Icon name="alert" width={24} className="fill icon-yellow" />
        </span>
        <span>
          An error occurred.
          {REACT_APP_SUPPORT_EMAIL ? (
            <span data-testid="support-info">
              {' '}
              If it continues, contact{' '}
              <Button className="button-link" onClick={handleEmail}>
                {REACT_APP_SUPPORT_EMAIL}
              </Button>
            </span>
          ) : null}
        </span>
      </span>
    );
  }
  if (
    [
      PROJECT_STATUS_CODES.INACTIVE,
      PROJECT_STATUS_CODES.INACTIVE_PENDING_IN_PROGRESS,
      PROJECT_STATUS_CODES.ARCHIVED,
      PROJECT_STATUS_CODES.ARCHIVING_PENDING_IN_PROGRESS
    ].includes(project.status)
  ) {
    return (
      <>
        <StatusPill
          dataTestId={`project-${project.id}-type`}
          title={getProjectType(project.type)}
        />
        <ProjectStatePill
          projectId={project.id}
          showWhenActive={false}
          isSmall
        />
      </>
    );
  }
  return (
    <>
      <span className="project-status align-items-center">
        <StatusPill
          title="Project Initiating"
          status="provisioning"
          dataTestId="project-status-pill"
        />
        <ProjectLoader />
      </span>
    </>
  );
};

export default ProjectStatus;
