import React from 'react';
import { useSelector } from 'react-redux';
import { Icon } from '@pwc/pwc-deals-ui';
import ProjectStatePill from '../../../features/projectStatePill/ProjectStatePill';
import useTabletOrMobile from '../../hooks/useTabletOrMobile';
import UserInitialsSnippet from '../UserInitialSnippet/UserInitialsSnippet';
import AddProjectItemsHeaderMenu from './AddProjectItemsHeaderMenu/AddProjectItemsHeaderMenu';
import './Header.scss';
import BreadcrumbsContainer from '../../../features/breadcrumbs/BreadcrumbsContainer';
import { getUserSettingsSelector } from '../../redux/userSettings/userSettingsSlice';
import AddProjectHeaderMenu from './AddProjectHeaderMenu/AddProjectHeaderMenu';

const Header = ({ projectId }) => {
  const userSettings = useSelector(getUserSettingsSelector);
  const isTabletOrMobile = useTabletOrMobile();
  return (
    <div className="header">
      <div className="header-content">
        <div className="header-content-breadcrumbs">
          <BreadcrumbsContainer />
        </div>
        <div className="snippet-pendo-container">
          {projectId ? (
            <AddProjectItemsHeaderMenu projectId={projectId} />
          ) : (
            <AddProjectHeaderMenu />
          )}
          {projectId && !isTabletOrMobile && (
            <ProjectStatePill projectId={projectId} showWhenActive={false} />
          )}
          {userSettings && userSettings.loaded && <UserInitialsSnippet />}
          <div className="pendo-help nowrap">
            <Icon
              name="help-question"
              width={30}
              className="outline icon-help"
            />{' '}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
