import React from 'react';
import './ContentItem.scss';
import { sanitize } from '../../../services/sanitizeService';

const RichTextLibraryItem = ({ item }) => {
  const {
    richTextContent: { richTextData }
  } = item;
  return (
    <div
      className="rich-text-content-item-container"
      dangerouslySetInnerHTML={{ __html: sanitize(richTextData) }}
    />
  );
};

export default RichTextLibraryItem;
