import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { getViewPathForAnalysis } from '../../analyses/common/analysisUrlHelper';
import { DisplayableContent } from './ConnectedKfaPropTypes';

export const AnalysisTitleLink = ({ analysis, projectId, testId }) => {
  return (
    <h5 key={analysis.contentId} className="analysis-title-heading">
      <Link
        to={getViewPathForAnalysis(projectId, analysis)}
        data-testid={testId}
        className="analysis-title-link"
      >
        {analysis.title}
      </Link>
    </h5>
  );
};

AnalysisTitleLink.propTypes = {
  analysis: PropTypes.shape(DisplayableContent).isRequired,
  projectId: PropTypes.string.isRequired,
  testId: PropTypes.string.isRequired
};
