import React from 'react';
import { Popover } from 'react-bootstrap';
import MenuButtonDelete from '../MenuButtonDelete';
import MenuButtonEdit from '../MenuButtonEdit';
import ShowForPermissions from '../../../features/security/ShowForPermissions';

const ContentActionsPopupMenu = React.forwardRef(
  (
    { projectId, itemId, onEditMenuClick, onDeleteMenuClick, ...props },
    ref
  ) => {
    return (
      <Popover
        ref={ref}
        id="actions-popup"
        data-testid={`content-actions-popup-menu-${itemId}`}
        {...props}
      >
        <Popover.Content className="popup-menu">
          <ShowForPermissions
            projectId={projectId}
            permissions={['Contribute']}
          >
            <MenuButtonEdit onClick={onEditMenuClick} />
            <MenuButtonDelete onClick={onDeleteMenuClick} />
          </ShowForPermissions>
        </Popover.Content>
      </Popover>
    );
  }
);

export default ContentActionsPopupMenu;
