import { find as _find, isEmpty as _isEmpty } from 'lodash';
import React, { useEffect, useState, useCallback } from 'react';
import PropTypes from 'prop-types';
import './ProjectStateChangeCards.scss';
import { useDispatch, useSelector } from 'react-redux';
import {
  getProjectStatus,
  useProjectsStatuses
} from '../../common/hooks/useProjectsStatuses';
import authorizationService from '../../common/services/security/authorizationService';
import { getProject } from '../project/projectSlice';
import { getScenarioCards } from './ProjectStateChangeCardsTypes';
import * as service from './projectStatusChangeSlice';
import {
  canUserApproveChange,
  canUserRequestChange
} from './ProjectUsersUtils';
import { isProjectInPendingState } from '../../common/helpers/projectStatusesUtils';
import { loadProjectsThunk } from '../projects/projectsSlice';

import WarningCard from './WarningCard';
import StateChangeCard from './StateChangeCard';

const ProjectStateChangeCards = ({ projectId }) => {
  const dispatch = useDispatch();
  const { users } = useSelector(getProject);
  const approvals = useSelector(service.getProjectApprovalsChart(projectId));
  const projectsStates = useProjectsStatuses();
  const projectStatus = getProjectStatus(projectsStates, projectId);
  const isPendingState = isProjectInPendingState(projectStatus);

  const userEmail = authorizationService.getEmail();
  const [isLeftCardClicked, setIsLeftCardClicked] = useState(false);
  const [isRightCardClicked, setIsRightCardClicked] = useState(false);
  const user = _find(users, _user => _user.email === userEmail);

  const isUserPrivilegedToApprove = canUserApproveChange(user);
  const isUserPrivilegedToRequest = canUserRequestChange(user);

  const cards = getScenarioCards(
    userEmail,
    projectId,
    projectStatus,
    approvals,
    isUserPrivilegedToApprove,
    isUserPrivilegedToRequest
  );

  useEffect(() => {
    if (userEmail) {
      dispatch(service.fetchProjectApprovals({ projectId, userId: userEmail }));
    }
  }, [dispatch, projectId, userEmail]);

  const refetchProjectsAndApprovals = useCallback(() => {
    dispatch(loadProjectsThunk());
    dispatch(service.fetchProjectApprovals({ projectId, userId: userEmail }));
  }, [dispatch, projectId, userEmail]);

  const handleOnLeftCardClick = useCallback(async () => {
    if (!isLeftCardClicked) {
      setIsLeftCardClicked(true);
    } else {
      await cards.leftCardAction();
      refetchProjectsAndApprovals();
      setIsLeftCardClicked(false);
    }
  }, [cards, isLeftCardClicked, refetchProjectsAndApprovals]);

  const handleOnRightCardClick = useCallback(async () => {
    if (!isRightCardClicked) {
      setIsRightCardClicked(true);
    } else {
      await cards.rightCardAction();
      refetchProjectsAndApprovals();
      setIsLeftCardClicked(false);
    }
  }, [cards, isRightCardClicked, refetchProjectsAndApprovals]);

  const handleOnCancelClick = () => {
    setIsLeftCardClicked(false);
    setIsRightCardClicked(false);
  };

  const handleOnApprove = async () => {
    await cards.warningCardApproveAction();
    refetchProjectsAndApprovals();
  };

  const handleOnDecline = async () => {
    await cards.warningCardCancelAction();
    refetchProjectsAndApprovals();
  };

  if (_isEmpty(projectsStates)) {
    return null;
  }

  /*
   * Since Archiving is not yet finished on backend side, right card is disabled
   */
  const rightCardEnabled = false;

  return (
    <>
      {(cards.leftCard || cards.rightCard) && (
        <div className="project-state-change-cards">
          {cards.leftCard && (
            <StateChangeCard
              onAccept={handleOnLeftCardClick}
              onCancel={handleOnCancelClick}
              flow={!isLeftCardClicked ? cards.leftCard : cards.leftCardConfirm}
              isDisabled={isPendingState || isRightCardClicked}
            />
          )}

          {rightCardEnabled && cards.rightCard && (
            <StateChangeCard
              onAccept={handleOnRightCardClick}
              onCancel={handleOnCancelClick}
              flow={
                !isRightCardClicked ? cards.rightCard : cards.rightCardConfirm
              }
              isDisabled={isPendingState || isLeftCardClicked}
            />
          )}
        </div>
      )}
      {cards.warningCard && (
        <WarningCard
          warning={cards.warningCard}
          onAccept={handleOnApprove}
          onCancel={handleOnDecline}
        />
      )}
    </>
  );
};

ProjectStateChangeCards.propTypes = {
  projectId: PropTypes.number.isRequired
};

export default ProjectStateChangeCards;
