import DROPPABLE_LOCATION_IDS from './droppableLocationIds';
import { generateDroppableLocationIdWithSection } from './generateDroppableLocationIdWithSection';

export function freeformLayoutIsValidNew(droppableLocations) {
  const sourceTray = droppableLocations[DROPPABLE_LOCATION_IDS.SOURCE_TRAY_ID];
  const sourceTrayIsEmpty =
    sourceTray &&
    sourceTray.libraryItemIds &&
    sourceTray.libraryItemIds.length === 0;

  if (!sourceTrayIsEmpty) return false;
  const filteredDroppableLocationIds = Object.keys(droppableLocations).filter(
    key => key !== DROPPABLE_LOCATION_IDS.SOURCE_TRAY_ID
  );
  const allDroppableContainersFilled = filteredDroppableLocationIds.every(
    droppableLocationId =>
      droppableLocations[droppableLocationId].libraryItemIds.length > 0
  );
  return allDroppableContainersFilled;
}

export default function freeformLayoutIsValid(
  droppableLocations,
  numberOfSections
) {
  const hasLibraryItems = (index, droppableLocationIdBase) => {
    const droppableLocationIdWithSection = generateDroppableLocationIdWithSection(
      droppableLocationIdBase,
      index + 1
    );
    if (
      droppableLocations[droppableLocationIdWithSection] &&
      droppableLocations[droppableLocationIdWithSection].libraryItemIds.length >
        0
    ) {
      return true;
    }
    return false;
  };
  const sourceTray = droppableLocations[DROPPABLE_LOCATION_IDS.SOURCE_TRAY_ID];
  const sourceTrayIsEmpty =
    sourceTray &&
    sourceTray.libraryItemIds &&
    sourceTray.libraryItemIds.length === 0;

  if (!sourceTrayIsEmpty) return false;
  for (let i = 0; i < numberOfSections; i += 1) {
    const nonSourceTrayDroppableLocationIds = Object.values(
      DROPPABLE_LOCATION_IDS
    ).filter(id => id !== DROPPABLE_LOCATION_IDS.SOURCE_TRAY_ID);

    let sectionHasAtLeastOneItem = false;
    for (let j = 0; j < nonSourceTrayDroppableLocationIds.length; j += 1) {
      const droppableLocationIdBase = nonSourceTrayDroppableLocationIds[j];
      if (Array.isArray(droppableLocationIdBase)) {
        for (let k = 0; k < droppableLocationIdBase.length; k += 1) {
          const hasLibraryItem = hasLibraryItems(i, droppableLocationIdBase[k]);

          if (hasLibraryItem) {
            sectionHasAtLeastOneItem = hasLibraryItem;
            break;
          }
        }
      } else {
        const hasLibraryItem = hasLibraryItems(i, droppableLocationIdBase);

        if (hasLibraryItem) {
          sectionHasAtLeastOneItem = hasLibraryItem;
          break;
        }
      }
    }
    if (!sectionHasAtLeastOneItem) {
      return false;
    }
  }
  return true;
}
