import { ProjectHttpClient } from './httpClient';

export const serviceUrl = (_strings, projectId) =>
  `projects/${projectId}/userNotificationSettings`;

export const getUserNotificationSettings = projectId =>
  ProjectHttpClient.get(serviceUrl`${projectId}`);

export const saveUserNotificationSettings = (projectId, receiveEmails) =>
  ProjectHttpClient.post(serviceUrl`${projectId}`, { receiveEmails });
