import React from 'react';
import { useSelector } from 'react-redux';
import { Constants } from '@pwc/pwc-deals-web-common';
import { getIsDarkModeSelector } from '../../../../../../common/redux/userSettings/userSettingsSlice';
import SelectedSectionIcon from './SelectedSectionIcon';

const SelectedSection = ({ layout, panel }) => {
  const isDarkMode = useSelector(getIsDarkModeSelector);

  const translateLayoutValueToIconNamePart = () => {
    switch (layout) {
      case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN.value:
        return 'two-column';

      case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_67_1A_R_33_1A.value:
        return 'two-column-narrow';

      case Constants.TOPIC_LAYOUT_TYPE.THREE_COLUMN.value:
        return 'three-column';

      case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value:
        return 'stacked-with-right-column';

      case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value:
        return 'stacked-two-block-bottom';

      case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_60_1A_B_40_1A.value:
        return 'stacked-medium-top';

      case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_80_1A_B_20_1A.value:
        return 'stacked-large-top';
      default:
        return 'one-column-full-width';
    }
  };

  const translatePanelValueToIconNamePart = () => {
    switch (layout) {
      case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN.value:
      case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_67_1A_R_33_1A.value: {
        if (panel === 1) {
          return 'left';
        }
        return 'right';
      }

      case Constants.TOPIC_LAYOUT_TYPE.THREE_COLUMN.value: {
        if (panel === 1) {
          return 'left';
        }
        if (panel === 2) {
          return 'middle';
        }
        return 'right';
      }

      case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value: {
        if (panel === 1) {
          return 'left';
        }
        if (panel === 2) {
          return 'right';
        }
        return 'btm-left';
      }

      case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value: {
        if (panel === 1) {
          return 'top';
        }
        if (panel === 2) {
          return 'btm-left';
        }
        return 'btm-right';
      }

      case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_80_1A_B_20_1A.value:
      case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_60_1A_B_40_1A.value: {
        if (panel === 1) {
          return 'top';
        }
        return 'btm';
      }

      default:
        return '';
    }
  };

  const getTitle = () =>
    `layout-${translateLayoutValueToIconNamePart()}-${translatePanelValueToIconNamePart()}`.replace(
      '--',
      '-'
    );
  const getIconName = () =>
    `${getTitle()}-${isDarkMode ? 'dark' : 'light'}`.replace('--', '-');

  return <SelectedSectionIcon name={getIconName()} title={getTitle()} />;
};

export default SelectedSection;
