import React, { useContext, useEffect, useState } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { useDispatch } from 'react-redux';
import './ProjectOverviewModule.scss';
import TopicPanel from './Topic/TopicPanel/TopicPanel';
import TopicHeader from './Topic/TopicHeader/TopicHeader';
import { getWorkflowStatus } from '../../../workflow/workflowService';
import { useApprovalWorkflow } from '../../../../common/hooks/useApprovalWorkflow';
import { getTopicVersions, updateTopicStatus } from '../../topicService';
import { getViewClientInfo } from '../../../../common/helpers/viewClientVersion';
import { showMoreMenuError } from '../../../../common/redux/errors/errorsSlice';
import NarrativesContext from '../../../../common/contexts/NarrativesContext';

const ProjectOverviewModule = ({
  topic,
  onDelete,
  onEdit,
  projectId,
  onTopicStatusUpdated,
  onViewSpecificVersion,
  onMove,
  moveOption
}) => {
  const { hypotheses } = useContext(NarrativesContext) || { hypotheses: [] };
  const [versions, setVersions] = useState(null);
  const [viewClient, setViewClient] = useState({});
  const dispatch = useDispatch();

  useEffect(() => {
    getTopicVersions(projectId, topic.contentThreadId).then(setVersions);
  }, [projectId, topic.contentThreadId, topic.dateModified]);

  useEffect(() => {
    setViewClient(
      getViewClientInfo(
        () => onViewSpecificVersion(Constants.VERSION_STATUS.SHARED.value),
        () => onViewSpecificVersion(),
        versions,
        topic && topic.versionNumber,
        topic && topic.versionStatus
      )
    );
  }, [topic, versions, onViewSpecificVersion]);

  const workflowActions = useApprovalWorkflow(
    getWorkflowStatus,
    async (_projectId, contentId, step) => {
      try {
        await updateTopicStatus(_projectId, `${contentId}`, step);
      } catch (e) {
        dispatch(
          showMoreMenuError({
            itemId: contentId,
            moreMenuErrorType: Constants.MoreMenuErrorType.UNABLE_TO_SAVE_CHANGE
          })
        );
        return;
      }

      onTopicStatusUpdated(topic);
    },
    projectId,
    topic.contentThreadId,
    topic,
    versions
  );

  return (
    <div>
      <div
        className="project-overview-module"
        key={topic.contentThreadId}
        data-testid="project-overview-module"
      >
        <TopicHeader
          topic={topic}
          onDelete={onDelete}
          onEdit={onEdit}
          workflowActions={workflowActions}
          viewClient={viewClient}
          onMove={onMove}
          moveOption={moveOption}
        />
        <div
          className="project-overview-module__inner"
          data-testid="project-overview-module-inner"
          data-topic-id={topic.contentThreadId}
        >
          {topic.panels &&
            topic.panels.length > 0 &&
            topic.panels.map(panel => {
              let hypothesis;
              if (
                panel.type === Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.value
              ) {
                hypothesis =
                  panel.hypothesis ||
                  hypotheses.find(
                    h => h.contentThreadId === panel.contentThreadId
                  );
              }
              return (
                <TopicPanel
                  key={panel.panelNumber}
                  topic={topic}
                  panel={panel}
                  hypothesis={hypothesis}
                  projectId={projectId}
                />
              );
            })}
        </div>
      </div>
    </div>
  );
};

export default ProjectOverviewModule;
