import { ProjectHttpClient } from '../../common/services/httpClient';

const baseServicePath = (projectId, contentThreadId) =>
  `projects/${projectId}/workflow/${contentThreadId || ''}`;

export const getWorkflowStatus = async (projectId, contentThreadId) => {
  const response = await ProjectHttpClient.get(
    baseServicePath(projectId, contentThreadId)
  );
  return response.data;
};
