import React from 'react';
import { Link } from 'react-router-dom';
import { sanitize } from '../../../common/services/sanitizeService';
import './NarrativeCard.scss';

const NarrativeCard = ({
  narrative,
  testId,
  getViewPath,
  truncate,
  showBody
}) => {
  return (
    <div className="narrative-card" data-testid="narrative-card">
      <Link
        to={getViewPath(narrative.contentThreadId)}
        className="narrative-card-title"
        data-testid={`narrative-card-title-${testId}`}
      >
        {narrative.title}
      </Link>
      {showBody && narrative.body && (
        <div
          className={`narrative-card-body ${truncate ? 'truncate' : ''}`}
          data-testid={`narrative-card-body-${testId}`}
          dangerouslySetInnerHTML={{ __html: sanitize(narrative.body) }}
        />
      )}
    </div>
  );
};

export default NarrativeCard;
