import React from 'react';
import { Link, useRouteMatch } from 'react-router-dom';
import './AddLibraryItemsBox.scss';
import { Icon } from '@pwc/pwc-deals-ui';
import { Constants, TextUtilities } from '@pwc/pwc-deals-web-common';
import LibraryItemCard from './LibraryItemCard/LibraryItemCard';
import RichTextCardInner from './RichTextCardInner';

const AddLibraryItemsBox = ({
  selectedLibraryItems = [],
  isValid,
  layoutSaved
}) => {
  const { url } = useRouteMatch();

  const libraryItemsBrowseLink = () => {
    const isEmpty = selectedLibraryItems.length > 0;
    const content = isEmpty ? (
      <>
        <Icon
          data-testid="icon-edit-data"
          name="pencil"
          className="fill icon-orange edit-data-icon"
          width="18"
        />
        Edit data
      </>
    ) : (
      <span className="as-button" data-testid="add-data-button-text">
        Add data
      </span>
    );
    const floatStyle = isEmpty ? { float: 'right' } : { float: 'left' };
    return (
      <Link
        className={!isValid ? 'button-disabled' : ''}
        data-testid="browse-library-items"
        to={`${url}/library-items`}
        style={floatStyle}
      >
        {content}
      </Link>
    );
  };

  return (
    <div
      className="add-library-items-box-container"
      data-testid="add-library-items-box"
    >
      Data
      <div className="card">
        {selectedLibraryItems.length === 0 && (
          <div data-testid="add-library-items-box-description">
            <p className="card-content">
              To add data you can browse the library of items already added to
              the platform or add your own to the library.
            </p>
          </div>
        )}
        <div className="library-item-card-container">
          {selectedLibraryItems.map(libraryItem => (
            <LibraryItemCard
              libraryItem={libraryItem}
              key={libraryItem.id ? libraryItem.id : libraryItem.reportId}
              hoverClass="hover-orange"
            >
              {libraryItem.type ===
                Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value && (
                <RichTextCardInner
                  isReadOnly
                  richText={TextUtilities.parseRichText(
                    libraryItem.isShared,
                    libraryItem.data
                  )}
                />
              )}
            </LibraryItemCard>
          ))}
        </div>
        <div className="editdatalink">
          {layoutSaved ? (
            <span data-testid="layout-saved-container">
              <Icon
                name="check"
                data-testid="icon-layout-saved"
                className="fill icon-green layout-saved-icon"
                width="18"
              />
              Data and layout changes saved
            </span>
          ) : null}
          {libraryItemsBrowseLink()}
        </div>
      </div>
    </div>
  );
};

export default AddLibraryItemsBox;
