import { Constants } from '@pwc/pwc-deals-web-common';

const getLibraryItemIconName = (libraryItemNumericalValue, hoverClass) => {
  switch (libraryItemNumericalValue) {
    case Constants.LIBRARY_ITEM_TYPES.EXCEL_CELL_RANGE.value:
    case Constants.LIBRARY_ITEM_TYPES.EXCEL_NAMED_RANGE.value:
      return 'charts';
    case Constants.LIBRARY_ITEM_TYPES.EXCEL_CHART.value:
      return 'bar-chart';
    case Constants.LIBRARY_ITEM_TYPES.POWER_BI_REPORT.value:
      return 'powerbi-new';
    case Constants.LIBRARY_ITEM_TYPES.POWER_BI_REPORT_PAGE.value:
      return hoverClass ? 'powerbi-report' : 'powerbi-new';
    case Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value:
      return 'textbox';
    case Constants.LIBRARY_ITEM_TYPES.POWER_POINT_GROUP.value:
      return 'powerpoint';
    default:
      return 'view';
  }
};

export default getLibraryItemIconName;
