import { Constants } from '@pwc/pwc-deals-web-common';
import classNames from 'classnames';
import { cloneDeep } from 'lodash';
import PropType from 'prop-types';
import React, { useState } from 'react';
import { Droppable } from 'react-beautiful-dnd';
import { v4 as uuidv4 } from 'uuid';
import { excelTable1 } from '../../../../../../../../contentLibrary/mock';
import {
  useDragAndDrop,
  useSections
} from '../../../../../../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { setCurrentSections } from '../../../../../../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';

import DraggableLibraryItemCard from '../../../../../DraggableLibraryItemCard';
import ObjectInjestPopover from '../../../../../ObjectInjestPopover/ObjectInjestPopover';

import './SectionArea.scss';
import DeleteButton from '../DeleteButton/DeleteButton';

function SectionArea({
  areaClass,
  droppableLocation,
  libraryItems,
  libraryItemMap,
  droppableLocations
}) {
  const canLibraryItemBePlacedInSection = libraryItemId => {
    const parsedLibraryItemId = Number.isInteger(parseInt(libraryItemId, 10))
      ? parseInt(libraryItemId, 10)
      : null;
    if (
      parsedLibraryItemId &&
      libraryItemMap[parsedLibraryItemId].type !==
        Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value &&
      droppableLocation.id.includes('text-overflow')
    ) {
      return false;
    }
    return true;
  };

  const [popOver, onPopover] = useState(false);

  const baseStyles = {
    border: 'var(--s1px) dashed var(--color-grey)',
    borderRadius: 'var(--s2px)',
    backgroundColor: 'var(--dragdrop-container-background-hover)'
  };
  const draggingOverBorder =
    'var(--s2px) dashed var(--hovered-section-border-color)';
  const draggingOverBorderError =
    'var(--s2px) solid var(--color-red-error-dark-mode)';
  const draggingOverBackgroundColor = 'var(--background-primary)';
  const isAreaFilled = () => {
    return libraryItems.length > 0;
  };

  const getStyle = droppableSnapshot => {
    const {
      isDraggingOver,
      draggingOverWith,
      draggingFromThisWith
    } = droppableSnapshot;
    const returnedStyle = { ...baseStyles };
    if (popOver)
      return {
        border: 'var(--s2px) dashed var(--hovered-section-border-color)',
        borderRadius: 'var(--s2px)',
        backgroundColor: 'var(--dragdrop-container-background-hover)'
      };
    if (
      isAreaFilled() &&
      draggingOverWith &&
      draggingOverWith === draggingFromThisWith
    ) {
      Object.assign(returnedStyle, {
        border: draggingOverBorder,
        backgroundColor: draggingOverBackgroundColor
      });
      return returnedStyle;
    }
    if (
      isAreaFilled() &&
      draggingOverWith &&
      !canLibraryItemBePlacedInSection(draggingOverWith)
    ) {
      Object.assign(returnedStyle, { border: draggingOverBorderError });
      return returnedStyle;
    }
    if (isAreaFilled()) {
      return {
        padding: 'var(--s2px)'
      };
    }
    if (
      !isAreaFilled() &&
      isDraggingOver &&
      draggingOverWith &&
      canLibraryItemBePlacedInSection(draggingOverWith)
    ) {
      Object.assign(returnedStyle, {
        border: draggingOverBorder,
        borderRadius: 'var(--s2px)',
        backgroundColor: draggingOverBackgroundColor
      });
      return returnedStyle;
    }
    if (
      !isAreaFilled() &&
      isDraggingOver &&
      draggingOverWith &&
      !canLibraryItemBePlacedInSection(draggingOverWith)
    ) {
      Object.assign(returnedStyle, { border: draggingOverBorderError });
      return returnedStyle;
    }
    // return no styles if area is not filled and not being dragged over and allow scss to set the styles
    return {};
  };

  // const { projectId } = useParams();
  const [
    // eslint-disable-next-line no-unused-vars
    { currentSections = [], nonLaidOutLibraryItems = [] },
    contextDispatch
  ] = useSections();
  const { setDroppableLocations } = useDragAndDrop();

  const onAddItem = async () => {
    onPopover(false);
    const name = uuidv4();
    const id = Date.now();
    /* cconst richTextData = JSON.stringify({
      RichTextData: Constants.EMPTY_RICH_TEXT_PLACEHOLDER
    });

   const id = await postRichText(projectId, name, richTextData); */

    const newSections = cloneDeep(currentSections);

    /*
    newSections[0].items.push({
      id,
      name,
      type: Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value,
      isShared: false,
      data: richTextData
    }); */

    newSections[0].items.push({
      id,
      name,
      type: Constants.LIBRARY_ITEM_TYPES.EXCEL_CHART.value,
      isShared: false,
      data: excelTable1.data.fileContents[0].value,
      analysisColumn: 1
    });

    setCurrentSections(newSections, contextDispatch);

    const newDroppableLocations = cloneDeep(droppableLocations);
    newDroppableLocations[droppableLocation.id] = {
      ...droppableLocation,
      libraryItemIds: [...droppableLocation.libraryItemIds, id]
    };
    setDroppableLocations(newDroppableLocations);
  };
  const [mouseOver, setMouseOver] = useState(false);
  const hideClearSectionButton = () => {
    setMouseOver(false);
  };
  const clearSection = e => {
    e.preventDefault();
    const newDroppableLocations = cloneDeep(droppableLocations);
    newDroppableLocations[droppableLocation.id] = {
      ...droppableLocation,
      libraryItemIds: []
    };
    setDroppableLocations(newDroppableLocations);
  };
  const [clearSectionHover, setClearSectionHover] = useState(false);

  return (
    <Droppable
      droppableId={droppableLocation.id}
      direction={droppableLocation.direction}
      key={droppableLocation.id}
      ignoreContainerClipping
    >
      {(provided, snapshot) => {
        const computedStyle = getStyle(snapshot);
        return (
          <div
            className={`${areaClass} section-area__droppable-container`}
            data-testid={areaClass}
            ref={provided.innerRef}
            {...provided.droppableProps}
            style={computedStyle}
            onMouseEnter={() => setMouseOver(true)}
            onFocus={() => setMouseOver(true)}
            onMouseLeave={hideClearSectionButton}
            onBlur={hideClearSectionButton}
          >
            <div
              className={classNames('section-area__item-container', {
                'background-left': droppableLocation.id.includes(
                  'text-overflow'
                )
              })}
            >
              {libraryItems.map((libraryItem, index) => {
                return (
                  <DraggableLibraryItemCard
                    key={libraryItem.id}
                    libraryItem={libraryItem}
                    index={index}
                    droppableLocationId={droppableLocation.id}
                    droppableLocations={droppableLocations}
                  />
                );
              })}
              {provided.placeholder}
            </div>

            <div
              className="clearSectionWrapper"
              style={{
                display:
                  mouseOver &&
                  libraryItems.length > 0 &&
                  !snapshot.isDraggingOver
                    ? 'block'
                    : 'none'
              }}
            >
              <DeleteButton
                tooltip="clear section"
                tooltipPlacement="top"
                iconType={`${
                  clearSectionHover ? 'fill' : 'outline'
                } icon-grey-theme`}
                handleClick={clearSection}
                handleMouseEnter={() => setClearSectionHover(true)}
                handleMouseLeave={() => setClearSectionHover(false)}
              />
            </div>

            {libraryItems.length < 1 && (
              <div
                className={classNames('section-area__background', {
                  'background-left': droppableLocation.id.includes(
                    'text-overflow'
                  )
                })}
              >
                <div className="section-area__background__content">
                  {
                    // TODO: remove once we can really insert something
                  }
                  <ObjectInjestPopover
                    onInsertPowerBi={onAddItem}
                    onPopover={onPopover}
                    droppableLocation={droppableLocation}
                  />
                </div>
              </div>
            )}
            {droppableLocation.id.includes('text-overflow') && (
              <div className="section-area__background background-right" />
            )}
          </div>
        );
      }}
    </Droppable>
  );
}

SectionArea.propTypes = {
  areaClass: PropType.string
};

SectionArea.defaultProps = {
  areaClass: ''
};

export default SectionArea;
