import { Constants } from '@pwc/pwc-deals-web-common';
import { chain as _chain } from 'lodash';

const checkIfUserHasRole = (user, role) => {
  if (user && user.roles) {
    return !_chain(user.roles)
      .find(_role => _role.name === role)
      .isEmpty()
      .value();
  }
  return false;
};

export const canUserApproveChange = user => {
  if (!user) {
    return false;
  }

  return (
    checkIfUserHasRole(user, Constants.USER_ROLES.ENGAGEMENT_LEAD) ||
    checkIfUserHasRole(user, Constants.USER_ROLES.ENGAGEMENT_MANAGER)
  );
};

export const canUserRequestChange = user => {
  if (!user) {
    return false;
  }

  return (
    checkIfUserHasRole(user, Constants.USER_ROLES.APPROVER) ||
    checkIfUserHasRole(user, Constants.USER_ROLES.TEAM_MEMBER) ||
    checkIfUserHasRole(user, Constants.USER_ROLES.PROJECT_ADMIN)
  );
};
