import React, { useState } from 'react';
import { Button } from '@pwc/pwc-deals-ui';
import RichTextEditor from '../RichTextEditor/RichTextEditor';
import './RichTextForm.scss';
import { noHeadingEditorConfig } from '../RichTextEditor/CKEditorConfig';

const RichTextForm = ({
  richText,
  setCurrentRichText,
  handleCancel,
  handleSave,
  lightModeVariant
}) => {
  const [focused, setFocused] = useState(false);

  return (
    <div
      className={`rich-text-form${
        lightModeVariant ? ' light-mode-variant' : ''
      }`}
    >
      <RichTextEditor
        richTextData={richText}
        onChange={setCurrentRichText}
        config={noHeadingEditorConfig}
        onBlur={(_, editor) => {
          setFocused(false);
          editor.ui.view.toolbar.element.style.display = 'none';
        }}
        onFocus={(_, editor) => {
          setFocused(true);
          editor.ui.view.toolbar.element.style.display = 'block';
        }}
        isFocused={focused}
        showCharactersCounter
      />
      <div className="edit-button-container">
        <Button
          data-testid="cancel-button"
          className="button-link mx-2 mr-4"
          onClick={handleCancel}
        >
          Cancel
        </Button>
        <Button
          data-testid="save-button"
          className="button-block button-small"
          onClick={handleSave}
          disabled={!richText}
        >
          Update
        </Button>
      </div>
    </div>
  );
};

export default RichTextForm;
