import React from 'react';
import { Link } from 'react-router-dom';
import ContentActionsRowMenu from '../../../common/components/ContentActions/ContentActionsRowMenu';
import { viewHypothesisPath } from '../../../common/services/urlService';
import AccordionConnectedContent from '../../narratives/AccordionConnectedContent/AccordionConnectedContent';
import TagList from '../../analyses/common/TagList/TagList';
import PillContainer from '../../../common/components/Pills/PillContainer';
import { sanitize } from '../../../common/services/sanitizeService';
import { AnalysesAndInsightsTable } from './AnalysesAndInsightsTable';
import ConnectedKfasSection from './ConnectedKfasSection';
import './HypothesisGridCard.scss';

const HypothesisGridCard = ({
  hypothesis,
  projectId,
  onEditMenuClick,
  onDeleteMenuClick,
  index
}) => {
  const narrativesForAccordion = {
    insights: hypothesis.connectedInsights,
    analyses: hypothesis.connectedAnalyses,
    hypotheses: hypothesis.connectedKfas
  };

  return (
    <div className="card-container" data-testid={`card-container-${index}`}>
      <div
        className="hypothesis-card-contents"
        data-testid="hypothesis-card-contents"
      >
        <div className="card-details" data-testid="kfa-card-details">
          <div className="card-title" data-testid="kfa-card-title">
            <Link
              to={viewHypothesisPath(projectId, hypothesis.contentThreadId)}
              data-testid={`hypothesis-detail-grid-link-${index}`}
              aria-label="View Key Focus Area"
            >
              <h2>{hypothesis.title}</h2>
            </Link>
          </div>
          <div
            className="card-description"
            data-testid={`hypothesis-description-${index}`}
            dangerouslySetInnerHTML={{ __html: sanitize(hypothesis.body) }}
          />
          <ConnectedKfasSection
            primaryKfaIndex={index}
            connectedKfas={hypothesis.connectedKfas}
            projectId={projectId}
          />
        </div>
        <AnalysesAndInsightsTable
          projectId={projectId}
          hypothesis={hypothesis}
          hypothesisIndex={index}
        />

        <div className="card-narrative-mobile">
          <AccordionConnectedContent
            narratives={narrativesForAccordion}
            projectId={projectId}
            index={index}
            showBody
          />
        </div>
      </div>

      <div
        className="hypothesis-grid-card-footer"
        data-testid="hypothesis-grid-card-footer"
      >
        <PillContainer
          className="align-center"
          projectId={projectId}
          content={hypothesis}
          allowDraftPill
        />
        <div className="right">
          <ContentActionsRowMenu
            projectId={projectId}
            itemId={hypothesis.contentThreadId}
            onEditMenuClick={onEditMenuClick}
            onDeleteMenuClick={onDeleteMenuClick}
            direction="left"
          />
        </div>
      </div>
      {hypothesis.tags && hypothesis.tags.length > 0 && (
        <TagList tags={hypothesis.tags} testId="hypothesis-tag-list" />
      )}
    </div>
  );
};

export default HypothesisGridCard;
