import {
  ProjectHttpClient
  // ContentServiceHttpClient // TODO: uncomment when /topics EP is ready
} from '../../common/services/httpClient';

const baseServicePath = (projectId, topicThreadId, versionStatus) =>
  ['projects', projectId, 'topics', topicThreadId, versionStatus]
    .filter(Boolean)
    .join('/');

export const postTopic = async (projectId, topicData) => {
  const response = await ProjectHttpClient.post(
    baseServicePath(projectId),
    topicData
  );
  return response.data;
};

export const putTopic = async (projectId, topicThreadId, topicData) => {
  const response = await ProjectHttpClient.put(
    baseServicePath(projectId, topicThreadId),
    topicData
  );
  return response.data;
};

export const getAllTopics = async projectId => {
  // TODO: use when the /contentapi/topics EP is completely ready
  // const response = await ContentServiceHttpClient.get(
  //   `projects/${projectId}/topics`
  // );

  // return response && response.data && response.data.items
  //   ? response.data.items
  //   : [];

  const response = await ProjectHttpClient.get(baseServicePath(projectId));
  return response.data;
};

export const getTopic = async (projectId, topicThreadId, versionStatus) => {
  const response = await ProjectHttpClient.get(
    baseServicePath(projectId, topicThreadId, versionStatus)
  );
  return response.data;
};

export const deleteTopic = async (projectId, topicThreadId) => {
  const response = await ProjectHttpClient.delete(
    baseServicePath(projectId, topicThreadId)
  );

  return response.data;
};

export const updateTopicStatus = async (
  projectId,
  topicThreadId,
  workflowAction
) => {
  const url = `${baseServicePath(projectId, topicThreadId)}/${workflowAction}`;
  const response = await ProjectHttpClient.post(url);

  return response.data;
};

export const getTopicVersions = async (projectId, topicThreadId) => {
  const response = await ProjectHttpClient.get(
    `${baseServicePath(projectId, topicThreadId)}/versions`
  );

  return response.data;
};

export const updateTopicOrder = async (projectId, topicOrder) => {
  const response = await ProjectHttpClient.post(
    `${baseServicePath(projectId)}/sortorder`,
    topicOrder
  );

  return response.data;
};
