import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useParams } from 'react-router-dom';
import classnames from 'classnames';
import { formatSurveyRoute } from '../../../../../Survey/routes';
import {
  fetchSurveyResponses,
  getSurveyResponses
} from '../../../../../Survey/surveySlice';
import EnableForProjectState from '../../../../security/EnableForProjectState';
import WelcomeBox from '../WelcomeBox';

const WelcomeBoxBeginQuestionnaire = () => {
  const { projectId } = useParams();
  const dispatch = useDispatch();

  const surveyResponses = useSelector(getSurveyResponses);

  useEffect(() => {
    if (!surveyResponses) {
      dispatch(fetchSurveyResponses(projectId));
    }
  }, [dispatch, projectId, surveyResponses]);

  return (
    surveyResponses && (
      <EnableForProjectState projectId={projectId}>
        {({ isProjectDisabled }) => (
          <WelcomeBox isDisabled={isProjectDisabled}>
            <p>
              Start your project by completing the Diagnostic Questionnaire.
            </p>
            <Link
              to={!isProjectDisabled && formatSurveyRoute(projectId)}
              className={classnames('link-button', {
                'grey-button': isProjectDisabled,
                'green-text white-button': !isProjectDisabled
              })}
              aria-label="Questionnaire"
              disabled
            >
              {!surveyResponses.answers.length
                ? 'Start Questionnaire'
                : 'Continue Questionnaire'}
            </Link>
          </WelcomeBox>
        )}
      </EnableForProjectState>
    )
  );
};

export default WelcomeBoxBeginQuestionnaire;
