import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import TitleFieldset from '../../../common/components/TitleFieldset/TitleFieldset';

const AnalysisTextInput = ({
  label,
  name,
  value,
  validate,
  handleValidationChange,
  handleValueChange,
  error
}) => {
  const [formValidationErrorMessage, setFormValidationErrorMessage] = useState(
    validate(value)
  );
  const [touched, setTouched] = useState(false);

  useEffect(() => {
    handleValidationChange(formValidationErrorMessage);
  }, [formValidationErrorMessage, handleValidationChange]);

  useEffect(() => {
    setFormValidationErrorMessage(validate(value));
  }, [value, validate]);

  const onChange = event => {
    setTouched(true);
    setFormValidationErrorMessage(validate(event.target.value));
    handleValueChange(event.target.value);
  };

  const field = {
    name,
    value,
    onChange
  };

  const validationErrorMessageForDisplay = touched
    ? formValidationErrorMessage
    : '';
  const errorMessageForDisplay = error || validationErrorMessageForDisplay;

  return (
    <TitleFieldset
      label={label}
      placeholder={label}
      className="title-fieldset analysis-title"
      field={field}
      error={errorMessageForDisplay}
      onBlur={() => setTouched(true)}
    />
  );
};

AnalysisTextInput.propTypes = {
  label: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  validate: PropTypes.func,
  handleValidationChange: PropTypes.func.isRequired,
  handleValueChange: PropTypes.func.isRequired,
  error: PropTypes.string
};

AnalysisTextInput.defaultProps = {
  validate: () => '',
  error: ''
};

export default AnalysisTextInput;
