import { Button } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import EnableForProjectState from '../../security/EnableForProjectState';
import ShowForPermissions from '../../security/ShowForPermissions';
import MoreMenu from '../../../common/components/ContentActions/MoreMenu';
import { entitySharedStatusUpdateSuccess } from '../../comments/commentsSlice';
import { viewAnalysisReportPreviewPath } from '../../../common/services/urlService';

const AnalysisControlsHeader = ({
  onEdit,
  onDelete,
  onRefresh,
  deleteConfirmation,
  analysis,
  viewClient,
  workflowActions = [],
  libraryItemCorruptedError,
  onCloseErrorState,
  children,
  isShared
}) => {
  const { projectId } = useParams();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(entitySharedStatusUpdateSuccess(isShared));
  }, [dispatch, isShared]);

  const reportPreviewUrl = viewAnalysisReportPreviewPath(
    projectId,
    analysis.contentThreadId,
    viewClient.isOnClientVersion ? 'shared' : 'latest',
    analysis.isUnStructured
  );

  return (
    <div className="analysis-top-bar-container">
      <div className="analysis-top-bar">
        {children}
        <div />
        <div>
          <ShowForPermissions
            projectId={projectId}
            permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
          >
            <EnableForProjectState projectId={projectId}>
              {({ isProjectDisabled }) => (
                <Button
                  data-testid="edit-button"
                  type="button"
                  onClick={onEdit}
                  className="button-block button-small mr-3"
                  disabled={isProjectDisabled || viewClient.isOnClientVersion}
                >
                  Edit
                </Button>
              )}
            </EnableForProjectState>
            <MoreMenu
              itemId={analysis.contentThreadId}
              onDelete={onDelete}
              onRefresh={onRefresh}
              deleteConfirmation={deleteConfirmation}
              workflowActions={workflowActions}
              viewClient={viewClient}
              libraryItemCorruptedError={libraryItemCorruptedError}
              onCloseErrorState={onCloseErrorState}
              reportPreviewUrl={reportPreviewUrl}
            />
          </ShowForPermissions>
        </div>
      </div>
    </div>
  );
};

AnalysisControlsHeader.propTypes = {
  onEdit: PropTypes.func.isRequired,
  onRefresh: PropTypes.func
};

AnalysisControlsHeader.defaultProps = {
  onRefresh: undefined
};

export default AnalysisControlsHeader;
