import { getLibraryItemForStructuredContent } from '../../features/contentLibrary/libraryItemService';

export const getRichTextLongDescription = async (
  projectId,
  documentCode,
  contentId,
  contentVersionNumber,
  adjustmentRowId,
  libraryItemLinks = []
) => {
  try {
    if (libraryItemLinks.length === 0) {
      return null;
    }

    const matchingLibraryItemLink = libraryItemLinks.find(
      link => link.textKey === adjustmentRowId
    );

    if (!matchingLibraryItemLink) {
      return null;
    }

    const libraryItem = await getLibraryItemForStructuredContent(
      projectId,
      matchingLibraryItemLink.libraryItemId,
      contentId,
      contentVersionNumber
    );

    if (libraryItem === null) {
      return null;
    }

    const richTextObject = {
      relatedTextKey: adjustmentRowId,
      libraryItemId: matchingLibraryItemLink.libraryItemId,
      data: libraryItem.data.richTextData,
      childrenData: libraryItem.childrenData
    };

    return richTextObject;
  } catch (e) {
    return null;
  }
};
