import React from 'react';
import './DraftPill.scss';

const DraftPill = ({ version }) => {
  return version ? (
    <div className="in-progress-pill" data-testid="draft-pill">
      {version > 1 ? `v${version} In progress` : 'In progress'}
    </div>
  ) : null;
};

export default DraftPill;

export const DraftPillClientView = () => {
  return (
    <div className="draft-pill" data-testid="draft-pill">
      Draft
    </div>
  );
};
