import { createSlice } from '@reduxjs/toolkit';
import { getAllProjects } from '../../common/services/projectService';

export const initialState = {
  projectsList: []
};

const projectsSlice = createSlice({
  name: 'projects',
  initialState,
  reducers: {
    fetchProjectsEnded: (state, action) => ({
      ...state,
      projectsList: action.payload
    })
  }
});
export default projectsSlice.reducer;

// Actions
export const { fetchProjectsEnded } = projectsSlice.actions;

// Selectors
export const getProjectsList = state =>
  state.projects && state.projects.projectsList;

// Thunks
export const loadProjectsThunk = () => async dispatch => {
  const projects = await getAllProjects();
  dispatch(fetchProjectsEnded(projects));
};
