import { Button, Icon } from '@pwc/pwc-deals-ui';
import React from 'react';

const WarningCard = ({
  warning: { text, secondaryText, leftButton, rightButton },
  onAccept,
  onCancel
}) => {
  return (
    <div className="project-state-change">
      <div className="project-state-change-warning">
        <div className="project-state-change-warning--body">
          <Icon className="fill icon-yellow" width="24" name="alert" />
          <div className="project-state-change-warning--text-container">
            <div
              className="project-state-change-warning--text-area"
              data-testid="warning-card-text"
            >
              {text}
            </div>
            <div
              className="project-state-change-warning--text-area"
              data-testid="warning-card-secondary-text"
            >
              {secondaryText}
            </div>
          </div>
        </div>
      </div>
      <div className="project-state-change--buttons">
        <Button
          className="transparent-button"
          onClick={onCancel}
          data-testid="warning-card-cancel-button"
        >
          {leftButton}
        </Button>
        {rightButton && (
          <Button data-testid="warning-card-approve-button" onClick={onAccept}>
            {rightButton}
          </Button>
        )}
      </div>
    </div>
  );
};

export default WarningCard;
