import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import DraftPill from './DraftPill';
import SharedPill from './SharedPill';

const VersionStatus = ({ content, allowDraftPill = true }) => {
  const draftStatuses = [
    Constants.VERSION_STATUS.DRAFT,
    Constants.VERSION_STATUS.APPROVAL_REJECTED
  ].map(_ => _.value);
  if (!content) return null;

  const draftVersionNumber = draftStatuses.includes(content.versionStatus)
    ? content.versionNumber
    : null;

  const sharedVersionNumber =
    // eslint-disable-next-line no-nested-ternary
    content.versionStatus === Constants.VERSION_STATUS.SHARED.value
      ? content.versionNumber
      : draftVersionNumber && draftVersionNumber > 1
      ? draftVersionNumber - 1
      : null;

  return (
    <>
      <SharedPill version={sharedVersionNumber} />
      {allowDraftPill && <DraftPill version={draftVersionNumber} />}
    </>
  );
};

export default VersionStatus;
