/* eslint-disable no-underscore-dangle */
import { ClassicEditor } from '@pwc/pwc-deals-rich-text-editor';
import authorizationService from '../../services/security/authorizationService';

const { CommentView } = ClassicEditor;

export const createVersionDisplay = (commentThreads, model) => {
  const thread = commentThreads.find(t => t.id === model.threadId);
  const comment =
    thread &&
    thread.comments.find((c, index) => c.commentId === model.id && index === 0);
  return comment && comment.entityVersion > 0
    ? ` | v${comment.entityVersion}`
    : '';
};

const getCommentAuthorClass = (users, model) => {
  const user = users.find(u => u.id.toString() === model.author.id);

  if (user) {
    if (authorizationService.isClientUser(user.email)) {
      return 'comment-client-user';
    }

    return 'comment-pwc-user';
  }

  return 'comment-unknown-user';
};

export default (commentThreads, users, isProjectDisabled) =>
  class CustomCommentView extends CommentView {
    _getTemplate() {
      const versionDisplay = createVersionDisplay(commentThreads, this._model);

      const templateDefinition = super._getTemplate();

      const buttonsArray =
        templateDefinition.children[0].children[1].children[1].children;
      const editButton = buttonsArray[0];
      const removeButton = buttonsArray[1];

      const avatarClass = getCommentAuthorClass(users, this._model);
      const commentContainer = templateDefinition.children[0];
      commentContainer.attributes = {
        ...commentContainer.attributes,
        class: [...commentContainer.attributes.class, avatarClass]
      };

      const userAndTimeContainer =
        templateDefinition.children[0].children[1].children[0];
      userAndTimeContainer.attributes = {
        style: { display: 'flex', 'flex-direction': 'column' }
      };

      const userDiv = userAndTimeContainer.children[0];
      userDiv.children.push({
        tag: 'span',
        children: [`${versionDisplay}`],
        attributes: { class: 'entity-version' }
      });

      const timeDiv = userAndTimeContainer.children[1];
      timeDiv.attributes = {
        ...timeDiv.attributes,
        style: { margin: '-4px 0px 0px 0px' }
      };

      const contentContainer =
        templateDefinition.children[0].children[1].children[2];
      contentContainer.attributes = {
        ...contentContainer.attributes,
        style: { 'margin-top': '8px' }
      };

      const editIcon =
        '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path d="M0 19v5h5L19.747 9.253l-5-5L0 19zM23.613 5.387c.52-.52.52-1.36 0-1.88l-3.12-3.12c-.52-.52-1.36-.52-1.88 0l-2.44 2.44 5 5 2.44-2.44z"/></svg>';
      const removeIcon =
        '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path d="M4 10.062h24l-.35 17.69a4.318 4.318 0 01-1.303 3.01A4.326 4.326 0 0123.312 32H8.854a4.284 4.284 0 01-2.397-.722 4.324 4.324 0 01-1.94-3.526L4 10.062zm19-8.06h5.999a2 2 0 110 4.001H3a2 2 0 110-4.002H9A2 2 0 0111.001 0H21C22.104 0 23 .896 23 2.001z"/></svg>';

      editButton.set({
        icon: editIcon,
        isEnabled: !isProjectDisabled
      });
      removeButton.set({ icon: removeIcon, isEnabled: !isProjectDisabled });

      return templateDefinition;
    }
  };
