import { Button } from '@pwc/pwc-deals-ui';
import { Field, Form } from 'formik';
import React from 'react';

import D3Alert from '../../../common/components/Alert/D3Alert';
import ActiveContentDraftHeader from '../../../common/components/ContentHeaders/ActiveContentDraftHeader';
import FormLayout from '../../../common/components/layouts/FormLayout';
import PublishButton from '../../../common/components/PublishButton';
import RichTextEditorFormikField from '../../../common/components/RichTextEditorFormikField/RichTextEditorFormikField';
import SourceInput from '../../../common/components/SourceInput';
import TitleFieldset from '../../../common/components/TitleFieldset/TitleFieldset';
import LibraryItemSelect from '../../contentLibrary/LibraryItemSelect/LibraryItemSelect';
import EditConnectedContent from '../../narratives/EditConnectedContent/EditConnectedContent';
import TagEditor from '../../tagging/common/TagEditor/TagEditor';
import LinkedAdjustments from '../structured-analysis/common/LinkedAdjustments/LinkedAdjustments';
import NotesInput from './NotesInput';
import './AnalysisForm.scss';
import Separator from '../../../common/components/Separator';

const AnalysisForm = ({
  documentCode = '',
  onCancel,
  isSaving,
  errorType,
  errors,
  touched,
  isValid,
  children,
  values,
  setFieldValue,
  canEditTitle = true,
  showLibraryItemPicker = true,
  showLinkedAdjustments = false,
  showNotes = false,
  projectId,
  parentContentId,
  narratives,
  setNarratives,
  adjustmentOptions,
  targetDocumentOptions,
  dirty: _dirty,
  onSaveText,
  standbyText,
  ...rest
}) => {
  const validateTitle = value => {
    let error;
    if (!value || value.trim().length === 0) {
      error = 'Title cannot be blank';
    }
    return error;
  };

  return (
    <FormLayout
      headerComponent={
        // eslint-disable-next-line react/jsx-wrap-multilines
        <ActiveContentDraftHeader
          onCancel={onCancel}
          isSaving={isSaving}
          errors={errors}
          touched={touched}
          isValid={isValid}
          dirty
          onSaveText={onSaveText}
          standbyText={standbyText}
        />
      }
    >
      {children}

      <Form
        data-testid="analysis-form"
        className="analysis-form mt-5"
        {...rest}
      >
        {errors.title && touched.title && <div>{errors.title}</div>}
        <div className="form-fields">
          <div className="input-container">
            <Field
              name="title"
              component={TitleFieldset}
              label="Title"
              placeholder="Start typing here to give your new Analysis a title"
              className="title-fieldset"
              disabled={!canEditTitle}
              validate={validateTitle}
            />

            <Field
              name="body"
              component={RichTextEditorFormikField}
              label="Description (optional)"
              dataTestId="text-area-body"
            />

            <div className="analysis-form-extras">
              <Field name="source" component={SourceInput} />

              {showNotes && <Field name="notes" component={NotesInput} />}

              {showLibraryItemPicker && (
                <Field name="libraryItemId" component={LibraryItemSelect} />
              )}

              {showLinkedAdjustments && (
                <LinkedAdjustments
                  adjustmentOptions={adjustmentOptions}
                  targetDocumentOptions={targetDocumentOptions}
                  formValues={values.linkedAdjustments}
                  setFieldValue={setFieldValue}
                  documentCode={documentCode}
                />
              )}
              <TagEditor projectId={projectId} />
            </div>
          </div>
        </div>

        <EditConnectedContent
          projectId={projectId}
          parentContentId={parentContentId}
          narratives={narratives}
          setNarratives={setNarratives}
        />

        <Separator />

        <div className="form-bottom-actions">
          <Button
            className="button-link form-cancel-button"
            type="button"
            onClick={onCancel}
            data-testid="cancel-button-bottom"
          >
            Cancel
          </Button>
          <PublishButton
            isSaving={isSaving}
            isValid={isValid}
            buttonPosition="bottom"
            onSaveText="Updating..."
            standbyText="Update"
          />
        </div>
        {errorType && <D3Alert configType={errorType} />}
      </Form>
    </FormLayout>
  );
};

export default AnalysisForm;
