import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { viewHypothesisPath } from '../../../common/services/urlService';
import { DisplayableContent } from './ConnectedKfaPropTypes';
import './ConnectedKfasSection.scss';

const ConnectedKfasSection = ({
  primaryKfaIndex,
  connectedKfas,
  projectId
}) => {
  return (
    connectedKfas.length > 0 && (
      <div
        className="connected-kfas-section"
        data-testid="connected-kfas-section"
      >
        <h4
          className="connected-kfas-section-heading"
          data-testid="connected-kfas-section-heading"
        >
          Connected Key Focus Areas
        </h4>
        {connectedKfas.map((connectedKfa, connectedKfaIndex) => (
          <h5
            data-testid={`kfa-connected-kfa-title-${primaryKfaIndex}-${connectedKfaIndex}`}
            className="kfa-connected-kfa-title"
            key={connectedKfa.contentId}
          >
            <Link
              data-testid={`kfa-connected-kfa-link-${primaryKfaIndex}-${connectedKfaIndex}`}
              to={viewHypothesisPath(projectId, connectedKfa.contentThreadId)}
              className="kfa-connected-kfa-link"
            >
              {connectedKfa.title}
            </Link>
          </h5>
        ))}
      </div>
    )
  );
};

ConnectedKfasSection.propTypes = {
  primaryKfaIndex: PropTypes.number.isRequired,
  connectedKfas: PropTypes.arrayOf(PropTypes.shape(DisplayableContent))
    .isRequired,
  projectId: PropTypes.string.isRequired
};
export default ConnectedKfasSection;
