import React from 'react';
import classnames from 'classnames';
import { Constants } from '@pwc/pwc-deals-web-common';
import ShowForRole from '../../../features/security/ShowForRole';
import UserViewStatusPill from './UserViewStatusPill';
import VersionStatus from './VersionStatus';
import { DraftPillClientView } from './DraftPill';
import ApprovalPill from './ApprovalPill';
import './PillContainer.scss';

const PillContainer = ({ className, content, projectId, allowDraftPill }) => {
  return (
    <div
      className={classnames('pill-container', className)}
      data-testid="pill-container"
    >
      <UserViewStatusPill userViewStatus={content.userViewStatus} />
      <ShowForRole
        projectId={projectId}
        roles={Constants.USER_ROLE_GROUPS.INTERNAL}
      >
        <VersionStatus content={content} allowDraftPill={allowDraftPill} />
      </ShowForRole>
      <ShowForRole
        projectId={projectId}
        roles={Constants.USER_ROLE_GROUPS.CLIENT}
      >
        <DraftPillClientView />
      </ShowForRole>
      <ApprovalPill versionStatus={content.versionStatus} />
    </div>
  );
};

export default PillContainer;
