import { Constants } from '@pwc/pwc-deals-web-common';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import StatusPill from '../../../common/components/Pills/StatusPill';
import { requestFileStatusChange } from '../../../common/redux/fileUpload/fileUploadThunk';
import userTokenService from '../../../common/services/security/userTokenService';
import CONSTANTS from '../Constants';
import CancelRequest from './CancelRequest';
import RequestApproval from './RequestApproval';
import Approve from './Approve';
import Decline from './Decline';
import './FileStatus.scss';

import SharedFile from './SharedFile/SharedFile';
import ApprovedFile from './ApprovedFile/ApprovedFile';

const FileStatus = ({ file }) => {
  const dispatch = useDispatch();
  const { status, fileId } = file || { status: '', fileId: 0 };
  const projectId = useSelector(state => state.project && state.project.id);
  const [isTeamMember, setIsTeamMember] = useState(true);
  const [isReadOnly, setIsReadOnly] = useState(true);

  useEffect(() => {
    const getPermissions = async () => {
      setIsTeamMember(
        await userTokenService.hasProjectRoles(projectId, [
          Constants.USER_ROLES.TEAM_MEMBER
        ])
      );
      setIsReadOnly(
        await userTokenService.hasProjectRoles(projectId, [
          Constants.USER_ROLES.PWC_READONLY
        ])
      );
    };
    getPermissions().then();
  }, [projectId]);

  const translateReadOnlyStatus = () => {
    // see https://wwtas-pwcdealsos.atlassian.net/browse/PWC-5471
    switch (status) {
      case CONSTANTS.STATUSES.CAN_REQUEST_APPROVAL:
        return CONSTANTS.CONTENTS.TEXT_STATUSES.CAN_REQUEST_APPROVAL;
      case CONSTANTS.STATUSES.CAN_BE_APPROVED_OR_DECLINED:
        return CONSTANTS.CONTENTS.TEXT_STATUSES.CAN_BE_APPROVED_OR_DECLINED;
      case CONSTANTS.STATUSES.APPROVED:
        return CONSTANTS.CONTENTS.TEXT_STATUSES.APPROVED;
      default:
        return status;
    }
  };

  switch (status) {
    case CONSTANTS.STATUSES.SHARED:
      return <SharedFile />;
    case CONSTANTS.STATUSES.CAN_REQUEST_APPROVAL:
      if (isReadOnly) break;
      return (
        <RequestApproval
          onClick={() =>
            dispatch(
              requestFileStatusChange(
                projectId,
                fileId,
                CONSTANTS.OPERATIONS.CAN_REQUEST_APPROVAL
              )
            )
          }
        />
      );
    case CONSTANTS.STATUSES.CAN_BE_APPROVED_OR_DECLINED: {
      if (isReadOnly) break;
      if (isTeamMember) {
        return (
          <CancelRequest
            onClick={() =>
              dispatch(
                requestFileStatusChange(
                  projectId,
                  fileId,
                  CONSTANTS.OPERATIONS.CAN_BE_APPROVED_OR_DECLINED.CANCEL
                )
              )
            }
          />
        );
      }
      return (
        <div className="approve-decline">
          <Approve
            onClick={() =>
              dispatch(
                requestFileStatusChange(
                  projectId,
                  fileId,
                  CONSTANTS.OPERATIONS.CAN_BE_APPROVED_OR_DECLINED.APPROVED
                )
              )
            }
          />
          <div className="divider" />
          <Decline
            onClick={() =>
              dispatch(
                requestFileStatusChange(
                  projectId,
                  fileId,
                  CONSTANTS.OPERATIONS.CAN_BE_APPROVED_OR_DECLINED.REJECTED
                )
              )
            }
          />
        </div>
      );
    }
    case CONSTANTS.STATUSES.APPROVED:
      if (isReadOnly) break;
      return <ApprovedFile file={file} />;
    default:
      return (
        <div>
          <StatusPill title={translateReadOnlyStatus()} />
        </div>
      );
  }

  return (
    <div>
      <StatusPill title={translateReadOnlyStatus()} />
    </div>
  );
};

export default FileStatus;
