import React, { Fragment } from 'react';
import { Form, Formik } from 'formik';
import { Constants } from '@pwc/pwc-deals-web-common';
import Separator from '../../common/components/Separator';
import {
  ProjectRoleUsersField,
  userPillId
} from '../../common/components/ProjectRoleUsersField/ProjectRoleUsersField';
import FormBottomActionsContainer from '../project/CreateProject/common/components/FormBottomActionsContainer';
import CancelButtonLink from '../project/CreateProject/common/components/CancelButtonLink';
import PublishButton from '../../common/components/PublishButton';
import D3Alert from '../../common/components/Alert/D3Alert';
import './UserEditSettings.scss';

const getDefaultInitialValues = () =>
  Constants.USER_ROLES_LIST.reduce(
    (init, role) => ({
      ...(init || {}),
      [role]: []
    }),
    {}
  );

export const getInitialState = (existingUsers = []) =>
  existingUsers
    .map(user => ({
      ...user,
      role: user.roles[0].name,
      pendingApproval: user.roles[0].pendingApproval,
      wasPending: user.roles[0].pendingApproval
    }))
    .map(user => ({ ...user, pillId: userPillId(user) }))
    .reduce(
      (vals, user) => ({
        ...vals,
        [user.role]: [...(vals[user.role] || []), user]
      }),
      getDefaultInitialValues()
    );

const UserEditSettings = ({
  initialValues = {},
  fieldConfigs = [],
  onSubmit,
  submitButtonText = 'Save',
  onCancel,
  children,
  isSaving,
  hasError,
  isDirty
}) => {
  const flattenValuesWrapper = fn => values => {
    const formattedValues = Constants.USER_ROLES_LIST.map(
      role => values[role]
    ).flat();
    return fn(formattedValues);
  };

  const checkIsValid = values => {
    const requiredConfigs = fieldConfigs.filter(fc => fc.required);
    const isValid = requiredConfigs.every(({ role, allowMany }) =>
      allowMany
        ? (values[role] || []).length > 0
        : (values[role] || []).length === 1
    );
    return isValid;
  };

  return (
    <div className="project-user-form">
      <div className="project-user-add-header">{children}</div>
      <div className="project-user-grid-headings">
        <span>Role</span>
        <span>
          {fieldConfigs.some(fc => fc.showEmail)
            ? 'Name / Email address'
            : 'Name'}
        </span>
      </div>
      <Separator />
      <Formik {...{ initialValues, onSubmit: flattenValuesWrapper(onSubmit) }}>
        {({ dirty, values, resetForm }) => {
          return (
            <Form>
              {fieldConfigs.map(fieldConfig => (
                <Fragment key={fieldConfig.role}>
                  <ProjectRoleUsersField {...fieldConfig} />
                  <Separator />
                </Fragment>
              ))}
              <FormBottomActionsContainer>
                {onCancel && (
                  <CancelButtonLink onClick={() => onCancel(resetForm)} />
                )}
                {onSubmit && (
                  <PublishButton
                    isValid={checkIsValid(values)}
                    isSaving={isSaving}
                    dirty={isDirty === undefined ? dirty : isDirty}
                    buttonSize="sm"
                    standbyText={
                      typeof submitButtonText === 'string'
                        ? submitButtonText
                        : flattenValuesWrapper(submitButtonText)(values)
                    }
                    buttonPosition="project-user-details"
                  />
                )}
              </FormBottomActionsContainer>
              {hasError && (
                <div>
                  <br />
                  <D3Alert />
                </div>
              )}
            </Form>
          );
        }}
      </Formik>
    </div>
  );
};
export default UserEditSettings;
