import React, { useState, useEffect } from 'react';
import { Button } from '@pwc/pwc-deals-ui';
import { ProjectListLayout } from '../../../common/components/layouts';
import { getAllProjects, hideProject, showProject } from './adminService';
import './AdminProjectList.scss';

const ProjectRow = ({ project, onHide, onShow }) => {
  const onClick = () =>
    project.deleted ? onShow(project.id) : onHide(project.id);

  return (
    <div className="project-row">
      <div className="project-panel">
        <div className="project-panel-row">
          <div className="project-title-description-container">
            {project.name}
            <Button
              className="button-menu"
              onClick={onClick}
              data-testid="menu-button-hide"
              disabled={!onClick}
            >
              {project.deleted ? 'Show' : 'Hide'}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

const AdminProjectList = () => {
  const [isDirty, setIsDirty] = useState(true);
  const [projects, setProjects] = useState([]);

  useEffect(() => {
    if (isDirty) {
      const getProjects = async () => {
        const allProjects = await getAllProjects();
        setIsDirty(false);
        setProjects(allProjects);
      };
      getProjects();
    }
  }, [isDirty]);

  const onHide = projectId => {
    hideProject(projectId).then(_ => setIsDirty(true));
  };

  const onShow = projectId => {
    showProject(projectId).then(_ => setIsDirty(true));
  };

  const displayedProjects = projects.map(p => (
    <ProjectRow project={p} key={p.id} onHide={onHide} onShow={onShow} />
  ));

  return (
    <ProjectListLayout>
      <div className="admin-project-list">
        <h2>All Projects</h2>
        <div className="project-container">{displayedProjects}</div>
      </div>
    </ProjectListLayout>
  );
};

export default AdminProjectList;
