import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, useHistory, useParams } from 'react-router-dom';

import { alertTypes } from '../../../common/components/Alert/AlertConfigs';
import D3Alert from '../../../common/components/Alert/D3Alert';
import CommentPanelContainer from '../../../common/components/CommentPanel/CommentPanelContainer';
import authorizationService from '../../../common/services/security/authorizationService';
import { projectDisclaimerPath } from '../../../common/services/urlService';
import AnalysesContentRouter from '../../analyses/AnalysesContentRouter';
import AnalysesContentRouter2 from '../../analyses2/AnalysesContentRouter2';
import DivestitureDiagnosticContentRouter from '../../divestitureDiagnostic/DivestitureDiagnosticContentRouter';
import HypothesesContentRouter from '../../hypotheses/HypothesesContentRouter';
import InsightsContentRouter from '../../insights/InsightsContentRouter';
import NotificationsContentRouter from '../../notifications/NotificationsContentRouter/NotificationsContentRouter';
import ProjectDisclaimerRouter from '../../projectDisclaimer/ProjectDisclaimerRouter';
import {
  getProjectDisclaimerSelector,
  loadProjectDisclaimerThunk
} from '../../projectDisclaimer/projectDisclaimerSlice';
import ReportsContentRouter from '../../reports/ReportsContentRouter';
import SettingsContentRouter from '../../settings/SettingsContentRouter';
import FileUploadContentRouter from '../../fileUpload/FileUploadContentRouter';
import {
  getProjectError,
  getProjectErrorStatusCode,
  getProjectLoading,
  loadProjectThunk
} from '../projectSlice';
import ProjectOverviewContentRouter from './ProjectOverviewContentRouter';
import './ProjectDetails.scss';

const isClientErrorCode = code => code >= 400 && code < 500;

const ProjectDetails = () => {
  const { projectId } = useParams();
  const isLoading = useSelector(getProjectLoading);
  const projectDisclaimer = useSelector(
    getProjectDisclaimerSelector(projectId)
  );
  const hasError = useSelector(getProjectError);
  const errorStatusCode = useSelector(getProjectErrorStatusCode);
  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    dispatch(loadProjectThunk(projectId));
    dispatch(loadProjectDisclaimerThunk(projectId));
  }, [dispatch, projectId]);

  if (hasError && isClientErrorCode(errorStatusCode)) {
    return null;
  }

  if (hasError && !isClientErrorCode(errorStatusCode)) {
    return <D3Alert configType={alertTypes.fetch} />;
  }

  if (authorizationService.isClientUser()) {
    if (!projectDisclaimer || projectDisclaimer.isLoading) {
      return null;
    }
    if (
      !projectDisclaimer.hasAccepted &&
      history.location.pathname !== projectDisclaimerPath(projectId)
    ) {
      return (
        <Redirect
          to={{
            pathname: projectDisclaimerPath(projectId),
            state: { from: history.location.pathname }
          }}
        />
      );
    }
  }

  return (
    <>
      <CommentPanelContainer projectId={projectId} />
      <div className="page-content">
        {isLoading}
        <div className="router-content" data-testid="project-details-content">
          <ProjectDisclaimerRouter />
          <ProjectOverviewContentRouter />
          <HypothesesContentRouter />
          <InsightsContentRouter />
          <AnalysesContentRouter />
          <AnalysesContentRouter2 />
          <SettingsContentRouter />
          <ReportsContentRouter />
          <NotificationsContentRouter />
          <DivestitureDiagnosticContentRouter />
          <FileUploadContentRouter />
        </div>
      </div>
    </>
  );
};
export default ProjectDetails;
