import React, { useEffect, useState } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { useDispatch, useSelector } from 'react-redux';
import { requestClientUsersUpdate } from '../../common/services/projectService';
import { getProject, loadProjectThunk } from '../project/projectSlice';
import UserEditSettings, { getInitialState } from './UserEditSettings';

const getUserListDifferences = (usersList1, usersList2) => {
  const usersList1Map = usersList1.reduce(
    (ul1Map, ul1) => ({
      ...ul1Map,
      [ul1.email]: ul1
    }),
    {}
  );
  return usersList2.filter(
    ul2 =>
      !usersList1Map[ul2.email] || usersList1Map[ul2.email].role !== ul2.role
  );
};

const getUsersToRemove = (oldClientUsers, newClientUsers) =>
  getUserListDifferences(newClientUsers, oldClientUsers).map(userToRemove => ({
    emailAddress: userToRemove.email
  }));

const getUsersToAdd = (oldClientUsers, newClientUsers) =>
  getUserListDifferences(oldClientUsers, newClientUsers).map(userToAdd => ({
    emailAddress: userToAdd.email,
    roleName: userToAdd.role
  }));

const getUpdateClientUsersRequest = (fieldConfigs, initialValues, newUsers) => {
  const rolesToConsider = fieldConfigs.map(fc => fc.role);
  const oldClientUsers = rolesToConsider
    .map(rtc => initialValues[rtc] || [])
    .flat();
  const newClientUsers = newUsers.filter(nu =>
    rolesToConsider.includes(nu.role)
  );
  const usersToRemove = getUsersToRemove(oldClientUsers, newClientUsers);
  const usersToAdd = getUsersToAdd(oldClientUsers, newClientUsers);
  return { usersToRemove, usersToAdd };
};

const ClientSettingsClientView = () => {
  const project = useSelector(getProject);
  const dispatch = useDispatch();
  const [initialValues, setInitialValues] = useState(null);
  const [initialValuesSet, setInitialValuesSet] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [hasError, setHasError] = useState(false);

  useEffect(() => {
    if (!initialValuesSet) {
      if (project.users) {
        setInitialValues(getInitialState(project.users));

        setInitialValuesSet(true);
      } else {
        setInitialValues(getInitialState());
      }
    }
  }, [project, setInitialValues, initialValuesSet, setInitialValuesSet]);

  const fieldConfigs = [
    {
      role: Constants.USER_ROLES.CLIENT_ADMIN,
      title: Constants.USER_ROLES.CLIENT_ADMIN,
      description: `View approved content, comment and assign client team members`,
      showEmail: true,
      allowMany: true,
      addAsPending: true
    },
    {
      role: Constants.USER_ROLES.CLIENT_TEAM_MEMBER,
      title: Constants.USER_ROLES.CLIENT_TEAM_MEMBER,
      description: `View approved content and comment`,
      showEmail: true,
      allowMany: true,
      addAsPending: true
    }
  ];
  const checkIsValid = () => true;

  const onSubmit = async newUsers => {
    setIsSaving(true);
    try {
      setHasError(false);

      const updateClientUsersRequest = getUpdateClientUsersRequest(
        fieldConfigs,
        initialValues,
        newUsers
      );
      if (
        updateClientUsersRequest.usersToRemove.length ||
        updateClientUsersRequest.usersToAdd.length
      ) {
        await requestClientUsersUpdate(project.id, updateClientUsersRequest);
        await dispatch(loadProjectThunk(project.id));
      }

      setIsSaving(false);
      setInitialValuesSet(false);
    } catch (e) {
      setIsSaving(false);
      setHasError(true);
    }
  };

  const onCancel = resetForm => resetForm();

  const submitButtonText = newUsers => {
    const updateClientUsersRequest = getUpdateClientUsersRequest(
      fieldConfigs,
      initialValues,
      newUsers
    );
    if (
      updateClientUsersRequest.usersToAdd.length &&
      updateClientUsersRequest.usersToRemove.length
    ) {
      return 'Request Access and Save';
    }
    if (updateClientUsersRequest.usersToAdd.length) {
      return 'Request Access';
    }
    return 'Save';
  };
  return (
    initialValuesSet && (
      <UserEditSettings
        initialValues={initialValues}
        fieldConfigs={fieldConfigs}
        onSubmit={onSubmit}
        onCancel={onCancel}
        submitButtonText={submitButtonText}
        checkIsValid={checkIsValid}
        isSaving={isSaving}
        hasError={hasError}
      >
        <div data-testid="clientSettingsClientView">
          <p>
            You can request access for your team by entering their email address
            next to the assigned role.
          </p>
        </div>
      </UserEditSettings>
    )
  );
};
export default ClientSettingsClientView;
