import React from 'react';
import { useDispatch } from 'react-redux';
import { openModal } from '../../../../common/redux/fileUpload/fileUploadSlice';
import FileListItemRowMenu from './FileListItemRowMenu';

const FileOptions = ({ file }) => {
  const dispatch = useDispatch();
  const { fileId, fileName } = file;

  const onUpdateMenuClick = () => {
    dispatch(openModal({ fileId, fileName }));
  };

  return (
    <FileListItemRowMenu
      itemId={fileId}
      onUpdateMenuClick={onUpdateMenuClick}
      filePassword={file.password}
      direction="left"
    />
  );
};

export default FileOptions;
