import React, { useMemo, useState } from 'react';
import classNames from 'classnames';

import ToggleRightPanelButton from './ToggleRightPanelButton';

import './RightPanelTabs.scss';

const RightPanelTab = ({ label, onClick, isActive }) => (
  <li
    onClick={onClick}
    onKeyDown={e => {
      if (['Enter', ' '].includes(e.key)) {
        e.preventDefault();
        onClick();
      }
    }}
    className={classNames('report-panel-tabs__list-item', {
      'report-panel-tabs__list-item--active': isActive
    })}
    tabIndex="0"
    // eslint-disable-next-line jsx-a11y/no-noninteractive-element-to-interactive-role
    role="button"
  >
    {label}
  </li>
);

const RightPanelTabs = ({
  tabs = [],
  initialActiveTab = 0,
  sectionHeight,
  panelExpanded = true
}) => {
  const [activeTab, setActiveTab] = useState(initialActiveTab);

  const activeTabContent = useMemo(
    () => tabs[activeTab] && tabs[activeTab].content,
    [activeTab, tabs]
  );

  const contentStyle = useMemo(() => {
    return {
      height: sectionHeight
    };
  }, [sectionHeight]);

  return (
    <div className="report-panel-tabs">
      <ul className="report-panel-tabs__list">
        {tabs.map(({ label }, index) => (
          <RightPanelTab
            // eslint-disable-next-line react/no-array-index-key
            key={`${index}-${label}`}
            label={label}
            onClick={() => setActiveTab(index)}
            isActive={activeTab === index}
          />
        ))}
        <li className="report-panel-tabs__list-toggle">
          <ToggleRightPanelButton expanded={panelExpanded} />
        </li>
      </ul>
      {panelExpanded && (
        <div
          data-testid="report-tabs-content"
          className="report-tabs-content"
          style={contentStyle}
        >
          {activeTabContent}
        </div>
      )}
    </div>
  );
};

export default RightPanelTabs;
