import { get as _get } from 'lodash';
import { ReportComponent } from '@pwc/pwc-deals-report-ui';
import { Route } from 'react-router-dom';
import React from 'react';
import {
  isProjectArchived,
  isProjectInactive
} from '../../common/helpers/projectStatusesUtils';
import {
  getProjectStatus,
  useProjectsStatuses
} from '../../common/hooks/useProjectsStatuses';

const ReportsContentRouter = () => {
  const projectsStates = useProjectsStatuses();

  return (
    <Route
      path="/projects/:projectid/reports/"
      render={props => {
        const projectId = _get(props, ['match', 'params', 'projectid']);

        const projectStatus = getProjectStatus(projectsStates, projectId);
        const isProjectDisabled =
          projectId &&
          (isProjectInactive(projectStatus) ||
            isProjectArchived(projectStatus));

        return (
          <ReportComponent {...props} isProjectDisabled={isProjectDisabled} />
        );
      }}
    />
  );
};

export default ReportsContentRouter;
