import { Constants } from '@pwc/pwc-deals-web-common';
import { get } from 'lodash';

export const mapTopicPanels = ({
  topicLayoutTypeValue,
  contentTypeArray,
  richTextArray,
  hypothesisArray,
  excelChartIdArray
}) => {
  let numberOfPanels;

  switch (topicLayoutTypeValue) {
    case 18:
      numberOfPanels = 3;
      break;
    case 15:
      numberOfPanels = 2;
      break;
    case 14:
      numberOfPanels = 2;
      break;
    case 11:
      numberOfPanels = 2;
      break;
    case 8:
      numberOfPanels = 3;
      break;
    case 3:
      numberOfPanels = 3;
      break;
    case 2:
      numberOfPanels = 2;
      break;
    default:
      numberOfPanels = 1;
  }
  const placeholderArray = Array(numberOfPanels).fill(null);
  return placeholderArray.map((_, i) => {
    const panel = {
      panelNumber: i + 1
    };
    switch (contentTypeArray[i]) {
      case Constants.TOPIC_PANEL_TYPES.RICH_TEXT.asString:
        panel.richText = richTextArray[i];
        panel.type = Constants.TOPIC_PANEL_TYPES.RICH_TEXT.value;
        break;
      case Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString:
        panel.hypothesisThreadId = get(
          hypothesisArray,
          [i, 'contentThreadId'],
          null
        );
        panel.type = Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.value;
        break;
      case Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString:
      default:
        panel.excelChartId = excelChartIdArray[i];
        panel.type = Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.value;
        break;
    }
    return panel;
  });
};

const mapTopicLinks = (hypothesisArray, contentTypeArray) =>
  hypothesisArray
    .filter(
      (h, i) =>
        h &&
        contentTypeArray[i] ===
          Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString
    )
    .map(h => h.contentId);

export const mapTopic = ({
  topicLayoutTypeValue,
  richTextArray,
  hypothesisArray = [],
  contentTypeArray,
  excelChartIdArray,
  topicTitle
}) => {
  const topicToSaveDto = {
    title: topicTitle,
    layoutType: topicLayoutTypeValue
  };

  topicToSaveDto.panels = mapTopicPanels({
    contentTypeArray,
    richTextArray,
    hypothesisArray,
    excelChartIdArray,
    topicLayoutTypeValue
  });

  topicToSaveDto.links = mapTopicLinks(hypothesisArray, contentTypeArray);

  return topicToSaveDto;
};
