import React, { useState, useRef } from 'react';
import { Overlay, Popover } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { Icon, Button } from '@pwc/pwc-deals-ui';
import './AddProjectItemsHeaderMenu.scss';
import { number } from 'prop-types';
import {
  addAnalysis2TypePath,
  addAnalysisTypePath,
  createHypothesisPath,
  createInsightPath,
  freeForm20Enabled
} from '../../../services/urlService';

const iconSize = 24;

const AddProjectItemsHeaderMenu = ({ projectId }) => {
  const addHypothesisHref = createHypothesisPath(projectId);
  const addInsightHref = createInsightPath(projectId);
  const addAnalysisTypeHref = addAnalysisTypePath(projectId);
  const addAnalysis2TypeHref = addAnalysis2TypePath(projectId);

  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);

  const handleClick = event => {
    setShow(!show);
    setTarget(event.target);
  };

  const handleClose = () => {
    setShow(false);
    setTimeout(() => {
      document.body.click();
    }, 200);
  };

  return (
    <>
      <div className="add-project-items-header-menu" ref={ref}>
        <Button
          data-testid="header-fab"
          id="header-fab"
          className="button-avoid-focus add-icon button-link add-popover-button"
          onClick={handleClick}
          title="Add Content"
        >
          <Icon name="add" className="fill icon-orange" width={32} />
        </Button>
        <Overlay
          show={show}
          target={target}
          placement="bottom"
          rootClose
          onHide={() => {
            setShow(!show);
          }}
          container={ref.current}
          popperConfig={{
            modifiers: [
              {
                name: 'offset',
                options: {
                  offset: [0, 12]
                }
              }
            ]
          }}
        >
          <Popover
            id="popover-add"
            ref={ref}
            className="header-add-popover"
            data-testid="header-add-popover"
          >
            <Popover.Content className="popup-user">
              <Link
                data-testid="add-hypothesis-button"
                className="dropdown-item"
                to={addHypothesisHref}
                onClick={handleClick}
              >
                <Icon
                  name="hypothesis"
                  width={iconSize}
                  className="outline icon-orange-theme"
                />
                Add a Key Focus Area
              </Link>
              <Link
                data-testid="add-insight-button"
                className="dropdown-item"
                to={addInsightHref}
                onClick={handleClick}
              >
                <Icon
                  name="insight"
                  width={iconSize}
                  className="outline icon-orange-theme"
                />
                Add an Insight
              </Link>
              {!freeForm20Enabled() ? (
                <Link
                  data-testid="add-analysis-button"
                  className="dropdown-item"
                  to={addAnalysisTypeHref}
                  onClick={handleClick}
                >
                  <Icon
                    name="analysis"
                    width={iconSize}
                    className="outline icon-orange-theme"
                  />
                  Add an Analysis
                </Link>
              ) : (
                <></>
              )}
              {freeForm20Enabled() ? (
                <Link
                  data-testid="add-analysis2-button"
                  className="dropdown-item"
                  to={addAnalysis2TypeHref}
                  onClick={handleClick}
                >
                  <Icon
                    name="analysis"
                    width={iconSize}
                    className="outline icon-orange-theme"
                  />
                  Add an Analysis 2.0
                </Link>
              ) : (
                <></>
              )}
            </Popover.Content>
          </Popover>
        </Overlay>
      </div>
      {show && (
        <div
          className="add-header-popover-overlay"
          onClick={handleClose}
          data-testid="add-header-popover-overlay"
          role="presentation"
        />
      )}
    </>
  );
};

AddProjectItemsHeaderMenu.propTypes = {
  projectId: number.isRequired
};

export default AddProjectItemsHeaderMenu;
