import React, { useState, useRef } from 'react';
import { Popover, Overlay, Modal } from 'react-bootstrap';
import { Icon, Button } from '@pwc/pwc-deals-ui';
import { Link, useRouteMatch } from 'react-router-dom';

import PageTitle from '../../../../../common/components/PageTitle/PageTitle';
import { useSections } from '../../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { setAddLibraryLocation } from '../../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';

import './ObjectInjestPopover.scss';

const ObjectInjestEmptyClipboardModal = ({
  show = true,
  onHide = () => {}
}) => {
  const ref = useRef();
  return (
    <Modal
      ref={ref}
      className="object-injest-empty-modal"
      show={show}
      onHide={onHide}
    >
      <Modal.Header closeButton>
        <Modal.Title>
          <PageTitle title="Adding an Excel or PowerPoint object" size="h2" />
        </Modal.Title>
      </Modal.Header>
      <div>
        <p>
          You can add chart or table objects from Excel or PowerPoint to your
          Junction analysis. Once added, they will automatically update, so you
          won&apos;t need to add it again.
        </p>
        <h4>To add an object:</h4>
        <ol>
          <li>
            Open the desired Excel or PowerPoint file in your{' '}
            <a href="/to-do">project&apos;s SharePoint folder</a>
          </li>
          <li>
            Highlight the cells that make up your preferred chart ore table and
            click the &apos;Add to Junction&apos; button on the toolbar.
          </li>
          <li>
            With the object chosen, you can add it to the analysis or text area.
            In Junction, select &apos;Add an Item&apos; and choose &apos;Excel
            or PowerPoint&apos;. To add it as a mini table, select &apos;+
            Minitable/chart&apos; in a text area.
          </li>
        </ol>
      </div>
    </Modal>
  );
};

const ObjectInjestPopoverContent = ({
  target,
  show,
  onCancel = () => {},
  // TODO: remove once we can really insert something
  onInsertPowerBi = () => {},
  droppableLocation
}) => {
  const ref = useRef();
  const [, dispatch] = useSections();

  const [noClipboardShow, setNoClipboardShow] = useState(false);
  const { url } = useRouteMatch();

  const handleAddLibraryItem = () => {
    setAddLibraryLocation(droppableLocation, dispatch);
  };

  const handlePowerBi = e => {
    e.preventDefault();
    if (onInsertPowerBi) {
      onInsertPowerBi();
    }
  };

  const handleText = e => {
    e.preventDefault();
  };

  if (noClipboardShow) {
    return (
      <ObjectInjestEmptyClipboardModal
        show={noClipboardShow}
        onHide={() => setNoClipboardShow(false)}
      />
    );
  }

  return (
    <div ref={ref} data-testid="injest-select-popover">
      <Overlay
        show={show}
        target={target}
        rootClose
        onHide={onCancel}
        container={ref.current}
        placement="bottom"
      >
        <Popover className="injest-select-popover">
          <Popover.Content className="popup-menu popup-confirmation injest-select-popover">
            <Link
              className="ui-button button-no-underline"
              to={`${url}/library-items`}
              onClick={handleAddLibraryItem}
            >
              <Icon
                name="powerpoint"
                width={24}
                height={24}
                className="outline icon-orange-theme"
              />
              <span>Excel or PowerPoint</span>
            </Link>
            <Button className="button-no-underline" onClick={handlePowerBi}>
              <Icon
                name="powerbi-new"
                width={24}
                height={24}
                className="outline icon-orange-theme"
              />
              <span>Power BI...</span>
            </Button>
            <Button className="button-no-underline" onClick={handleText}>
              <Icon
                name="textbox"
                width={24}
                height={24}
                className="outline icon-orange-theme"
              />
              <span>Text</span>
            </Button>
          </Popover.Content>
        </Popover>
      </Overlay>
    </div>
  );
};

const ObjectInjestPopover = ({
  onInsertPowerBi = () => {},
  onPopover = () => {},
  droppableLocation
}) => {
  const target = useRef();
  const [isOpen, setIsOpen] = useState(false);

  const showPopover = e => {
    e.preventDefault();
    setIsOpen(true);
    onPopover(true);
  };

  const hidePopover = () => {
    setIsOpen(false);
    onPopover(false);
  };

  return (
    <>
      <div ref={target}>
        <Button
          className="ui-button button-link object-injest-toggle"
          onClick={showPopover}
        >
          <Icon name="plus" width={24} className="outline icon-orange" />
        </Button>
      </div>
      <ObjectInjestPopoverContent
        target={target}
        show={isOpen}
        onCancel={hidePopover}
        onInsertPowerBi={onInsertPowerBi}
        droppableLocation={droppableLocation}
      />
      <p className="object-injest-label">Add an item</p>
    </>
  );
};

export default ObjectInjestPopover;
