import { v4 as uuidv4 } from 'uuid';
import { UserUtilities } from '@pwc/pwc-deals-web-common';
import {
  addCommentToEntity,
  deleteComment,
  deleteCommentThread,
  updateComment
} from '../../features/comments/commentService';
import {
  loadCommentsThunk,
  setHasCommented
} from '../../features/comments/commentsSlice';

export const formatComment = ({
  data,
  commentId = uuidv4(),
  projectId,
  entityType,
  entityId,
  entityVersion,
  authorId,
  shareWithAll = false,
  createdAt = new Date()
}) => ({
  ...data,
  commentId,
  projectId,
  entityType,
  entityId,
  entityVersion,
  authorId,
  shareWithAll,
  createdAt
});

export const getCommentsRepositoryPluginAdapter = (
  project,
  currentUser,
  comments,
  entityType,
  entityId,
  entityVersion,
  commentThreads,
  dispatch,
  formatThread
) => ({
  async addComment(data, format = formatComment) {
    const foundComment = comments.find(_ => data.commentId === _.commentId);

    if (foundComment) return Promise.resolve(foundComment);

    const firstCommentInThread = comments.filter(
      comment => comment.threadId === data.threadId
    )[0];

    const shareWithAll = firstCommentInThread
      ? firstCommentInThread.shareWithAll
      : !UserUtilities.isPractitioner(currentUser, project.id);

    const threadVersion = firstCommentInThread
      ? firstCommentInThread.entityVersion
      : entityVersion;

    const comment = format({
      data,
      projectId: project.id,
      entityType,
      entityId,
      entityVersion: threadVersion,
      authorId: currentUser.id,
      shareWithAll
    });

    dispatch(setHasCommented());

    await addCommentToEntity(comment);

    dispatch(
      loadCommentsThunk(project.id, entityType, entityId, entityVersion)
    );

    return Promise.resolve();
  },

  updateComment(data) {
    const foundComment =
      comments.find(_ => data.commentId === _.commentId) || {};
    const updatedComment = { ...foundComment, ...data };

    return updateComment(updatedComment);
  },

  async removeComment(data) {
    const foundThread = commentThreads.find(_ => data.threadId === _.id);

    if (foundThread && foundThread.comments[0].commentId === data.commentId) {
      await deleteCommentThread({
        threadId: data.threadId,
        projectId: project.id,
        entityType,
        entityId
      });
    } else {
      await deleteComment({
        threadId: data.threadId,
        projectId: project.id,
        entityType,
        entityId,
        commentId: data.commentId
      });
    }

    dispatch(
      loadCommentsThunk(project.id, entityType, entityId, entityVersion)
    );

    return Promise.resolve();
  },

  getCommentThread(data) {
    const foundThread = commentThreads.find(
      thread => thread.id === data.threadId || thread.id === data.id
    );

    return Promise.resolve(formatThread(foundThread));
  }
});
