import { Button } from '@pwc/pwc-deals-ui';
import { Formik } from 'formik';
import _get from 'lodash/get';
import React, { useCallback, useEffect, useState } from 'react';
import { Route, Switch, useParams, useRouteMatch } from 'react-router-dom';
import { ErrorUtilities } from '@pwc/pwc-deals-web-common';
import D3Alert from '../../../../common/components/Alert/D3Alert';
import ActiveContentHeader from '../../../../common/components/ContentHeaders/ActiveContentHeader';
import { FormLayout } from '../../../../common/components/layouts';
import PublishButton from '../../../../common/components/PublishButton';
import Separator from '../../../../common/components/Separator';
import EditConnectedContent from '../../../narratives/EditConnectedContent/EditConnectedContent';
import { getNarratives } from '../../../narratives/narrativesService';
import TagEditor from '../../../tagging/common/TagEditor/TagEditor';
import AddLibraryItemsBox from '../../common/AddLibraryItemsBox';
import AnalysisTextInput from '../../CreateAnalysis/AnalysisTextInput';
import FreeformAnalysisLayoutContainer from '../FreeformAnalysisLayout/FreeformAnalysisLayoutContainer';
import LibraryItemListContainer from '../LibraryItemListContainer/LibraryItemListContainer';
import './FreeformAnalysisForm.scss';
import { useSectionsState } from './FreeformAnalysisFormContext';
import {
  checkIsValid,
  mapDto,
  validateTitle
} from './FreeformAnalysisFormUtil/FreeformAnalysisFormUtil';
import RichTextEditorFormikField from '../../../../common/components/RichTextEditorFormikField/RichTextEditorFormikField';
import { shortIntroHeadingOnlyEditorConfig } from '../../../../common/components/RichTextEditor/CKEditorConfig';

const FreeformAnalysisForm = ({
  analysisTitle,
  analysisBody,
  publishAction,
  cancelAction,
  formTitle,
  appliedTags,
  onSaveText,
  standbyText,
  parentContentId,
  sections,
  lightModeVariant
}) => {
  const [errorType, setErrorType] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [formErrors, setFormErrors] = useState({ title: '' });
  const [tags, setTags] = useState(appliedTags || []);
  const [title, setTitle] = useState(analysisTitle);
  const [body, setBody] = useState(analysisBody);
  const [isValid, setIsValid] = useState(false);
  const { path, url } = useRouteMatch();
  const { currentSections } = useSectionsState();
  const [narratives, setNarratives] = useState(null);
  const { projectId, analysisId } = useParams();
  const [layoutSaved, setLayoutSaved] = useState(false);
  const isPublishError = errorType === 'publish';

  const titleFieldError =
    !isPublishError &&
    _get(errorType, 'fieldName', '').toLowerCase() === 'title'
      ? errorType.apiErrorMessage
      : undefined;

  const generalError =
    isPublishError || !titleFieldError ? errorType : undefined;
  useEffect(() => {
    getNarratives(projectId).then(narrs => {
      if (!analysisId) {
        setNarratives(narrs);
        return;
      }

      const narrativesWithoutThisAnalysis = {
        ...narrs,
        analyses: narrs.analyses.filter(
          a => a.contentThreadId !== parseInt(analysisId, 10)
        )
      };
      setNarratives(narrativesWithoutThisAnalysis);
    });
  }, [analysisId, projectId]);

  useEffect(() => {
    setIsValid(checkIsValid(formErrors));
  }, [formErrors]);

  const handleTitleValidationChange = useCallback(errorMsg => {
    setFormErrors(previousFormErrors => ({
      ...previousFormErrors,
      title: errorMsg
    }));
  }, []);

  const bodyField = {
    name: 'body',
    value: body
  };

  const handlePublish = async () => {
    setIsSaving(true);
    try {
      setErrorType();
      await publishAction(
        mapDto(title, body, tags, currentSections),
        narratives
      );
    } catch (e) {
      setIsSaving(false);
      setErrorType(ErrorUtilities.getContentErrorType(e));
    }
  };

  const handleTagSelect = values => {
    setTags(values);
  };

  return (
    <Switch>
      <Route exact path={path}>
        <form
          onSubmit={async event => {
            event.preventDefault();
            await handlePublish();
          }}
        >
          <FormLayout
            headerComponent={
              // eslint-disable-next-line react/jsx-wrap-multilines
              <ActiveContentHeader>
                <Button
                  className="button-link button-theme mx-2 mr-4"
                  onClick={cancelAction}
                  data-testid="cancel-button-top"
                  type="button"
                >
                  Cancel
                </Button>
                <PublishButton
                  isSaving={isSaving}
                  isValid={isValid}
                  buttonSize="button-small"
                  buttonPosition="top"
                  onSaveText={onSaveText}
                  standbyText={standbyText}
                />
              </ActiveContentHeader>
            }
          >
            <div className="form-content-wrapper">
              <h1>{formTitle}</h1>

              <div
                className={`input-container${
                  lightModeVariant ? ' light-mode-variant' : ''
                }`}
              >
                <AnalysisTextInput
                  label="Title"
                  name="title"
                  value={title}
                  validate={validateTitle}
                  handleValidationChange={handleTitleValidationChange}
                  handleValueChange={setTitle}
                  error={titleFieldError}
                />
                <RichTextEditorFormikField
                  field={bodyField}
                  form={{
                    setFieldValue: (_, newValue) => {
                      setBody(newValue);
                    }
                  }}
                  label="Description (optional)"
                  dataTestId="text-area-body"
                  config={shortIntroHeadingOnlyEditorConfig}
                />
                <AddLibraryItemsBox
                  selectedLibraryItems={currentSections.flatMap(s => s.items)}
                  layoutSaved={layoutSaved}
                  isValid={isValid}
                />
              </div>

              <div className="analysis-form-extras">
                <Separator />

                <Formik initialValues={{ tags }} enableReinitialize>
                  {() => (
                    <TagEditor
                      projectId={projectId}
                      onSelect={handleTagSelect}
                    />
                  )}
                </Formik>
                <Separator />
                <EditConnectedContent
                  projectId={projectId}
                  parentContentId={parentContentId}
                  narratives={narratives}
                  setNarratives={setNarratives}
                />
              </div>
              <Separator />
              <div className="form-bottom-actions">
                <Button
                  className="button-link form-cancel-button"
                  type="button"
                  onClick={cancelAction}
                  data-testid="cancel-button-bottom"
                >
                  Cancel
                </Button>
                <PublishButton
                  isSaving={isSaving}
                  isValid={isValid}
                  buttonPosition="bottom"
                  onSaveText={onSaveText}
                  standbyText={standbyText}
                />
              </div>
            </div>

            {generalError && <D3Alert configType={generalError} />}
          </FormLayout>
        </form>
      </Route>
      <Route path={`${path}/library-items`}>
        <LibraryItemListContainer
          title="Add data"
          returnUrl={url}
          operationType={analysisId ? 'edit' : 'add'}
        />
      </Route>
      <Route path={`${path}/layout`}>
        <FreeformAnalysisLayoutContainer
          goBackUrl={`${url}/library-items`}
          basePageUrl={url}
          operationType={analysisId ? 'edit' : 'add'}
          setLayoutSaved={setLayoutSaved}
          initialSections={sections}
          analysisTitle={title}
          analysisDescription={body}
          tags={tags}
        />
      </Route>
    </Switch>
  );
};
export default FreeformAnalysisForm;
