import React from 'react';
import { Button } from '@pwc/pwc-deals-ui';
import { Link } from 'react-router-dom';
import { createInsightPath } from '../../common/services/urlService';
import EnableForProjectState from '../security/EnableForProjectState';

const CreateInsightLinkButton = ({ projectId }) => {
  const addInsightHref = createInsightPath(projectId);

  return (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <Link to={!isProjectDisabled && addInsightHref}>
          <Button disabled={isProjectDisabled} className="mt-3">
            Create Insight
          </Button>
        </Link>
      )}
    </EnableForProjectState>
  );
};

export default CreateInsightLinkButton;
