import React from 'react';
import { useSelector } from 'react-redux';
import '../../common/analysis-detail-screen.scss';
import PageContentLabel from '../../../../common/components/PageContentLabel/PageContentLabel';
import PageTitle from '../../../../common/components/PageTitle/PageTitle';
import PageSection from '../../../../common/components/PageSection';
import './AnalysisDetailScreen.scss';
import Section from '../Section';
import StatusContainer from '../../../../common/components/StatusContainer/StatusContainer';
import { selectAnalysisSections } from '../../analysisSlice';

const AnalysisDetailScreen = ({ projectId, analysis, viewClient }) => {
  const sections = useSelector(selectAnalysisSections);
  const sortedSectionByOrder = [...sections].sort((a, b) => a.order - b.order);

  return (
    <>
      <PageContentLabel cssLabel="analysis" label="Analysis" />
      <div className="section-name">Title</div>
      <PageTitle title={analysis.title} />
      <StatusContainer
        className="analysis-pill-container"
        projectId={projectId}
        content={analysis}
        allowDraftPill={!viewClient.isOnClientVersion}
      />
      <PageSection label="Description" value={analysis.body} />
      {sortedSectionByOrder.map(section => (
        <Section
          key={section.order}
          projectId={projectId}
          analysisId={analysis.contentThreadId}
          section={section}
        />
      ))}
    </>
  );
};

export default AnalysisDetailScreen;
