import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Constants } from '@pwc/pwc-deals-web-common';
import { NavLink, useLocation } from 'react-router-dom';
import { MenuItem } from '@pwc/pwc-deals-ui';
import { NotificationBell } from '@pwc/pwc-deals-notification-ui';
import Icon from '@pwc/pwc-deals-ui/build/common/Icon';
import {
  projectOverviewPath,
  hypothesesListPath,
  insightsListPath,
  analysesListPath,
  reportsListPath,
  projectSettingsPath,
  filesPath,
  freeForm20Enabled,
  analysesListPath2
} from '../../services/urlService';
import {
  setShowProjectList,
  showProjectListMenu
} from '../../../features/appSettings/appSettingsSlice';
import AddProjectItemsNavMenu from './AddProjectItemsNavMenu';
import ShowForPermissions from '../../../features/security/ShowForPermissions';
import './ProjectSideNavButtons.scss';
import { getProject } from '../../../features/project/projectSlice';
import ProjectListSideNavButtons from './ProjectListSideNavButtons';
import { loadProjectsThunk } from '../../../features/projects/projectsSlice';

const ICON_SIZE = 24;

const ProjectSideNavButtons = ({ closeSideNav, projectId, projectName }) => {
  const project = useSelector(getProject);
  const showProjectList = useSelector(showProjectListMenu);
  const location = useLocation();
  const [activeMenuItem, setActiveMenuItem] = useState(location.pathname);

  const dispatch = useDispatch();

  useEffect(() => {
    setActiveMenuItem(location.pathname);
  }, [location]);

  const isExactActive = linkPath => {
    return linkPath === activeMenuItem ? 'menu-active' : '';
  };

  const isActive = linkPath => {
    return activeMenuItem.includes(linkPath) ? 'menu-active' : '';
  };

  const handleClick = navTarget => {
    setActiveMenuItem(navTarget);
    closeSideNav();
  };

  const handleShowAllProjects = () => {
    dispatch(loadProjectsThunk());
    dispatch(setShowProjectList());
  };

  return (
    <>
      {showProjectList ? (
        <ProjectListSideNavButtons closeSideNav={closeSideNav} mobile />
      ) : (
        <>
          <NavLink
            to={projectOverviewPath(project.id)}
            aria-label={`All ${process.env.REACT_APP_PLATFORM_NAME} projects`}
            className="all-projects-link"
            onClick={handleShowAllProjects}
            data-testid="all-projects-link"
          >
            <span className="menu-item-icon">
              <Icon
                name="left-chevron"
                className="fill icon-orange"
                width={20}
              />
            </span>
            <div
              className="all-projects-link-title"
              data-testid="all-projects-link-title"
            >
              All&nbsp;
              <span key={process.env.REACT_APP_PLATFORM_NAME}>
                {process.env.REACT_APP_PLATFORM_NAME}
              </span>
              &nbsp;projects
            </div>
          </NavLink>
          <div className="menu-navigation">
            <ShowForPermissions
              projectId={projectId}
              permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
            >
              <AddProjectItemsNavMenu projectId={projectId} />
            </ShowForPermissions>

            <div className="sidebar-project-name">
              <h3>{projectName}</h3>
            </div>
            <MenuItem
              navTarget={projectOverviewPath(project.id)}
              icon="folder"
              iconClasses="outline icon-grey"
              iconWidth={ICON_SIZE}
              title="Project Overview"
              onClick={() => handleClick(projectOverviewPath(project.id))}
              linkClass={isExactActive(projectOverviewPath(project.id))}
            />
            <MenuItem
              navTarget={hypothesesListPath(project.id)}
              icon="hypothesis"
              iconClasses="outline icon-grey"
              iconWidth={ICON_SIZE}
              title="Key Focus Areas"
              onClick={() => handleClick(hypothesesListPath(project.id))}
              linkClass={isActive(hypothesesListPath(project.id))}
            />
            <MenuItem
              navTarget={insightsListPath(project.id)}
              icon="insight"
              iconClasses="outline icon-grey"
              iconWidth={ICON_SIZE}
              title="Insights"
              onClick={() => handleClick(insightsListPath(project.id))}
              linkClass={isActive(insightsListPath(project.id))}
            />
            {freeForm20Enabled() ? (
              <MenuItem
                navTarget={analysesListPath2(project.id)}
                icon="analysis"
                iconClasses="outline icon-grey"
                iconWidth={ICON_SIZE}
                title="Analyses"
                onClick={() => handleClick(analysesListPath2(project.id))}
                linkClass={isActive(analysesListPath2(project.id))}
              />
            ) : (
              <MenuItem
                navTarget={analysesListPath(project.id)}
                icon="analysis"
                iconClasses="outline icon-grey"
                iconWidth={ICON_SIZE}
                title="Analyses"
                onClick={() => handleClick(analysesListPath(project.id))}
                linkClass={isActive(analysesListPath(project.id))}
              />
            )}
            <MenuItem
              navTarget={reportsListPath(project.id)}
              icon="report"
              iconClasses="outline icon-grey"
              iconWidth={ICON_SIZE}
              title="Reports"
              onClick={() => handleClick(reportsListPath(project.id))}
              linkClass={isActive(reportsListPath(project.id))}
            />
            <MenuItem
              navTarget={filesPath(project.id)}
              icon="files-feature"
              iconClasses="outline icon-grey"
              iconWidth={ICON_SIZE}
              title="Files"
              onClick={() => handleClick(filesPath(project.id))}
              linkClass={isActive(filesPath(project.id))}
            />
          </div>
          <div className="menu-footer">
            <NotificationBell
              onClick={handleClick}
              activeMenuItem={activeMenuItem}
              iconWidth={ICON_SIZE}
              projectId={projectId}
            />
            <MenuItem
              data-testid="project-settings-menu-item"
              navTarget={projectSettingsPath(project.id)}
              icon="settings"
              iconClasses="outline icon-grey"
              iconWidth={ICON_SIZE}
              title="Settings"
              onClick={() => handleClick(projectSettingsPath(project.id))}
              linkClass={`menu-item-footer ${isActive(
                projectSettingsPath(project.id)
              )}`}
            />
          </div>
        </>
      )}
    </>
  );
};

export default ProjectSideNavButtons;
