import React, { useMemo, useEffect, useState } from 'react';
import { Loader } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import { Formik } from 'formik';
import { useSelector } from 'react-redux';
import ModalConfirmation from '../../../common/components/ModalConfirmation';
import ToggleGridList from '../../../common/components/ToggleGridList/ToggleGridList';
import AnalysesGrid from '../AnalysesGrid/AnalysesGrid';
import EmptyAnalysesCard from '../EmptyAnalyses/EmptyAnalysesCard';
import './AnalysesView.scss';
import TagEditor from '../../tagging/common/TagEditor/TagEditor';
import NoContentForTagsCard from '../../../common/components/ContentActions/NoContentForTagsCard/NoContentForTagsCard';
import ShowForRole from '../../security/ShowForRole';
import ViewAnalysisSourceLinks from '../common/ViewAnalysisSourceLinks/ViewAnalysisSourceLinks';
import ContentList from '../../contentList/ContentList';
import { contentTypeAnalyses } from '../../../common/components/proptypes/commonProptypes';
import { updateAnalysesOrder } from '../analysesService';
import { selectAllTags } from '../analysisSlice';

const AnalysesView = ({
  projectId,
  dataLength,
  currentPage,
  pageSize,
  next,
  hasMore,
  analyses,
  isFetching,
  toggleView,
  onFilter,
  viewGrid,
  onDelete,
  onReorder
}) => {
  const [isLoading, setIsLoading] = useState(true);
  // TODO: After 5 Analyses, this should be refactored to use one get endpoint to grab all analyses
  // LINK: https://wwtas-pwcdealsos.atlassian.net/wiki/spaces/WWT/pages/284590088/Future+Us+Problems
  const [isDeleteConfirmationVisible, setDeleteConfirmationVisible] = useState(
    false
  );
  const [selectedAnalysis, setSelectedAnalysis] = useState(null);
  const [tagFilters, setTagFilters] = useState([]);
  const [data, setData] = useState(null);

  const allTags = useSelector(selectAllTags);

  useEffect(() => {
    if (analyses && analyses.length > 0) {
      analyses.sort(
        (analysisA, analysisB) => analysisA.sortOrder - analysisB.sortOrder
      );
      setData(analyses);
      setIsLoading(false);
    } else if (analyses && analyses.length === 0 && tagFilters.length > 0) {
      setData(analyses);
      setIsLoading(false);
    } else {
      setData(null);
      if (!isFetching) {
        setIsLoading(false);
      } else {
        setIsLoading(true);
      }
    }
  }, [analyses, isFetching, tagFilters.length]);

  const showDeleteConfirmation = analysis => {
    setSelectedAnalysis(analysis);
    setDeleteConfirmationVisible(true);
  };

  const updateTags = async values => {
    setTagFilters(values);
    if (viewGrid) {
      const selectedTags = allTags.filter(tag => values.includes(tag.text));
      if (selectedTags && selectedTags.length > 0) {
        const tagIds = selectedTags.map(val => val.id);
        onFilter(tagIds);
      } else {
        next();
      }
    }
  };

  const filteredAnalysesNew = () => {
    return (
      data &&
      data.length > 0 &&
      data.filter(item => {
        const analysisTags = new Set((item.tags || []).map(tag => tag.text));
        return tagFilters.length
          ? tagFilters.some(tag => analysisTags.has(tag))
          : true;
      })
    );
  };

  const memoizedFilteredAnalyses = useMemo(filteredAnalysesNew, [
    data,
    tagFilters
  ]);

  return (
    <>
      <ModalConfirmation
        show={isDeleteConfirmationVisible}
        onCancel={() => setDeleteConfirmationVisible(false)}
        onConfirm={() => {
          setDeleteConfirmationVisible(false);
          onDelete(selectedAnalysis);
        }}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage="This Analysis will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />

      <div
        data-testid="analysis-list-container"
        className="analysis-list-container"
      >
        <div className="list-container-header">
          <h2 className="analyses-view-h2">Analyses</h2>

          <div className="list-container-header-source-links">
            <ShowForRole
              roles={Constants.USER_ROLE_GROUPS.INTERNAL_READWRITE}
              projectId={projectId}
              restrictedForRoleElement={
                <div data-testid="analysis-source-links-placeholder" />
              }
            >
              <ViewAnalysisSourceLinks toolTipDirection="left" />
            </ShowForRole>
            <ToggleGridList viewGrid={viewGrid} onClick={toggleView} />
          </div>
        </div>

        <Formik initialValues={{ tags: [] }}>
          {() => (
            <TagEditor
              projectId={projectId}
              onSelect={updateTags}
              tagsRight
              title={false}
              subtitle={false}
              allowCreateTags={false}
              placeholderText="Filter by tag"
            />
          )}
        </Formik>

        {isLoading && !data && <Loader dataTestId="loader" />}
        {!isLoading && data && (
          <div className="content">
            {memoizedFilteredAnalyses &&
              memoizedFilteredAnalyses.length > 0 &&
              viewGrid && (
                <div data-testid="analysis-grid-view">
                  <AnalysesGrid
                    data={memoizedFilteredAnalyses}
                    hasContent={!!data.length}
                    projectId={projectId}
                    onDelete={showDeleteConfirmation}
                    dataLength={dataLength}
                    currentPage={currentPage}
                    pageSize={pageSize}
                    hasMore={hasMore}
                    next={next}
                  />
                </div>
              )}

            {memoizedFilteredAnalyses &&
              memoizedFilteredAnalyses.length > 0 &&
              !viewGrid && (
                <div data-testid="analysis-list-view">
                  <ContentList
                    contentType={contentTypeAnalyses}
                    projectId={projectId}
                    contentItems={memoizedFilteredAnalyses}
                    projectHasContent={!!data.length}
                    onDelete={onDelete}
                    onReorder={onReorder}
                    updateSortOrderApiCall={updateAnalysesOrder}
                    isFiltered={!!tagFilters.length}
                  />
                </div>
              )}

            {(!memoizedFilteredAnalyses ||
              memoizedFilteredAnalyses.length === 0) && (
              <NoContentForTagsCard />
            )}
          </div>
        )}
        {!isLoading && !data && <EmptyAnalysesCard projectId={projectId} />}
      </div>
    </>
  );
};

export default AnalysesView;
