import React from 'react';
// eslint-disable-next-line import/no-unresolved
import { Icon } from '@pwc/pwc-deals-ui';
import getLibraryItemIconName from './getLibraryItemIconName';

const renderLibraryItemIcon = (type, iconClass, hoverClass, size = 24) => {
  return (
    <Icon
      name={getLibraryItemIconName(type, hoverClass)}
      width={size}
      className={iconClass}
    />
  );
};

export default renderLibraryItemIcon;
