import { debounce } from 'lodash';
import { useCallback, useRef, useState } from 'react';
import { userSearchService } from '../services/userSearchService';

export const useUserSearch = (
  role,
  maxResultCount = 10,
  debounceTime = 250,
  startSearchingLength = 3
) => {
  const [usersFound, setUsersFound] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [hasError, setHasError] = useState(false);
  const [lastSearch, setLastSearch] = useState('');
  const lastSearchRef = useRef(lastSearch);
  lastSearchRef.current = lastSearch;

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const searchUsers = useCallback(userSearchService(role, maxResultCount), [
    role,
    maxResultCount
  ]);

  const debouncedSearchUsers = debounce(async (searchText = '') => {
    setLastSearch(searchText);
    setIsLoading(true);
    setHasError(false);
    if (searchText.trim().length < startSearchingLength) {
      if (lastSearchRef.current === searchText) {
        setUsersFound([]);
        setIsLoading(false);
      }
      return;
    }

    let users = [];
    try {
      users = await searchUsers(searchText);
    } catch (e) {
      if (e.message !== 'tokenCanceled') setHasError(true);
    }
    if (lastSearchRef.current === searchText) {
      setIsLoading(false);
      setUsersFound(users);
    }
  }, debounceTime);

  const clearUsers = () => {
    setUsersFound([]);
    setIsLoading(false);
    setHasError(false);
  };

  return {
    usersFound,
    searchUsers: debouncedSearchUsers,
    clearUsers,
    isLoading,
    hasError
  };
};
