import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Modal } from 'react-bootstrap';
import PageTitle from '../../../common/components/PageTitle/PageTitle';
import {
  getFileUploadState,
  setFileUploadError
} from '../../../common/redux/fileUpload/fileUploadSlice';
import DropZone from './DropZone/DropZone';
import './FileUploadModal.scss';
import ModalActions from './ModalActions';
import PasswordSection from './PasswordSection';
import { updateFile } from '../../../common/redux/fileUpload/fileUploadThunk';

const FileUploadModal = ({ onHide }) => {
  const { projectId } = useParams();
  const [file, setFile] = useState(null);
  const [password, setPassword] = useState('');

  const dispatch = useDispatch();
  const { uploadedFileId, uploadedFileName } = useSelector(getFileUploadState);
  const post = () => {
    dispatch(updateFile(projectId, password, file, uploadedFileId));
  };

  const { loading, error } = useSelector(getFileUploadState);

  const uploadFile = files => {
    if (files.length > 0) {
      setFile(files[0]);
    }
  };

  const clearFile = () => {
    dispatch(setFileUploadError({ error: null }));
    setFile(null);
  };

  const pageTitle = uploadedFileId
    ? `Update ${uploadedFileName}`
    : 'Upload a file';

  return (
    <>
      <Modal.Header closeButton>
        <div>
          <Modal.Title>
            <PageTitle title={pageTitle} size="h2" />
          </Modal.Title>
          <p>
            Word, Excel, PowerPoint and PDF files are uploaded in their
            respective file format. All other file types are compressed into a
            ZIP file so that a password can be added.
          </p>
        </div>
      </Modal.Header>
      <Modal.Body>
        <DropZone
          file={file}
          onDropAccepted={uploadFile}
          clearFile={clearFile}
          loading={loading}
          error={error}
        />
        <PasswordSection password={password} setPassword={setPassword} />
      </Modal.Body>
      <Modal.Footer>
        <ModalActions
          post={post}
          disabled={!file || password === '' || loading || !!error}
          closeModal={onHide}
        />
      </Modal.Footer>
    </>
  );
};

export default FileUploadModal;
