import { Button } from '@pwc/pwc-deals-ui';
import React from 'react';

import './ShowPasswordDialog.scss';

const ShowPasswordDialog = ({ password, onCloseClick }) => (
  <div className="password-dialog">
    <div>File password</div>

    <div>{password}</div>

    <div>
      <div>
        <Button
          onClick={onCloseClick}
          className="button-outline button-small"
          data-testid="menu-button-show-password-close"
          disabled={!onCloseClick}
        >
          Close
        </Button>
      </div>

      <div>
        <Button
          className="button-small"
          onClick={() => navigator.clipboard.writeText(password)}
        >
          Copy
        </Button>
      </div>
    </div>
  </div>
);

export default ShowPasswordDialog;
