import React, { useState } from 'react';
import { Button, Tooltip, Icon } from '@pwc/pwc-deals-ui';
import { useDispatch } from 'react-redux';
import classNames from 'classnames';
import { toggleRightPanel } from '../../../../../../../../common/redux/reportFormat/reportFormatSlice';

import './RightPanelExpand.scss';

export const testId = 'right-panel-expand';
export const buttonClassName = 'right-panel-expand__button';

const RightPanelExpand = () => {
  const dispatch = useDispatch();
  const [iconType, setIconType] = useState('outline');

  const handleCollapse = () => {
    dispatch(toggleRightPanel());
  };
  const handleMouseEnter = () => {
    setIconType('fill');
  };
  const handleMouseLeave = () => {
    setIconType('outline');
  };

  return (
    <div data-testid={testId} className="right-panel-expand">
      <div className="right-panel-expand__tooltip-container">
        <Tooltip
          direction="top"
          tip="Expand sidebar"
          dataTestId="expand-tooltip"
        >
          <Button
            data-testid={buttonClassName}
            className={classNames('button-no-underline', buttonClassName)}
            title="Expand sidebar"
            onClick={handleCollapse}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
          >
            <Icon
              name="left-chevron"
              width="20px"
              height="20px"
              className={`${iconType} icon-orange-theme`}
            />
          </Button>
        </Tooltip>
      </div>
    </div>
  );
};

export default RightPanelExpand;
