import React, { useEffect, useState, useRef } from 'react';
import { useSelector } from 'react-redux';
import classNames from 'classnames';
import { isEqual } from 'lodash';
import { DragDropContext } from 'react-beautiful-dnd';

import LeftPanel from './LeftPanel/LeftPanel';
import RightPanel from './RightPanel/RightPanel';
import RightPanelExpand from './RightPanel/RightPanelExpand/RightPanelExpand';

import { useSections } from '../../../FreeformAnalysisForm/FreeformAnalysisFormContext';

import {
  setCurrentSections,
  setNonLaidOutLibraryItems
} from '../../../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';
import generateLayoutTypeChangeHandler from '../../generateLayoutTypeChangeHandler';
import { getReportFormatLayoutState } from '../../../../../../common/redux/reportFormat/reportFormatSlice';

import './EditLayout.scss';
import BottomPanel from './BottomPanel/BottomPanel';
import useTabletOrMobile from '../../../../../../common/hooks/useTabletOrMobile';
import useActiveSection from '../useActiveSection';

const EditLayout = ({
  showRightPanel = true,
  libraryItemMap,
  droppableLocations,
  onAddNewSection,
  setDroppableLocations,
  onDragStart,
  onDragUpdate,
  onDragEnd
}) => {
  const [
    { currentSections = [], nonLaidOutLibraryItems = [] },
    contextDispatch
  ] = useSections();
  const bottomPanelRef = useRef(null);
  const rightPanelRef = useRef(null);
  const [renderedSectionElements, setRenderedSectionElements] = useState(null);
  const { activeSectionIndex } = useSelector(getReportFormatLayoutState);
  const isTabletOrMobile = useTabletOrMobile();
  const sectionElements = Array.from(
    document.querySelectorAll('.edit-layout__left .section')
  );

  useActiveSection(renderedSectionElements, bottomPanelRef, rightPanelRef);

  useEffect(() => {
    if (sectionElements && !isEqual(sectionElements, renderedSectionElements)) {
      setRenderedSectionElements(sectionElements);
    }
  }, [sectionElements, renderedSectionElements]);

  const updateCurrentSections = sections => {
    setCurrentSections(sections, contextDispatch);
  };
  const editNonLaidOutLibraryItems = items => {
    setNonLaidOutLibraryItems(items, contextDispatch);
  };
  const handleLayoutTypeChange = generateLayoutTypeChangeHandler(
    activeSectionIndex,
    currentSections,
    setDroppableLocations,
    updateCurrentSections,
    nonLaidOutLibraryItems,
    editNonLaidOutLibraryItems
  );

  return (
    <div
      className={classNames('edit-layout', {
        'edit-layout--collapsed-column': !showRightPanel && !isTabletOrMobile,
        'edit-layout--mobile': isTabletOrMobile
      })}
      data-testid="edit-layout"
    >
      <DragDropContext
        onDragStart={onDragStart}
        onDragUpdate={onDragUpdate}
        onDragEnd={onDragEnd}
      >
        <div className="edit-layout__left" data-testid="edit-layout-left-panel">
          <LeftPanel
            libraryItemMap={libraryItemMap}
            droppableLocations={droppableLocations}
            onAddNewSection={onAddNewSection}
            setDroppableLocations={setDroppableLocations}
          />
        </div>

        {!isTabletOrMobile && (
          <div
            className="edit-layout__right"
            data-testid="edit-layout-right-panel"
          >
            {showRightPanel ? (
              <RightPanel
                handleLayoutTypeChange={handleLayoutTypeChange}
                droppableLocations={droppableLocations}
                panelRef={rightPanelRef}
                panelExpanded={showRightPanel}
              />
            ) : (
              <RightPanelExpand />
            )}
          </div>
        )}
        {isTabletOrMobile && (
          <div
            className="edit-layout__bottom"
            data-testid="edit-layout-bottom-panel"
          >
            <BottomPanel
              handleLayoutTypeChange={handleLayoutTypeChange}
              droppableLocations={droppableLocations}
              panelRef={bottomPanelRef}
              panelExpanded={showRightPanel}
            />
          </div>
        )}
      </DragDropContext>
    </div>
  );
};

export default EditLayout;
