import React from 'react';
import { useDispatch } from 'react-redux';
import { Icon, Button } from '@pwc/pwc-deals-ui';

import { toggleRightPanel2 } from '../../../../../../../common/redux/reportFormat/reportFormatSlice';
import LayoutTab from './LayoutTab/LayoutTab';
import { useClickedOutsideOfRef } from '../../../../../../../common/hooks/useClickedOutsideOfRef';

import './RightPanel.scss';

export const testId = 'right-panel';

const RightPanel = ({ handleLayoutTypeChange }) => {
  const dispatch = useDispatch();

  const handleCollapse = () => {
    dispatch(toggleRightPanel2());
  };

  const ref = useClickedOutsideOfRef(
    clickOutside => clickOutside && handleCollapse()
  );

  return (
    <div ref={ref} className="right-panel2">
      <div className="right-panel2__header">
        <Button className="button-no-underline" onClick={handleCollapse}>
          <Icon name="close" width="20" className="icon fill icon-orange" />
        </Button>
      </div>
      <LayoutTab handleLayoutTypeChange={handleLayoutTypeChange} />
    </div>
  );
};

export default RightPanel;
