import { Constants } from '@pwc/pwc-deals-web-common';
import { createSlice } from '@reduxjs/toolkit';

export const initialState = {
  modal: {
    show: false,
    modalErrorType: Constants.ModalErrorType.NONE
  },
  moreMenu: {
    itemId: null,
    show: false,
    moreMenuErrorType: Constants.MoreMenuErrorType.NONE
  }
};

const errorsSlice = createSlice({
  name: 'errors',
  initialState,
  reducers: {
    showModalError: (state, action) => ({
      ...state,
      modal: {
        show: true,
        modalErrorType: action.payload
      }
    }),
    hideModalError: state => ({ ...state, modal: initialState.modal }),
    showMoreMenuError: (state, action) => ({
      ...state,
      moreMenu: {
        show: true,
        itemId: action.payload.itemId,
        moreMenuErrorType: action.payload.moreMenuErrorType
      }
    }),
    hideMoreMenuError: state => ({ ...state, moreMenu: initialState.moreMenu })
  }
});

// Actions
export const {
  showModalError,
  hideModalError,
  showMoreMenuError,
  hideMoreMenuError
} = errorsSlice.actions;

// Selectors
export const getErrorModalState = state => state.errors.modal;
export const getErrorMoreMenuState = state => state.errors.moreMenu;

export default errorsSlice.reducer;
