/* eslint-disable react/jsx-wrap-multilines */
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

import EmptyInsightsCard from '../EmptyInsights/EmptyInsightsCard';
import ModalConfirmation from '../../../common/components/ModalConfirmation';
import { editInsightPath } from '../../../common/services/urlService';
import InsightsGridCard from '../InsightsGridCard/InsightsGridCard';
import { filterNarrativesForContent } from '../../narratives/narrativesUtilities';
import NoContentForTagsCard from '../../../common/components/ContentActions/NoContentForTagsCard/NoContentForTagsCard';
import './InsightsGrid.scss';

const InsightsGrid = ({
  data,
  narratives,
  onDelete,
  projectId,
  hasContent
}) => {
  const history = useHistory();
  const noop = () => {};
  const [
    isDeleteConfirmationVisible,
    setIsDeleteConfirmationVisible
  ] = useState(false);
  const [processDeleteFn, setProcessDeleteFn] = useState(() => noop);
  const handleHideConfirmation = () => setIsDeleteConfirmationVisible(false);
  const handleEditInsight = id => history.push(editInsightPath(projectId, id));

  const handleDeleteConfirmation = id => {
    const processConfirmedDeleteForId = () => {
      setIsDeleteConfirmationVisible(false);
      onDelete(id);
    };
    setProcessDeleteFn(() => processConfirmedDeleteForId);
    setIsDeleteConfirmationVisible(true);
  };

  const hasInsights = data.length > 0;

  return (
    <>
      <ModalConfirmation
        show={isDeleteConfirmationVisible}
        onCancel={handleHideConfirmation}
        onConfirm={processDeleteFn}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage="This Insight will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />

      <div className="insights-grid-container">
        {hasInsights && (
          <div className="grid-cards list-grid" data-testid="list-grid">
            {data.map((insight, i) => (
              <InsightsGridCard
                insight={insight}
                narratives={filterNarrativesForContent(
                  narratives,
                  insight.contentId
                )}
                projectId={projectId}
                onEditMenuClick={handleEditInsight}
                onDeleteMenuClick={handleDeleteConfirmation}
                index={i}
                key={`content-grid-card-${insight.contentThreadId}`}
              />
            ))}
          </div>
        )}
        {!hasInsights && !hasContent && (
          <EmptyInsightsCard projectId={projectId} />
        )}
        {!hasInsights && hasContent && <NoContentForTagsCard />}
      </div>
    </>
  );
};

export default InsightsGrid;
