import React, { useCallback, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Form, Formik, Field } from 'formik';
import { Alert } from 'react-bootstrap';
import { Button } from '@pwc/pwc-deals-ui';
import Separator from '../../common/components/Separator';
import PublishButton from '../../common/components/PublishButton';
import RadioGroupFieldset from '../../common/components/RadioGroup/RadioGroupFieldset';
import { getProjectId } from '../project/projectSlice';
import {
  getSaveErrorUserNotificationSettingsSelector,
  getUserNotificationSettingsSelector
} from '../../common/redux/userNotificationSettings/userNotificationSettingsSlice';
import {
  loadUserNotificationSettingsThunk,
  saveUserNotificationSettingsThunk
} from '../../common/redux/userNotificationSettings/userNotificationSettingsThunk';

export const allowEmailNotificationsOptions = [
  { label: 'Yes', value: '1' },
  { label: 'No', value: '0' }
];

const NotificationSettings = () => {
  const [isSaving, setIsSaving] = useState(false);
  const projectId = useSelector(getProjectId);
  const userNotificationSettings = useSelector(
    getUserNotificationSettingsSelector(projectId)
  );
  const saveErrorUserNotificationSettings = useSelector(
    getSaveErrorUserNotificationSettingsSelector(projectId)
  );
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(loadUserNotificationSettingsThunk(projectId));
  }, [dispatch, projectId]);

  const handleSubmit = useCallback(
    newSettings => {
      const receiveEmails = !!parseInt(newSettings.receiveEmails, 10);
      setIsSaving(true);
      dispatch(saveUserNotificationSettingsThunk(projectId, receiveEmails));
      setIsSaving(false);
    },
    [dispatch, projectId]
  );

  const initialValues = {
    ...userNotificationSettings.settings,
    receiveEmails: userNotificationSettings.settings.receiveEmails ? '1' : '0'
  };
  return (
    <div className="settings-container" data-testid="notification-settings">
      <Formik
        initialValues={initialValues}
        enableReinitialize
        onSubmit={handleSubmit}
      >
        {({ isValid, dirty, resetForm }) => (
          <Form
            data-testid="notification-settings-form"
            className="notification-settings-form-container"
          >
            <div className="form-fields">
              <Field
                name="receiveEmails"
                component={RadioGroupFieldset}
                options={allowEmailNotificationsOptions}
                direction="row"
                label="Would you like to receive email notifications?"
              />
            </div>

            <Separator />

            {saveErrorUserNotificationSettings && (
              <Alert variant="danger" data-testid="error-message">
                An error occurred while saving settings
              </Alert>
            )}
            <div className="form-bottom-actions">
              <Button
                className="button-link form-cancel-button"
                onClick={() => resetForm()}
                type="button"
                data-testid="cancel-button-bottom"
              >
                Cancel
              </Button>
              <PublishButton
                isSaving={isSaving}
                isValid={isValid && dirty}
                buttonSize="sm"
                onSaveText="Saving..."
                standbyText="Save"
                buttonPosition="bottom"
              />
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default NotificationSettings;
