import React from 'react';
import { string } from 'prop-types';
import { TextUtilities } from '@pwc/pwc-deals-web-common';
import './UserSnippet.scss';
import authorizationService from '../services/security/authorizationService';

const UserSnippet = ({ dataTestId, firstName, lastName, position, email }) => {
  const userTypeClass = authorizationService.getUserTypeClass(email);
  return (
    <div className="usersnippet-container" data-testid={dataTestId}>
      <div
        className={`usersnippet-photo  ${userTypeClass}`}
        data-testid={`${dataTestId}-initials`}
      >
        {TextUtilities.getUserInitials(firstName, lastName)}
      </div>
      <div className="usersnippet-text">
        <div className="usersnippet-name">{`${firstName} ${lastName}`}</div>
        <div className="usersnippet-position">{position}</div>
      </div>
    </div>
  );
};

UserSnippet.propTypes = {
  firstName: string,
  lastName: string,
  email: string,
  dataTestId: string
};

UserSnippet.defaultProps = {
  firstName: '',
  lastName: '',
  email: '',
  dataTestId: 'user-snippet'
};

export default UserSnippet;
