import React from 'react';
import { Button } from '@pwc/pwc-deals-ui';
import PropTypes from 'prop-types';

const IngestStructuredAnalysisButton = ({ documentCode, onIngestion }) => {
  return (
    <Button
      data-testid={`ingest-analysis-button-${documentCode}`}
      onClick={onIngestion}
    >
      Add
    </Button>
  );
};

IngestStructuredAnalysisButton.propTypes = {
  documentCode: PropTypes.string
};

IngestStructuredAnalysisButton.defaultProps = {
  documentCode: ''
};

export default IngestStructuredAnalysisButton;
