import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import Highcharts from 'highcharts';
import useAppTitle from '../common/hooks/useAppTitle';
import { getFooterThunk } from '../common/redux/termsAndConditions/getFooterThunk';
import AppLayout from './AppLayout';
import RouteChangeFocusResetter from './RouteChangeFocusResetter';

const App = () => {
  window.Highcharts = Highcharts;
  const isInIframe = window.self !== window.top;
  const dispatch = useDispatch();

  const title = useAppTitle();
  useEffect(() => {
    document.title = title;
    const topOfPage = document.getElementById('top-of-page');
    topOfPage.textContent = `${title}: Top Of Page`;
  }, [title]);

  useEffect(() => {
    dispatch(getFooterThunk());
  }, [dispatch]);

  if (isInIframe) {
    return <span style={{ color: 'red' }}>App cannot be run in an iFrame</span>;
  }

  return (
    <>
      <AppLayout />
      <RouteChangeFocusResetter />
    </>
  );
};

export default App;
