import React, { useMemo } from 'react';

import LibraryItemsListData from '../LibraryItemsListData/LibraryItemsListData';
import { useSections } from '../../../../../FreeformAnalysisForm/FreeformAnalysisFormContext';

export const DataLibraryLabel = () => {
  const [{ nonLaidOutLibraryItems = [] }] = useSections();

  const dataLabel = useMemo(() => `Data (${nonLaidOutLibraryItems.length})`, [
    nonLaidOutLibraryItems
  ]);

  return dataLabel;
};

const DataLibraryTab = ({ droppableLocations }) => {
  const [{ nonLaidOutLibraryItems = [] }, dispatch] = useSections();

  return (
    <LibraryItemsListData
      libraryItems={nonLaidOutLibraryItems}
      dispatch={dispatch}
      droppableLocations={droppableLocations}
    />
  );
};

export default DataLibraryTab;
