import React from 'react';
import './FileListZeroStateCopy.scss';
import authorizationService from '../../../common/services/security/authorizationService';
import CONSTANTS from '../Constants';

const FileListZeroStateCopy = () => {
  const isClientUser = authorizationService.isClientUser();
  return (
    <div
      className="file-list-panel text-center"
      data-testid={CONSTANTS.TEST_IDS.ZERO_STATE}
    >
      {isClientUser ? (
        <div>{CONSTANTS.CONTENTS.ZERO_STATE.CLIENT_USER}</div>
      ) : (
        <div>{CONSTANTS.CONTENTS.ZERO_STATE.PWC_USER}</div>
      )}
    </div>
  );
};

export default FileListZeroStateCopy;
