import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Helmet from 'react-helmet';
import './CommentPanel.scss';
import { Icon, Loader, Button } from '@pwc/pwc-deals-ui';
import {
  getHasCommented,
  getViewingStatus,
  setViewingStateThunk,
  toggleCommentPanelOpened,
  getCommentPanelOpened,
  getCommentsIsLoading
} from '../../../features/comments/commentsSlice';
import EnableForProjectState from '../../../features/security/EnableForProjectState';
import CommentPanelContent from '../CommentPanelContent/CommentPanelContent';

const CommentPanel = ({ projectId }) => {
  const dispatch = useDispatch();
  const commentsIsLoading = useSelector(getCommentsIsLoading);
  const commentPanelOpened = useSelector(getCommentPanelOpened);
  const hasCommented = useSelector(getHasCommented);
  const viewingStatus = useSelector(getViewingStatus);

  const commentIcon = hasCommented ? (
    <Button
      className="comment-icon"
      aria-label="comment icon"
      data-testid="comment-icon"
      onClick={() => dispatch(toggleCommentPanelOpened())}
    >
      <Icon name="comment-chevron" className="fill icon-orange" width={25} />
    </Button>
  ) : (
    <Button
      className="comment-icon"
      aria-label="comment icon"
      data-testid="comment-icon"
      onClick={() => dispatch(toggleCommentPanelOpened())}
    >
      <Icon name="comment-plus" className="fill icon-orange" width={25} />
    </Button>
  );

  return (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <div
          className={`comment-panel-container comment-panel-${
            commentPanelOpened ? 'open' : 'closed'
          }`}
        >
          <Helmet>
            <body data-comments={commentPanelOpened ? 'open' : 'closed'} />
          </Helmet>
          {commentPanelOpened ? (
            <>
              <Button
                className="collapse-icon"
                aria-label="collapse icon"
                data-testid="collapse-icon"
                onClick={() => {
                  dispatch(setViewingStateThunk('unresolved'));
                  dispatch(toggleCommentPanelOpened());
                }}
              >
                <Icon
                  name="comment-chevron"
                  className="fill icon-orange"
                  width={25}
                />
              </Button>

              <div className="comment-panel-actions">
                <Button
                  className={`unresolved-comments-button ${
                    viewingStatus === 'unresolved' ? 'action-active' : ''
                  }`}
                  aria-label="unresolved comments button"
                  data-testid="unresolved-comments-button"
                  onClick={() => dispatch(setViewingStateThunk('unresolved'))}
                >
                  Unresolved
                </Button>
                <Button
                  className={`resolved-comments-button ${
                    viewingStatus === 'resolved' ? 'action-active' : ''
                  }`}
                  aria-label="resolved comments button"
                  data-testid="resolved-comments-button"
                  onClick={() => dispatch(setViewingStateThunk('resolved'))}
                >
                  Resolved
                </Button>
              </div>
            </>
          ) : (
            commentIcon
          )}
          <Loader
            dataTestId="comment-loading-indicator"
            isVisible={commentsIsLoading && commentPanelOpened}
          />

          <CommentPanelContent commentPanelOpened={commentPanelOpened} />

          <div
            className="new-comment-button-container"
            hidden={viewingStatus === 'resolved' || !commentPanelOpened}
          >
            <Button
              className="new-comment-button button-outline"
              aria-label="new comment button"
              data-testid="new-comment-button"
              tabIndex={0}
              disabled={isProjectDisabled}
            >
              Add a new thread
            </Button>
          </div>
        </div>
      )}
    </EnableForProjectState>
  );
};

export default CommentPanel;
