import _get from 'lodash/get';
import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { Constants } from '@pwc/pwc-deals-web-common';
import authorizationService from '../../common/services/security/authorizationService';
import { structuredRoutes } from '../../common/services/urlService';
import ProjectPermissionRoute from '../security/ProjectPermissionRoute';
import AddAnalysisFromTemplate from './AddAnalysisFromTemplate/AddAnalysisFromTemplate';
import AddAnalysisType from './AddAnalysisType/AddAnalysisType';
import AnalysesContainer from './AnalysesContainer/AnalysesContainer';
import AnalysisDetailScreenContainer from './AnalysesScreen/AnalysisDetailScreen/AnalysisDetailScreenContainer';
import CreateFreeformAnalysis from './Freeform/CreateFreeformAnalysis/CreateFreeformAnalysis';
import EditFreeformAnalysis from './Freeform/EditFreeformAnalysis/EditFreeformAnalysis';
import DebtDebtLikeContainer from './structured-analysis/DebtDebtLike/DebtDebtLikeContainer/DebtDebtLikeContainer';
import DebtDebtLikeForm from './structured-analysis/DebtDebtLike/DebtDebtLikeForm/DebtDebtLikeForm';
import NetWorkingCapitalContainer from './structured-analysis/NetWorkingCapital/NetWorkingCapitalContainer/NetWorkingCapitalContainer';
import NetWorkingCapitalEditForm from './structured-analysis/NetWorkingCapital/NetWorkingCapitalEditForm/NetWorkingCapitalEditForm';
import QofEContainer from './structured-analysis/QofE/QofEContainer/QofEContainer';
import QofEEditForm from './structured-analysis/QofE/QofEEditForm/QofEEditForm';
import { withStructuredContainer } from './structured-analysis/StructuredAnalysisContainer/StructuredAnalysisContainer';
import AnalysisReportPreview from './common/AnalysisReportPreview/AnalysisReportPreview';

const basePath = '/projects/:projectId/analyses2/';

const qofEContainer = withStructuredContainer(QofEContainer);
const nwcContainer = withStructuredContainer(NetWorkingCapitalContainer);
const ddlContainer = withStructuredContainer(DebtDebtLikeContainer);

const AnalysesContentRouter2 = () => {
  const routes = [
    { path: '', component: AnalysesContainer },
    {
      path: structuredRoutes[Constants.NET_WORKING_CAPITAL],
      component: nwcContainer
    },
    {
      path: structuredRoutes[Constants.QUALITY_OF_EARNINGS],
      component: qofEContainer
    },
    {
      path: structuredRoutes[Constants.DEBT_AND_DEBT_LIKE],
      component: ddlContainer
    },
    { path: ':analysisId/view', component: AnalysisDetailScreenContainer }
  ];

  const projectPermissionRoutes = [
    { path: 'add-type', component: AddAnalysisType },
    { path: 'create', exact: false, component: CreateFreeformAnalysis },
    { path: 'add-from-template', component: AddAnalysisFromTemplate },
    {
      path: `${structuredRoutes[Constants.DEBT_AND_DEBT_LIKE]}/edit`,
      component: DebtDebtLikeForm
    },
    {
      path: `${structuredRoutes[Constants.NET_WORKING_CAPITAL]}/edit`,
      component: NetWorkingCapitalEditForm
    },
    {
      path: `${structuredRoutes[Constants.QUALITY_OF_EARNINGS]}/edit`,
      component: QofEEditForm
    },
    { path: ':analysisId/edit', exact: false, component: EditFreeformAnalysis },
    {
      path: ':analysisId/report-preview/:version(latest|shared)',
      exact: true,
      component: AnalysisReportPreview
    }
  ];

  return (
    <Switch>
      {routes.map(route => (
        <Route
          key={route.path}
          exact={_get(route, 'exact', true)}
          path={`${basePath}${route.path}`}
          component={route.component}
        />
      ))}

      {projectPermissionRoutes.map(route => (
        <ProjectPermissionRoute
          key={route.path}
          exact={_get(route, 'exact', true)}
          permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
          isAuthorized={authorizationService.hasValidToken}
          path={`${basePath}${route.path}`}
          component={route.component}
        />
      ))}

      <Route
        path={basePath}
        render={({ match }) => (
          <Redirect to={`/projects/${match.params.projectId}/analyses/`} />
        )}
      />
    </Switch>
  );
};

export default AnalysesContentRouter2;
