import classnames from 'classnames';
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Icon, Button } from '@pwc/pwc-deals-ui';
import EnableForProjectState from '../../../features/security/EnableForProjectState';
import {
  addAnalysis2TypePath,
  addAnalysisTypePath,
  createHypothesisPath,
  createInsightPath,
  freeForm20Enabled
} from '../../services/urlService';
import './AddProjectItemsNavMenu.scss';

const iconSize = 24;

const AddProjectItemsNavMenu = ({ projectId }) => {
  const [open, setOpen] = useState(false);

  const addHypothesisHref = createHypothesisPath(projectId);
  const addInsightHref = createInsightPath(projectId);
  const addAnalysisTypeHref = addAnalysisTypePath(projectId);
  const addAnalysis2TypeHref = addAnalysis2TypePath(projectId);

  return (
    <div className="menu-wrapper" data-testid="add-project-items-nav-menu">
      <EnableForProjectState projectId={projectId}>
        {({ isProjectDisabled }) => (
          <Button
            data-testid="fab"
            id="add-popover"
            className="button-avoid-focus menu-item hide-for-mobile add-icon"
            onClick={() => !isProjectDisabled && setOpen(!open)}
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
            disabled={isProjectDisabled}
          >
            <span className="menu-item-icon">
              <Icon
                name="add"
                className={classnames('fill', {
                  'icon-orange': !isProjectDisabled,
                  'icon-grey': isProjectDisabled
                })}
                width={iconSize}
              />
            </span>
            <span className="menu-item-title">Add Content</span>
          </Button>
        )}
      </EnableForProjectState>
      <div
        className={`add-popover ${open ? 'show' : ''}`}
        data-testid="add-popover"
      >
        <Link
          data-testid="add-hypothesis-button"
          className="dropdown-item"
          to={addHypothesisHref}
          onClick={() => setOpen(false)}
        >
          <Icon
            name="hypothesis"
            width={iconSize}
            className="outline icon-orange-theme"
          />
          Add a Key Focus Area
        </Link>
        <Link
          data-testid="add-insight-button"
          className="dropdown-item"
          to={addInsightHref}
          onClick={() => setOpen(false)}
        >
          <Icon
            name="insight"
            width={iconSize}
            className="outline icon-orange-theme"
          />
          Add an Insight
        </Link>
        {!freeForm20Enabled() ? (
          <Link
            data-testid="add-analysis-button"
            className="dropdown-item"
            to={addAnalysisTypeHref}
            onClick={() => setOpen(false)}
          >
            <Icon
              name="analysis"
              width={iconSize}
              className="outline icon-orange-theme"
            />
            Add an Analysis
          </Link>
        ) : (
          <></>
        )}
        {freeForm20Enabled() ? (
          <Link
            data-testid="add-analysis-button"
            className="dropdown-item"
            to={addAnalysis2TypeHref}
            onClick={() => setOpen(false)}
          >
            <Icon
              name="analysis"
              width={iconSize}
              className="outline icon-orange-theme"
            />
            Add an Analysis 2.0
          </Link>
        ) : (
          <></>
        )}
      </div>
      {open && (
        <div
          className="add-popover-overlay"
          onClick={() => setOpen(!open)}
          data-testid="add-popover-overlay"
          role="presentation"
        />
      )}
      <div className="menu-separator" />
    </div>
  );
};

AddProjectItemsNavMenu.propTypes = {
  projectId: PropTypes.number.isRequired
};

export default AddProjectItemsNavMenu;
