/* eslint-disable react/no-this-in-sfc */
import CKEditor from '@ckeditor/ckeditor5-react';
import { ClassicEditor } from '@pwc/pwc-deals-rich-text-editor';
import React, { useEffect, useState } from 'react';
import { renderToString } from 'react-dom/server';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import { UserUtilities } from '@pwc/pwc-deals-web-common';
import {
  getCommentsHasLoaded,
  getCurrentComments,
  getCurrentCommentThreads,
  loadCommentsThunk,
  getViewingStatus
} from '../../features/comments/commentsSlice';
import { getProject } from '../../features/project/projectSlice';
import userTokenService from '../services/security/userTokenService';
import { getCommentsAdapter } from './CommentsAdapter';
import CustomCommentThreadView from './views/CustomCommentThreadView';
import CustomCommentView from './views/CustomCommentView';
import './CommentEditor.scss';

// eslint-disable-next-line import/no-mutable-exports
export let currentThread = {};

const MentionPlugin = ClassicEditor.builtinPlugins.find(
  plugin => plugin.pluginName === 'Mention'
);

const { CustomMentionView } = ClassicEditor;

export const filterExistingComments = (commentThreads, commentRepository) =>
  commentThreads.filter(
    thread => !commentRepository.hasCommentThread(thread.id)
  );

export const formatThread = thread => {
  const comments = (thread.comments || []).map(comment => ({
    ...comment,
    authorId: (comment.authorId || '').toString()
  }));

  return {
    ...thread,
    threadId: thread.id,
    comments
  };
};

export const getFeedItems = (queryText, users, projectId, activeThread) => {
  function isItemMatching(item) {
    // Make the search case-insensitive.
    const searchString = queryText.toLowerCase();

    // Include an item in the search results if the name or username includes the current user input.
    return item.toLowerCase().includes(searchString);
  }

  const mentionList =
    !activeThread || !activeThread.shareWithAll
      ? users.filter(user => UserUtilities.isPractitioner(user, +projectId))
      : users;

  const usersWithFormattedNames = mentionList.map(user => ({
    ...user,
    userId: user.id,
    id: `@${user.firstName} ${user.lastName}`
  }));

  return usersWithFormattedNames
    .filter(user => isItemMatching(user.id))
    .slice(0, 10);
};

const CommentEditor = ({
  children,
  context,
  entityType,
  entityId,
  entityVersion,
  isEntitySharedWithClient,
  channelId = uuidv4(),
  isProjectDisabled
}) => {
  const dispatch = useDispatch();
  const { projectId } = useParams();

  const [currentUser, setCurrentUser] = useState({});
  const [commentEditor, setCommentEditor] = useState();

  const project = useSelector(getProject);
  const hasLoadedComments = useSelector(getCommentsHasLoaded);
  const commentThreads = useSelector(getCurrentCommentThreads);
  const comments = useSelector(getCurrentComments);
  const viewingStatus = useSelector(getViewingStatus);

  useEffect(() => {
    dispatch(loadCommentsThunk(projectId, entityType, entityId, entityVersion));
  }, [
    dispatch,
    projectId,
    entityType,
    entityId,
    entityVersion,
    isEntitySharedWithClient
  ]);

  useEffect(() => {
    let isMounted = true;
    (async () => {
      if (project.id) {
        const userEmail = await userTokenService.getProjectTokenProperty(
          project.id,
          'email'
        );
        if (isMounted) {
          setCurrentUser(
            project.users.find(
              _ => _.email.toLowerCase() === userEmail.toLowerCase()
            )
          );
        }
      }
      return () => {
        isMounted = false;
      };
    })();
  }, [project]);

  const CommentsAdapter = getCommentsAdapter(
    project,
    currentUser,
    comments,
    entityType,
    entityId,
    entityVersion,
    commentThreads,
    dispatch,
    formatThread
  );

  // We have to do this because CKEditor will try to
  // use the sidebar *before* it's been rendered :(
  const [sidebar, setSidebar] = useState();
  useEffect(() => {
    setSidebar(document.querySelector('#comment-panel'));
  }, [context]);

  useEffect(() => {
    const commentIcon = document.querySelector('.comment-icon');

    if (commentEditor && commentIcon) {
      const commentsRepository = commentEditor.plugins.get(
        'CommentsRepository'
      );

      commentIcon.addEventListener('click', () => {
        commentsRepository.openNewCommentThread({
          channelId,
          threadId: uuidv4(),
          target: document.querySelector('.top')
        });
      });
    }
  }, [commentEditor, channelId, isProjectDisabled]);

  useEffect(() => {
    const commentButton = document.querySelector('.new-comment-button');

    if (commentEditor && commentButton) {
      const commentsRepository = commentEditor.plugins.get(
        'CommentsRepository'
      );

      commentButton.addEventListener('click', () => {
        commentsRepository.openNewCommentThread({
          channelId,
          threadId: uuidv4(),
          target: document.querySelector('.top')
        });
      });
    }
  }, [commentEditor, channelId, isProjectDisabled]);

  useEffect(() => {
    if (commentEditor) {
      const commentRepository = commentEditor.plugins.get('CommentsRepository');

      commentThreads
        .filter(thread => !commentRepository.hasCommentThread(thread.id))
        .filter(thread => {
          if (thread.resolved && viewingStatus === 'resolved') return true;
          if (!thread.resolved && viewingStatus === 'unresolved') return true;
          return false;
        })
        .forEach(thread => {
          const formattedThread = formatThread(thread);
          const addedCommentThread = commentRepository.addCommentThread({
            ...formattedThread,
            target: document.querySelector('.bottom')
          });

          if (addedCommentThread && (thread.resolved || isProjectDisabled)) {
            addedCommentThread.isReadOnly = true;
          }
        });

      commentRepository.on(
        'change:activeCommentThread',
        (evt, propName, activeThread) => {
          if (activeThread) {
            const activeSavedThread = commentThreads.find(
              thread => thread.id === activeThread.id
            );

            currentThread = activeSavedThread;
          }
        }
      );
    }
  }, [commentEditor, commentThreads, isProjectDisabled, viewingStatus]);

  return (
    <div
      id="container"
      style={{ display: 'flex', position: 'relative', width: '100%' }}
    >
      {hasLoadedComments && project.id && currentUser.id && sidebar && (
        <CKEditor
          onInit={editor => {
            const annotations = editor.plugins.get('Annotations');
            annotations.switchTo('wideSidebar');
            setCommentEditor(editor);
          }}
          editor={ClassicEditor}
          config={{
            licenseKey: process.env.REACT_APP_CK_EDITOR_LICENSE,
            extraPlugins: [CommentsAdapter],
            toolbar: {
              items: []
            },
            sidebar: {
              container: sidebar
            },
            language: 'en',
            context,
            collaboration: { channelId },
            comments: {
              editorConfig: {
                extraPlugins: [MentionPlugin, CustomMentionView],
                mention: {
                  feeds: [
                    {
                      marker: '@',
                      feed: queryText =>
                        getFeedItems(
                          queryText,
                          project.users,
                          projectId,
                          currentThread
                        ),
                      minimumCharacters: 1
                    }
                  ]
                }
              },
              CommentThreadView: CustomCommentThreadView(
                dispatch,
                commentThreads,
                isEntitySharedWithClient,
                currentUser,
                project.users,
                entityVersion,
                isProjectDisabled
              ),
              CommentView: CustomCommentView(
                commentThreads,
                project.users,
                isProjectDisabled
              )
            }
          }}
          data={renderToString(children)}
        />
      )}
    </div>
  );
};

export default CommentEditor;
