import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Loader } from '@pwc/pwc-deals-ui';
import { useParams, useRouteMatch } from 'react-router-dom';
import { getReportPreviewState } from '../../../../../../common/redux/reportFormat/reportFormatSlice';
import {
  getReportPreviewThunk,
  submitAnalysisThunk
} from '../../../../../../common/redux/reportFormat/reportFormatThunk';
import './SaveAndPreview.scss';

const SaveAndPreview = ({ analysisDto }) => {
  const { projectId, analysisId, version } = useParams();
  const { data, loading, error, errorMessage } = useSelector(
    getReportPreviewState
  );
  const dispatch = useDispatch();
  const [reportData, setReportData] = useState(null);
  const match = useRouteMatch();

  useEffect(() => {
    async function updateData(...args) {
      const [projectID, analysisID, analysisDTO, baseUrl] = args;
      await dispatch(
        submitAnalysisThunk(projectID, analysisID, analysisDTO, baseUrl)
      );
    }
    if (
      projectId &&
      analysisDto &&
      !analysisId &&
      match.url.includes('create')
    ) {
      const baseUrl = `/projects/${projectId}/analyses`;
      updateData(projectId, undefined, analysisDto, baseUrl);
    } else if (
      projectId &&
      analysisDto &&
      analysisId &&
      match.url.includes('edit')
    ) {
      updateData(projectId, analysisId, analysisDto, undefined);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    async function getReportData() {
      await dispatch(
        getReportPreviewThunk(projectId, analysisId, version || 'latest')
      );
    }
    if (analysisId && !reportData) {
      getReportData();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (!reportData && data) {
      setReportData(data);
    }
  }, [reportData, data]);

  return (
    <div className="save-and-preview" data-testid="save-and-preview">
      {!loading && error && <div data-testid="error">{errorMessage}</div>}
      {!loading && !error && reportData && reportData.length === 0 && (
        <div data-testid="save-and-preview__no-data">
          Failed to load content
        </div>
      )}
      {loading && !error && <Loader data-testid="loading" />}

      {!loading && !error && reportData && reportData.length ? (
        <div data-testid="save-and-preview__preview-image">
          {reportData.map(image => {
            return (
              <img
                className="preview-image"
                key={image}
                src={image}
                alt="report preview"
              />
            );
          })}
        </div>
      ) : null}
    </div>
  );
};

export default SaveAndPreview;
