import { Constants } from '@pwc/pwc-deals-web-common';
import React, { useState } from 'react';
import { Popover } from 'react-bootstrap';
import { Button } from '@pwc/pwc-deals-ui';
import { useDispatch, useSelector } from 'react-redux';
import {
  getErrorMoreMenuState,
  hideMoreMenuError
} from '../../redux/errors/errorsSlice';

const MoreMenuPopup = React.forwardRef(({ itemId, actions, ...props }, ref) => {
  const [actionToConfirm, setActionToConfirm] = useState();
  const moreMenuError = useSelector(getErrorMoreMenuState);
  const dispatch = useDispatch();

  const onActionClick = action => {
    if (action.confirmation) {
      return () => {
        setActionToConfirm(action);
      };
    }
    return action.update;
  };

  const cancelActionToConfirm = () => {
    setActionToConfirm(undefined);
  };

  const confirmActionToConfirm = () => {
    actionToConfirm.update();
    setActionToConfirm(undefined);
  };

  const closeErrorState = () => {
    dispatch(hideMoreMenuError());
    cancelActionToConfirm();
  };

  const renderContent = () => {
    if (moreMenuError && moreMenuError.itemId === itemId) {
      let title = 'Unable to save change.';
      let message =
        'Due to a system error we are currently unable to save the change you made.';
      if (
        moreMenuError.moreMenuErrorType ===
        Constants.MoreMenuErrorType.LIBRARY_ITEM_CORRUPTED
      ) {
        title = "Can't share this analysis.";
        message =
          'Some content is missing. Please review and update it before trying again.';
      }

      return (
        <Popover
          id="popover-moremenu"
          ref={ref}
          data-testid={`content-actions-popup-menu-${itemId}`}
          {...props}
          className="popover-confirm-error"
        >
          <>
            <Popover.Content className="popup-menu">
              <Popover.Title>{title}</Popover.Title>
              <p
                data-testid="popover-confirmation-error-message"
                className="popup-description"
              >
                {message}
              </p>
              <div className="confirm-button-error-state">
                <Button
                  data-testid="popover-confirmation-error-cancel"
                  onClick={closeErrorState}
                  className="button-link mr-3"
                >
                  Cancel
                </Button>
              </div>
            </Popover.Content>
          </>
        </Popover>
      );
    }

    return (
      <Popover
        id="popover-moremenu"
        ref={ref}
        data-testid={`content-actions-popup-menu-${itemId}`}
        {...props}
        className={actionToConfirm ? 'popover-confirm' : ''}
      >
        {!actionToConfirm ? (
          <Popover.Content className="popup-menu">
            {actions.map(action => (
              <Button
                key={action.action}
                className="button-menu"
                onClick={onActionClick(action)}
                data-testid={`menu-button-${action.name}`}
                disabled={action.disabled}
              >
                {action.icon ? action.icon : null}
                {action.text}
              </Button>
            ))}
          </Popover.Content>
        ) : (
          <>
            <Popover.Content className="popup-menu popup-confirmation">
              <Popover.Title>
                {actionToConfirm.confirmation.title}
              </Popover.Title>
              <p data-testid="popover-confirmation-message">
                {actionToConfirm.confirmation.message}
              </p>
              <div className="confirm-buttons">
                <Button
                  data-testid="popover-confirmation-cancel"
                  onClick={cancelActionToConfirm}
                  className="button-link mr-3"
                >
                  Cancel
                </Button>
                <Button
                  data-testid="popover-confirmation-confirm"
                  onClick={confirmActionToConfirm}
                  className="button-small"
                >
                  {actionToConfirm.confirmation.confirmButtonText}
                </Button>
              </div>
            </Popover.Content>
          </>
        )}
      </Popover>
    );
  };

  return renderContent();
});

export default MoreMenuPopup;
