import React, { useState, useEffect, useRef } from 'react';
import { Formik } from 'formik';
import { useParams, useHistory } from 'react-router-dom';
import { ErrorUtilities } from '@pwc/pwc-deals-web-common';
import InsightForm from '../common/InsightForm';
import { insightSchema, emptyInsight } from '../common/insightModel';
import { postInsight } from '../insightsService';
import { viewInsightPath } from '../../../common/services/urlService';
import { getNarratives } from '../../narratives/narrativesService';
import { getLinksFromNarratives } from '../../narratives/narrativesUtilities';

const parentContentId = -1;

const CreateInsight = () => {
  const { projectId } = useParams();
  const history = useHistory();
  const formikRef = useRef();

  const [defaultInsight] = useState(emptyInsight());
  const [errorType, setErrorType] = useState();
  const [isSaving, setIsSaving] = useState(false);
  const [narratives, setNarratives] = useState(null);

  useEffect(() => {
    if (formikRef.current) {
      formikRef.current.resetForm();
      formikRef.current.validateForm(defaultInsight);
    }
  }, [history.location, formikRef, defaultInsight]);

  useEffect(() => {
    (async () => {
      getNarratives(projectId).then(setNarratives);
    })();
  }, [projectId]);

  const cancelAndGoBack = () => history.goBack();
  const gotoInsightsView = contentId =>
    history.replace(viewInsightPath(projectId, contentId));

  const handlePublish = async insight => {
    setIsSaving(true);
    try {
      const links = getLinksFromNarratives(narratives, parentContentId);
      const contentId = await postInsight(projectId, {
        ...insight,
        title: insight.title.trim(),
        body: insight.body.trim(),
        tags: insight.tags.map(tag => ({
          text: tag
        })),
        links
      });
      setErrorType();
      gotoInsightsView(contentId);
    } catch (e) {
      setIsSaving(false);
      setErrorType(ErrorUtilities.getContentErrorType(e));
    }
  };

  return (
    <Formik
      initialValues={defaultInsight}
      onSubmit={handlePublish}
      validationSchema={insightSchema}
      validateOnMount
      validateOnChange
      enableReinitialize
      innerRef={formikRef}
    >
      {({ errors, touched, isValid }) => (
        <InsightForm
          onCancel={cancelAndGoBack}
          isSaving={isSaving}
          isValid={isValid}
          errorType={errorType}
          errors={errors}
          touched={touched}
          projectId={projectId}
          parentContentId={parentContentId}
          narratives={narratives}
          setNarratives={setNarratives}
          onSaveText="Adding..."
          standbyText="Add"
        >
          <h1>Add an Insight</h1>
        </InsightForm>
      )}
    </Formik>
  );
};

export default CreateInsight;
