/* eslint-disable react/jsx-wrap-multilines */
import React, { useState } from 'react';
import { Constants, TextUtilities } from '@pwc/pwc-deals-web-common';
import RichTextForm from '../../../../common/components/RichTextForm/RichTextForm';
import { useSections } from '../../Freeform/FreeformAnalysisForm/FreeformAnalysisFormContext';
import { SECTION_STATE_ACTION } from '../../Freeform/FreeformAnalysisForm/FreeformAnalysisFormUtil/sectionsReducer';
import RichTextCardInner from '../RichTextCardInner';
import LibraryItemCard from './LibraryItemCard';
import './RichTextLibraryItemCard.scss';

const RichTextLibraryItemCard = ({
  libraryItem,
  isInSourceTray,
  setIsDragDisabled,
  isDragging,
  innerRef,
  ...props
}) => {
  const [, dispatch] = useSections();
  const [editState, setEditState] = useState(false);
  const [richText, setRichText] = useState(
    TextUtilities.parseRichText(libraryItem.isShared, libraryItem.data)
  );
  const [currentRichText, setCurrentRichText] = useState('');

  const getRenderableRichText = text =>
    text === Constants.EMPTY_RICH_TEXT_PLACEHOLDER ? '' : text;

  const handleEditClick = () => {
    setEditState(true);
    setIsDragDisabled(true);
    setCurrentRichText(getRenderableRichText(richText));
  };

  const handleCancel = () => {
    setEditState(false);
    setIsDragDisabled(false);
    setCurrentRichText('');
  };
  const handleSave = () => {
    setRichText(currentRichText);
    setEditState(false);
    setIsDragDisabled(false);

    dispatch({
      type: SECTION_STATE_ACTION.UPDATE_RICH_TEXT,
      payload: {
        libraryItemId: libraryItem.id,
        richText: currentRichText
      }
    });
  };

  const getHandleDeleteClick = () => {
    dispatch({
      type: SECTION_STATE_ACTION.ITEM_DELETED,
      payload: libraryItem.id
    });
  };

  return editState ? (
    <div ref={innerRef} {...props} className="rich-text-library-item-edit">
      <RichTextForm
        richText={currentRichText}
        setCurrentRichText={setCurrentRichText}
        handleCancel={handleCancel}
        handleSave={handleSave}
      />
    </div>
  ) : (
    <LibraryItemCard
      {...props}
      isDragging={isDragging}
      innerRef={innerRef}
      libraryItem={libraryItem}
    >
      <RichTextCardInner
        richText={richText}
        isInSourceTray={isInSourceTray}
        handleDeleteClick={getHandleDeleteClick}
        handleEditClick={handleEditClick}
      />
    </LibraryItemCard>
  );
};

export default RichTextLibraryItemCard;
