import { UserAccessTokenClient, UserAuthClient } from '../httpClient';
import { clearAllTokens, getAllTokens } from './userTokenService';

export const refreshTokens = async () => {
  const response = await UserAuthClient.get(
    `access/refreshTokens?redirectUri=${window.location.origin}/access}`
  );
  return response.data;
};

export const invalidateTokens = async () => {
  const tokens = getAllTokens();
  clearAllTokens();

  if (tokens.length) {
    try {
      await UserAccessTokenClient.post('access/invalidateTokens', {
        tokens
      });
      // eslint-disable-next-line no-empty
    } catch (_) {}
  }
};

export const getAuthenticationUrl = async (redirectUri, state) => {
  const response = await UserAccessTokenClient.get(
    `access/authenticationUrl?redirectUri=${redirectUri}&state=${state}`
  );

  return response.data;
};

export const getSignoutUrl = async () => {
  const response = await UserAccessTokenClient.get(`access/signOutUrl`);

  return response.data;
};

export const getTokens = async accessCode => {
  const response = await UserAccessTokenClient.get(
    `access/tokens?code=${accessCode}&redirectUri=${window.location.origin}/access`
  );

  return response.data;
};
