import { Icon, Loader } from '@pwc/pwc-deals-ui';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useLocation, useParams } from 'react-router-dom';
import qs from 'query-string';
import { Constants } from '@pwc/pwc-deals-web-common';
import '../analysis-top-bar-container.scss';
import './AnalysisReportPreview.scss';
import { getCurrentAnalysis, resetAnalysis } from '../../analysisSlice';
import {
  loadAnalysisThunk,
  loadStructuredAnalysisThunk
} from '../../analysisSliceThunk';
import ReportBuilderLink from './ReportBuilderLink';
import { getReportPreview } from './AnalysisReportService';
import StatusContainer from '../../../../common/components/StatusContainer/StatusContainer';
import {
  viewAnalysisPath,
  viewStructuredAnalysisPathForSubType
} from '../../../../common/services/urlService';

const AnalysisReportPreview = () => {
  const { projectId, analysisId, version } = useParams();
  const location = useLocation();
  const [reportData, setReportData] = useState([]);
  const [isReportLoading, setIsReportLoading] = useState(true);
  const [hasError, setHasError] = useState(false);

  const dispatch = useDispatch();
  const analysis = useSelector(getCurrentAnalysis);

  const { isUnStructured } = qs.parse(location.search);

  useEffect(() => {
    async function fetchReportPreview() {
      try {
        const reportPreview = await getReportPreview(
          projectId,
          analysisId,
          version
        );
        setReportData(reportPreview);
      } catch (e) {
        setHasError(true);
      } finally {
        setIsReportLoading(false);
      }
    }

    fetchReportPreview();
  }, [analysisId, projectId, version]);

  useEffect(() => {
    const analysisVersion =
      version === 'shared' ? Constants.VERSION_STATUS.SHARED.value : '';
    if (isUnStructured === 'false') {
      dispatch(
        loadStructuredAnalysisThunk(projectId, analysisId, analysisVersion)
      );
    } else {
      dispatch(loadAnalysisThunk(projectId, analysisId, analysisVersion));
    }
    return () => dispatch(resetAnalysis());
  }, [dispatch, projectId, analysisId, version, isUnStructured]);

  const analysisLink = analysis.isUnStructured
    ? viewAnalysisPath(projectId, analysis.contentThreadId)
    : viewStructuredAnalysisPathForSubType(projectId, analysis.contentSubType);

  return (
    <div className="analysis-report-preview">
      <div className="analysis-top-bar-container">
        <div className="analysis-top-bar">
          {analysis.contentId && (
            <Link
              to={analysisLink}
              className="analysis-report-preview__back-link"
            >
              <Icon
                name="left-chevron"
                width={14}
                className="analysis-report-preview__back-icon fill icon-orange"
              />
              Back to working view
            </Link>
          )}
        </div>
      </div>

      <div className="analysis-report-preview-container">
        <div className="analysis-report-preview__inner">
          <div className="analysis-report-preview__inner__status">
            {analysis.contentId && (
              <StatusContainer
                projectId={projectId}
                content={analysis}
                className="analysis-pill-container"
                allowDraftPill
              />
            )}
          </div>

          <div className="analysis-report-preview__inner__builder-link">
            <ReportBuilderLink projectId={projectId} />
          </div>
        </div>
        {hasError && <div>Failed to load content</div>}
        <>
          {isReportLoading || analysis.isLoading ? (
            <Loader dataTestId="loading-spinner" />
          ) : (
            reportData.map(image => {
              return (
                <img
                  className="preview-image"
                  key={image}
                  src={image}
                  alt="report preview"
                />
              );
            })
          )}
        </>
      </div>
    </div>
  );
};

export default AnalysisReportPreview;
