import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Button } from '@pwc/pwc-deals-ui';
import EnableForProjectState from '../security/EnableForProjectState';
import {
  getProjectDisclaimerSelector,
  loadProjectDisclaimerThunk,
  updateProjectDisclaimerThunk
} from './projectDisclaimerSlice';
import './ProjectDisclaimerSettings.scss';
import Separator from '../../common/components/Separator';
import RichTextEditor from '../../common/components/RichTextEditor/RichTextEditor';
import { sanitize } from '../../common/services/sanitizeService';

const ProjectDisclaimerSettings = ({ lightModeVariant }) => {
  const { projectId } = useParams();
  const dispatch = useDispatch();
  const [editMode, setEditMode] = useState(false);
  const [richText, setRichText] = useState('');
  const [focused, setFocused] = useState(false);

  useEffect(() => {
    dispatch(loadProjectDisclaimerThunk(projectId));
  }, [dispatch, projectId]);

  const projectDisclaimer = useSelector(
    getProjectDisclaimerSelector(projectId)
  );

  useEffect(() => {
    if (projectDisclaimer) {
      setRichText(projectDisclaimer.disclaimerHtml);
    }
  }, [projectDisclaimer]);

  const toggleEditMode = () => {
    if (editMode) {
      setRichText(projectDisclaimer.disclaimerHtml);
    }
    setEditMode(!editMode);
  };

  const onSubmit = () => {
    dispatch(updateProjectDisclaimerThunk(projectId, richText));

    toggleEditMode();
  };

  const disabledCheck = () => {
    if (projectDisclaimer.disclaimerHtml && richText) {
      return richText.length === 0;
    }
    return true;
  };

  return projectDisclaimer && !projectDisclaimer.isLoading ? (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <div
          data-testid="p-d-settings-container"
          className={`p-d-settings-container${
            lightModeVariant ? ' light-mode-variant' : ''
          }`}
        >
          <div className="p-d-header">
            <div data-testid="p-d-title" className="p-d-settings-title">
              {editMode ? 'Edit Project Disclaimer' : 'Project Disclaimer'}
            </div>
            {!editMode && (
              <div
                data-testid="p-d-settings-button-container"
                className="p-d-settings-button-container"
              >
                <Button
                  className="button-block sm p-d-edit-button"
                  type="submit"
                  disabled={isProjectDisabled}
                  data-testid="p-d-edit-button"
                  onClick={toggleEditMode}
                >
                  Edit
                </Button>
              </div>
            )}
          </div>

          <Separator />

          {editMode ? (
            <>
              <RichTextEditor
                richTextData={richText}
                onChange={setRichText}
                label="Description"
                onBlur={(_, editor) => {
                  setFocused(false);
                  editor.ui.view.toolbar.element.style.display = 'none';
                }}
                onFocus={(_, editor) => {
                  setFocused(true);
                  editor.ui.view.toolbar.element.style.display = 'block';
                }}
                isFocused={focused}
              />
              <div className="p-d-button-container">
                <Button
                  className="button-link mx-2 mr-4"
                  onClick={toggleEditMode}
                  data-testid="p-d-cancel-button"
                >
                  Cancel
                </Button>
                <Button
                  onClick={onSubmit}
                  data-testid="p-d-update-button"
                  disabled={disabledCheck()}
                >
                  Save
                </Button>
              </div>
            </>
          ) : (
            <div
              className="p-d-settings-content"
              data-testid="p-d-settings-content"
              dangerouslySetInnerHTML={{
                __html: sanitize(projectDisclaimer.disclaimerHtml)
              }}
            />
          )}
        </div>
      )}
    </EnableForProjectState>
  ) : null;
};

export default ProjectDisclaimerSettings;
