import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Constants } from '@pwc/pwc-deals-web-common';
import { updateProjectUsers } from '../../common/services/projectService';
import { getProject, loadProjectThunk } from '../project/projectSlice';
import UserEditSettings, { getInitialState } from './UserEditSettings';

const ClientSettings = () => {
  const project = useSelector(getProject);
  const dispatch = useDispatch();
  const [initialValues, setInitialValues] = useState(null);
  const [initialValuesSet, setInitialValuesSet] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [hasError, setHasError] = useState(false);

  useEffect(() => {
    if (!initialValuesSet) {
      if (project.users) {
        setInitialValues(getInitialState(project.users));

        setInitialValuesSet(true);
      } else {
        setInitialValues(getInitialState());
      }
    }
  }, [project, setInitialValues, initialValuesSet, setInitialValuesSet]);

  const fieldConfigs = [
    {
      role: Constants.USER_ROLES.CLIENT_ADMIN,
      title: Constants.USER_ROLES.CLIENT_ADMIN,
      description: `View approved content, add comments or assign client team members.`,
      showEmail: true,
      allowMany: true,
      allowApprovePending: true
    },
    {
      role: Constants.USER_ROLES.CLIENT_TEAM_MEMBER,
      title: Constants.USER_ROLES.CLIENT_TEAM_MEMBER,
      description: `View approved content or add comments.`,
      showEmail: true,
      allowMany: true,
      allowApprovePending: true
    }
  ];

  const onSubmit = async formattedValues => {
    setIsSaving(true);
    try {
      setHasError(false);

      await updateProjectUsers(project.id, formattedValues);

      await dispatch(loadProjectThunk(project.id));
      setIsSaving(false);
      setInitialValuesSet(false);
    } catch (e) {
      setIsSaving(false);
      setHasError(true);
    }
  };

  const onCancel = resetForm => resetForm();
  return (
    initialValuesSet && (
      <UserEditSettings
        initialValues={initialValues}
        fieldConfigs={fieldConfigs}
        onSubmit={onSubmit}
        onCancel={onCancel}
        submitButtonText="Save"
        isSaving={isSaving}
        hasError={hasError}
      >
        <div data-testid="clientSettings">
          <p>
            Client details need to be added to the PwC Identity Group and User
            Management Database before they can be invited to a project. Once
            they&apos;re on the database, enter their name or email address
            below. Once you&apos;re done, click &lsquo;Save&rsquo;.
          </p>
        </div>
      </UserEditSettings>
    )
  );
};
export default ClientSettings;
