import { Formik } from 'formik';
import React, { useState, useEffect, useRef } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { ErrorUtilities } from '@pwc/pwc-deals-web-common';
import { viewHypothesisPath } from '../../../common/services/urlService';
import HypothesisForm from '../common/HypothesisForm';
import { emptyHypothesis, hypothesisSchema } from '../common/hypothesisModel';
import { postHypothesis } from '../hypothesesService';
import './create-hypothesis.scss';
import { getNarratives } from '../../narratives/narrativesService';
import { getLinksFromNarratives } from '../../narratives/narrativesUtilities';

const parentContentId = -1;

const CreateHypothesis = () => {
  const history = useHistory();
  const { projectId } = useParams();
  const formikRef = useRef();

  const [errorType, setErrorType] = useState();
  const [isSaving, setIsSaving] = useState(false);
  const [defaultHypothesis] = useState(emptyHypothesis());
  const [narratives, setNarratives] = useState(null);

  useEffect(() => {
    if (formikRef.current) {
      formikRef.current.resetForm();
      formikRef.current.validateForm(defaultHypothesis);
    }
  }, [history.location, formikRef, defaultHypothesis]);

  useEffect(() => {
    (async () => {
      getNarratives(projectId).then(setNarratives);
    })();
  }, [projectId]);

  const cancelAndGoBack = () => history.goBack();

  const gotoHypothesisView = contentId => {
    history.replace(viewHypothesisPath(projectId, contentId));
  };

  const handlePublish = async hypothesis => {
    setIsSaving(true);
    try {
      const links = getLinksFromNarratives(narratives, parentContentId);
      const contentId = await postHypothesis(projectId, {
        ...hypothesis,
        title: hypothesis.title.trim(),
        body: hypothesis.body.trim(),
        conclusion: hypothesis.conclusion.trim(),
        tags: hypothesis.tags.map(tag => ({
          text: tag
        })),
        links
      });
      setErrorType();
      gotoHypothesisView(contentId);
    } catch (e) {
      setIsSaving(false);
      setErrorType(ErrorUtilities.getContentErrorType(e));
    }
  };

  return (
    <Formik
      initialValues={{
        ...defaultHypothesis
      }}
      enableReinitialize
      onSubmit={handlePublish}
      validationSchema={hypothesisSchema}
      validateOnMount
      validateOnChange
      innerRef={formikRef}
    >
      {({ errors, touched, isValid }) => (
        <HypothesisForm
          onCancel={cancelAndGoBack}
          isSaving={isSaving}
          errorType={errorType}
          errors={errors}
          touched={touched}
          isValid={isValid}
          projectId={projectId}
          parentContentId={parentContentId}
          narratives={narratives}
          setNarratives={setNarratives}
          onSaveText="Adding..."
          standbyText="Add"
        >
          <h1>Add a Key Focus Area</h1>
        </HypothesisForm>
      )}
    </Formik>
  );
};

export default CreateHypothesis;
