import React, { useState, useEffect, useRef, useMemo } from 'react';
import { get, isEmpty } from 'lodash';
import { useSelector } from 'react-redux';
import { Loader } from '@pwc/pwc-deals-ui';
import PowerBiReportDisplayConfig from './PowerBiReportDisplayConfig';
import { getReportEmbedData } from '../powerBiService';
import { getIsDarkModeSelector } from '../../../common/redux/userSettings/userSettingsSlice';
import {
  buildPowerBiReportConfig,
  computeContainerDimensions
} from './powerBiReportHelpers';

import './PowerBiReport.scss';

const PowerBiReport = ({ item, projectId }) => {
  const [isE2eEnvironment] = useState(Boolean(window.Cypress));
  const [reportData, setReportData] = useState(null);
  const powerBiContainerRef = useRef(null);
  const isDarkMode = useSelector(getIsDarkModeSelector);
  const [powerBiContainerDimensions, setPowerBiContainerDimensions] = useState({
    height: '650px',
    width: '100%'
  });

  const toFetchEmbeddedData = useMemo(() => {
    if (!isEmpty(item.data) && !item.data.pageName) {
      const data = JSON.parse(item.data);
      if (!isEmpty(data.ReportId)) {
        return true;
      }
      return false;
    }
    return false;
  }, [item]);

  useEffect(() => {
    const fetchReportData = async () => {
      window.powerbi.reset(powerBiContainerRef.current);
      window.powerbi.bootstrap(powerBiContainerRef.current, {
        type: 'report'
      });
      const powerBiData = await getReportEmbedData(
        projectId,
        item.id,
        item.isShared
      );
      const pageName = get(JSON.parse(item.data), 'PageName', '');

      setReportData({ ...powerBiData, pageName });
    };
    if (item && item.type && !toFetchEmbeddedData) {
      if (!isEmpty(item.data)) {
        const { pageName } = item.data;
        setReportData({ ...item, pageName });
      } else {
        setReportData(item);
      }
    } else if (toFetchEmbeddedData) {
      fetchReportData();
    }
  }, [item, projectId, toFetchEmbeddedData]);

  useEffect(() => {
    async function embedPowerBi() {
      const embeddedReport = window.powerbi.embed(
        powerBiContainerRef.current,
        buildPowerBiReportConfig({
          embedToken: reportData.embedToken,
          embedUrl: reportData.embedUrl,
          pageName: reportData.pageName,
          reportId: reportData.reportId,
          isDarkMode
        })
      );
      embeddedReport.on('loaded', () => {
        embeddedReport.getPages().then(pages => {
          const foundPageIndex = pages.findIndex(
            page => page.name === reportData.pageName
          );
          const pageIndex = foundPageIndex < 0 ? 0 : foundPageIndex;
          const { height, width } = pages[pageIndex].defaultSize;
          const [computedHeight, computedWidth] = computeContainerDimensions(
            height,
            width,
            !!reportData.pageName
          );
          setPowerBiContainerDimensions({
            height: computedHeight,
            width: computedWidth
          });
        });
      });
    }
    if (reportData) {
      embedPowerBi();
    }
  }, [reportData, isDarkMode]);

  const showPowerBi = reportData;
  const powerBiDisplay = !showPowerBi ? 'none' : null;

  return (
    <>
      {!reportData && <Loader dataTestId="library-item-loading-indicator" />}
      <div data-testid="powerbi-wrapper" className="powerbi-wrapper">
        {isE2eEnvironment && reportData && (
          <PowerBiReportDisplayConfig
            embedToken={reportData.embedToken}
            reportId={reportData.reportId}
            embedUrl={reportData.embedUrl}
            reportTitle={reportData.reportTitle}
            pageName={reportData.pageName}
          />
        )}
        <div
          ref={powerBiContainerRef}
          data-testid="powerbi-container"
          style={{
            height: powerBiContainerDimensions.height,
            maxWidth: powerBiContainerDimensions.width,
            display: powerBiDisplay
          }}
        >
          Loading Power BI Report...
        </div>
      </div>
    </>
  );
};

export default PowerBiReport;
