import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Route, Redirect } from 'react-router-dom';
import { setLoggedInUserThunk } from '../../common/redux/security/loggedInUser/setLoggedInUserThunk';
import authorizationService from '../../common/services/security/authorizationService';
import { PendoStarter } from '../userAnalytics/PendoStarter';

const PrivateRoute = ({
  component: Component,
  tAndCHasAccepted,
  requireTAndCAcceptance = true,
  ...rest
}) => {
  const dispatch = useDispatch();
  const hasValidToken = authorizationService.hasValidToken();
  useEffect(() => {
    if (hasValidToken) {
      dispatch(setLoggedInUserThunk());
    }
  }, [hasValidToken, dispatch]);

  return (
    <Route
      {...rest}
      render={({ location }) => {
        if (hasValidToken) {
          if (
            requireTAndCAcceptance &&
            authorizationService.isClientUser() &&
            !tAndCHasAccepted
          ) {
            return (
              <Redirect to={{ pathname: '/', state: { from: location } }} />
            );
          }
          return (
            <PendoStarter>
              <Component />
            </PendoStarter>
          );
        }
        return (
          <Redirect
            to={{
              pathname: '/',
              state: { from: location }
            }}
          />
        );
      }}
    />
  );
};

export default PrivateRoute;
