import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Tabs } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import { FormLayout } from '../../common/components/layouts';
import PageTitle from '../../common/components/PageTitle/PageTitle';
import ProjectDetails from './ProjectDetails';
import ProjectStatus from './ProjectStatus';
import ClientSettings from './ClientSettings';
import PwcTeamSettings from './PwCTeamSettings';
import NotificationsSettings from './NotificationSettings';
import { getProject } from '../project/projectSlice';
import userTokenService from '../../common/services/security/userTokenService';
import ProjectDisclaimerSettings from '../projectDisclaimer/ProjectDisclaimerSettings';
import './Settings.scss';
import ClientSettingsClientView from './ClientSettingsClientView';

const Settings = () => {
  const project = useSelector(getProject);
  const [projectDetailsTabVisible, setProjectDetailsTabVisible] = useState(
    false
  );
  const [projectStatusTabVisible, setProjectStatusTabVisible] = useState(false);
  const [clientTabVisible, setClientTabVisible] = useState(false);
  const [clientViewClientTabVisible, setClientViewClientTabVisible] = useState(
    false
  );
  const [pwcTeamTabVisible, setPwcTeamTabVisible] = useState(false);
  const [
    projectDisclaimerTabVisible,
    setProjectDisclaimerTabVisible
  ] = useState(false);
  const [notificationsTabVisible, setNotificationsTabVisible] = useState(false);

  useEffect(() => {
    if (project.id) {
      Promise.all([
        userTokenService.hasProjectPermission(project.id, [
          Constants.USER_PERMISSIONS.MANAGE_USERS
        ]),
        userTokenService.hasProjectPermission(project.id, [
          Constants.USER_PERMISSIONS.MANAGE_CLIENT_USERS
        ]),
        userTokenService.hasProjectPermission(project.id, [
          Constants.USER_PERMISSIONS.CONTRIBUTE
        ]),
        userTokenService.hasProjectRoles(
          project.id,
          Constants.USER_ROLE_GROUPS.CLIENT
        ),
        userTokenService.hasProjectRoles(project.id, [
          Constants.USER_ROLES.CLIENT_ADMIN
        ])
      ]).then(
        ([
          hasManageUsersPermission,
          hasManageClientUsersPermission,
          hasProjectDisclaimerPermission,
          isClient,
          isClientAdmin
        ]) => {
          setProjectDetailsTabVisible(hasManageUsersPermission && !isClient);
          setProjectStatusTabVisible(!isClient);
          setPwcTeamTabVisible(hasManageUsersPermission && !isClient);
          setClientTabVisible(hasManageClientUsersPermission && !isClient);
          setProjectDisclaimerTabVisible(
            hasProjectDisclaimerPermission && !isClient
          );
          setClientViewClientTabVisible(isClientAdmin);
          setNotificationsTabVisible(true);
        }
      );
    }
  }, [project.id]);

  return (
    project.id && (
      <FormLayout className="settings">
        <div className="settings-container" data-testid="settings-container">
          <PageTitle title="Settings" size="h2" />
          <Tabs>
            {projectDetailsTabVisible && (
              <div label="Project Details">
                <ProjectDetails />
              </div>
            )}
            {projectStatusTabVisible && (
              <div label="Project Status">
                <ProjectStatus projectId={project.id} />
              </div>
            )}
            {pwcTeamTabVisible && (
              <div label="PwC Team">
                <PwcTeamSettings />
              </div>
            )}
            {clientTabVisible && (
              <div label="Client">
                <ClientSettings />
              </div>
            )}
            {clientViewClientTabVisible && (
              <div label="Team">
                <ClientSettingsClientView />
              </div>
            )}
            {projectDisclaimerTabVisible && (
              <div label="Project Disclaimer">
                <ProjectDisclaimerSettings />
              </div>
            )}
            {notificationsTabVisible && (
              <div label="Notifications">
                <NotificationsSettings />
              </div>
            )}
          </Tabs>
        </div>
      </FormLayout>
    )
  );
};

export default Settings;
