import React, { useMemo, useState } from 'react';
import { useSelector } from 'react-redux';

import { LAYOUTS_BY_TYPE } from './Layouts';
import './LayoutTab.scss';

import { useSections } from '../../../../../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { getReportFormatLayoutState } from '../../../../../../../../common/redux/reportFormat/reportFormatSlice';

export const testId = 'rs-panel-layout-tab';

const useActiveSectionLayoutId = () => {
  const [{ currentSections = [] }] = useSections();
  const { activeSectionIndex } = useSelector(getReportFormatLayoutState);

  return useMemo(
    () =>
      currentSections[activeSectionIndex] &&
      currentSections[activeSectionIndex].layoutType,
    [currentSections, activeSectionIndex]
  );
};

const LayoutTab = ({ handleLayoutTypeChange }) => {
  const [layoutsByType] = useState(LAYOUTS_BY_TYPE);

  const activeSectionLayoutId = useActiveSectionLayoutId();

  const changeLayout = layoutId => {
    handleLayoutTypeChange(layoutId);
  };
  return (
    <div data-testid={testId} className="rs-panel-layout-tab2">
      <div className="rs-panel-available-layouts2">
        {layoutsByType.map((layouts, idx) => (
          <ul
            // eslint-disable-next-line react/no-array-index-key
            key={`${layouts.length}-${idx}`}
            className="rs-panel-available-layouts2__list"
            data-testid="rs-panel-available-layouts2__list"
          >
            {layouts.map(layout => {
              return (
                <li
                  className="small-container"
                  key={`${layout.id}-${layout.type}`}
                  data-testid="layout-container"
                >
                  <img
                    onClick={() => {
                      changeLayout(layout.id);
                    }}
                    onKeyDown={() => {
                      changeLayout(layout.id);
                    }}
                    src={layout.image}
                    id={layout.id}
                    alt="One column"
                    // eslint-disable-next-line jsx-a11y/no-noninteractive-element-to-interactive-role
                    role="button"
                    data-testid={`layout-image:${layout.id}`}
                    tabIndex={0}
                  />
                  {layout.id === activeSectionLayoutId && (
                    <div className="layout-image-overlay" />
                  )}
                </li>
              );
            })}
          </ul>
        ))}
      </div>
    </div>
  );
};

export default LayoutTab;
