const replaceSlugs = (params, path) => {
  const paramsKeys = Object.keys(params);
  const hasParams = paramsKeys.length > 0;

  if (!hasParams) {
    return path;
  }

  return paramsKeys.reduce(
    (desluggedPath, param) => desluggedPath.replace(`:${param}`, params[param]),
    path
  );
};

const BreadcrumbsContainer = () => {
  const location = useLocation();
  const [hide, setHide] = useState(true);
  const customNames = useCustomRouteNames();

  const path = location.pathname;
  useEffect(() => {
    setHide(
      path.toLowerCase() === '/unauthorized' ||
        path.toLowerCase() === '/notfound' ||
        path.toLowerCase() === '/unauthenticated'
    );
  }, [path]);

  if (hide) {
    return null;
  }

  if (path.indexOf('policies') > -1) {
    const breadcrumbs = [
      { text: 'Projects', path: '/projects' },
      { text: 'Terms & Conditions', path }
    ];
    return (
      <div>
        <span className="breadcrumb-root">Junction</span>
        {' | '}
        <Breadcrumbs breadcrumbs={breadcrumbs} />
      </div>
    );
  }

  return (
    <div>
      <span className="breadcrumb-root">
        {EnvironmentVariableUtilities.platformName}
      </span>
      {' | '}
      {allRoutes.map(route => (
        <Route
          exact
          path={route.path}
          key={route.path}
          render={props => {
            const routeParams = props.match.params;
            const routePath = props.match.path;

            let breadcrumbs;

            if (routeParams.projectId === 'create-project') {
              breadcrumbs = [
                { text: 'Projects', path: '/projects' },
                { text: 'New', path: '/projects/create-project' }
              ];
            } else {
              breadcrumbs = allRoutes
                .filter(breadcrumbRoute =>
                  routePath.includes(breadcrumbRoute.path)
                )
                .map(breadcrumbRoute => {
                  return {
                    text: breadcrumbRoute.customName
                      ? getDisplayName(
                          customNames[breadcrumbRoute.customName] || ''
                        )
                      : breadcrumbRoute.text,
                    path: replaceSlugs(routeParams, breadcrumbRoute.path)
                  };
                });
            }

            return <Breadcrumbs breadcrumbs={breadcrumbs} />;
          }}
        />
      ))}
    </div>
  );
};

export default BreadcrumbsContainer;
