import _flatMap from 'lodash/flatMap';
import _values from 'lodash/values';
import _difference from 'lodash/difference';

export const filterNarrativesForContent = (narratives, contentId) => {
  const id = parseInt(contentId, 10);
  const filteredLinks = {};
  Object.keys(narratives).forEach(key => {
    filteredLinks[key] = narratives[key].filter(item => {
      return item.links.map(link => link.id).includes(id);
    });
  });
  return filteredLinks;
};

export const getLinksFromNarratives = (narratives, contentId) => {
  const links = [];
  Object.keys(narratives).forEach(key => {
    narratives[key].forEach(narrative => {
      narrative.links.forEach(link => {
        if (link.linked !== false && link.id === contentId) {
          links.push(narrative.contentId);
        }
      });
    });
  });
  return links;
};

export const getTopicLinksForContent = (
  narratives,
  existingContentLinks = []
) => {
  const narrativeLinks = _flatMap(_values(narratives), contentArray =>
    contentArray.map(c => c.contentId)
  );
  const existingLinks = existingContentLinks.map(c => c.id);
  const topicLinks = _difference(existingLinks, narrativeLinks);

  return topicLinks;
};
