import { ProjectStateManagementHttpClient } from '../../common/services/httpClient';

export const getProjectApproval = async (projectId, userId) => {
  try {
    const response = await ProjectStateManagementHttpClient().get(
      `approval/${projectId}?userid=${userId}`
    );

    return response.data;
  } catch (error) {
    // eslint-disable-next-line
    console.log(error);
    return error;
  }
};

// Pausing

export const callPauseEndpoint = async (
  action,
  projectId,
  userId,
  approvalId
) => {
  try {
    const response = await ProjectStateManagementHttpClient().post(
      `pause/${projectId}/${action}`,
      {
        userId,
        approvalId: approvalId || ''
      }
    );

    return response.data;
  } catch (error) {
    // eslint-disable-next-line
    console.log(error);
    return error;
  }
};

export const callPauseApprovalEndpoint = async (
  action,
  projectId,
  userId,
  approvalId
) => {
  try {
    const response = await ProjectStateManagementHttpClient().post(
      `approval/${projectId}/pause/${action}`,
      {
        userId,
        approvalId: String(approvalId) || ''
      }
    );

    return response.data;
  } catch (error) {
    // eslint-disable-next-line
    console.log(error);
    return error;
  }
};

export const callReactivateApprovalEndpoint = async (
  action,
  projectId,
  userId,
  approvalId
) => {
  try {
    const response = await ProjectStateManagementHttpClient().post(
      `approval/${projectId}/unpause/${action}`,
      {
        userId,
        approvalId: String(approvalId) || ''
      }
    );

    return response.data;
  } catch (error) {
    // eslint-disable-next-line
    console.log(error);
    return error;
  }
};

