import React from 'react';
import { Link } from 'react-router-dom';
import Separator from '../../../common/components/Separator';
import ContentActionsRowMenu from '../../../common/components/ContentActions/ContentActionsRowMenu';
import { viewInsightPath } from '../../../common/services/urlService';
import ShowForPermissions from '../../security/ShowForPermissions';
import AccordionConnectedContent from '../../narratives/AccordionConnectedContent/AccordionConnectedContent';
import './InsightsGridCard.scss';
import TagList from '../../analyses/common/TagList/TagList';
import PillContainer from '../../../common/components/Pills/PillContainer';
import { sanitize } from '../../../common/services/sanitizeService';

const InsightsGridCard = ({
  insight,
  narratives,
  projectId,
  onEditMenuClick,
  onDeleteMenuClick,
  index
}) => {
  return (
    <div className="card-container">
      <div className="row">
        <div className="col down">
          <div className="card-title">
            <Link
              to={viewInsightPath(projectId, insight.contentThreadId)}
              data-testid={`insight-detail-grid-link-${index}`}
              aria-label="View Insight"
            >
              <h2>{insight.title}</h2>
            </Link>
          </div>
          <span
            className="card-description"
            data-testid={`insight-description-${index}`}
            dangerouslySetInnerHTML={{ __html: sanitize(insight.body) }}
          />

          <AccordionConnectedContent
            narratives={narratives}
            projectId={projectId}
            index={index}
          />
        </div>
      </div>

      <Separator />

      <div className="card-action-footer">
        <PillContainer projectId={projectId} content={insight} allowDraftPill />
        <div className="card-actions">
          <ShowForPermissions
            projectId={projectId}
            permissions={['Contribute']}
          >
            <ContentActionsRowMenu
              projectId={projectId}
              itemId={insight.contentThreadId}
              onEditMenuClick={onEditMenuClick}
              onDeleteMenuClick={onDeleteMenuClick}
            />
          </ShowForPermissions>
        </div>
      </div>
      <div className="row">
        {insight.tags && insight.tags.length > 0 && (
          <TagList tags={insight.tags} testId="insights-tag-list" />
        )}
      </div>
    </div>
  );
};

export default InsightsGridCard;
