import React, { useEffect, useRef } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { NavLink, useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import PageTitle from '../../../common/components/PageTitle/PageTitle';
import './TermsAndConditionsView.scss';
import {
  getTermsAndConditions,
  getTermsAndConditionsLoading
} from '../../../common/redux/termsAndConditions/termsAndConditionsSlice';
import { getTermsAndConditionsThunk } from '../../../common/redux/termsAndConditions/getTermsAndConditionsThunk';
import authorizationService from '../../../common/services/security/authorizationService';

import Policy from '../common/Policy';

const TermsAndConditionsView = () => {
  const tAndCs = useSelector(getTermsAndConditions);
  const { policyId } = useParams();
  const selectedPolicy = tAndCs && tAndCs.find(tAndC => tAndC.id === policyId);
  const contentRef = useRef();
  const tAndCsLoading = useSelector(getTermsAndConditionsLoading);
  const dispatch = useDispatch();
  const shouldShowLinks = tAndCs && tAndCs.length > 0;

  useEffect(() => {
    if (authorizationService.hasValidToken() && !tAndCs && !tAndCsLoading) {
      dispatch(getTermsAndConditionsThunk());
    }

    if (contentRef.current) {
      contentRef.current.scrollTop = 0;
    }
  }, [policyId, contentRef, dispatch, tAndCs, tAndCsLoading]);

  return (
    <div className="terms-and-conditions-view project-overview">
      <PageTitle title="Legal Information" size="h1" />

      {shouldShowLinks && (
        <div className="tabs" data-testid="terms-and-conditions-tabs">
          <div className="tab-list">
            {Constants.TERMS_AND_CONDITIONS_LINKS.map(link => (
              <NavLink
                key={link.key}
                to={`/policies/${link.key}`}
                data-testid={`terms-and-conditions-tab-${link.key}`}
              >
                {link.text}
              </NavLink>
            ))}
          </div>

          {selectedPolicy && (
            <div
              ref={contentRef}
              className="tab-content"
              data-testid="terms-and-conditions-tab-content"
            >
              <Policy policy={selectedPolicy} hideDateModified />
              <div className="terms-and-conditions-footer-fade" />
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default TermsAndConditionsView;
