import { ProjectHttpClient } from '../../common/services/httpClient';
import { getNarrativesForKfas } from '../narratives/narrativesService';

const mapHypothesisToApi = hypothesis => ({
  ...hypothesis,
  body: hypothesis.body || null,
  conclusion: hypothesis.conclusion || null,
  createdBy: hypothesis.createdBy || 'unset'
});

const sortByNewestDate = hypotheses =>
  hypotheses.sort((hypothesisA, hypothesisB) => {
    const dateA = new Date(hypothesisA.dateModified);
    const dateB = new Date(hypothesisB.dateModified);
    return dateB - dateA;
  });

const sortBySortOrder = hypotheses =>
  hypotheses.sort(
    (hypothesisA, hypothesisB) => hypothesisA.sortOrder - hypothesisB.sortOrder
  );

const baseServicePath = (projectId, hypothesisThreadId, versionStatus) => {
  const pathForId = hypothesisThreadId ? `/${hypothesisThreadId}` : '';
  const pathForStatus = versionStatus ? `/${versionStatus}` : '';

  return `projects/${projectId}/hypotheses${pathForId}${pathForStatus}`;
};

export const getHypotheses = async projectId => {
  const response = await ProjectHttpClient.get(baseServicePath(projectId));
  return sortBySortOrder(response.data);
};

export const getHypothesesWithConnectedData = async projectId => {
  const kfas = await getHypotheses(projectId);
  const kfasWithConnectedContentData = await getNarrativesForKfas(projectId);

  const emptyConnectedContent = {
    connectedKfas: [],
    connectedInsights: [],
    connectedAnalyses: []
  };
  return kfas.map(kfa => {
    const connectedContent =
      kfasWithConnectedContentData.find(
        kfaWithConnectedContentData =>
          kfaWithConnectedContentData.contentThreadId === kfa.contentThreadId
      ) || emptyConnectedContent;

    return {
      ...kfa,
      ...connectedContent
    };
  });
};

export const getSharedHypotheses = async projectId => {
  const response = await ProjectHttpClient.get(
    `projects/${projectId}/hypotheses?onlyShared=true`
  );
  return sortByNewestDate(response.data);
};

export const getHypothesis = async (
  projectId,
  hypothesisThreadId,
  versionStatus
) => {
  const response = await ProjectHttpClient.get(
    `${baseServicePath(projectId, hypothesisThreadId, versionStatus)}`
  );
  return response.data;
};

export const postHypothesis = async (projectId, hypothesis) => {
  const response = await ProjectHttpClient.post(
    baseServicePath(projectId),
    mapHypothesisToApi(hypothesis)
  );
  return response.data;
};

export const putHypothesis = async (projectId, hypothesis) => {
  const response = await ProjectHttpClient.put(
    `${baseServicePath(projectId, hypothesis.contentThreadId)}`,
    mapHypothesisToApi(hypothesis)
  );

  return response.data;
};

export const deleteHypothesis = async (projectId, hypothesisThreadId) => {
  const response = await ProjectHttpClient.delete(
    baseServicePath(projectId, hypothesisThreadId)
  );
  return response.data;
};

export const getHypothesisVersions = async (projectId, hypothesisThreadId) => {
  const response = await ProjectHttpClient.get(
    `${baseServicePath(projectId, hypothesisThreadId)}/versions`
  );
  return response.data;
};

export const updateHypothesisStatus = async (
  projectId,
  hypothesisThreadId,
  workflowAction
) => {
  const url = `${baseServicePath(
    projectId,
    hypothesisThreadId
  )}/${workflowAction}`;
  const response = await ProjectHttpClient.post(url);

  return response.data;
};

export const updateHypothesesOrder = async (
  projectId,
  orderedContentThreadIds
) => {
  const response = await ProjectHttpClient.post(
    `${baseServicePath(projectId)}/sortorder`,
    orderedContentThreadIds
  );

  return response.data;
};
