import React, { useState } from 'react';
import { Button, Icon, TextInput } from '@pwc/pwc-deals-ui';
import EditConnectedContentSection from '../EditConnectedContentSection/EditConnectedContentSection';
import useConnectedNarratives from './useConnectedNarratives';
import './EditConnectedContent.scss';

const mergeContent = (narratives, updatedNarratives, key) => {
  return narratives[key].map(original => ({
    ...original,
    ...(updatedNarratives[key].filter(
      updated => updated.contentId === original.contentId
    )[0] || {})
  }));
};

const EditConnectedContent = ({
  projectId,
  parentContentId,
  narratives,
  setNarratives
}) => {
  const [isViewingAll, setIsViewingAll] = useState(false);
  const [search, setSearch] = useState('');

  const {
    availableToConnectNarratives,
    filteredAvailableToConnectNarratives,
    previouslyConnectedNarratives
  } = useConnectedNarratives(parentContentId, narratives, search);

  const handleSetNarratives = updatedSectionNarratives => {
    const updatedNarratives = {
      hypotheses: mergeContent(
        narratives,
        updatedSectionNarratives,
        'hypotheses'
      ),
      insights: mergeContent(narratives, updatedSectionNarratives, 'insights'),
      analyses: mergeContent(narratives, updatedSectionNarratives, 'analyses')
    };
    setNarratives(updatedNarratives);
  };

  const hasNarratives = narratives && Object.keys(narratives).length > 0;
  const hasPreviouslyConnectedNarratives =
    previouslyConnectedNarratives &&
    Object.values(previouslyConnectedNarratives).filter(_ => _.length).length;

  const flattenedNarratives =
    narratives &&
    Object.keys(narratives)
      .map(key => narratives[key])
      .reduce((x, y) => x.concat(y), []);

  const searchField = {
    name: 'connected-content-search',
    value: search,
    onChange: e => setSearch(e.target.value)
  };

  return (
    hasNarratives && (
      <>
        <br />
        <div className="connected-content-controls">
          <div>
            <Icon
              name="hyperlink"
              width={16}
              className="icon fill connect-content-icon"
            />
            Connect a Key Focus Area, Insight or Analysis (optional)
          </div>
          <div className="halfwidth">
            <TextInput
              field={searchField}
              placeholder="Search and select"
              iconLeft="search"
              basic
            />
            {!isViewingAll &&
              flattenedNarratives.filter(_ => _.contentId !== parentContentId)
                .length > 0 && (
                <Button
                  className="button-link"
                  onClick={e => {
                    e.preventDefault();
                    setIsViewingAll(!isViewingAll);
                  }}
                  data-testid="btn-view-all-content"
                >
                  View All
                </Button>
              )}
          </div>
        </div>

        {(isViewingAll || !!search) && (
          <EditConnectedContentSection
            projectId={projectId}
            narratives={filteredAvailableToConnectNarratives}
            allNarratives={availableToConnectNarratives}
            parentContentId={parentContentId}
            setNarratives={handleSetNarratives}
            testId="unconnected-content"
            search={search}
          />
        )}

        {hasPreviouslyConnectedNarratives ? (
          <EditConnectedContentSection
            projectId={projectId}
            narratives={previouslyConnectedNarratives}
            allNarratives={narratives}
            parentContentId={parentContentId}
            setNarratives={handleSetNarratives}
            testId="connected-content"
            title="Connected content"
          />
        ) : null}
      </>
    )
  );
};

export default EditConnectedContent;
