import React, { useEffect, useState } from 'react';
import { renderToStaticMarkup } from 'react-dom/server';
import { Loader } from '@pwc/pwc-deals-ui';
import { getLibraryItemWithHtml } from '../../../../features/contentLibrary/libraryItemService';
import './ContentItem.scss';
import { sanitize } from '../../../services/sanitizeService';

const ContentItem = ({
  projectId,
  libraryItemId,
  contentId,
  sharedContent,
  children = <h5>Failed To Load Content</h5>
}) => {
  const [htmlData, setHtmlData] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  // eslint-disable-next-line no-unused-vars
  const [svgWidth, setSvgWidth] = useState(null);
  const [hasError, setHasError] = useState(false);

  useEffect(() => {
    const getWidthFromSvg = data => {
      if (data.includes('svg')) {
        const xmlDoc = new DOMParser().parseFromString(data, 'application/xml');
        const svg = xmlDoc.getElementsByTagName('svg')[0];
        if (svg) {
          const viewBox = svg.getAttribute('viewbox');
          if (viewBox) {
            return viewBox.split(' ')[2];
          }
        }
      }
      return null;
    };

    if (sharedContent) {
      setHtmlData(sharedContent);
      setSvgWidth(getWidthFromSvg(sharedContent));
      setIsLoading(false);
      return;
    }

    getLibraryItemWithHtml(projectId, libraryItemId, contentId)
      .then(data => {
        setHasError(false);
        setHtmlData(data);
        setSvgWidth(getWidthFromSvg(data));
        setIsLoading(false);
      })
      .catch(() => {
        setHasError(true);
        setIsLoading(false);
      });
  }, [projectId, libraryItemId, contentId, sharedContent]);

  return (
    <>
      {isLoading ? (
        <Loader dataTestId="excel-content-loader" />
      ) : (
        <div
          data-testid="excel-content-item"
          className="content-item-container"
          dangerouslySetInnerHTML={{
            __html: sanitize(
              hasError ? renderToStaticMarkup(children) : htmlData
            )
          }}
        />
      )}
    </>
  );
};

export default ContentItem;
