import { forEach as _forEach } from 'lodash';
import React, { useState, useRef } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Icon } from '@pwc/pwc-deals-ui';
import { Overlay } from 'react-bootstrap';
import EnableForProjectState from '../../../features/security/EnableForProjectState';
import RowActionsButton from './RowActionsButton';
import MoreMenuPopup from './MoreMenuPopup';

const hideOverlay = (callback, setShow) => () => {
  setShow(false);
  if (callback) {
    callback();
  }
};

const createDeleteAction = (onDelete, disabled, setShow) => ({
  name: 'delete',
  action: 'delete',
  text: 'Delete',
  update: hideOverlay(onDelete, setShow),
  icon: <Icon name="bin" width="21" className="sr2 outline icon-orange" />,
  disabled
});

const createEditAction = (onEdit, setShow) => ({
  name: 'edit',
  action: 'edit',
  text: 'Edit',
  update: hideOverlay(onEdit, setShow),
  icon: <Icon name="pencil" width="21" className="sr2 outline icon-orange" />
});

const createViewClientAction = viewClient => ({
  action: 'View Content Version',
  name: viewClient.isOnClientVersion ? 'in-progress-version' : 'client-version',
  text: viewClient.isOnClientVersion
    ? 'Switch to In Progress Version'
    : 'Switch to client version',
  update: viewClient.isOnClientVersion
    ? viewClient.onShowDraftVersion
    : viewClient.onShowClientVersion,
  icon: (
    <Icon name="partnership" width="21" className="sr2 outline icon-orange" />
  ),
  disabled: viewClient.isOnClientVersion ? false : !viewClient.hasClientVersion
});

const createRefreshAction = (onRefresh, disabled, setShow) => ({
  name: 'refresh',
  action: 'refresh',
  text: 'Refresh data',
  update: hideOverlay(onRefresh, setShow),
  confirmation: undefined,
  icon: <Icon name="refresh" width="21" className="sr2 outline icon-orange" />,
  disabled
});

const createPreviewReportAction = (url, history) => ({
  action: 'Preview Report',
  name: 'preview-report',
  text: 'Preview report',
  update: () => {
    history.push(url);
  },
  icon: <Icon name="report" width="21" className="sr2 outline icon-orange" />
});

const MoreMenu = ({
  itemId,
  onDelete,
  onRefresh,
  onEdit,
  viewClient,
  workflowActions,
  reportPreviewUrl,
  isDisabled
}) => {
  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);
  const history = useHistory();
  const { projectId } = useParams();

  const isOnClientVersion = viewClient && viewClient.isOnClientVersion;

  const workflowActionsWithIcons = (workflowActions || []).map(action => ({
    ...action,
    disabled: isDisabled || isOnClientVersion,
    icon: (
      <Icon name={action.icon} width="21" className="sr2 outline icon-orange" />
    )
  }));

  let actions = [];
  if (onRefresh) {
    actions.push(createRefreshAction(onRefresh, isOnClientVersion, setShow));
  }

  actions = actions.concat(workflowActionsWithIcons);

  if (viewClient) {
    actions.push(createViewClientAction(viewClient));
  }

  if (onEdit) {
    actions.push(createEditAction(onEdit, setShow));
  }

  actions.push(createDeleteAction(onDelete, isOnClientVersion, setShow));

  if (reportPreviewUrl) {
    actions.push(createPreviewReportAction(reportPreviewUrl, history));
  }

  if (isDisabled) {
    _forEach(actions, action => {
      action.disabled = action.name === 'share' ? action.disabled : true;
    });
  }

  const handleClick = event => {
    setShow(!show);
    setTarget(event.target);
    if (show === true) {
      setTimeout(() => {
        document.body.click();
      }, 200);
    }
  };

  return (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <div ref={ref}>
          <RowActionsButton
            isDisabled={isProjectDisabled}
            onClick={handleClick}
          />
          <Overlay
            show={show}
            target={target}
            placement="bottom-end"
            rootClose
            onHide={() => {
              setShow(!show);
            }}
            container={ref.current}
          >
            <MoreMenuPopup itemId={itemId} actions={actions || []} />
          </Overlay>
        </div>
      )}
    </EnableForProjectState>
  );
};

MoreMenu.defaultProps = {
  isDisabled: false
};

export default MoreMenu;
