const diagnostic =
  (process.env.REACT_APP_DIVESTITURE_DIAGNOSTIC || '').replace(/\W/g, '') !==
  'false';

const diagnosticDebuggingControls = !!(
  process.env.REACT_APP_ENVIRONMENT_NAMESPACE.match(/dev$/) ||
  process.env.REACT_APP_ENVIRONMENT_NAMESPACE.match(/qa$/) ||
  process.env.REACT_APP_ENVIRONMENT_NAMESPACE.match(/test$/) ||
  process.env.REACT_APP_ENVIRONMENT_NAMESPACE.match(/uat$/)
);

export default { diagnostic, diagnosticDebuggingControls };
