export const isDeletable = async (projectId, libraryItemId) =>
  ProjectHttpClient.get(
    `${baseServicePath(projectId)}/${libraryItemId}/deletable`
  ).then(res => res.data.isDeletable);

export const deleteLibraryItem = async (projectId, libraryItemId) =>
  ProjectHttpClient.delete(`${baseServicePath(projectId)}/${libraryItemId}`);
