import {
  ProjectHttpClient,
  ProjectManagementHttpClient,
  MasterApiClient
} from './httpClient';

export const getAllProjects = async () => {
  const response = await MasterApiClient.get('projects');
  return response.data;
};

export const getProjectById = async projectId => {
  const response = await MasterApiClient.get(`projects/${projectId}`);
  return response.data;
};

export const getProjectSettingsById = async projectId => {
  const response = await ProjectHttpClient.get(
    `projects/${projectId}/projectsettings`
  );
  return response.data;
};

export const postProject = async project => {
  const response = await ProjectManagementHttpClient().post('projects', {
    ...project,
    industryId: parseInt(project.industryId, 10),
    serviceIds: (project.serviceIds || []).map(sp => parseInt(sp, 10))
  });
  return response.data;
};

export const updateProjectDetails = async project => {
  const response = await ProjectManagementHttpClient().put(
    `projects/${project.id}`,
    {
      ...project,
      type: parseInt(project.type, 10),
      restriction: parseInt(project.restriction, 10),
      industryId: parseInt(project.industryId, 10),
      serviceIds: (project.serviceIds || []).map(sp => parseInt(sp, 10))
    }
  );
  return response.data;
};

export const updateProjectSettings = async (projectId, settings) => {
  const response = await ProjectHttpClient.patch(
    `projects/${projectId}/projectSettings`,
    { ...settings }
  );
  return response.data;
};

export const updateProjectUsers = async (projectId, users) => {
  const response = await ProjectManagementHttpClient(projectId).put(
    `projects/${projectId}/users`,
    users.map(_ => ({
      EmailAddress: _.email,
      RoleName: _.role,
      PendingApproval: _.pendingApproval
    }))
  );
  return response.data;
};

export const requestClientUsersUpdate = async (
  projectId,
  clientUsersRequest
) => {
  const response = await ProjectManagementHttpClient(projectId).put(
    `projects/${projectId}/clientUsers`,
    clientUsersRequest
  );
  return response.data;
};

export const getUserViewStatusCountsForProject = async projectId => {
  const response = await ProjectHttpClient.get(
    `projects/${projectId}/userviewstatus/counts`
  );
  return response.data;
};

export const getProjectOptions = async () => {
  const response = await MasterApiClient.get('projects/projectOptions');
  return response.data;
};
