import React, { useState } from 'react';
import { Button, Tooltip, Icon } from '@pwc/pwc-deals-ui';
import { useDispatch } from 'react-redux';

import { toggleRightPanel } from '../../../../../../../../common/redux/reportFormat/reportFormatSlice';
import useTabletOrMobile from '../../../../../../../../common/hooks/useTabletOrMobile';

import './ToggleRightPanelButton.scss';

const ToggleRightPanelButton = ({ expanded }) => {
  const dispatch = useDispatch();
  const [iconType, setIconType] = useState('outline');

  const handleCollapse = () => {
    dispatch(toggleRightPanel());
  };
  const handleMouseEnter = () => {
    setIconType('fill');
  };
  const handleMouseLeave = () => {
    setIconType('outline');
  };
  const isTabletOrMobile = useTabletOrMobile();

  return (
    <Tooltip
      direction="top"
      tip="Collapse sidebar"
      dataTestId="collapse-tooltip"
    >
      <Button
        className="button-no-underline report-panel-toggle-button"
        onClick={handleCollapse}
        data-testid="toggle-right-panel-button"
        title="Collapse sidebar"
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <Icon
          name={
            // eslint-disable-next-line no-nested-ternary
            isTabletOrMobile
              ? expanded
                ? 'down-chevron'
                : 'up-chevron'
              : 'right-chevron'
          }
          width="20px"
          height="20px"
          className={`${
            isTabletOrMobile ? 'fill' : `${iconType}`
          } icon-orange-theme`}
        />
      </Button>
    </Tooltip>
  );
};

export default ToggleRightPanelButton;
