import React from 'react';
import { Button, Icon, Tooltip } from '@pwc/pwc-deals-ui';

const DeleteButton = ({
  iconType,
  handleClick,
  handleMouseEnter,
  handleMouseLeave,
  tooltipPlacement,
  tooltip
}) => {
  return (
    <Tooltip direction={tooltipPlacement} tip={tooltip}>
      <Button
        className="button-no-underline"
        onClick={handleClick}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        data-testid="remove-section"
      >
        <Icon
          name="bin"
          className={`${iconType} icon-orange-theme`}
          width={24}
          height={24}
        />
      </Button>
    </Tooltip>
  );
};

export default DeleteButton;
