import React from 'react';
import './DropArea.scss';
import DropArea from './DropArea';
import FileUploaded from './FileUploaded';

const DropZone = ({ file, onDropAccepted, clearFile, loading, error }) => {
  if (file) {
    return (
      <FileUploaded
        file={file}
        clearFile={clearFile}
        loading={loading}
        error={error}
      />
    );
  }
  return <DropArea onDropAccepted={onDropAccepted} />;
};

export default DropZone;
