import { useState, useEffect } from 'react';
import { isNil } from 'lodash';

export const WORKFLOW_ACTIONS = Object.freeze({
  UNDEFINED: 0,
  REQUEST_APPROVAL: 1,
  STOP_APPROVAL_REQUEST: 2,
  APPROVE: 3,
  REJECT: 4,
  SHARE: 5
});

const latestVersionFromVersions = versions => {
  let value = '';
  if (isNil(versions)) {
    value = '';
  } else if (versions.length === 0) {
    value = 'v1';
  } else {
    const clonedVersions = [...versions];
    const sorted = clonedVersions.sort(
      (a, b) => a.versionNumber > b.versionNumber
    );
    const latest = sorted[sorted.length - 1];
    value = `v${latest.versionNumber}`;
  }
  return value;
};

const WorkflowActionsDictionary = {
  [WORKFLOW_ACTIONS.UNDEFINED]: {
    actionName: undefined,
    buttonText: () => undefined,
    popOverText: undefined,
    icon: ''
  },
  [WORKFLOW_ACTIONS.REQUEST_APPROVAL]: {
    actionName: 'request-approval',
    buttonText: () => 'Request approval',
    popOverText: '',
    icon: 'confirmed'
  },
  [WORKFLOW_ACTIONS.STOP_APPROVAL_REQUEST]: {
    actionName: 'stop-approval-request',
    buttonText: () => 'Stop approval request',
    popOverText: undefined,
    icon: 'end-process'
  },
  [WORKFLOW_ACTIONS.APPROVE]: {
    actionName: 'approve',
    buttonText: () => 'Approve',
    popOverText: undefined,
    icon: 'thumbs-up'
  },
  [WORKFLOW_ACTIONS.REJECT]: {
    actionName: 'reject',
    buttonText: () => 'Decline Approval',
    popOverText: undefined,
    icon: ''
  },
  [WORKFLOW_ACTIONS.SHARE]: {
    actionName: 'share',
    buttonText: version => `Share ${version} with client`,
    popOverText: {
      title: `Share with client team?`,
      message:
        'This will allow the client team to view the latest version of the content.',
      confirmButtonText: 'Yes, share now'
    },
    icon: 'view'
  }
};

const workflowButtonText = (action, versions) => {
  const version = latestVersionFromVersions(versions);
  return WorkflowActionsDictionary[action].buttonText(version);
};

const workflowActionFactory = (
  content,
  versions,
  getWorkflowState,
  setWorkflowState
) => nextSteps => (action, statusUpdater) =>
  nextSteps.has(action)
    ? {
        action,
        name: WorkflowActionsDictionary[action].actionName,
        text: workflowButtonText(action, versions),
        confirmation: WorkflowActionsDictionary[action].popOverText,
        update: () =>
          statusUpdater().then(() => {
            getWorkflowState().then(setWorkflowState);
          }),
        icon: WorkflowActionsDictionary[action].icon
      }
    : undefined;

export const useApprovalWorkflow = (
  getWorkflow,
  handleClick,
  projectId,
  contentId,
  content,
  versions
) => {
  const [nextSteps, setNextSteps] = useState(new Set([]));

  const setWorkflowState = workflow => {
    setNextSteps(new Set((workflow && workflow.nextSteps) || []));
  };

  useEffect(() => {
    if (content && !content.hasError && contentId) {
      getWorkflow(projectId, contentId).then(workflow => {
        setWorkflowState(workflow);
      });
    }
  }, [content, contentId, projectId, getWorkflow]);

  const createWorkflowAction = workflowActionFactory(
    content,
    versions,
    () => getWorkflow(projectId, content && contentId),
    setWorkflowState
  )(nextSteps);
  return Array.from(nextSteps).map(step =>
    createWorkflowAction(step, () =>
      handleClick(projectId, content && contentId, step)
    )
  );
};
