import React from 'react';
import PropTypes from 'prop-types';

const DisplayContentStatus = ({ shareWithAll }) => {
  return (
    <div
      data-testid="display-content-status"
      className="display-content-status"
    >
      {shareWithAll ? 'Shared' : 'Not Shared'}
    </div>
  );
};

DisplayContentStatus.propTypes = {
  shareWithAll: PropTypes.bool.isRequired
};

export default DisplayContentStatus;
