const parseFiles = files =>
  files.sort(
    (a, b) => new Date(b.uploadedDateTime) - new Date(a.uploadedDateTime)
  );

export const getFilesThunk = projectId => async dispatch => {
  dispatch(setFilesDataLoading({ loading: true }));
  try {
    const data = await getFiles(projectId).then(parseFiles);
    dispatch(setFilesData({ data: { files: data } }));
  } catch (error) {
    dispatch(setFilesDataError({ error }));
  }
};

export const updateFile = (
  projectId,
  password,
  file,
  uploadedFileId
) => async dispatch => {
  dispatch(setFileUploadLoading({ loading: true }));
  try {
    if (uploadedFileId)
      await updateFileContent(projectId, password, file, uploadedFileId);
    else await uploadFile(projectId, password, file);
    dispatch(setFileUploadLoading({ loading: false }));
    dispatch(showSuccessMessage(`${file.name} uploaded successfully`));
    await getFilesThunk(projectId)(dispatch);
  } catch (error) {
    dispatch(setFileUploadError({ error }));
  }
};

export const removeFile = (projectId, fileId) => async dispatch => {
  dispatch(setFilesDataLoading({ loading: true }));
  try {
    await deleteFile(projectId, fileId);
    await getFilesThunk(projectId)(dispatch);
  } catch (error) {
    dispatch(setFilesDataError({ error }));
  }
};

export const requestFileStatusChange = (
  projectId,
  file,
  operation
) => async dispatch => {
  dispatch(setFilesDataLoading({ loading: true }));
  try {
    await updateFileStatus(projectId, file, operation);
    await getFilesThunk(projectId)(dispatch);
  } catch (error) {
    dispatch(setFilesDataError({ error }));
  }
};
