/* eslint-disable react/jsx-wrap-multilines */
import PropTypes from 'prop-types';
import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import classnames from 'classnames';
import { useDispatch, useSelector } from 'react-redux';
import { linkedAdjustmentsShape } from '../../../../../common/components/proptypes/commonProptypes';
import { isLastNPeriodLabel } from '../../../../../common/services/textService';
import AdjustmentToggle from '../AdjustmentToggle';
import FinancialValue from '../FinancialValue';
import {
  getAnalysisDescription,
  setDescriptorForAdjustmentThunk
} from '../../../../../common/engine/reducers/structuredDocumentDescriptionSlice';
import AdjustmentControlsColumn from '../AdjustmentControlsColumn';
import AdjustmentLinkOverlayTrigger from '../LinkedAdjustmentPopover/LinkedAdjustmentsOverlayTrigger';

const Adjustment = ({
  projectId,
  documentCode = Constants.QUALITY_OF_EARNINGS,
  contentId,
  contentVersionNumber,
  adjustmentType,
  adjustmentKey,
  adjustment,
  timePeriods,
  adjustmentLinks,
  isActive,
  showToggle,
  disableToggle,
  isPublishMode,
  isFinalRow
}) => {
  const dispatch = useDispatch();
  const isNwcTable = documentCode === Constants.NET_WORKING_CAPITAL;
  const analysisDescription = useSelector(getAnalysisDescription);

  return (
    <tr
      role="row"
      className={classnames('adjustment', {
        inactive: !isActive,
        'final-details-row': isFinalRow
      })}
      data-testid="adjustment"
    >
      <th
        role="rowheader"
        scope="row"
        className={`adjustment__heading ${
          analysisDescription.selectedAdjustment === adjustmentKey
            ? 'adjustment-selected'
            : ''
        }`}
      >
        {isPublishMode ? (
          <span>{adjustmentKey}</span>
        ) : (
          <button
            className="button-avoid-focus adjustment__button"
            onClick={() => {
              dispatch(
                setDescriptorForAdjustmentThunk(
                  adjustmentType,
                  adjustmentKey,
                  documentCode,
                  projectId,
                  contentId,
                  contentVersionNumber
                )
              );
            }}
            type="button"
            data-testid="adjustment-header-button"
            aria-controls="analysisDescription"
          >
            <span>{adjustmentKey}</span>
            <span
              data-testid="adjustment-button-helper-text"
              className="visually-hidden"
            >
              . Click for long description
            </span>
          </button>
        )}
      </th>
      {timePeriods.map(timePeriod => {
        const isLastNPeriod = isLastNPeriodLabel(timePeriod);
        const showColumnHighlight = isNwcTable && isLastNPeriod;

        return (
          <FinancialValue
            key={timePeriod}
            text={adjustment[timePeriod]}
            showColumnHighlight={showColumnHighlight}
          />
        );
      })}
      {!isPublishMode && (
        <AdjustmentControlsColumn>
          {showToggle && (
            <AdjustmentToggle
              isActive={isActive}
              disabled={disableToggle}
              documentCode={documentCode}
              adjustmentType={adjustmentType}
              adjustmentKey={adjustmentKey}
            />
          )}

          <AdjustmentLinkOverlayTrigger
            adjustmentLinks={adjustmentLinks}
            projectId={projectId}
          />
        </AdjustmentControlsColumn>
      )}
    </tr>
  );
};

Adjustment.propTypes = {
  projectId: PropTypes.string.isRequired,
  documentCode: PropTypes.string.isRequired,
  contentId: PropTypes.number.isRequired,
  contentVersionNumber: PropTypes.number.isRequired,
  adjustmentType: PropTypes.string.isRequired,
  adjustmentKey: PropTypes.string.isRequired,
  adjustment: PropTypes.shape({}).isRequired,
  timePeriods: PropTypes.arrayOf(PropTypes.string).isRequired,
  adjustmentLinks: PropTypes.arrayOf(linkedAdjustmentsShape),
  isActive: PropTypes.bool,
  showToggle: PropTypes.bool,
  disableToggle: PropTypes.bool,
  isPublishMode: PropTypes.bool.isRequired,
  isFinalRow: PropTypes.bool
};

Adjustment.defaultProps = {
  adjustmentLinks: [],
  isActive: true,
  showToggle: true,
  disableToggle: false,
  isFinalRow: false
};

export default Adjustment;
