import { cloneDeep, omit } from 'lodash';
import {
  getExcelLibraryItemsByGroup,
  getPptLibraryItemsByGroup,
  getRichTextLibraryItemsByGroup
} from '../contentLibrary/libraryItemService';
import {
  getAnalysis,
  getAnalysisVersions,
  getAnalysisDetailsStatus,
  getAnalysisDetailsShared,
  getAnalysisDetailsDraft,
  getAnalysisDetailsVersions
} from './analysesService';
import { getPowerBiReportData } from './powerBiService';
import {
  fetchAnalysisBegan,
  fetchAnalysisSucceeded,
  fetchAnalysisFailed,
  fetchAnalysisDetailBegin,
  fetchAnalysisContent,
  fetchAnalysisLibraryItemGroups,
  fetchAnalysisDetailFailed,
  fetchAnalysisVersionsSuccess,
  fetchLibraryItemsNew,
  updateSectionItems,
  updateSections,
  updateSharedStatus,
  updateVersions
} from './analysisSlice';
import { getStructuredAnalysis } from '../../common/engine/services/structuredAnalysisService';
import { STRUCTURED_DISPLAY_NAME_BY_SUBTYPE } from '../../common/engine/utilities/constants';

const formatTypeMap = {
  300: 'SVG',
  301: 'HTML',
  500: 'HTML'
};

const libraryItemTypeApiMethodMap = {
  300: getExcelLibraryItemsByGroup,
  301: getExcelLibraryItemsByGroup,
  500: getPptLibraryItemsByGroup,
  400: getPowerBiReportData,
  401: getPowerBiReportData
};

const requestArrayType = {
  500: 'pptLibraryItems',
  301: 'excelLibraryItems',
  300: 'excelLibraryItems'
};

const updateLibraryItemsThunk = itemsToAdd => async (dispatch, getState) => {
  const {
    analysis: {
      libraryItemsNew: { items }
    }
  } = getState();
  const newItems = cloneDeep(items).concat(itemsToAdd);
  dispatch(fetchLibraryItemsNew({ libraryItems: newItems }));
  dispatch(updateSectionItems());
};

const transformPowerBiResponse = (response, data) => {
  const { libraryItemId, libraryItemType } = response;
  const clonedResponse = cloneDeep(
    omit(response, ['libraryItemId', 'libraryItemType'])
  );
  const transformedResponse = {
    ...clonedResponse,
    id: libraryItemId,
    type: libraryItemType,
    data
  };
  return transformedResponse;
};

const fetchPowerBiSingleItemThunk = (
  projectId,
  reqData,
  libItem
) => async dispatch => {
  try {
    const { data } = reqData;
    const response = await libraryItemTypeApiMethodMap[libItem.type](
      projectId,
      reqData
    );
    const transformedResponse = transformPowerBiResponse(response, data);
    dispatch(updateLibraryItemsThunk(transformedResponse));
  } catch (error) {
    // eslint-disable-next-line no-console
    console.log(error);
    // TODO: dispatch action to update group status message to failure
  }
};

export const loadPowerBiLibraryItemsThunk = projectId => async (
  dispatch,
  getState
) => {
  const {
    analysis: {
      libraryItemGroups: { powerBiLibraryItemList }
    }
  } = getState();
  if (powerBiLibraryItemList && powerBiLibraryItemList.length === 0) {
    return;
  }
  powerBiLibraryItemList.forEach(group => {
    const reqBody = {
      reportId: group.reportId,
      embedUrl: group.embedUrl
    };
    group.powerBiLibraryItems.forEach(libItem => {
      const reqData = {
        ...reqBody,
        libraryItemId: libItem.id,
        libraryItemType: libItem.type,
        workspaceId: libItem.workspaceId,
        name: libItem.name,
        data: libItem.data
      };

      dispatch(fetchPowerBiSingleItemThunk(projectId, reqData, libItem));
    });
  });
};

const transformExcelPptGroupResponse = response => {
  const { fileName, fileContents } = response;
  return fileContents.map(item => {
    const cloned = cloneDeep(item);
    return {
      ...cloned,
      fileName
    };
  });
};

const fetchExcelPptSingleGroupThunk = (
  projectId,
  reqBody,
  groupType
) => async dispatch => {
  try {
    const response = await libraryItemTypeApiMethodMap[groupType](
      projectId,
      reqBody
    );
    const transformedItems = transformExcelPptGroupResponse(response);
    dispatch(updateLibraryItemsThunk(transformedItems));
  } catch (error) {
    // eslint-disable-next-line no-console
    console.log(error);
    // TODO: dispatch action to update status for group to failure
  }
};

export const loadExcelPptLibraryItemsItemsThunk = projectId => async (
  dispatch,
  getState
) => {
  const {
    analysis: {
      libraryItemGroups: { excelPptLibraryItemList }
    }
  } = getState();
  if (excelPptLibraryItemList && excelPptLibraryItemList.length === 0) {
    return;
  }

  excelPptLibraryItemList.forEach(group => {
    const reqBody = {
      groupedByKey: group.groupedByKey,
      sharePointItemUrl: group.sharePointItemUrl,
      driveId: group.driveId,
      driveItemId: group.driveItemId,
      siteId: group.siteId
    };
    const groupType = group.excelPptLibraryItems[0].type;
    reqBody[requestArrayType[groupType]] = group.excelPptLibraryItems.map(
      libItem => {
        const cloned = cloneDeep(libItem);
        const { data } = cloned;
        data.formatType = formatTypeMap[libItem.type];
        return { ...cloned, data };
      }
    );
    dispatch(fetchExcelPptSingleGroupThunk(projectId, reqBody, groupType));
  });
};

export const fetchRichTextGroupedItemsThunk = projectId => async (
  dispatch,
  getState
) => {
  const {
    analysis: {
      libraryItemGroups: { richTextLibraryItemList }
    }
  } = getState();
  if (richTextLibraryItemList && richTextLibraryItemList.length === 0) {
    return;
  }
  try {
    const richTextLibItems = richTextLibraryItemList
      .reduce((accum, item) => {
        const { libraryItemId: itemId } = item;
        const parsedId = Number.isInteger(parseInt(itemId, 10))
          ? parseInt(itemId, 10)
          : null;
        return accum.concat([parsedId]);
      }, [])
      .filter(item => item !== null);
    const reqBody = {
      libraryItemIds: richTextLibItems
    };
    const response = await getRichTextLibraryItemsByGroup(projectId, reqBody);
    const { richTextLibraryItems } = response;
    dispatch(updateLibraryItemsThunk(richTextLibraryItems));
  } catch (e) {
    // eslint-disable-next-line no-console
    console.log(e);
    // TODO: dispatch action to update group status to failure message
  }
};

const transformSharedItem = (existingSectionItem, sharedItem) => {
  const { type } = sharedItem;
  let newSectionItem;
  if (type === 100) {
    const { data } = sharedItem;
    newSectionItem = {
      ...existingSectionItem,
      ...omit(sharedItem, ['data']),
      richTextContent: { richTextData: data }
    };
  } else if (type === 400 || type === 401) {
    newSectionItem = { ...existingSectionItem, ...sharedItem };
  } else if (type === 300 || type === 301 || type === 500) {
    const { data } = sharedItem;
    newSectionItem = {
      ...existingSectionItem,
      ...omit(sharedItem, ['data']),
      value: data
    };
  }
  return newSectionItem;
};

const updateSharedLibraryItemsThunk = itemsToAdd => async (
  dispatch,
  getState
) => {
  const {
    analysis: {
      libraryItemsShared: { items: sharedItems },
      sections
    }
  } = getState();
  const newItems = cloneDeep(sharedItems).concat(itemsToAdd);
  const newSections = sections.map(section => {
    const { items } = section;
    const transformedItems = items.map(item => {
      const clonedItem = cloneDeep(item);
      const updatedItem = newItems.find(
        _ => _.id === (item.id || item.libraryItemId)
      );
      let transformedSharedItem;
      if (updatedItem) {
        transformedSharedItem = transformSharedItem(clonedItem, updatedItem);
        return {
          ...transformedSharedItem
        };
      }
      return {
        ...clonedItem
      };
    });
    return {
      ...section,
      items: transformedItems
    };
  });
  dispatch(updateSections({ newSections }));
};

export const loadAnalysisVersionsThunk = (
  projectId,
  analysisId
) => async dispatch => {
  try {
    const versions = await getAnalysisVersions(projectId, analysisId);
    dispatch(fetchAnalysisVersionsSuccess({ versions }));
  } catch (error) {
    // eslint-disable-next-line no-console
    console.log(error);
  }
};

export const loadAnalysisWithDraftContent = (
  projectId,
  analysisId
) => async dispatch => {
  try {
    dispatch(fetchAnalysisDetailBegin());
    const response = await getAnalysisDetailsDraft(projectId, analysisId);
    const { content = {}, libraryItemGroups = {} } = response;
    dispatch(fetchAnalysisContent({ content }));
    dispatch(fetchAnalysisLibraryItemGroups({ libraryItemGroups }));

    dispatch(loadExcelPptLibraryItemsItemsThunk(projectId));
    dispatch(loadPowerBiLibraryItemsThunk(projectId));
    dispatch(fetchRichTextGroupedItemsThunk(projectId));
  } catch (e) {
    // eslint-disable-next-line no-console
    console.log(e);
    dispatch(
      fetchAnalysisDetailFailed({
        isLoading: false,
        hasError: true,
        errorMessage: e.toString()
      })
    );
  }
};

export const loadAnalysisWithSharedContent = (
  projectId,
  analysisId
) => async dispatch => {
  try {
    const response = await getAnalysisDetailsShared(projectId, analysisId);
    const { content, items } = response;
    dispatch(fetchAnalysisContent({ content }));
    dispatch(updateSharedLibraryItemsThunk(items));
  } catch (error) {
    // eslint-disable-next-line no-console
    console.log(error);
    // TODO: better error handling
  }
};

