// Thunks
const loadThunk = async (dispatch, state, loadFunction) => {
  try {
    if (!state.isLoading) {
      dispatch(fetchAnalysisBegan());

      const response = await loadFunction();

      dispatch(fetchAnalysisSucceeded(response));
    }
  } catch (error) {
    dispatch(fetchAnalysisFailed(error.response ? error.response.status : 500));
  }
};

export const loadAnalysisThunk = (
  projectId,
  analysisId,
  versionStatus
) => async (dispatch, state) => {
  await loadThunk(dispatch, state, () =>
    getAnalysis(projectId, analysisId, versionStatus)
  );
};

export const loadStructuredAnalysisThunk = (
  projectId,
  analysisId,
  versionStatus
) => async (dispatch, state) => {
  await loadThunk(dispatch, state, async () => {
    const analysis = await getStructuredAnalysis(
      projectId,
      analysisId,
      versionStatus
    );
    analysis.title =
      STRUCTURED_DISPLAY_NAME_BY_SUBTYPE[analysis.contentSubType];
    return analysis;
  });
};
