/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import EditNarrativeCard from '../EditNarrativeCard/EditNarrativeCard';
import '../NarrativeSection/NarrativeSection.scss';

const NarrativeSection = ({
  title,
  narratives,
  allNarratives,
  getViewPath,
  index = null,
  setNarratives,
  parentContentId,
  search
}) => {
  const titleIdentifier = title.replace(/\s/g, '-').toLowerCase();
  const titleIdentifierWithIndex =
    titleIdentifier + (index !== null ? `-${index}` : '');
  const hasNarratives = narratives.length;

  const sectionClasses = [
    'narrative-section',
    `narrative-section-${titleIdentifier}`
  ];
  if (!hasNarratives) {
    sectionClasses.push('narrative-section-empty');
  }

  const linkNarrative = newNarrative => {
    const newNarratives = narratives;
    const replaceAtIndex = newNarratives.findIndex(
      n => n.contentThreadId === newNarrative.contentThreadId
    );
    newNarratives[replaceAtIndex] = newNarrative;
    setNarratives(newNarratives);
  };

  const getEmptyMessage = () => {
    if (allNarratives.length) {
      if (search === undefined) {
        return `No connected ${title}`;
      }
      return `0 ${title} found for '${search}'`;
    }
    return `No ${title.toLowerCase()} available to connect`;
  };

  return (
    <div
      className={sectionClasses.join(' ')}
      data-testid={`narrative-section-${titleIdentifierWithIndex}`}
    >
      <div
        className="narrative-section-title"
        data-testid={`narrative-section-title-${titleIdentifierWithIndex}`}
      >
        {title}
      </div>
      {narratives.length ? (
        narratives.map((narrative, narrativeIndex) => (
          <EditNarrativeCard
            key={`narrative-card-${narrative.title}`}
            narrative={narrative}
            testId={`${titleIdentifierWithIndex}-${narrativeIndex}`}
            getViewPath={getViewPath}
            parentContentId={parentContentId}
            linkNarrative={linkNarrative}
          />
        ))
      ) : (
        <div
          className="narrative-card narrative-card-empty"
          data-testid={`narrative-section-empty-${titleIdentifierWithIndex}`}
        >
          {getEmptyMessage()}
        </div>
      )}
    </div>
  );
};

export default NarrativeSection;
