import React, { useCallback, useEffect } from 'react';
import { Icon } from '@pwc/pwc-deals-ui';

import './UploadSuccessBanner.scss';
import { useDispatch, useSelector } from 'react-redux';
import {
  getFileUploadState,
  hideSuccessMessage
} from '../../../../common/redux/fileUpload/fileUploadSlice';

const UploadSuccessBanner = () => {
  const { successMessage } = useSelector(getFileUploadState);
  const dispatch = useDispatch();
  const onClose = useCallback(() => dispatch(hideSuccessMessage()), [dispatch]);

  useEffect(() => {
    setTimeout(onClose, 10000);
  }, [onClose, successMessage]);
  if (!successMessage) return null;

  return (
    <div className="upload-success-banner">
      <p className="icon-checkmark">
        <Icon name="checkmark" className="fill icon-white" width={12} />
      </p>
      <p>{successMessage}</p>
      <button className="icon-button" type="button" onClick={onClose}>
        <Icon name="close" className="fill icon-white" width={12} />
      </button>
    </div>
  );
};

export default UploadSuccessBanner;
