/* eslint-disable react/jsx-wrap-multilines */
import React, { useState, useRef } from 'react';
import { string } from 'prop-types';
import { Overlay } from 'react-bootstrap';
import EnableForProjectState from '../../../features/security/EnableForProjectState';
import ContentActionsPopupMenu from './ContentActionsPopupMenu';
import RowActionsButton from './RowActionsButton';
import ShowForPermissions from '../../../features/security/ShowForPermissions';

const ContentActionsRowMenu = ({
  projectId,
  itemId,
  onEditMenuClick,
  onDeleteMenuClick,
  direction
}) => {
  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);

  const handleButtonClick = handler =>
    handler
      ? () => {
          setShow(false);
          handler(itemId);
        }
      : null;

  const handleClick = event => {
    setShow(!show);
    setTarget(event.target);
    if (show === true) {
      setTimeout(() => {
        document.body.click();
      }, 200);
    }
  };

  return (
    <ShowForPermissions projectId={projectId} permissions={['Contribute']}>
      <EnableForProjectState projectId={projectId}>
        {({ isProjectDisabled }) => (
          <div ref={ref} className="content-actions-row-menu">
            <RowActionsButton
              isDisabled={isProjectDisabled}
              onClick={handleClick}
            />
            <Overlay
              show={show}
              target={target}
              placement={direction}
              rootClose
              onHide={() => {
                setShow(!show);
              }}
              container={ref.current}
            >
              <ContentActionsPopupMenu
                projectId={projectId}
                itemId={itemId}
                onEditMenuClick={handleButtonClick(onEditMenuClick)}
                onDeleteMenuClick={handleButtonClick(onDeleteMenuClick)}
              />
            </Overlay>
          </div>
        )}
      </EnableForProjectState>
    </ShowForPermissions>
  );
};

ContentActionsRowMenu.propTypes = {
  direction: string
};

ContentActionsRowMenu.defaultProps = {
  direction: 'bottom'
};

export default ContentActionsRowMenu;
