export const getScenarioCards = (
  userEmail,
  projectId,
  projectStatus,
  approvals,
  canApprove,
  canRequest
) => {
  const cardsToDisplay = {
    leftCard: null,
    rightCard: null,
    leftCardConfirm: null,
    rightCardConfirm: null,
    leftCardAction: null,
    rightCardAction: null,
    leftCardNextState: null,
    rightCardNextState: null,
    warningCard: null,
    warningCardApproveAction: null,
    warningCardCancelAction: null
  };

  const isUserPrivilegedToApprove = canApprove;
  const isUserPrivilegedToRequest = canApprove ? false : canRequest;

  const pauseParams = {
    projectId,
    userId: userEmail
  };

  const approvalParams = {
    approvalId: null,
    projectId,
    userId: userEmail
  };

  if (!isUserPrivilegedToApprove && !isUserPrivilegedToRequest) {
    return {};
  }

  if (approvals.isRequestedToBePaused) {
    approvalParams.approvalId = approvals.pauseApprovalId;

    if (isUserPrivilegedToApprove) {
      cardsToDisplay.warningCard = warnings.inactivePending.approveRequest;
      cardsToDisplay.warningCardApproveAction = service.approveRequestPauseProject(
        approvalParams
      );
      cardsToDisplay.warningCardCancelAction = service.declineRequestPauseProject(
        approvalParams
      );
    } else if (isUserPrivilegedToRequest) {
      cardsToDisplay.warningCard = warnings.inactivePending.cancelRequest;
      cardsToDisplay.warningCardCancelAction = service.cancelRequestPauseProject(
        approvalParams
      );
    }
  } else if (approvals.isRequestedToBeArchived) {
    approvalParams.approvalId = approvals.archiveApprovalId;
    if (isUserPrivilegedToApprove) {
      cardsToDisplay.warningCard = warnings.archivePending.approveRequest;
      cardsToDisplay.warningCardApproveAction = () => null;
      cardsToDisplay.warningCardCancelAction = () => null;
    } else if (isUserPrivilegedToRequest) {
      cardsToDisplay.warningCard = warnings.archivePending.cancelRequest;
      cardsToDisplay.warningCardCancelAction = () => null;
    }
  } else if (approvals.isRequestedToBeReactivated) {
    if (isUserPrivilegedToApprove) {
      cardsToDisplay.warningCard = warnings.reactivatePending.approveRequest;
      cardsToDisplay.warningCardApproveAction = service.approveRequestReactivateProject(
        approvalParams
      );
      cardsToDisplay.warningCardCancelAction = service.declineRequestReactivateProject(
        approvalParams
      );
    } else if (isUserPrivilegedToRequest) {
      cardsToDisplay.warningCard = warnings.reactivatePending.cancelRequest;
      cardsToDisplay.warningCardCancelAction = service.cancelRequestReactivateProject(
        approvalParams
      );
    }
  } else if (approvals.isRequestedToBeUnarchived) {
    if (isUserPrivilegedToApprove) {
      cardsToDisplay.warningCard = warnings.unarchivePending.approveRequest;
      cardsToDisplay.warningCardApproveAction = service.approveRequestUnarchiveProject(
        approvalParams
      );
      cardsToDisplay.warningCardCancelAction = service.declineRequestUnarchiveProject(
        approvalParams
      );
    } else if (isUserPrivilegedToRequest) {
      cardsToDisplay.warningCard = warnings.unarchivePending.cancelRequest;
      cardsToDisplay.warningCardCancelAction = service.cancelRequestUnarchiveProject(
        approvalParams
      );
    }
  } else {
    switch (projectStatus) {
      case PROJECT_STATUS_CODES.COMPLETED:
      case PROJECT_STATUS_CODES.REACTIVE_PENDING_IN_PROGRESS:
      case PROJECT_STATUS_CODES.UNARCHIVING_PENDING_IN_PROGRESS:
        approvalParams.approvalId = approvals.pauseApprovalId;

        cardsToDisplay.leftCard = cards.inactivate.overview;
        cardsToDisplay.rightCard = cards.archive.overview;

        if (isUserPrivilegedToApprove) {
          cardsToDisplay.leftCardConfirm = cards.inactivate.confirmation;
          cardsToDisplay.rightCardConfirm = cards.archive.confirmation;
          cardsToDisplay.leftCardAction = service.pauseProject(pauseParams);
          cardsToDisplay.rightCardAction = service.archiveProject(pauseParams);
        } else if (isUserPrivilegedToRequest) {
          cardsToDisplay.leftCardConfirm = cards.inactivate.request;
          cardsToDisplay.rightCardConfirm = cards.archive.request;
          cardsToDisplay.leftCardAction = service.requestPauseProject(
            approvalParams
          );
          cardsToDisplay.rightCardAction = service.requestArchiveProject(
            approvalParams
          );
        }

        break;

      case PROJECT_STATUS_CODES.INACTIVE:
      case PROJECT_STATUS_CODES.INACTIVE_PENDING_IN_PROGRESS:
        approvalParams.approvalId = approvals.reactivateApprovalId;

        cardsToDisplay.leftCard = cards.reactivate.overview;
        cardsToDisplay.rightCard = cards.archive.overview;

        if (isUserPrivilegedToApprove) {
          cardsToDisplay.leftCardConfirm = cards.reactivate.confirmation;
          cardsToDisplay.rightCardConfirm = cards.archive.confirmation;

          cardsToDisplay.leftCardAction = service.reactivateProject(
            pauseParams
          );
          cardsToDisplay.rightCardAction = service.archiveProject(pauseParams);
        } else if (isUserPrivilegedToRequest) {
          cardsToDisplay.leftCardConfirm = cards.reactivate.request;
          cardsToDisplay.rightCardConfirm = cards.archive.request;

          cardsToDisplay.leftCardAction = service.requestReactivateProject(
            approvalParams
          );
          cardsToDisplay.rightCardAction = service.requestArchiveProject(
            approvalParams
          );
        }
        break;

      case PROJECT_STATUS_CODES.ARCHIVED:
      case PROJECT_STATUS_CODES.ARCHIVING_PENDING_IN_PROGRESS:
        approvalParams.approvalId = approvals.unarchiveApprovalId;

        cardsToDisplay.leftCard = cards.unarchive.overview;
        if (isUserPrivilegedToApprove) {
          cardsToDisplay.leftCardConfirm = cards.unarchive.confirmation;
          cardsToDisplay.leftCardAction = service.unarchiveProject;

          cardsToDisplay.leftCardNextState =
            PROJECT_STATUS_CODES.UNARCHIVING_PENDING_IN_PROGRESS;
        } else if (isUserPrivilegedToRequest) {
          cardsToDisplay.leftCardConfirm = cards.unarchive.request;
          cardsToDisplay.leftCardAction = service.requestUnarchiveProject(
            approvalParams
          );

          cardsToDisplay.leftCardNextState =
            PROJECT_STATUS_CODES.UNARCHIVING_PENDING_NEEDS_APPROVAL;
        }
        break;

      default:
    }
  }

  return cardsToDisplay;
};
