import isBlank from '../../common/helpers/isBlank';

const validateMetaDataFormValues = formValues => {
  const services =
    formValues.services && formValues.services.length > 0
      ? ''
      : 'At least one service / product is required';

  const result = {
    dealType: isBlank(formValues.dealType) ? 'Deal type must be selected' : '',
    clientName: isBlank(formValues.clientName) ? 'Client name is required' : '',
    projectName: isBlank(formValues.projectName)
      ? 'Project name is required'
      : '',
    restriction: isBlank(formValues.restriction)
      ? 'Restriction must be selected'
      : '',
    services
  };

  return result;
};

export default validateMetaDataFormValues;
