import React, { useEffect, useState } from 'react';
import TextArea from '@pwc/pwc-deals-ui/build/common/TextArea';
import { Button, RadioGroup, Icon } from '@pwc/pwc-deals-ui';
import { Constants, ErrorUtilities } from '@pwc/pwc-deals-web-common';
import Modal from 'react-bootstrap/Modal';
import { useParams } from 'react-router-dom';
import { get } from 'lodash';
import { getLibraryItemWithHtml } from '../../../../../contentLibrary/libraryItemService';
import { postTopic, putTopic } from '../../../../topicService';
import SingleColumnLayout from '../SingleColumnLayout/SingleColumnLayout';
import { mapTopic } from '../mapTopic';
import topicFormIsValid from '../topicFormIsValid';
import { POSSIBLE_NUMBER_OF_TOPIC_PANELS } from '../topicConstants';
import D3Alert from '../../../../../../common/components/Alert/D3Alert';
import './TopicFormModal.scss';
import PageTitle from '../../../../../../common/components/PageTitle/PageTitle';
import SelectedSection from './SelectedSection';

const defaultContentTypeArray = Array(POSSIBLE_NUMBER_OF_TOPIC_PANELS).fill(
  Constants.TOPIC_PANEL_TYPES.RICH_TEXT.asString
);

const defaultRichTextArray = Array(POSSIBLE_NUMBER_OF_TOPIC_PANELS).fill('');

const singleColumnArray = [
  {
    key: 'first-column',
    label: <div className="content-type-title">Content type</div>
  }
];

const selectedSection = (layout, panel, name) => (
  <div className="content-type-title">
    <span className="icon-margin">
      <SelectedSection layout={layout} panel={panel} />
    </span>
    {name} - Content type
  </div>
);

const leftRightColumns = [
  { panel: 1, name: 'Left column', key: 'first-column' },
  { panel: 2, name: 'Right column', key: 'second-column' }
];

const doubleColumnArray = leftRightColumns.map(({ panel, name, key }) => ({
  key,
  label: selectedSection(
    Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN.value,
    panel,
    name
  )
}));

const threeColumnArray = [
  { panel: 1, name: 'Left column', key: 'first-column' },
  { panel: 2, name: 'Middle column', key: 'second-column' },
  { panel: 3, name: 'Right column', key: 'third-column' }
].map(({ panel, name, key }) => ({
  key,
  label: selectedSection(
    Constants.TOPIC_LAYOUT_TYPE.THREE_COLUMN.value,
    panel,
    name
  )
}));

const twoColumnNarrowRightArray = leftRightColumns.map(
  ({ panel, name, key }) => ({
    key,
    label: selectedSection(
      Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_67_1A_R_33_1A.value,
      panel,
      name
    )
  })
);

const stackedTop80Bottom20Array = [
  { panel: 1, name: 'Top row', key: 'top-row' },
  { panel: 2, name: 'Bottom row', key: 'bottom-row' }
].map(({ panel, name, key }) => ({
  key,
  label: selectedSection(
    Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_80_1A_B_20_1A.value,
    panel,
    name
  )
}));

const stackedTop60Bottom40Array = [
  { panel: 1, name: 'Top row', key: 'top-medium-row' },
  { panel: 2, name: 'Bottom row', key: 'bottom-medium-row' }
].map(({ panel, name, key }) => ({
  key,
  label: selectedSection(
    Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_60_1A_B_40_1A.value,
    panel,
    name
  )
}));

const stackedT501AB502AArray = [
  { panel: 1, name: 'Top row', key: 'top-row' },
  { panel: 2, name: 'Bottom left row', key: 'bottom-left-row' },
  { panel: 3, name: 'Bottom right row', key: 'bottom-right-row' }
].map(({ panel, name, key }) => ({
  key,
  label: selectedSection(
    Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value,
    panel,
    name
  )
}));

const stackedWithRightColumnArray = [
  { panel: 1, name: 'Top left', key: 'top-left-section' },
  { panel: 3, name: 'Bottom left', key: 'bottom-left-section' },
  { panel: 2, name: 'Right column', key: 'right-column' }
].map(({ panel, name, key }) => ({
  key,
  label: selectedSection(
    Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value,
    panel,
    name
  )
}));

const getColumnDataArray = layout => {
  switch (layout) {
    case Constants.TOPIC_LAYOUT_TYPE.THREE_COLUMN.value:
      return threeColumnArray;
    case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN.value:
      return doubleColumnArray;
    case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_67_1A_R_33_1A.value:
      return twoColumnNarrowRightArray;
    case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_80_1A_B_20_1A.value:
      return stackedTop80Bottom20Array;
    case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_60_1A_B_40_1A.value:
      return stackedTop60Bottom40Array;
    case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value:
      return stackedT501AB502AArray;
    case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value:
      return stackedWithRightColumnArray;
    case Constants.TOPIC_LAYOUT_TYPE.ONE_COLUMN.value:
    default:
      return singleColumnArray;
  }
};

const fetchAndSetRichTextArray = async (projectId, topic, setRichTextArray) => {
  const richTextPromiseArray = topic.panels.map(async panel => {
    if (panel.type === Constants.TOPIC_PANEL_TYPES.RICH_TEXT.value) {
      const { libraryItemId } = panel;
      const libraryItemHtml = await getLibraryItemWithHtml(
        projectId,
        libraryItemId,
        topic.contentThreadId
      );
      return libraryItemHtml;
    }
    return '';
  });
  const resolvedRichTextPromiseArray = await Promise.all(richTextPromiseArray);
  setRichTextArray(resolvedRichTextPromiseArray);
};

const getInitialContentTypeArray = theTopic => {
  const contentTypes = [...defaultContentTypeArray];

  if (theTopic) {
    theTopic.panels.forEach((panel, index) => {
      contentTypes[index] = Constants.getTopicPanelTypeAsString(panel.type);
    });
  }
  return contentTypes;
};

const getInitialExcelChartIdArray = topic =>
  Array(POSSIBLE_NUMBER_OF_TOPIC_PANELS)
    .fill(null)
    .map((_, i) => {
      if (topic && topic.panels && topic.panels[i]) {
        const panel = topic.panels[i];
        return panel.type === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.value
          ? panel.libraryItemId
          : null;
      }
      return null;
    });

const getInitialKfaArray = topic =>
  Array(POSSIBLE_NUMBER_OF_TOPIC_PANELS)
    .fill(null)
    .map((_, i) => {
      if (topic && topic.panels && topic.panels[i]) {
        const panel = topic.panels[i];
        return panel.type === Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.value
          ? { contentThreadId: panel.contentThreadId }
          : null;
      }
      return null;
    });

const shouldShowWarning = (layout, richTextArray, contentTypeArray) =>
  !!richTextArray.find(
    (item, i) =>
      item &&
      (i >= layout ||
        contentTypeArray[i] !== Constants.TOPIC_PANEL_TYPES.RICH_TEXT.asString)
  );

const TopicFormModal = ({ show, handleClose, topic }) => {
  if (
    topic &&
    topic.layoutType ===
      Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value
  ) {
    [topic.panels[1], topic.panels[2]] = [topic.panels[2], topic.panels[1]];
  }
  const [layout, setLayout] = useState(
    topic ? topic.layoutType : Constants.TOPIC_LAYOUT_TYPE.ONE_COLUMN.value
  );
  const [isSaving, setIsSaving] = useState(false);
  const [isValid, setIsValid] = useState(false);
  const { projectId } = useParams();
  const [error, setError] = useState('');
  const [title, setTitle] = useState(topic ? topic.title : '');
  const [contentTypeArray, setContentTypeArray] = useState(
    getInitialContentTypeArray(topic)
  );
  const [richTextArray, setRichTextArray] = useState(defaultRichTextArray);
  const [hypothesisArray, setHypothesisArray] = useState(
    getInitialKfaArray(topic)
  );
  const [excelChartIdArray, setExcelChartIdArray] = useState(
    getInitialExcelChartIdArray(topic)
  );

  const columnDataArray = getColumnDataArray(parseInt(layout, 10));

  useEffect(() => {
    const topicFormData = columnDataArray.map((_, index) => ({
      contentType: contentTypeArray[index],
      description: richTextArray[index],
      hypothesisId: get(hypothesisArray, [index, 'contentThreadId'], null),
      excelChartId: excelChartIdArray[index]
    }));

    setIsValid(
      // Get validity of child components
      topicFormIsValid({ title, panels: topicFormData })
    );
  }, [
    contentTypeArray,
    columnDataArray,
    excelChartIdArray,
    hypothesisArray,
    richTextArray,
    layout,
    title
  ]);

  useEffect(() => {
    if (topic) {
      fetchAndSetRichTextArray(projectId, topic, setRichTextArray);
    }
  }, [projectId, topic]);

  const handleSave = async () => {
    let topicToSaveDto;
    if (
      layout === Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value
    ) {
      const contentTypeArrayTemp = contentTypeArray;
      const richTextArrayTemp = richTextArray;
      const hypothesisArrayTemp = hypothesisArray;
      const excelChartIdArrayTemp = excelChartIdArray;
      [contentTypeArrayTemp[1], contentTypeArrayTemp[2]] = [
        contentTypeArrayTemp[2],
        contentTypeArrayTemp[1]
      ];
      [richTextArrayTemp[1], richTextArrayTemp[2]] = [
        richTextArrayTemp[2],
        richTextArrayTemp[1]
      ];
      [hypothesisArrayTemp[1], hypothesisArrayTemp[2]] = [
        hypothesisArrayTemp[2],
        hypothesisArrayTemp[1]
      ];
      [excelChartIdArrayTemp[1], excelChartIdArrayTemp[2]] = [
        excelChartIdArrayTemp[2],
        excelChartIdArrayTemp[1]
      ];
      topicToSaveDto = mapTopic({
        topicLayoutTypeValue: layout,
        contentTypeArray: contentTypeArrayTemp,
        richTextArray: richTextArrayTemp,
        hypothesisArray: hypothesisArrayTemp,
        excelChartIdArray: excelChartIdArrayTemp,
        topicTitle: title
      });
    } else {
      topicToSaveDto = mapTopic({
        topicLayoutTypeValue: layout,
        contentTypeArray,
        richTextArray,
        hypothesisArray,
        excelChartIdArray,
        topicTitle: title
      });
    }
    try {
      setIsSaving(true);
      if (topic) {
        await putTopic(projectId, topic.contentThreadId, topicToSaveDto);
      } else {
        await postTopic(projectId, topicToSaveDto);
      }
    } catch (e) {
      setIsSaving(false);
      setError(ErrorUtilities.getContentErrorType(e));
      return;
    }

    handleClose();
  };

  const handleLayoutChange = e => {
    setLayout(parseInt(e.target.value, 10));
  };

  const layoutField = {
    name: 'layout',
    onChange: handleLayoutChange,
    value: layout.toString()
  };

  const layoutField1 = {
    name: '',
    onChange: handleLayoutChange,
    value: layout.toString()
  };

  const layoutField2 = {
    name: '',
    onChange: handleLayoutChange,
    value: layout.toString()
  };

  const handleTitleChange = e => setTitle(e.target.value);
  const titleField = {
    name: 'title',
    onChange: handleTitleChange,
    value: title
  };

  const layoutOptions = Object.keys(Constants.TOPIC_LAYOUT_TYPE).map(
    (topicLayout, i) => ({
      key:
        Constants.TOPIC_LAYOUT_TYPE[topicLayout].displayName + Date.now() + i,
      label: (
        <div
          className="radio-label-aligner"
          key={`layout-radio-button-${topicLayout}-${Date.now()}`}
        >
          <div
            className={
              Constants.TOPIC_LAYOUT_TYPE[topicLayout].value ===
              parseInt(layout, 10)
                ? Constants.TOPIC_LAYOUT_TYPE[topicLayout].checkedClassName
                : Constants.TOPIC_LAYOUT_TYPE[topicLayout].uncheckedClassName
            }
          />
          <span className="radio-layout-label">
            {Constants.TOPIC_LAYOUT_TYPE[topicLayout].displayName}{' '}
          </span>
        </div>
      ),
      value: Constants.TOPIC_LAYOUT_TYPE[topicLayout].value
    })
  );

  const layoutOptions1 = [layoutOptions[2], layoutOptions[3], layoutOptions[6]];
  const layoutOptions2 = [layoutOptions[7], layoutOptions[4]];
  layoutOptions.copyWithin(2, 5, 6);
  layoutOptions.splice(3);

  layoutOptions1[2] = {
    key: Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.displayName,
    label: (
      <div
        className="radio-label-aligner"
        key={`layout-radio-button-${Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.displayName}`}
      >
        <div
          className={
            Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value ===
            parseInt(layout, 10)
              ? Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A
                  .checkedClassName
              : Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A
                  .uncheckedClassName
          }
        >
          <div
            className={
              Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value ===
              parseInt(layout, 10)
                ? 'bottom-area-checked'
                : 'bottom-area-unchecked'
            }
          >
            <div />
            <div className="inner-div-right" />
          </div>
        </div>
        <span className="radio-layout-label">
          {Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.displayName}{' '}
        </span>
      </div>
    ),
    value: Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value
  };

  layoutOptions2[0] = {
    key: Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.displayName,
    label: (
      <div
        className="radio-label-aligner"
        key={`layout-radio-button-${Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.displayName}`}
      >
        <div
          className={
            Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value ===
            parseInt(layout, 10)
              ? Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A
                  .checkedClassName
              : Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A
                  .uncheckedClassName
          }
        >
          <div
            className={
              Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value ===
              parseInt(layout, 10)
                ? 'left-area-checked'
                : 'left-area-unchecked'
            }
          >
            <div />
          </div>
        </div>
        <span className="radio-layout-label">
          {Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.displayName}{' '}
        </span>
      </div>
    ),
    value: Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value
  };

  const projectOverviewText =
    "You can create 'Project Overview' topics to highlight existing content on the project overview page. Choose from existing Key Focus Area and Chart content, or select Text area to add commentary to your content.";
  const addOrUpdate = topic ? 'update' : 'add';
  const warningText = `You have information in hidden columns that will be permanently lost if you ${addOrUpdate} this topic.`;
  const showWarning = shouldShowWarning(
    layout,
    richTextArray,
    contentTypeArray
  );

  const isPublishError = error === 'publish';

  const titleFieldError =
    !isPublishError && get(error, 'fieldName', '').toLowerCase() === 'title'
      ? error.apiErrorMessage
      : undefined;

  const generalError = isPublishError || !titleFieldError ? error : undefined;

  return (
    <Modal
      size="lg"
      enforceFocus={false}
      show={show}
      onHide={handleClose}
      backdrop="static"
      contentClassName="topic-modal"
    >
      <div className="topic-modal-header">
        <Modal.Header>
          <Modal.Title data-testid="add-topic-modal">
            <PageTitle title="Add a Topic" size="h2" />
            <button
              className="ui-button button-no-appearance topic-close-button"
              aria-label="Close topic modal"
              onClick={handleClose}
              type="button"
              data-testid="topic-close-button"
            >
              <Icon
                name="close"
                className="fill icon-orange-theme"
                width="18"
                height="18"
              />
            </button>
            <p className="project-overview-description-text">
              {projectOverviewText}
            </p>
          </Modal.Title>
        </Modal.Header>
      </div>
      <div className="topic-modal-body">
        <Modal.Body className="modal-body-section">
          <h6>Report layout</h6>
          <RadioGroup
            field={layoutField}
            options={layoutOptions}
            direction="row"
            className="layout-radio-group layout-align radio-margin"
          />
          <RadioGroup
            field={layoutField1}
            options={layoutOptions1}
            direction="row"
            className="layout-radio-group layout-align radio-margin"
          />
          <RadioGroup
            field={layoutField2}
            options={layoutOptions2}
            direction="row"
            className="layout-radio-group layout-align"
          />
          <TextArea
            label="Title"
            field={titleField}
            basic
            helperText="This will be the title visible in the report"
            helperIcon="information"
            helperIconColor="yellow"
            className="title-text-input textarea-as-textinput"
            error={titleFieldError}
            maxLength={75}
          />{' '}
          {columnDataArray.map((data, index) => (
            <>
              <div className="separator" />
              <SingleColumnLayout
                key={`topic-column-${data.key}`}
                column={index}
                contentType={contentTypeArray[index]}
                setContentTypes={setContentTypeArray}
                richText={richTextArray[index]}
                setRichTexts={setRichTextArray}
                hypothesisId={get(
                  hypothesisArray,
                  [index, 'contentThreadId'],
                  null
                )}
                setHypothesisArray={setHypothesisArray}
                excelChartId={excelChartIdArray[index]}
                setExcelChartIdArray={setExcelChartIdArray}
                labelComponent={data.label}
              />
            </>
          ))}
          {generalError && <D3Alert configType={generalError} />}
        </Modal.Body>
      </div>
      <div className="separator" />
      <div className="topic-modal-footer">
        <Modal.Footer>
          {showWarning && (
            <div
              className="hidden-content-container"
              data-testid="hidden-content-warning"
            >
              <Icon name="alert" className="fill icon-black" width={16} />
              <span className="hidden-content-warning">{warningText}</span>
            </div>
          )}
          <div className="">
            <Button
              className="button-link form-cancel-button mr-4"
              type="button"
              onClick={handleClose}
              data-testid="add-topic-modal-cancel-button"
            >
              Cancel
            </Button>
            <Button
              disabled={!isValid || isSaving}
              className="button"
              onClick={handleSave}
              type="button"
              data-testid="save-topic-input"
            >
              {topic ? 'Update' : 'Add'}
            </Button>
          </div>
        </Modal.Footer>
      </div>
    </Modal>
  );
};

export default TopicFormModal;
