import React from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Constants } from '@pwc/pwc-deals-web-common';
import { postAnalysis } from '../../analysesService';
import FreeformAnalysisForm from '../FreeformAnalysisForm/FreeformAnalysisForm';
import { SectionsProvider } from '../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { viewAnalysisPath } from '../../../../common/services/urlService';
import { getLinksFromNarratives } from '../../../narratives/narrativesUtilities';

const parentContentId = -1;

const CreateFreeformAnalysis = () => {
  const history = useHistory();
  const { projectId } = useParams();

  const cancelAndGoBack = () => history.goBack();

  const redirectToAnalysisView = analysisId => {
    history.replace(viewAnalysisPath(projectId, analysisId));
  };

  const publishAction = async (analysisDto, narratives) => {
    analysisDto.links = getLinksFromNarratives(narratives, parentContentId);
    const result = await postAnalysis(projectId, analysisDto);
    redirectToAnalysisView(result);
  };

  const initialSections = [
    {
      order: 1,
      layoutType: Constants.SECTION_LAYOUT_TYPE.ONE_COLUMN,
      items: []
    }
  ];
  return (
    <SectionsProvider value={{ initialSections }}>
      <FreeformAnalysisForm
        analysisTitle=""
        analysisBody=""
        publishAction={publishAction}
        cancelAction={cancelAndGoBack}
        formTitle="Add an Analysis"
        appliedTags={[]}
        onSaveText="Adding..."
        standbyText="Add"
        sections={initialSections}
        parentContentId={parentContentId}
      />
    </SectionsProvider>
  );
};

export default CreateFreeformAnalysis;
