import { Icon, Button, Select, Loader } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import { Field, Form, Formik } from 'formik';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import D3Alert from '../../common/components/Alert/D3Alert';
import CheckboxFormikField from '../../common/components/Checkbox/CheckboxFormikField';
import PublishButton from '../../common/components/PublishButton';
import RadioGroupFieldset from '../../common/components/RadioGroup/RadioGroupFieldset';
import SelectMultiple from '../../common/components/SelectMultiple/SelectMultiple';
import Separator from '../../common/components/Separator';
import TextAreaFieldset from '../../common/components/TextAreaFieldset/TextAreaFieldset';
import TextInputFieldset from '../../common/components/TextInputFieldset/TextInputFieldset';
import featureFlags from '../../common/helpers/featureFlags';
import { useProjectOptionsFromApi } from '../../common/hooks/useProjectOptionsFromApi';
import { updateProjectDetails } from '../../common/services/projectService';
import { projectSettingsSchema } from '../project/common/ProjectModel';
import {
  defaultRestrictionOption,
  financialMagnitudeOptions,
  restrictionOptions,
  currencyOptions,
  financialsDecimalCountOptions,
  dealOptions,
  consentLevelOptions,
  projectType
} from '../project/common/projectOptions';
import {
  getProject,
  loadProjectThunk,
  updateProjectSettingsThunk
} from '../project/projectSlice';
import EnableForProjectState from '../security/EnableForProjectState';
import ShowForPermissions from '../security/ShowForPermissions';

const ProjectDetails = () => {
  const { projectId } = useParams();
  const [hasError, setHasError] = useState(false);
  const [isSaving, setIsSaving] = useState(false);

  const project = useSelector(getProject);

  const dispatch = useDispatch();
  const handleSubmit = async updatedFormValues => {
    const {
      financialsMagnitude,
      currency,
      financialsDecimalCount,
      consentLevel
    } = updatedFormValues;

    try {
      setIsSaving(true);
      setHasError(false);

      const projectForUpdate = {
        ...project,
        ...updatedFormValues,
        serviceIds: updatedFormValues.services.map(_ => _.value),
        services: undefined
      };

      if (!featureFlags.diagnostic) {
        delete projectForUpdate.showDiagnostic;
      }

      await updateProjectDetails(projectForUpdate);
      await dispatch(
        updateProjectSettingsThunk(project.id, {
          financialsMagnitude,
          currency,
          financialsDecimalCount,
          consentLevel
        })
      );

      await dispatch(loadProjectThunk(project.id));
    } catch (e) {
      setHasError(true);
    } finally {
      setIsSaving(false);
    }
  };

  // RadioGroupFieldset require the value to be strings.
  const asString = value => `${value}`;

  // TODO: in the future defaulting client type will probably not be a thing
  const clientTypeRestriction = project.restriction
    ? project.restriction
    : defaultRestrictionOption.value;

  const {
    isLoading,
    industryOptions,
    serviceOptions
  } = useProjectOptionsFromApi();

  if (isLoading) {
    return <Loader data-testid="project-options-loading" />;
  }

  const initialValues = {
    territory: project.territory || Constants.TERRITORIES.UNITED_STATES,
    type: `${project.type}`,
    restriction: asString(clientTypeRestriction),
    name: project.name,
    clientName: project.clientName,
    target: project.target,
    description: project.description,
    industryId: `${project.industryId}`,
    currency: project.currency,
    financialsMagnitude: project.financialsMagnitude,
    consentLevel: project.consentLevel,
    financialsDecimalCount: asString(project.financialsDecimalCount),
    services: project.serviceIds.map(_ =>
      serviceOptions.find(s => s.value === `${_}`)
    ),
    showDiagnostic: project.showDiagnostic
  };

  return (
    project.id && (
      <EnableForProjectState projectId={projectId}>
        {({ isProjectDisabled }) => (
          <ShowForPermissions
            projectId={project.id}
            permissions={[Constants.USER_PERMISSIONS.MANAGE_USERS]} // TODO: Change this to the proper permission when it's available
          >
            <div className="settings-container" data-testid="projectSettings">
              <div className="settings-description">
                Edit the project details here, or you can change the currency
                format and how the data appears in the project. Any changes you
                make will apply for all users.
              </div>

              <Separator />
              <Formik
                initialValues={initialValues}
                enableReinitialize
                onSubmit={handleSubmit}
                validateOnMount
                validateOnChange
                validationSchema={projectSettingsSchema}
              >
                {({ isValid, dirty, values, setFieldValue, resetForm }) => (
                  <Form
                    data-testid="project-settings-form"
                    className="project-settings-form-container"
                  >
                    <div className="form-fields">
                      <Field
                        name="territory"
                        label="Territory"
                        component={TextInputFieldset}
                        basic
                        disabled
                      />
                      <Field
                        name="type"
                        component={({ field: formikField }) => {
                          const field = {
                            ...formikField,
                            onChange: e => {
                              if (`${projectType.buy.id}` === e.target.value) {
                                setFieldValue('showDiagnostic', false);
                              } else if (
                                `${projectType.sell.id}` === e.target.value
                              ) {
                                setFieldValue('showDiagnostic', true);
                              }

                              formikField.onChange(e);
                            }
                          };
                          return (
                            <RadioGroupFieldset
                              field={field}
                              options={dealOptions}
                              direction="row"
                              label="Deal type"
                              disabled={isProjectDisabled}
                            />
                          );
                        }}
                      />
                      {featureFlags.diagnostic &&
                        values.type === `${projectType.sell.id}` && (
                          <Field
                            name="showDiagnostic"
                            component={CheckboxFormikField}
                            direction="row"
                            label="Show Diagnostic Questionnaire"
                            dataTestId="show-diagnostic-questionnaire"
                            disabled={isProjectDisabled}
                          />
                        )}
                      <Field
                        name="restriction"
                        component={RadioGroupFieldset}
                        options={restrictionOptions}
                        direction="row"
                        label="Client type"
                        disabled={isProjectDisabled}
                      />
                      <SelectMultiple
                        matchOnStartsWith={false}
                        minimumSearchStringLength={2}
                        fieldName="services"
                        label="Services / Products"
                        placeholder="Search for services and products"
                        options={serviceOptions}
                        selections={values.services || []}
                        onChange={selections =>
                          setFieldValue('services', selections)
                        }
                        noMatchElement={null}
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="name"
                        label="Project name"
                        component={TextInputFieldset}
                        basic
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="clientName"
                        label="Client name"
                        component={TextInputFieldset}
                        basic
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="target"
                        label="Target (optional)"
                        component={TextInputFieldset}
                        basic
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="description"
                        label="Description (optional)"
                        component={TextAreaFieldset}
                        basic
                        disabled={isProjectDisabled}
                      />
                      <Select
                        data-testid="industry-select"
                        basic
                        options={industryOptions}
                        label="Industry (optional)"
                        onChange={event =>
                          setFieldValue('industryId', event.target.value, true)
                        }
                        value={values.industryId}
                        placeholder="Please select the client's industry"
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="consentLevel"
                        component={RadioGroupFieldset}
                        options={consentLevelOptions}
                        direction="row"
                        label="Data Use Consent Level"
                        disabled={isProjectDisabled}
                      />
                      <h2>Format details</h2>
                      <p
                        style={{
                          display: 'flex',
                          alignItems: 'center'
                        }}
                      >
                        <Icon
                          name="information"
                          className="fill icon-yellow"
                          width={16}
                          style={{
                            marginRight: '0.5rem'
                          }}
                        />
                        Automated for structured analysis only
                      </p>
                      <Field
                        name="currency"
                        component={RadioGroupFieldset}
                        options={currencyOptions}
                        direction="row"
                        label="Currency"
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="financialsMagnitude"
                        component={RadioGroupFieldset}
                        options={financialMagnitudeOptions}
                        direction="row"
                        label="Number format"
                        disabled={isProjectDisabled}
                      />
                      <Field
                        name="financialsDecimalCount"
                        component={RadioGroupFieldset}
                        options={financialsDecimalCountOptions}
                        direction="row"
                        label="Decimals"
                        disabled={isProjectDisabled}
                      />
                    </div>

                    <Separator />

                    <div className="form-bottom-actions">
                      <Button
                        className="button-link form-cancel-button"
                        onClick={() => resetForm()}
                        type="button"
                        data-testid="cancel-button-bottom"
                      >
                        Cancel
                      </Button>
                      <PublishButton
                        isSaving={isSaving}
                        isValid={isValid && dirty}
                        buttonSize="sm"
                        onSaveText="Saving..."
                        standbyText="Save Changes"
                        buttonPosition="bottom"
                      />
                    </div>
                    {hasError && <D3Alert configType="publish" />}
                  </Form>
                )}
              </Formik>
            </div>
          </ShowForPermissions>
        )}
      </EnableForProjectState>
    )
  );
};
export default ProjectDetails;
