import React from 'react';
import { Link } from 'react-router-dom';

const Breadcrumbs = ({ breadcrumbs }) => {
  return breadcrumbs.map((crumb, index) => {
    const testId = `breadcrumb-${crumb.text.toLowerCase().replace(/ /g, '-')}`;

    return index === breadcrumbs.length - 1 ? (
      <span className="breadcrumb-text" data-testid={testId} key={crumb.path}>
        {crumb.text}
      </span>
    ) : (
      <span key={crumb.path}>
        <Link to={crumb.path} data-testid={testId} className="breadcrumb-link">
          {crumb.text}
        </Link>
      </span>
    );
  });
};

export default Breadcrumbs;
