import React, { useCallback } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { FieldArray, useFormikContext } from 'formik';
import './ProjectRoleUsersField.scss';
import { ProjectRoleUserPill } from '../Pills/ProjectRoleUserPill';
import { UserSearchSelect } from '../UserSearchSelect/UserSearchSelect';

export const userPillId = user =>
  `${user.role
    .toLowerCase()
    .split(' ')
    .join('-')}-${user.email
    .toLowerCase()
    .split(/[\s,@.]+/)
    .join('-')}`;

export const ProjectRoleUsersField = ({
  title,
  description,
  role,
  validateNotAlreadyIn = Constants.USER_ROLES_LIST,
  allowMany = false,
  showEmail = false,
  allowRemoving = true,
  allowAdding = true,
  addAsPending = false,
  allowApprovePending = false
}) => {
  const { values } = useFormikContext();

  const handleUserSubmit = arrayHelpers => user =>
    arrayHelpers.push({
      ...user,
      pillId: userPillId(user),
      pendingApproval: addAsPending,
      newlyAdded: true
    });

  const handleRemoval = useCallback(
    (arrayHelpers, index) =>
      allowRemoving
        ? () => {
            arrayHelpers.remove(index);
          }
        : undefined,
    [allowRemoving]
  );

  const handleTogglePending = useCallback(
    (arrayHelpers, user, index) =>
      user.wasPending
        ? () => {
            arrayHelpers.replace(index, {
              ...user,
              pendingApproval: !user.pendingApproval
            });
          }
        : undefined,
    []
  );

  const showTextInput =
    allowAdding && (!(values[role] || []).length || allowMany);

  const roleIdPart = role
    .split(' ')
    .join('-')
    .toLowerCase();

  const usersAndIndex = values[role].map((user, index) => ({ user, index }));
  const approvedUsers = usersAndIndex.filter(
    ({ user }) => !user.pendingApproval
  );
  const pendingUsers = usersAndIndex.filter(({ user }) => user.pendingApproval);
  const unavailableUsers = validateNotAlreadyIn.map(key => values[key]).flat();

  const allowsManyClass =
    usersAndIndex.length && allowMany ? 'allows-many' : '';
  const hasUsersClass = usersAndIndex.length ? 'has-users' : '';
  return (
    <FieldArray
      name={role}
      render={arrayHelpers => (
        <div
          className={`project-role-user-field ${allowsManyClass} ${hasUsersClass}`}
          data-testid={`project-user-role-${roleIdPart}`}
        >
          <div className="project-user-description-cell">
            <h4 data-testid={`project-user-role-title-${roleIdPart}`}>
              {title}
            </h4>
            <span data-testid={`project-user-role-description-${roleIdPart}`}>
              {description}
            </span>
          </div>
          {showTextInput ? (
            <UserSearchSelect
              role={role}
              onUserSelected={handleUserSubmit(arrayHelpers)}
              unavailableUsers={unavailableUsers}
            />
          ) : null}
          {approvedUsers.length ? (
            <div className="project-user-pill-container">
              {approvedUsers.map(({ user, index }) => (
                <ProjectRoleUserPill
                  key={user.pillId}
                  user={user}
                  showEmail={showEmail}
                  onClearClick={handleRemoval(arrayHelpers, index)}
                  onTogglePending={
                    allowApprovePending
                      ? handleTogglePending(arrayHelpers, user, index)
                      : undefined
                  }
                />
              ))}
            </div>
          ) : null}
          {pendingUsers.length ? (
            <>
              <h4 className="project-user-pill-label">Pending approval</h4>
              <div className="project-user-pill-container pending">
                {pendingUsers.map(({ user, index }) => (
                  <ProjectRoleUserPill
                    key={user.pillId}
                    user={user}
                    showEmail={showEmail}
                    onClearClick={handleRemoval(arrayHelpers, index)}
                    onTogglePending={
                      allowApprovePending
                        ? handleTogglePending(arrayHelpers, user, index)
                        : undefined
                    }
                  />
                ))}
              </div>
            </>
          ) : null}
        </div>
      )}
    />
  );
};
