import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { Loader } from '@pwc/pwc-deals-ui';
import ContentItemNew from '../../../common/charts/types/ContentItem/ContentItemNew';
import PowerBiReport from '../PowerBiReport/PowerBiReport';
import RichTextLibraryItem from '../../../common/charts/types/ContentItem/RichTextLibraryItem';

const AnalysisLibraryItem = React.memo(
  ({ item, projectId, analysisId }) => {
    const renderableContentItemTypes = [
      Constants.LIBRARY_ITEM_TYPES.EXCEL_CHART.value,
      Constants.LIBRARY_ITEM_TYPES.EXCEL_NAMED_RANGE.value,
      Constants.LIBRARY_ITEM_TYPES.POWER_POINT_GROUP.value
    ];

    const isPowerBiReport =
      item.type === Constants.LIBRARY_ITEM_TYPES.POWER_BI_REPORT.value ||
      item.type === Constants.LIBRARY_ITEM_TYPES.POWER_BI_REPORT_PAGE.value;

    return (
      <div
        data-testid="analysis-library-item"
        data-library-item-name={item.name}
        className="analysis-library-item-container"
      >
        <div className="analysis-library-item-content">
          {item.type && (
            <>
              {renderableContentItemTypes.includes(item.type) && (
                <ContentItemNew
                  projectId={projectId}
                  libraryItemId={item.id}
                  contentId={analysisId}
                  content={item.value || null}
                />
              )}
              {item.type === Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value && (
                <RichTextLibraryItem item={item} />
              )}

              {isPowerBiReport && (
                <PowerBiReport item={item} projectId={projectId} />
              )}
            </>
          )}
          {!item.type && <Loader dataTestId="excel-content-loader" />}
        </div>
      </div>
    );
  },
  (prevProps, nextProps) =>
    nextProps.item.id === prevProps.item.id &&
    nextProps.item.isShared === prevProps.item.isShared
);
export default AnalysisLibraryItem;
