/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react';
import NarrativeCard from '../NarrativeCard/NarrativeCard';
import './NarrativeSection.scss';

const NarrativeSection = ({
  title,
  narratives,
  projectId,
  getViewPath,
  index = null,
  truncate = true,
  showBody = true
}) => {
  const titleIdentifier = title.replace(/\s/g, '-').toLowerCase();
  const titleIdentifierWithIndex =
    titleIdentifier + (index !== null ? `-${index}` : '');
  const hasNarratives = narratives.length;

  const sectionClasses = [
    'narrative-section',
    `narrative-section-${titleIdentifier}`
  ];
  if (!hasNarratives) {
    sectionClasses.push('narrative-section-empty');
  }

  return (
    <div
      className={sectionClasses.join(' ')}
      data-testid={`narrative-section-${titleIdentifierWithIndex}`}
    >
      <div
        className="narrative-section-title"
        data-testid={`narrative-section-title-${titleIdentifierWithIndex}`}
      >
        {title}
      </div>
      {narratives.length ? (
        narratives.map((narrative, narrativeIndex) => (
          <NarrativeCard
            key={`narrative-card-${narrative.title}`}
            narrative={narrative}
            testId={`${titleIdentifierWithIndex}-${narrativeIndex}`}
            getViewPath={getViewPath}
            truncate={truncate}
            projectId={projectId}
            showBody={showBody}
          />
        ))
      ) : (
        <div
          className="narrative-card narrative-card-empty"
          data-testid={`narrative-section-empty-${titleIdentifierWithIndex}`}
        >
          No connected {title}
        </div>
      )}
    </div>
  );
};

export default NarrativeSection;
