import React from 'react';
import { Icon } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import RichTextTopicPanel from './RichTextTopicPanel';
import KeyFocusAreaTopicPanel from './KeyFocusAreaTopicPanel';
import ExcelChartTopicPanel from './ExcelChartTopicPanel';
import './TopicPanel.scss';
import authorizationService from '../../../../../../common/services/security/authorizationService';

const getClassName = (layoutType, panelNumber) => {
  let className = 'topic-panel';
  switch (layoutType) {
    case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel--equal-left-column`;
      } else {
        className = `${className} topic-panel--equal-right-column`;
      }
      break;
    }
    case Constants.TOPIC_LAYOUT_TYPE.THREE_COLUMN.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel--small-left-column`;
      } else if (panelNumber === 2) {
        className = `${className} topic-panel--middle-column`;
      } else {
        className = `${className} topic-panel--right-column`;
      }
      break;
    }
    case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_67_1A_R_33_1A.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel--big-left-column`;
      } else {
        className = `${className} topic-panel--right-column`;
      }
      break;
    }

    case Constants.TOPIC_LAYOUT_TYPE.TWO_COLUMN_L_50_2A_R_50_1A.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel-2col-l50-2a-r50-1a top-left`;
      } else if (panelNumber === 2) {
        className = `${className} topic-panel-2col-l50-2a-r50-1a right-column`;
      } else {
        className = `${className} topic-panel-2col-l50-2a-r50-1a bottom-left`;
      }
      break;
    }

    case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_80_1A_B_20_1A.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel-stacked-large-top-small-bottom big-top-panel`;
      } else {
        className = `${className} topic-panel-stacked-large-top-small-bottom small-bottom-panel`;
      }
      break;
    }

    case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_60_1A_B_40_1A.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel--stacked-medium-top-row`;
      } else {
        className = `${className} topic-panel--stacked-medium-bottom-row`;
      }
      break;
    }

    case Constants.TOPIC_LAYOUT_TYPE.TWO_ROW_T_50_1A_B_50_2A.value: {
      if (panelNumber === 1) {
        className = `${className} topic-panel--full-column`;
      } else if (panelNumber === 2) {
        className = `${className} topic-panel--equal-left-column`;
      } else {
        className = `${className} topic-panel--equal-right-column`;
      }
      break;
    }
    default:
  }
  return className;
};

const DeletedContentWarning = () => (
  <div data-testid="deleted-content-warning">
    <h5>
      <Icon name="alert" className="fill icon-yellow" width="20" />
      &nbsp; Content Not Found
    </h5>
    <p>
      The content cannot be displayed as it has been deleted. Please update or
      delete this topic.
    </p>
  </div>
);

const TopicPanel = ({ topic, panel, hypothesis, projectId }) => {
  const className = getClassName(topic.layoutType, panel.panelNumber);

  return (
    <div data-testid="topic-panel" className={className}>
      {panel.type === Constants.TOPIC_PANEL_TYPES.RICH_TEXT.value && (
        <RichTextTopicPanel topic={topic} panel={panel} projectId={projectId} />
      )}
      {panel.type === Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.value &&
        hypothesis && (
          <KeyFocusAreaTopicPanel
            hypothesis={hypothesis}
            projectId={projectId}
          />
        )}
      {panel.type === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.value && (
        <ExcelChartTopicPanel topic={topic} panel={panel} projectId={projectId}>
          {!authorizationService.isClientUser() && <DeletedContentWarning />}
        </ExcelChartTopicPanel>
      )}
      {panel.type === Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.value &&
        !hypothesis &&
        !authorizationService.isClientUser() && <DeletedContentWarning />}
    </div>
  );
};

export default TopicPanel;
