import { isEmpty as _isEmpty } from 'lodash';
import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import {
  getProjectStatus,
  useProjectsStatuses
} from '../../common/hooks/useProjectsStatuses';
import {
  isProjectArchived,
  isProjectInactive
} from '../../common/helpers/projectStatusesUtils';
import { loadProjectsThunk } from '../projects/projectsSlice';

const EnableForProjectState = ({ projectId, children }) => {
  const dispatch = useDispatch();
  const projectsStates = useProjectsStatuses();
  const projectStatus = getProjectStatus(projectsStates, projectId);
  const [isProjectListFetched, setIsProjectListFetched] = useState(
    window.isProjectsListFetched
  );
  const isJestTesting = process.env.JEST_WORKER_ID !== undefined;

  useEffect(() => {
    if (_isEmpty(projectsStates) && !isProjectListFetched && !isJestTesting) {
      dispatch(loadProjectsThunk());
      setIsProjectListFetched(true);
      window.isProjectsListFetched = true;
    }
  }, [dispatch, projectsStates, isProjectListFetched, isJestTesting]);

  return children({
    isProjectDisabled: projectStatus
      ? isProjectArchived(projectStatus) || isProjectInactive(projectStatus)
      : false
  });
};

EnableForProjectState.propTypes = {
  projectId: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired
};

export default EnableForProjectState;
