import React, { useEffect, useState } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { useHistory, useParams } from 'react-router-dom';
import { Button } from '@pwc/pwc-deals-ui';
import { useDispatch, useSelector } from 'react-redux';
import EnableForProjectState from '../../security/EnableForProjectState';
import {
  getCurrentInsight,
  getInsightLoading,
  loadInsightThunk,
  resetInsight
} from '../insightSlice';
import {
  deleteInsight,
  getInsightVersions,
  updateInsightStatus
} from '../insightsService';
import { FormLayout } from '../../../common/components/layouts';
import PageTitle from '../../../common/components/PageTitle/PageTitle';
import ModalConfirmation from '../../../common/components/ModalConfirmation';
import {
  editInsightPath,
  insightsListPath
} from '../../../common/services/urlService';
import PageSection from '../../../common/components/PageSection';
import './ViewInsight.scss';
import PageContentLabel from '../../../common/components/PageContentLabel/PageContentLabel';
import ShowForPermissions from '../../security/ShowForPermissions';
import ShowForRole from '../../security/ShowForRole';
import ConnectedContent from '../../narratives/ConnectedContent/ConnectedContent';
import { getWorkflowStatus } from '../../workflow/workflowService';
import { useApprovalWorkflow } from '../../../common/hooks/useApprovalWorkflow';
import TagList from '../../analyses/common/TagList/TagList';
import MoreMenu from '../../../common/components/ContentActions/MoreMenu';
import { getViewClientInfo } from '../../../common/helpers/viewClientVersion';
import { entitySharedStatusUpdateSuccess } from '../../comments/commentsSlice';
import { showMoreMenuError } from '../../../common/redux/errors/errorsSlice';
import StatusContainer from '../../../common/components/StatusContainer/StatusContainer';

const ViewInsight = () => {
  const { projectId, insightId: insightContentThreadId } = useParams();
  const dispatch = useDispatch();
  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);
  const [versions, setVersions] = useState(null);
  const history = useHistory();
  const insight = useSelector(getCurrentInsight);
  const isLoading = useSelector(getInsightLoading);

  const handleDeleteInsight = async () => {
    setShowDeleteConfirmation(false);
    const success = await deleteInsight(projectId, insightContentThreadId);
    if (success) {
      history.replace(insightsListPath(projectId));
    }
  };

  useEffect(() => {
    dispatch(loadInsightThunk(projectId, insightContentThreadId));

    return () => dispatch(resetInsight());
  }, [dispatch, projectId, insightContentThreadId]);

  useEffect(() => {
    if (insight) {
      dispatch(entitySharedStatusUpdateSuccess(insight.shareWithAll));
    }
  }, [dispatch, insight]);

  useEffect(() => {
    if (insight && !insight.hasError) {
      getInsightVersions(
        projectId,
        insightContentThreadId
      ).then(insightVersions => setVersions(insightVersions));
    }
  }, [insight, projectId, insightContentThreadId]);

  const workflowActions = useApprovalWorkflow(
    getWorkflowStatus,
    async (_projectId, contentId, step) => {
      try {
        await updateInsightStatus(_projectId, `${contentId}`, step);
      } catch (e) {
        dispatch(
          showMoreMenuError({
            itemId: contentId,
            moreMenuErrorType: Constants.MoreMenuErrorType.UNABLE_TO_SAVE_CHANGE
          })
        );
        return;
      }

      dispatch(loadInsightThunk(projectId, insightContentThreadId));
    },
    projectId,
    insightContentThreadId,
    insight,
    versions
  );

  const gotoEditInsight = () =>
    history.push(editInsightPath(projectId, insightContentThreadId));

  const showClientVersion = () => {
    if (!isLoading) {
      dispatch(
        loadInsightThunk(
          projectId,
          insightContentThreadId,
          Constants.VERSION_STATUS.SHARED.value
        )
      );
    }
  };
  const showDraftVersion = () => {
    if (!isLoading) {
      dispatch(loadInsightThunk(projectId, insightContentThreadId));
    }
  };

  const viewClient = getViewClientInfo(
    showClientVersion,
    showDraftVersion,
    versions,
    insight && insight.versionNumber,
    insight && insight.versionStatus
  );

  const headerComponent = (
    <>
      {insight && (
        <div className="insight-top-bar-container">
          <div className="insight-top-bar">
            <div />
            <div>
              <ShowForPermissions
                projectId={projectId}
                permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
              >
                <EnableForProjectState projectId={projectId}>
                  {({ isProjectDisabled }) => (
                    <Button
                      data-testid="edit-button"
                      type="button"
                      onClick={gotoEditInsight}
                      className="button-block button-small mr-3"
                      disabled={
                        isProjectDisabled || viewClient.isOnClientVersion
                      }
                    >
                      Edit
                    </Button>
                  )}
                </EnableForProjectState>
                <MoreMenu
                  projectId={projectId}
                  itemId={insightContentThreadId}
                  onDelete={() => setShowDeleteConfirmation(true)}
                  workflowActions={workflowActions}
                  viewClient={viewClient}
                />
              </ShowForPermissions>
            </div>
          </div>
        </div>
      )}
      <ModalConfirmation
        show={showDeleteConfirmation}
        onCancel={() => setShowDeleteConfirmation(false)}
        onConfirm={handleDeleteInsight}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage="This Insight will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />
    </>
  );

  return (
    <FormLayout headerComponent={headerComponent}>
      <div className="insight-content">
        <PageContentLabel cssLabel="insight" label="Insight" />
        <StatusContainer
          projectId={projectId}
          content={insight}
          allowDraftPill={!viewClient.isOnClientVersion}
        />
        <ShowForRole
          projectId={projectId}
          roles={Constants.USER_ROLE_GROUPS.INTERNAL}
        >
          <div className="section-name">Title</div>
        </ShowForRole>
        <PageTitle title={insight.title} />
        <PageSection label="Description" value={insight.body} />
      </div>
      <ConnectedContent projectId={projectId} contentId={insight.contentId} />
      {insight.tags && insight.tags.length > 0 && (
        <TagList tags={insight.tags} testId="insight-tag-list" />
      )}
    </FormLayout>
  );
};

export default ViewInsight;
