import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  MenuSwitch,
  Menu,
  HeaderLogo,
  AppBannerSpacer
} from '@pwc/pwc-deals-ui';
import ProjectListSideNavButtons from './ProjectListSideNavButtons';
import ProjectSideNavButtons from './ProjectSideNavButtons';
import {
  toggleMobileSidebarOpened,
  getMobileSidebarOpened
} from '../../../features/appSettings/appSettingsSlice';
import { getProject } from '../../../features/project/projectSlice';

const SideNav = () => {
  const dispatch = useDispatch();
  const open = useSelector(getMobileSidebarOpened);
  const menuId = 'navigation';
  const project = useSelector(getProject);
  const projectId = project && project.id;
  const hasCurrentProject = !!projectId;

  return (
    <div className={`side-bar ${open}`}>
      <AppBannerSpacer />
      <div className="side-bar-logo">
        <MenuSwitch
          open={open}
          onClick={() => dispatch(toggleMobileSidebarOpened())}
          menuId={menuId}
        />
        <HeaderLogo width={48} dataTestId="header-logo" />
      </div>
      <Menu data-testid="side-bar" open={open}>
        {hasCurrentProject ? (
          <ProjectSideNavButtons
            closeSideNav={() => dispatch(toggleMobileSidebarOpened(false))}
            projectId={projectId}
            projectName={project.name}
          />
        ) : (
          <ProjectListSideNavButtons
            closeSideNav={() => dispatch(toggleMobileSidebarOpened(false))}
            mobile={open}
          />
        )}
        <AppBannerSpacer />
      </Menu>
    </div>
  );
};

export default SideNav;
