import { createSlice } from '@reduxjs/toolkit';
import { getHypothesis } from './hypothesesService';

export const initialState = {
  id: null,
  name: null,
  isLoading: false,
  hasError: false,
  errorStatusCode: null
};

const hypothesisSlice = createSlice({
  name: 'hypothesis',
  initialState,
  reducers: {
    resetHypothesis: () => initialState,
    fetchHypothesisBegan: state => ({
      ...state,
      isLoading: true,
      hasError: false,
      errorStatusCode: null
    }),
    fetchHypothesisSucceeded: (state, action) => {
      return {
        ...state,
        ...action.payload,
        isLoading: false,
        hasError: false,
        errorStatusCode: null
      };
    },
    fetchHypothesisFailed: (state, action) => ({
      ...state,
      isLoading: false,
      hasError: true,
      errorStatusCode: action.payload
    })
  }
});
export default hypothesisSlice.reducer;

// Actions
export const {
  resetHypothesis,
  fetchHypothesisBegan,
  fetchHypothesisSucceeded,
  fetchHypothesisFailed
} = hypothesisSlice.actions;

// Selectors
export const getHypotheisId = state => state.hypothesis.id;
export const getHypothesisTitle = state => state.hypothesis.title;
export const getHypothesisLoading = state => state.hypothesis.isLoading;
export const getHypothesisError = state => state.hypothesis.hasError;
export const getHypothesisErrorStatusCode = state =>
  state.hypothesis.errorStatusCode;
export const getCurrentHypothesis = state => state.hypothesis;

// Thunks
export const loadHypothesisThunk = (
  projectId,
  hypothesisId,
  versionStatus
) => async dispatch => {
  try {
    dispatch(fetchHypothesisBegan());

    const response = await getHypothesis(
      projectId,
      hypothesisId,
      versionStatus
    );

    dispatch(fetchHypothesisSucceeded(response));
  } catch (error) {
    dispatch(
      fetchHypothesisFailed(error.response ? error.response.status : 500)
    );
  }
};
