import _orderBy from 'lodash/orderBy';
import { ProjectHttpClient } from '../../common/services/httpClient';

const baseServicePath = (projectId, insightThreadId, versionStatus) =>
  ['projects', projectId, 'insights', insightThreadId, versionStatus]
    .filter(Boolean)
    .join('/');

const mapInsightToApi = insight => ({
  ...insight,
  createdBy: insight.createdBy || 'unset'
});

const sortBySortOrder = insights => _orderBy(insights, 'sortOrder');

export const getInsights = async projectId => {
  const response = await ProjectHttpClient.get(baseServicePath(projectId));
  return sortBySortOrder(response.data);
};

export const getInsight = async (projectId, insightThreadId, versionStatus) => {
  const response = await ProjectHttpClient.get(
    baseServicePath(projectId, insightThreadId, versionStatus)
  );
  return response.data;
};

export const getInsightVersions = async (projectId, insightThreadId) => {
  const response = await ProjectHttpClient.get(
    `${baseServicePath(projectId, insightThreadId)}/versions`
  );
  return response.data;
};

export const postInsight = async (projectId, insight) => {
  const response = await ProjectHttpClient.post(
    baseServicePath(projectId),
    mapInsightToApi(insight)
  );
  return response.data;
};

export const putInsight = async (projectId, insight) => {
  const response = await ProjectHttpClient.put(
    baseServicePath(projectId, insight.contentThreadId),
    mapInsightToApi(insight)
  );
  return response.data;
};

export const deleteInsight = async (projectId, insightThreadId) => {
  const response = await ProjectHttpClient.delete(
    baseServicePath(projectId, insightThreadId)
  );
  return response.data;
};

export const updateInsightStatus = async (
  projectId,
  insightThreadId,
  workflowAction
) => {
  const url = `${baseServicePath(
    projectId,
    insightThreadId
  )}/${workflowAction}`;
  const response = await ProjectHttpClient.post(url);

  return response.data;
};

export const updateInsightsOrder = async (
  projectId,
  orderedContentThreadIds
) => {
  const response = await ProjectHttpClient.post(
    `${baseServicePath(projectId)}/sortorder`,
    orderedContentThreadIds
  );

  return response.data;
};
