import { createReducer } from '@reduxjs/toolkit';
import adjustmentsForDocumentUpdated from '../actions/adjustmentsForDocumentUpdated';
import { clearAllProjectData } from '../actions/clearAllProjectData';
import resetCalculationDataForDocument from '../actions/resetCalculationDataForDocument';

const isInvalidPayload = payload =>
  payload === undefined ||
  payload.documentCode === undefined ||
  payload.adjustments === undefined;

const adjustmentsByDocument = createReducer(
  {},
  {
    [adjustmentsForDocumentUpdated]: (state, action) => {
      if (isInvalidPayload(action.payload)) {
        return state;
      }
      const { documentCode, adjustments } = action.payload;
      state[documentCode] = adjustments;
      return state;
    },
    [resetCalculationDataForDocument]: (state, action) => {
      if (!action.payload || !action.payload.documentCode) {
        return state;
      }
      const { documentCode } = action.payload;
      delete state[documentCode];
      return state;
    },
    [clearAllProjectData]: () => ({})
  }
);

export default adjustmentsByDocument;
