import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import DisplayPill from './DisplayPill';

const UserViewStatusPill = ({ userViewStatus }) => {
  return userViewStatus > Constants.USER_VIEW_STATUS.NONE.value ? (
    <DisplayPill dataTestId="user-view-status-pill" forUserView>
      {Constants.getUserViewStatus(userViewStatus)}
    </DisplayPill>
  ) : null;
};

export default UserViewStatusPill;
