import React, { useState, useLayoutEffect } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Loader } from '@pwc/pwc-deals-ui';
import { Formik } from 'formik';
import { contentTypeInsights } from '../../common/components/proptypes/commonProptypes';
import ContentList from '../contentList/ContentList';
import InsightsGrid from './InsightsGrid/InsightsGrid';
import ToggleGridList from '../../common/components/ToggleGridList/ToggleGridList';
import {
  getInsights,
  deleteInsight,
  updateInsightsOrder
} from './insightsService';
import { getNarratives } from '../narratives/narrativesService';
import TagEditor from '../tagging/common/TagEditor/TagEditor';
import {
  getUserSettingsSelector,
  setUserSettings
} from '../../common/redux/userSettings/userSettingsSlice';

const InsightsListContainer = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [isError, setIsError] = useState(false);

  const { projectId } = useParams();
  const [insights, setInsights] = useState([]);
  const [tagFilters, setTagFilters] = useState([]);
  const [narratives, setNarratives] = useState({});
  const userSettings = useSelector(getUserSettingsSelector);
  const dispatch = useDispatch();

  useLayoutEffect(() => {
    const fetch = async () => {
      setIsError(false);
      setIsLoading(true);

      Promise.all([getInsights(projectId), getNarratives(projectId)])
        .then(([projectInsights, projectNarratives]) => {
          setInsights(projectInsights);
          setNarratives(projectNarratives);
          setIsLoading(false);
        })
        .catch(() => {
          setIsError(true);
          setIsLoading(false);
        });
    };
    fetch();
  }, [projectId]);

  const handleDeleteInsight = async insightId => {
    const success = await deleteInsight(projectId, insightId);
    if (success) {
      const data = await getInsights(projectId);
      setInsights(data);
    }
  };

  const handleReorder = orderedInsights => {
    setInsights(orderedInsights);
  };

  const toggleView = () =>
    dispatch(
      setUserSettings({
        viewGrid: !userSettings.settings.viewGrid
      })
    );

  const updateTags = values => {
    setTagFilters(values);
  };

  const filteredInsights = insights.filter(insight => {
    const insightTags = new Set((insight.tags || []).map(tag => tag.text));
    return tagFilters.length
      ? tagFilters.some(tag => insightTags.has(tag))
      : true;
  });

  return (
    <>
      <div className="insights-list-container">
        <div className="list-container-header">
          <h2 className="insights-list-h2">Insights</h2>
          <ToggleGridList
            viewGrid={userSettings.settings.viewGrid}
            onClick={toggleView}
          />
        </div>

        {isError && <div>You don&apos;t have access to this content</div>}
        <div className="tag-editor-container">
          <Formik initialValues={{ tags: [] }}>
            {() => (
              <TagEditor
                projectId={projectId}
                onSelect={updateTags}
                tagsRight
                title={false}
                subtitle={false}
                allowCreateTags={false}
                placeholderText="Filter by tag"
              />
            )}
          </Formik>
        </div>
        {isLoading ? (
          <Loader dataTestId="loader" />
        ) : (
          <>
            {userSettings.settings.viewGrid === true ? (
              <InsightsGrid
                projectId={projectId}
                data={filteredInsights}
                hasContent={!!insights.length}
                narratives={narratives}
                onDelete={handleDeleteInsight}
              />
            ) : (
              <ContentList
                contentType={contentTypeInsights}
                projectId={projectId}
                contentItems={filteredInsights}
                projectHasContent={!!insights.length}
                onDelete={({ contentThreadId }) =>
                  handleDeleteInsight(contentThreadId)
                }
                onReorder={handleReorder}
                updateSortOrderApiCall={updateInsightsOrder}
                isFiltered={tagFilters.length > 0}
              />
            )}
          </>
        )}
      </div>
    </>
  );
};

export default InsightsListContainer;
