import React from 'react';
import { Button, Icon, Tooltip } from '@pwc/pwc-deals-ui';

const DeleteButton = ({
  iconType,
  handleClick,
  handleMouseEnter,
  handleMouseLeave,
  tooltipPlacement,
  tooltip
}) => {
  const className = iconType.includes('-theme')
    ? iconType
    : `${iconType} icon-orange-theme`;

  return (
    <Tooltip direction={tooltipPlacement} tip={tooltip}>
      <Button
        className="button-no-underline"
        onClick={handleClick}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        data-testid="remove-section"
      >
        <Icon name="bin" className={className} width={24} height={24} />
      </Button>
    </Tooltip>
  );
};

export default DeleteButton;
