import React from 'react';
import PropTypes from 'prop-types';
import { Icon } from '@pwc/pwc-deals-ui';
import classNames from 'classnames';
import './ProjectStatePill.scss';
import {
  getProjectStatus,
  getProjectStatusIcon,
  useProjectsStatuses
} from '../../common/hooks/useProjectsStatuses';
import {
  isProjectActive,
  isProjectArchived,
  isProjectInactive,
  isProjectInactivePendingInProgress,
  isProjectReactivePendingInProgress
} from '../../common/helpers/projectStatusesUtils';

const ProjectStatePill = ({
  showWhenActive,
  isSmall,
  projectId,
  showPendingStatus
}) => {
  const projectsStates = useProjectsStatuses();
  const projectStatus = getProjectStatus(projectsStates, projectId);
  const projectStateIcon = getProjectStatusIcon(projectsStates, projectId);
  const iconSize = 13;

  if (
    !isProjectActive(projectStatus) &&
    !isProjectInactive(projectStatus) &&
    !isProjectArchived(projectStatus)
  ) {
    return null;
  }

  if (!projectStatus || (!showWhenActive && isProjectActive(projectStatus))) {
    return null;
  }

  return (
    <div
      className="project-state-pill-container"
      data-testid="project-state-pill"
    >
      <div
        className={classNames('project-state-pill', {
          'project-state-pill__no-icon': isProjectActive(projectStatus),
          'project-state-pill__small': isSmall
        })}
      >
        {(isProjectInactive(projectStatus) ||
          isProjectArchived(projectStatus)) && (
          <Icon
            name={projectStateIcon}
            className="fill icon-black"
            width={iconSize}
            height={iconSize}
          />
        )}
        {isProjectActive(projectStatus) && (
          /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
          <span tabIndex={0} data-testid="project-state-pill-status">
            {`Active${
              isProjectReactivePendingInProgress(projectStatus) &&
              showPendingStatus
                ? ', pending'
                : ''
            }`}
          </span>
        )}
        {isProjectInactive(projectStatus) && (
          /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
          <span tabIndex={0} data-testid="project-state-pill-status">
            {`Inactive${
              isProjectInactivePendingInProgress(projectStatus) &&
              showPendingStatus
                ? ', pending'
                : ''
            }`}
          </span>
        )}
        {isProjectArchived(projectStatus) && (
          /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
          <span tabIndex={0} data-testid="project-state-pill-status">
            Archived
          </span>
        )}
      </div>
    </div>
  );
};

ProjectStatePill.propTypes = {
  projectId: PropTypes.number.isRequired,
  showWhenActive: PropTypes.bool,
  showPendingStatus: PropTypes.bool,
  isSmall: PropTypes.bool
};

ProjectStatePill.defaultProps = {
  showWhenActive: true,
  showPendingStatus: false,
  isSmall: false
};

export default ProjectStatePill;
