import authorizationService from '../../../services/security/authorizationService';
import { setLoggedInUserSucceeded } from './loggedInUserSlice';
import loggedInUserMapper from '../../../services/security/loggedInUserMapper';

export const setLoggedInUserThunk = () => async dispatch => {
  const jwtToken = authorizationService.getToken();
  const setLoggedInUser = loggedInUserMapper.map(jwtToken);
  dispatch(setLoggedInUserSucceeded(setLoggedInUser));
};
