import React from 'react';
import { Button } from '@pwc/pwc-deals-ui';

const PublishButton = ({
  isSaving,
  isValid,
  buttonPosition,
  buttonSize,
  onSaveText = 'Publishing...',
  standbyText = 'Publish',
  dirty = true,
  dataTestId = undefined
}) => {
  const isPublishable = !isSaving && isValid && dirty !== false;

  return (
    <Button
      className={`button-block ${buttonSize}`}
      type="submit"
      disabled={!isPublishable}
      data-testid={dataTestId || `publish-button-${buttonPosition}`}
    >
      {isSaving ? onSaveText : standbyText}
    </Button>
  );
};

export default PublishButton;
