import PropTypes from 'prop-types';
import React from 'react';
import { Popover } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { viewStructuredAnalysisPathForDocument } from '../../../../../common/services/urlService';
import { getLinksPopoverData } from '../AdjustmentGroup/AdjustmentGroupHelpers';
import { linkedAdjustmentsShape } from '../../../../../common/components/proptypes/commonProptypes';
import './LinkedAdjustmentPopover.scss';

const LinkedAdjustmentPopover = React.forwardRef(
  ({ adjustmentLinks, projectId, ...props }, ref) => {
    const popoverData = getLinksPopoverData(adjustmentLinks);

    return (
      <Popover
        id="popover-linked-adjustments"
        ref={ref}
        data-testid="popover-linked-adjustments"
        {...props}
      >
        <Popover.Content className="popup-menu scrollable">
          <div className="linked-adjustments-title">Linked Adjustments</div>
          {popoverData.map(target => {
            return (
              <section
                className="link-target-document"
                key={`link-target-${target.documentDisplayName}`}
              >
                <div className="link-target-document-name">
                  {target.documentDisplayName}
                </div>
                {target.links.map(link => {
                  if (link.isBroken) {
                    return (
                      <span
                        className="link-target-adjustment-name strikethrough"
                        key={`link-target-${link.displayName}`}
                      >
                        {link.displayName}
                      </span>
                    );
                  }

                  const targetDocumentUrl = viewStructuredAnalysisPathForDocument(
                    projectId,
                    target.documentCode
                  );

                  return (
                    <Link
                      className="link-target-adjustment-name"
                      to={{
                        pathname: targetDocumentUrl,
                        search: link.queryString
                      }}
                      key={`link-target-${link.displayName}`}
                    >
                      {link.displayName}
                    </Link>
                  );
                })}
              </section>
            );
          })}
        </Popover.Content>
      </Popover>
    );
  }
);

LinkedAdjustmentPopover.propTypes = {
  adjustmentLinks: PropTypes.arrayOf(linkedAdjustmentsShape),
  projectId: PropTypes.string.isRequired
};

LinkedAdjustmentPopover.defaultProps = {
  adjustmentLinks: []
};

export default LinkedAdjustmentPopover;
