import React, { useState, useEffect } from 'react';
import './KPIContainer.scss';
import { useParams } from 'react-router-dom';
import KPISection from './KPISection';
import { getKPIs } from '../../KPIService';

const KPIContainer = () => {
  const { projectId } = useParams();
  const [kpis, setKpis] = useState([]);

  useEffect(() => {
    let isMounted = true;

    (async () => {
      const response = await getKPIs(projectId);
      if (isMounted) setKpis(response);
    })();

    return () => {
      isMounted = false;
      setKpis([]);
    };
  }, [projectId]);

  return kpis.length > 0 ? (
    <div className="kpi-container" data-testid="kpi-container">
      <h2>KPIs</h2>
      <div className="kpi-subpanel">
        {kpis.map(({ name, indicators }, index) => (
          <KPISection
            name={name}
            index={index}
            indicators={indicators}
            key={`kpi-section-${name}`}
          />
        ))}
      </div>
    </div>
  ) : null;
};

export default KPIContainer;
