import React from 'react';
import './NewAndUpdated.scss';
import {
  NotificationCounts,
  NotificationCountLink
} from '@pwc/pwc-deals-notification-ui';

const NewAndUpdated = ({
  newContent,
  updatedContent,
  projectId,
  shouldRetrieveNotificationCount
}) => {
  return (
    <div className="new-and-updated-container">
      <NotificationCounts
        projectId={projectId}
        shouldRetrieveNotificationCount={shouldRetrieveNotificationCount}
      />
      <NotificationCountLink
        text="New Content"
        to={
          shouldRetrieveNotificationCount
            ? `/projects/${projectId}/notifications/project-activity`
            : '/projects'
        }
        count={newContent}
      />
      <NotificationCountLink
        text="Updated Content"
        to={
          shouldRetrieveNotificationCount
            ? `/projects/${projectId}/notifications/project-activity`
            : '/projects'
        }
        count={updatedContent}
      />
    </div>
  );
};
export default NewAndUpdated;
