import React from 'react';
import Handlebars from 'handlebars';
import { useSelector } from 'react-redux';
import Separator from '../Separator';
import authorizationService from '../../services/security/authorizationService';
import { getUserTermsAndConditionsHasAccepted } from '../../redux/userTermsAndConditions/userTermsAndConditionsSlice';
import { getProjectId } from '../../../features/project/projectSlice';
import { sanitize } from '../../services/sanitizeService';
import {
  getFooterLoaded,
  getFooterTemplate
} from '../../redux/termsAndConditions/footerSlice';
import TermsConditionsLinks from './TermsConditionsLinks';
import './Footer.scss';

const Footer = () => {
  const footerLoaded = useSelector(getFooterLoaded);
  const projectId = useSelector(getProjectId);
  const hasAccepted = useSelector(getUserTermsAndConditionsHasAccepted);
  const footerTemplate = useSelector(getFooterTemplate);
  const isInternalUser = !authorizationService.isClientUser();
  const shouldShowLinks = hasAccepted || isInternalUser;

  if (!footerLoaded) {
    return null;
  }

  const getCurrentYear = () => new Date().getFullYear();
  const fallbackCopyrightStatement = `<div class="copyright"><p> ${getCurrentYear()} PwC. All rights reserved.</p></div>`;

  const showSubText = shouldShowLinks && projectId;
  const templateTokens = {
    showSubText,
    projectId,
    currentYear: getCurrentYear()
  };

  let interpolatedTemplate;
  try {
    const template = Handlebars.compile(footerTemplate);
    interpolatedTemplate = template(templateTokens);
  } catch (e) {
    interpolatedTemplate = fallbackCopyrightStatement;
  }

  return (
    <footer>
      <Separator />
      {shouldShowLinks ? <TermsConditionsLinks /> : null}
      <div
        className="copyright"
        dangerouslySetInnerHTML={{
          __html: sanitize(interpolatedTemplate)
        }}
      />
    </footer>
  );
};
export default Footer;
