import locationService from '../services/locationService';
import authorizationService from '../services/security/authorizationService';
import userActivityService from '../services/security/userActivityService';
import projectTokenCache from '../services/security/projectTokenCache';
import {
  invalidateTokens,
  refreshTokens
} from '../services/security/userAuthService';

export const tokenRefreshJob = async () => {
  const cutoffInSeconds = 240;

  if (
    userActivityService.isUserActive() &&
    authorizationService.hasValidToken()
  ) {
    const secondsFromExp = authorizationService.secondsFromExp();
    if (secondsFromExp < cutoffInSeconds) {
      const refreshTokenSet = await refreshTokens();
      authorizationService.setTokens(refreshTokenSet);
    }
  } else {
    projectTokenCache.reset();
    await invalidateTokens();
    userActivityService.clearActivityTimer();
    locationService.redirect(
      `${window.location.protocol}//${window.location.host}`
    );
  }
};
