const ValidateTag = value => {
  if (value.trim().length === 0) {
    return 'Tag cannot be empty.';
  }
  if (value.trim().length > 30) {
    return 'Tag cannot be more than 30 characters long.';
  }
  const onlyLettersAndNumbers = !value.match(/[^A-Za-z0-9 ]+/g);
  if (!onlyLettersAndNumbers) {
    return 'Tag cannot contain special characters.';
  }
  return null;
};

export default ValidateTag;
