const toolbar = [
  'heading',
  '|',
  'bold',
  '|',
  'italic',
  '|',
  'bulletedList',
  '|',
  'numberedList',
  '|',
  'indent',
  '|',
  'outdent',
  '|',
  'link'
];

const introConversion = {
  model: 'intro',
  view: {
    name: 'p',
    classes: 'rich-text-editor-intro'
  }
};

const paragraphConversion = {
  model: 'paragraph',
  view: {
    name: 'p',
    classes: 'rich-text-editor-paragraph'
  }
};

const footnotesConversion = {
  model: 'footnotes',
  view: {
    name: 'p',
    classes: 'rich-text-editor-footnotes'
  }
};

export const customHeadingPlugin = editor => {
  editor.conversion.elementToElement(introConversion);
  editor.conversion.elementToElement(paragraphConversion);
  editor.conversion.elementToElement(footnotesConversion);
};

export const customIntroHeadingOnlyPlugin = editor => {
  const addIntroClass = (_, data, conversionApi) => {
    conversionApi.writer.addClass(
      'rich-text-editor-intro',
      conversionApi.mapper.toViewElement(data.item)
    );
  };
  editor.conversion.for('downcast').add(dispatcher => {
    dispatcher.on('insert:listItem', addIntroClass, { priority: 'low' });
    dispatcher.on('insert:paragraph', addIntroClass, { priority: 'low' });
  });
};

const editorConfig = {
  language: 'en',
  licenseKey: process.env.REACT_APP_CK_EDITOR_LICENSE,
  toolbar,
  placeholder: 'Start writing here...',
  extraPlugins: [customHeadingPlugin],
  heading: {
    options: [
      {
        model: 'heading1',
        view: 'h3',
        title: 'Heading 1',
        class: 'ck-heading-option-heading3'
      },
      {
        model: 'heading2',
        view: 'h4',
        title: 'Heading 2',
        class: 'ck-heading-option-heading4'
      },
      {
        ...introConversion,
        title: 'Intro',
        class: 'ck-heading-option-intro'
      },
      {
        ...paragraphConversion,
        title: 'Paragraph',
        class: 'ck-heading-option-paragraph'
      },
      {
        ...footnotesConversion,
        title: 'Footnotes',
        class: 'ck-heading-option-footnotes'
      }
    ]
  }
};

export const noHeadingEditorConfig = {
  ...editorConfig,
  toolbar: toolbar.slice(2)
};

export const introHeadingOnlyEditorConfig = {
  ...noHeadingEditorConfig,
  extraPlugins: [customIntroHeadingOnlyPlugin]
};

export const shortEditorConfig = {
  ...editorConfig,
  toolbar: ['bold', '|', 'italic', '|', 'link']
};

export const shortIntroHeadingOnlyEditorConfig = {
  ...editorConfig,
  extraPlugins: [customIntroHeadingOnlyPlugin],
  toolbar: ['bold', '|', 'italic', '|', 'link']
};

export default editorConfig;
