import isEqual from 'lodash/isEqual';

export const checkIsValid = errors => {
  const errorValues = Object.values(errors);
  for (let index = 0; index < errorValues.length; index += 1) {
    if (errorValues[index] !== '') {
      return false;
    }
  }
  return true;
};

export const validateTitle = value => {
  let error = '';
  if (!value || value.trim().length === 0) {
    error = 'Title is required';
  }
  return error;
};

export const mapDto = (title, body, tags, sections) => {
  return {
    title,
    body,
    tags: tags.map(tag => ({
      text: tag
    })),
    sections
  };
};

export const formIsDirty = ({
  title,
  body,
  analysisTitle,
  analysisBody,
  tagsDirty,
  linkedNarrativesChanged,
  initialSections,
  currentSections
}) => {
  if (title !== analysisTitle) return true;
  if (body !== analysisBody) return true;
  if (linkedNarrativesChanged) return true;
  if (tagsDirty) return true;

  const initialAndCurrentSectionsAreEqual = isEqual(
    initialSections,
    currentSections
  );
  if (!initialAndCurrentSectionsAreEqual) return true;
  return false;
};
