import { createSlice } from '@reduxjs/toolkit';

export const initialState = {
  template: null,
  loaded: false,
  loading: false
};

const footerSlice = createSlice({
  name: 'footer',
  initialState,
  reducers: {
    setFooterRequested: state => {
      return {
        ...state,
        template: null,
        loaded: false,
        loading: true
      };
    },
    setFooterReceived: (state, action) => {
      return {
        ...state,
        template: action.payload.body,
        loaded: true,
        loading: false
      };
    }
  }
});

// Actions
export const { setFooterRequested, setFooterReceived } = footerSlice.actions;

// Selectors
export const getFooterLoaded = state => state.footer.loaded;
export const getFooterLoading = state => state.footer.loading;
export const getFooterTemplate = state => state.footer.template;

export default footerSlice.reducer;
