import React, { useState, useEffect } from 'react';
import NarrativeSection from '../NarrativeSection/NarrativeSection';
import { getNarrativesForContent } from '../narrativesService';
import {
  viewHypothesisPath,
  viewInsightPath
} from '../../../common/services/urlService';
import './ConnectedContent.scss';
import { analysisLinkBuilder } from '../../analyses/common/analysisUrlHelper';

const ConnectedContent = ({ projectId, contentId, classModifier }) => {
  const [narratives, setNarratives] = useState(null);

  useEffect(() => {
    const fetchNarratives = async () => {
      const fetchedNarratives = await getNarrativesForContent(
        projectId,
        contentId
      );
      setNarratives(fetchedNarratives);
    };

    const canFetchNarratives = projectId && contentId;
    if (canFetchNarratives) {
      fetchNarratives();
    }
    return () => setNarratives(null);
  }, [projectId, contentId]);

  const hasNarratives =
    narratives && Object.values(narratives).some(_ => _.length);

  return hasNarratives ? (
    <div
      className={`connected-content ${classModifier}`}
      data-testid="connected-content"
    >
      <div className="connected-content-title">Connected content</div>
      <div className="linked-narratives" data-testid="linked-narratives">
        <NarrativeSection
          title="Key Focus Areas"
          narratives={narratives.hypotheses}
          getViewPath={hypothesisId =>
            viewHypothesisPath(projectId, hypothesisId)
          }
          setNarratives={setNarratives}
          projectId={projectId}
        />
        <NarrativeSection
          title="Insights"
          narratives={narratives.insights}
          getViewPath={insightId => viewInsightPath(projectId, insightId)}
          setNarratives={setNarratives}
          projectId={projectId}
        />
        <NarrativeSection
          title="Analyses"
          narratives={narratives.analyses}
          getViewPath={analysisLinkBuilder(narratives.analyses, projectId)}
          setNarratives={setNarratives}
          projectId={projectId}
        />
      </div>
    </div>
  ) : null;
};

export default ConnectedContent;
