import React from 'react';
import { useSelector } from 'react-redux';
import { AppBanner, Button } from '@pwc/pwc-deals-ui';
import { getAppBannerConfig } from './structuredAnalysisBannerSelectors';

const StructuredAnalysisViewingToggledDataBanner = ({
  showDisclaimerModal
}) => {
  const config = useSelector(getAppBannerConfig);
  return (
    <AppBanner
      showBanner={config.showBanner}
      dataTestId="viewing-toggled-data-banner"
    >
      <div data-testid="disclaimer-warning-text">
        You are outside of the PwC default view. Your data selections may not be
        consistent with the commentary and/or observations presented on this
        screen. Information should be viewed in conjunction with the
        following&nbsp;
        <Button
          className="button-link"
          key="project-disclaimer"
          data-testid="project-disclaimer-link"
          onClick={() => showDisclaimerModal(true)}
        >
          disclaimer.
        </Button>
      </div>
    </AppBanner>
  );
};

export default StructuredAnalysisViewingToggledDataBanner;
