import CKEditor from '@ckeditor/ckeditor5-react';
import { ClassicEditor } from '@pwc/pwc-deals-rich-text-editor';
import React, { useEffect, useState } from 'react';
import initializeCKEditor from './initializeCKEditor';
import readOnlyCkEditorConfig from './readOnlyCKEditorConfig';

const ReadOnlyRichTextEditor = ({ richTextData, childrenData = [] }) => {
  const [editor, setEditor] = useState(null);
  useEffect(() => {
    if (editor) {
      initializeCKEditor(editor, richTextData, childrenData);
    }
  }, [richTextData, childrenData, editor]);

  return (
    <div className="text-input-fieldset fieldset">
      <div data-testid="ckEditor" className="text-input-container basic">
        <CKEditor
          editor={ClassicEditor}
          onInit={setEditor}
          config={readOnlyCkEditorConfig}
        />
      </div>
    </div>
  );
};

export default ReadOnlyRichTextEditor;
