    projectId,
    targetAdjustmentType,
    targetAdjustmentName,
    hasError,
    analysis,
    isConfirmVisible,
    setIsConfirmVisible,
    isLoading,
    versions,
    chartKey,
    incrementChartKey,
    viewClient,
    handleEdit,
    handleDelete,
    handleRefresh,
    workflowActions,
    isClientView
