import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import StatusPill from './StatusPill';

const ApprovalPill = ({ versionStatus, dataTestId = 'versionStatus' }) => {
  const statusesToDisplay = [
    Constants.VERSION_STATUS.APPROVED,
    Constants.VERSION_STATUS.APPROVAL_REQUESTED
  ].map(_ => _.value);

  return statusesToDisplay.includes(versionStatus) ? (
    <StatusPill
      dataTestId={dataTestId}
      title={Constants.getVersionStatus(versionStatus)}
    />
  ) : null;
};

export default ApprovalPill;
