import classNames from 'classnames';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Constants } from '@pwc/pwc-deals-web-common';
import DealDetails from '../../../common/components/DealDetails';
import Separator from '../../../common/components/Separator';
import StatusPill from '../../../common/components/Pills/StatusPill';
import UserSnippet from '../../../common/components/UserSnippet';
import {
  useProjectsStatuses,
  getProjectStatus
} from '../../../common/hooks/useProjectsStatuses';
import { getUserViewStatusCountsForProject } from '../../../common/services/projectService';
import {
  addAnalysisTypePath,
  createHypothesisPath,
  createInsightPath
} from '../../../common/services/urlService';
import {
  getNarratives,
  getNarrativesCount
} from '../../narratives/narrativesService';
import ProjectStatePill from '../../projectStatePill/ProjectStatePill';
import ShowForRole from '../../security/ShowForRole';
import NewAndUpdated from '../common/NewAndUpdated';
import { getUserByRole } from '../common/ProjectModel';
import { getProjectType } from '../common/projectOptions';
import { fetchUserViewCountsSucceeded, getProject } from '../projectSlice';
import ContentLinkSubPanel from './ContentLinkSubPanel';
import KPIContainer from './KPIs/KPIContainer';
import ProjectOverviewModuleContainer from './ProjectOverview/ProjectOverviewModuleContainer';
import DiagnosticContainer from './Diagnostic/DiagnosticContainer';
import { isProjectReadOnly } from '../../../common/helpers/projectStatusesUtils';
import '../../projects/ProjectList.scss';
import './ProjectOverview.scss';
import NarrativesContext from '../../../common/contexts/NarrativesContext';
import Accordion from '../../../common/components/Accordion/AccordionOnlyMobile';

const ProjectOverview = () => {
  const dispatch = useDispatch();
  const project = useSelector(getProject);
  const engagementLead = getUserByRole(
    project,
    Constants.USER_ROLES.ENGAGEMENT_LEAD
  );
  const projectsStates = useProjectsStatuses();
  const projectStatus = getProjectStatus(projectsStates, project.id);

  const engagementManager = getUserByRole(
    project,
    Constants.USER_ROLES.ENGAGEMENT_MANAGER
  );
  const addHypothesisTypeHref = createHypothesisPath(project.id);
  const addInsightTypeHref = createInsightPath(project.id);
  const addAnalysisTypeHref = addAnalysisTypePath(project.id);

  const [counts, setCounts] = useState(false);
  const [narratives, setNarratives] = useState(null);

  useEffect(() => {
    let isMounted = true;
    const fetch = async () => {
      if (project.id) {
        const userViewCountsResponse = await getUserViewStatusCountsForProject(
          project.id
        );
        dispatch(fetchUserViewCountsSucceeded(userViewCountsResponse));

        const projectNarratives = await getNarratives(project.id);
        if (isMounted && projectNarratives) setNarratives(projectNarratives);

        const narrativeCounts = await getNarrativesCount(project.id);
        if (isMounted && narrativeCounts) setCounts(narrativeCounts);
      }
    };

    fetch();

    return () => {
      isMounted = false;
    };
  }, [project.id, dispatch]);

  if (!project.id) {
    return null;
  }

  return (
    <div className="project-overview">
      <h2>Project Overview</h2>
      <div className="project-container content-container">
        <div
          key={project.id}
          className={classNames('project-panel', {
            'project-panel__readonly': isProjectReadOnly(projectStatus)
          })}
        >
          <div className="project-panel-row">
            <div className="project-panel-col project-title-description-container">
              <span className="project-title" data-testid="project-title">
                <span className="title-overflow">{project.name}</span>
                <div className="pills-container">
                  <StatusPill
                    dataTestId={`project-${project.id}-type`}
                    title={getProjectType(project.type)}
                  />
                  <ProjectStatePill
                    projectId={project.id}
                    showWhenActive={false}
                    isSmall
                  />
                </div>
              </span>
            </div>
          </div>
          <div className="project-description-container">
            <div
              className="project-description"
              data-testid="project-description"
            >
              {project.description || ' '}
            </div>
            <NewAndUpdated
              newContent={project.newCount || 0}
              updatedContent={project.updatedCount || 0}
              projectId={project.id}
              shouldRetrieveNotificationCount
            />
          </div>
          <ShowForRole
            projectId={project.id}
            roles={Constants.USER_ROLE_GROUPS.INTERNAL}
          >
            <Separator />
            <Accordion title="Info">
              <div className="project-panel-columns">
                <div className="user-details-container">
                  <div>
                    <UserSnippet
                      dataTestId={`project-${project.id}-engagement-lead`}
                      firstName={engagementLead.firstName}
                      lastName={engagementLead.lastName}
                      email={engagementLead.email}
                      position="Engagement Lead"
                    />
                  </div>
                  <div>
                    <UserSnippet
                      dataTestId={`project-${project.id}-engagement-manager`}
                      firstName={engagementManager.firstName}
                      lastName={engagementManager.lastName}
                      email={engagementManager.email}
                      position="Engagement Manager"
                    />
                  </div>
                </div>
                <div className="deal-details-container">
                  <div className="detail-section">
                    <DealDetails
                      dataTestId={`project-${project.id}-client-details`}
                      title="Client"
                      content={project.clientName}
                    />
                  </div>
                  <div className="detail-section">
                    <DealDetails
                      dataTestId={`project-${project.id}-target-details`}
                      title="Target"
                      content={project.target}
                    />
                  </div>
                  <div className="detail-section">
                    <DealDetails
                      dataTestId={`project-${project.id}-industry-status`}
                      title="Industry"
                      content={project.industry}
                    />
                  </div>
                </div>
              </div>
            </Accordion>
          </ShowForRole>
        </div>

        <DiagnosticContainer
          id={project.id}
          type={project.type}
          showDiagnostic={project.showDiagnostic}
        />

        <KPIContainer />
        <NarrativesContext.Provider value={narratives}>
          <ProjectOverviewModuleContainer />
        </NarrativesContext.Provider>
        {counts && (
          <div
            className="project-overview-subpanel-container"
            data-testid="projectOverviewSubpanelContainer"
          >
            {counts.kfas >= 0 && (
              <ContentLinkSubPanel
                itemCount={counts.kfas}
                contentAddLinkTo={addHypothesisTypeHref}
                contentListIconName="hypothesis"
                contentListLinkTo={`/projects/${project.id}/key-focus-areas`}
                contentType="Key Focus Areas"
              />
            )}
            {counts.insights >= 0 && (
              <ContentLinkSubPanel
                itemCount={counts.insights}
                contentAddLinkTo={addInsightTypeHref}
                contentListIconName="insight"
                contentListLinkTo={`/projects/${project.id}/insights`}
                contentType="Insights"
              />
            )}
            {counts.analyses >= 0 && (
              <ContentLinkSubPanel
                itemCount={counts.analyses}
                contentAddLinkTo={addAnalysisTypeHref}
                contentListIconName="analysis"
                contentListLinkTo={`/projects/${project.id}/analyses`}
                contentType="Analyses"
              />
            )}
          </div>
        )}
      </div>
    </div>
  );
};
export default ProjectOverview;
