import { Loader } from '@pwc/pwc-deals-ui';
import React from 'react';
import { useSelector } from 'react-redux';

import { getFilesState } from '../../../common/redux/fileUpload/fileUploadSlice';
import CONSTANTS from '../Constants';
import FileListItem from './FileListItem';
import FileListZeroStateCopy from './FileListZeroStateCopy';

const FileList = () => {
  const { data, loading, error } = useSelector(getFilesState);

  if (error) {
    // eslint-disable-next-line no-console
    console.error(error);
  }

  return (
    <>
      {loading && <Loader dataTestId={CONSTANTS.TEST_IDS.LOADER} />}
      {!loading &&
        data &&
        data.files &&
        (data.files.length === 0 ? (
          <FileListZeroStateCopy />
        ) : (
          data.files.map(file => <FileListItem key={file.fileId} file={file} />)
        ))}
    </>
  );
};

export default FileList;
