import React from 'react';
import { v4 as uuidv4 } from 'uuid';
import { useParams } from 'react-router-dom';
import { Droppable } from 'react-beautiful-dnd';
import { Button, Icon } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';

import DraggableLibraryItemCard from '../../../../DraggableLibraryItemCard';
import { postRichText } from '../../../../../../../contentLibrary/libraryItemService';
import { setNonLaidOutLibraryItems } from '../../../../../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';

import './LibraryItemsListData.scss';
import useTabletOrMobile from '../../../../../../../../common/hooks/useTabletOrMobile';

const LibraryItemsListData = ({
  libraryItems = [],
  dispatch,
  droppableLocations
}) => {
  const { projectId } = useParams();
  const droppableLocationId = 'source-tray';
  const isTabletOrMobile = useTabletOrMobile();

  const handleAddTextBox = async () => {
    const name = uuidv4();
    const richTextData = JSON.stringify({
      RichTextData: Constants.EMPTY_RICH_TEXT_PLACEHOLDER
    });
    const id = await postRichText(projectId, name, richTextData);
    setNonLaidOutLibraryItems(
      [
        {
          id,
          name,
          type: Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value,
          isShared: false,
          data: richTextData
        },
        ...libraryItems
      ],
      dispatch
    );
  };

  return (
    <div
      className="library-items-list-data"
      data-testid="library-items-list-data"
    >
      <Button
        className="button-no-underline library-items-list-data__button"
        onClick={handleAddTextBox}
      >
        <Icon
          name="add"
          width={24}
          height={24}
          className="fill icon-orange-theme"
        />
        <span className="library-items-list-data__button__text">
          Add text-box
        </span>
      </Button>
      <Droppable
        droppableId={droppableLocationId}
        direction={!isTabletOrMobile ? 'vertical' : 'horizontal'}
      >
        {provided => {
          return (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              className="library-items-list-data__droppable-container"
              data-testid="source-tray"
            >
              <ul className="library-items-list-data__droppable-container__list">
                {libraryItems &&
                  libraryItems.map((libraryItem, index) => {
                    return (
                      <DraggableLibraryItemCard
                        key={libraryItem.id}
                        libraryItem={libraryItem}
                        index={index}
                        droppableLocationId={droppableLocationId}
                        droppableLocations={droppableLocations}
                      />
                    );
                  })}
                {provided.placeholder}
              </ul>
            </div>
          );
        }}
      </Droppable>
    </div>
  );
};

export default LibraryItemsListData;
