import { ProjectHttpClient } from '../../common/services/httpClient';

export const getProjectDisclaimer = async projectId => {
  const response = await ProjectHttpClient.get(
    `projects/${projectId}/projectDisclaimer`
  );
  return response.data;
};

export const setProjectUserAccepted = async projectId => {
  const response = await ProjectHttpClient.post(
    `projects/${projectId}/projectUser`
  );
  return response.data;
};

export const updateProjectDisclaimer = async (projectId, disclaimerText) => {
  const response = await ProjectHttpClient.put(
    `projects/${projectId}/projectDisclaimer`,
    { disclaimerHtml: disclaimerText, hasAccepted: true }
  );
  return response.data;
};
