import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import StructuredAnalysisTable from '../structured-analysis/StructuredAnalysisTable/StructuredAnalysisTable';
import { getAllStructuredIngestionDocuments } from '../../../common/engine/services/structuredAnalysisService';
import './AddAnalysisFromTemplate.scss';

const AddAnalysisFromTemplate = () => {
  const { projectId } = useParams();
  const [
    allStructuredIngestionDocuments,
    setAllStructuredIngestionDocuments
  ] = useState([]);

  useEffect(() => {
    const fetchAll = async () => {
      const response = await getAllStructuredIngestionDocuments(projectId);
      setAllStructuredIngestionDocuments(response);
    };

    fetchAll();
  }, [projectId]);

  return (
    <div
      data-testid="structured-analysis-container"
      className="add-analysis-from-template-container"
    >
      <h2>Add Analysis from template</h2>
      <p className="description">
        Please select the Analyses required for this project.
      </p>
      <div className="divider" />
      <StructuredAnalysisTable
        data-testid="structured-analysis-table"
        projectId={projectId}
        structuredDocuments={allStructuredIngestionDocuments}
      />
    </div>
  );
};

export default AddAnalysisFromTemplate;
