import { FileLibraryServiceClient } from './httpClient';

const baseServicePath = projectId => `projects/${projectId}/files`;

const baseServicePathWithFile = (projectId, fileId) =>
  `projects/${projectId}/files/${fileId}`;

export const getFiles = async projectId => {
  const response = await FileLibraryServiceClient(projectId).get(
    baseServicePath(projectId)
  );
  return response.data;
};

export const uploadFile = async (projectId, password, file) => {
  const response = await FileLibraryServiceClient(projectId).postFile(
    baseServicePath(projectId),
    { password },
    file
  );
  return response.data;
};

export const updateFileContent = async (
  projectId,
  password,
  file,
  uploadedFileId
) => {
  const response = await FileLibraryServiceClient(projectId).putFile(
    baseServicePathWithFile(projectId, uploadedFileId),
    { password },
    file
  );
  return response.data;
};

export const deleteFile = async (projectId, fileId) => {
  const response = await FileLibraryServiceClient(projectId).delete(
    baseServicePathWithFile(projectId, fileId)
  );
  return response.data;
};

export const postShareFile = async (projectId, fileId) => {
  const response = await FileLibraryServiceClient(projectId).post(
    `${baseServicePathWithFile(projectId, fileId)}/ShareWithClient`
  );
  return response.data;
};

export const updateFileStatus = async (projectId, fileId, operation) => {
  const response = await FileLibraryServiceClient(projectId).post(
    `projects/${projectId}/files/${fileId}/${operation}`
  );
  return response.data;
};

export const downloadFile = async (projectId, fileId) => {
  const response = await FileLibraryServiceClient(projectId).get(
    `${baseServicePathWithFile(projectId, fileId)}`
  );
  return response.data;
};
