import { Constants } from '@pwc/pwc-deals-web-common';

const endSlash = new RegExp(/\/$/);
const startSlash = new RegExp(/^\//);

const assembleUrl = base => path => {
  const cleanBase = base.replace(endSlash, '');
  const cleanPath = path.replace(startSlash, '');

  return `${cleanBase}/${cleanPath}`;
};

export const structuredRoutes = {
  [Constants.QUALITY_OF_EARNINGS]: 'quality-of-earnings',
  [Constants.NET_WORKING_CAPITAL]: 'net-working-capital',
  [Constants.DEBT_AND_DEBT_LIKE]: 'debt-debt-like'
};

export const structuredRoutesForSubType = {
  [Constants.structuredDocumentSubtypeCodes[Constants.QUALITY_OF_EARNINGS]]:
    'quality-of-earnings',
  [Constants.structuredDocumentSubtypeCodes[Constants.NET_WORKING_CAPITAL]]:
    'net-working-capital',
  [Constants.structuredDocumentSubtypeCodes[Constants.DEBT_AND_DEBT_LIKE]]:
    'debt-debt-like'
};

export const buildUrl = (base, path) => assembleUrl(base)(path);

