import React, { useEffect, useState, useCallback } from 'react';
import { useHistory, useLocation, useParams } from 'react-router-dom';
import EditLayoutTabs from '../EditLayoutTabs/EditLayoutTabs';
import { freeformLayoutIsValidNew } from '../../freeformLayoutIsValid';
import CONSTANTS from '../Constants';
import './AddReportDataTabs.scss';

const AddReportDataTabs = ({ droppableLocations }) => {
  const history = useHistory();
  const location = useLocation();
  const { projectId, analysisId } = useParams();
  const { pathname } = location;

  const pathMap = {
    [CONSTANTS.TABS.EDIT_LAYOUT]: CONSTANTS.TABS_ROUTE.LAYOUT,
    [CONSTANTS.TABS.SAVE_PREVIEW_REPORT]: CONSTANTS.TABS_ROUTE.PREVIEW
  };

  const getDefaultTab = useCallback(() => {
    if (pathname.includes(CONSTANTS.TABS_ROUTE.PREVIEW)) {
      return CONSTANTS.TABS.SAVE_PREVIEW_REPORT;
    }
    return CONSTANTS.TABS.EDIT_LAYOUT;
  }, [pathname]);
  const [defaultTab, setDefaultTab] = useState(() => getDefaultTab());

  const redirect = route => {
    const baseRoute = `/projects/${projectId}/analyses/`;
    let newRoute;

    if (route === CONSTANTS.TABS_ROUTE.PREVIEW && pathname.includes('create')) {
      newRoute = `${baseRoute}${CONSTANTS.PARTIAL_ROUTE.CREATE_LAYOUT_PREVIEW}`;
    } else if (
      route === CONSTANTS.TABS_ROUTE.PREVIEW &&
      pathname.includes('edit')
    ) {
      newRoute = `${baseRoute}${analysisId}/${CONSTANTS.PARTIAL_ROUTE.EDIT_LAYOUT_PREVIEW}`;
    } else if (
      route === CONSTANTS.TABS_ROUTE.LAYOUT &&
      pathname.includes('create')
    ) {
      newRoute = `${baseRoute}${CONSTANTS.PARTIAL_ROUTE.CREATE_LAYOUT}`;
    } else if (
      route === CONSTANTS.TABS_ROUTE.LAYOUT &&
      pathname.includes('edit')
    ) {
      newRoute = `${baseRoute}${analysisId}/${CONSTANTS.PARTIAL_ROUTE.EDIT_LAYOUT}`;
    }

    history.push(newRoute);
  };

  const setTab = tab => {
    const notificationPath = pathMap[tab];
    if (notificationPath) {
      redirect(notificationPath);
    }
  };

  const setTabByIndex = index => {
    const tabList = Object.keys(pathMap);
    const tab = tabList[index];
    setTab(tab);
  };

  useEffect(() => {
    setDefaultTab(() => getDefaultTab());
  }, [getDefaultTab, location]);

  return (
    <div
      className="add-report-data-layout-main-tabs"
      data-testid="add-report-data-tabs"
    >
      <EditLayoutTabs
        defaultTab={defaultTab}
        onChange={setTab}
        setTabByIndex={setTabByIndex}
      >
        <div label={CONSTANTS.TABS.EDIT_LAYOUT} />

        <div
          label={CONSTANTS.TABS.SAVE_PREVIEW_REPORT}
          disabled={!freeformLayoutIsValidNew(droppableLocations)}
        />
      </EditLayoutTabs>
    </div>
  );
};

export default React.memo(AddReportDataTabs);
