import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { getLinksFromNarratives } from '../../../narratives/narrativesUtilities';
import { getAnalysis, putAnalysis } from '../../analysesService';
import FreeformAnalysisForm from '../FreeformAnalysisForm/FreeformAnalysisForm';
import { SectionsProvider } from '../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { viewAnalysisPath2 } from '../../../../common/services/urlService';

const EditFreeformAnalysis = () => {
  const { projectId, analysisId } = useParams();
  const history = useHistory();
  const [analysis, setAnalysis] = useState(null);

  useEffect(() => {
    getAnalysis(projectId, analysisId).then(analysisData => {
      setAnalysis(analysisData);
    });
  }, [projectId, analysisId]);

  const redirectToAnalysisDetailPage = () =>
    history.replace(viewAnalysisPath2(projectId, analysisId));

  const publishAction = async (analysisDto, narratives) => {
    analysisDto.links = getLinksFromNarratives(narratives, analysis.contentId);
    await putAnalysis(projectId, analysisId, analysisDto);
    redirectToAnalysisDetailPage();
  };
  return (
    analysis && (
      <SectionsProvider value={{ initialSections: analysis.sections }}>
        <FreeformAnalysisForm
          projectId={projectId}
          analysisTitle={analysis.title}
          analysisBody={analysis.body}
          publishAction={publishAction}
          cancelAction={redirectToAnalysisDetailPage}
          formTitle="Edit Analysis"
          appliedTags={analysis.tags ? analysis.tags.map(tag => tag.text) : []}
          onSaveText="Updating..."
          standbyText="Update"
          parentContentId={analysis.contentId}
          sections={analysis.sections}
        />
      </SectionsProvider>
    )
  );
};

export default EditFreeformAnalysis;
