import React from 'react';
import { TextInput } from '@pwc/pwc-deals-ui';
import './TextInputFieldset.scss';

const TextInputFieldset = ({
  field,
  form,
  label,
  error,
  disabled,
  basic,
  size,
  className,
  type,
  helperText,
  helperIcon,
  helperIconColor,
  placeholder
}) => {
  return (
    <>
      <TextInput
        error={error || ''}
        label={label}
        form={form}
        field={field}
        size={size}
        basic={basic}
        type={type}
        className={className}
        disabled={disabled}
        helperText={helperText}
        helperIcon={helperIcon}
        helperIconColor={helperIconColor}
        placeholder={placeholder}
      />
    </>
  );
};

export default TextInputFieldset;
