import React from 'react';
import renderLibraryItemIcon from '../../CreateAnalysis/renderLibraryItemIcon';
import './LibraryItemCard.scss';

const DefaultCardInner = ({ libraryItemName }) => {
  return (
    <div
      className="library-item-card-title"
      data-testid="library-item-card-title"
    >
      {libraryItemName}
    </div>
  );
};

const LibraryItemCard = ({
  libraryItem,
  isDragging,
  hoverClass,
  innerRef,
  children,
  ...props
}) => {
  const outlineTypes = [100, 400, 401, 500];
  const getLibraryItemIconClass = () => {
    let iconClass = '';
    if (isDragging && !hoverClass) {
      iconClass = 'fill icon-grey';
    } else if (!isDragging && hoverClass) {
      const iconBaseColor = 'icon-grey-theme';
      const iconVariant = outlineTypes.includes(libraryItem.type)
        ? 'outline'
        : 'fill';
      iconClass = `${iconVariant} ${iconBaseColor} ${hoverClass}`;
    } else {
      iconClass = 'outline icon-grey';
    }
    return iconClass;
  };
  const computedIconClass = getLibraryItemIconClass();

  return (
    <div
      className="library-item-card"
      data-testid="library-item-card"
      {...props}
      ref={innerRef}
    >
      <div
        className="library-item-card-icon"
        data-testid="library-item-card-icon"
      >
        {renderLibraryItemIcon(libraryItem.type, computedIconClass, hoverClass)}
      </div>
      {children || <DefaultCardInner libraryItemName={libraryItem.name} />}
    </div>
  );
};
export default LibraryItemCard;
