import { Button, Icon } from '@pwc/pwc-deals-ui';
import React from 'react';
import './RichTextCardInner.scss';
import { RichTextPlainView } from './RichTextPlainView';

const RichTextCardInner = ({
  handleDeleteClick,
  richText,
  isInSourceTray,
  handleEditClick,
  isReadOnly
}) => {
  return (
    <div className="rich-text-card-inner">
      <RichTextPlainView richText={richText} />
      {!isReadOnly &&
        (isInSourceTray ? (
          <Button
            className="rich-text-card-inner__delete-button"
            onClick={handleDeleteClick}
            data-testid="delete-text-box-button"
          >
            <Icon className="rich-text-card-inner__delete-icon" name="delete" />
          </Button>
        ) : (
          <Button
            className="rich-text-card-inner__edit-button"
            onClick={handleEditClick}
            data-testid="edit-text-box-button"
          >
            <Icon className="rich-text-card-inner__edit-icon" name="pencil" />
          </Button>
        ))}
    </div>
  );
};

export default RichTextCardInner;
