import React, { useState, useEffect } from 'react';
import NarrativeSection from '../NarrativeSection/NarrativeSection';
import { getConnectedContent } from '../ConnectedContentService';
import {
  viewHypothesisPath,
  viewInsightPath
} from '../../../common/services/urlService';
import './ConnectedContent.scss';
import { analysisLinkBuilder } from '../../analyses/common/analysisUrlHelper';

const AnalysisDetailConnectedContent = ({
  projectId,
  contentId,
  classModifier
}) => {
  const [connectedContent, setConnectedContent] = useState(null);

  useEffect(() => {
    const fetchConnectedContent = async () => {
      const fetchedConnectedContent = await getConnectedContent(
        projectId,
        contentId
      );
      setConnectedContent(fetchedConnectedContent);
    };
    if (projectId && contentId) {
      fetchConnectedContent();
    }
    return () => setConnectedContent(null);
  }, [projectId, contentId]);

  const hasConnectedContent =
    connectedContent && Object.values(connectedContent).some(_ => _.length);

  return hasConnectedContent ? (
    <div
      className={`connected-content ${classModifier}`}
      data-testid="connected-content"
    >
      <div className="connected-content-title">Connected content</div>
      <div className="linked-narratives" data-testid="linked-narratives">
        <NarrativeSection
          title="Key Focus Areas"
          narratives={connectedContent.hypotheses}
          getViewPath={hypothesisId =>
            viewHypothesisPath(projectId, hypothesisId)
          }
          setNarratives={setConnectedContent}
          projectId={projectId}
        />
        <NarrativeSection
          title="Insights"
          narratives={connectedContent.insights}
          getViewPath={insightId => viewInsightPath(projectId, insightId)}
          setNarratives={setConnectedContent}
          projectId={projectId}
        />
        <NarrativeSection
          title="Analyses"
          narratives={connectedContent.analyses}
          getViewPath={analysisLinkBuilder(
            connectedContent.analyses,
            projectId
          )}
          setNarratives={setConnectedContent}
          projectId={projectId}
        />
      </div>
    </div>
  ) : null;
};

export default AnalysisDetailConnectedContent;
