import { Button } from '@pwc/pwc-deals-ui';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import React, { useState, useEffect } from 'react';

import SelectablePill from './SelectablePill';
import { keyParser } from '../../../../../common/engine/utilities/linkedAdjustmentsUtilities';
import DocumentAndAdjustmentPill from './DocumentAndAdjustmentPill';
import './LinkedAdjustments.scss';

const LinkedAdjustment = ({
  documentCode,
  value,
  name,
  editMode,
  adjustmentOptions,
  targetDocumentOptions,
  onToggleEditMode,
  onChange,
  onDelete
}) => {
  const targetItems = keyParser(value.targetAdjustment);

  const [targetDocument, setTargetDocument] = useState(targetItems.code || '');

  const [sourceAdjustmentName, setSourceAdjustmentName] = useState(
    value.sourceAdjustment || ''
  );
  const [targetAdjustmentName, setTargetAdjustmentName] = useState(
    value.targetAdjustment || ''
  );
  const [targetAdjustmentOptions, setTargetAdjustmentOptions] = useState([]);

  useEffect(() => {
    setTargetAdjustmentOptions(adjustmentOptions[targetDocument] || []);
  }, [adjustmentOptions, targetDocument]);

  const isAddButtonDisabled = () => {
    const isValueEmpty = v => v === undefined || v === '';
    return (
      isValueEmpty(sourceAdjustmentName) ||
      isValueEmpty(targetAdjustmentName) ||
      isValueEmpty(targetDocument)
    );
  };

  const onAddHandler = () => {
    onToggleEditMode();
    onChange({
      sourceAdjustment: sourceAdjustmentName,
      targetAdjustment: targetAdjustmentName
    });
  };

  return (
    <div
      data-testid="linked-adjustment"
      className={classnames(
        'linked-adjustment-row',
        editMode ? 'editable' : 'viewing'
      )}
    >
      <SelectablePill
        editMode={editMode}
        name={`source-adjustment-${name}`}
        onChange={setSourceAdjustmentName}
        onDelete={onDelete}
        options={adjustmentOptions[documentCode]}
        value={sourceAdjustmentName}
        placeholder="Select Adjustment"
      />

      <DocumentAndAdjustmentPill
        editMode={editMode}
        name={name}
        onDocumentChange={setTargetDocument}
        onAdjustmentChange={setTargetAdjustmentName}
        onDelete={onDelete} // Note: When we get to "grouping" the right side, this will have to have more smarts than "delete the row"
        documentOptions={targetDocumentOptions}
        adjustmentOptions={targetAdjustmentOptions}
        documentValue={targetDocument}
        adjustmentValue={targetAdjustmentName}
      />

      {editMode && (
        <div className="linked-adjustment-row-actions">
          <Button
            type="button"
            className={`button-outline ${isAddButtonDisabled()}`}
            data-testid={`button-add-${name}`}
            onClick={onAddHandler}
            disabled={isAddButtonDisabled()}
          >
            Add
          </Button>
        </div>
      )}
    </div>
  );
};

LinkedAdjustment.propTypes = {
  documentCode: PropTypes.string.isRequired,
  adjustmentOptions: PropTypes.shape({
    QofE: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
    ),
    NWC: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
    ),
    DDL: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
    )
  }),
  targetDocumentOptions: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
  ),
  editMode: PropTypes.bool.isRequired,
  onToggleEditMode: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  value: PropTypes.shape({
    sourceAdjustment: PropTypes.string,
    targetDocument: PropTypes.string
  }),
  name: PropTypes.string.isRequired
};

LinkedAdjustment.defaultProps = {
  adjustmentOptions: { QofE: [], NWC: [], DDL: [] },
  targetDocumentOptions: [],
  value: {
    sourceAdjustment: '',
    targetDocument: ''
  }
};

export default LinkedAdjustment;
