import React, { useEffect, useState, useMemo } from 'react';
import { Formik } from 'formik';
import { useParams, useHistory } from 'react-router-dom';
import { ErrorUtilities } from '@pwc/pwc-deals-web-common';
import InsightForm from '../common/InsightForm';
import { insightSchema } from '../common/insightModel';
import { putInsight, getInsight } from '../insightsService';
import {
  insightsListPath,
  viewInsightPath
} from '../../../common/services/urlService';
import { getNarratives } from '../../narratives/narrativesService';
import useLinkedNarratives from '../../narratives/EditConnectedContent/useLinkedNarratives';
import { getLinksFromNarratives } from '../../narratives/narrativesUtilities';

const EditInsight = () => {
  const { projectId, insightId: insightContentThreadId } = useParams();
  const history = useHistory();

  const [insight, setInsight] = useState(null);
  const [narratives, setNarratives] = useState(null);
  const [errorType, setErrorType] = useState();
  const [isSaving, setIsSaving] = useState(false);

  const gotoInsightsList = useMemo(
    () => () => history.replace(insightsListPath(projectId)),
    [history, projectId]
  );

  const gotoInsightView = () =>
    history.push(viewInsightPath(projectId, insightContentThreadId));

  useEffect(() => {
    getInsight(projectId, insightContentThreadId).then(value =>
      setInsight({
        ...value,
        body: value.body || ''
      })
    );
    getNarratives(projectId).then(allContent => {
      setNarratives({
        ...allContent,
        insights: allContent.insights.filter(
          i => i.contentThreadId.toString() !== insightContentThreadId
        )
      });
    });
  }, [projectId, insightContentThreadId, gotoInsightsList]);

  const handlePublish = async values => {
    setIsSaving(true);
    try {
      const links = getLinksFromNarratives(narratives, insight.contentId);

      await putInsight(projectId, {
        ...insight,
        title: values.title.trim(),
        body: values.body.trim(),
        tags: values.tags.map(tag => ({
          text: tag
        })),
        links
      });
      setErrorType();
      gotoInsightView();
    } catch (e) {
      setIsSaving(false);
      setErrorType(ErrorUtilities.getContentErrorType(e));
    }
  };

  const linkedNarrativesChanged = useLinkedNarratives(
    narratives,
    insight ? insight.contentId : null
  );

  return insight ? (
    <Formik
      initialValues={{
        ...insight,
        tags: insight.tags ? insight.tags.map(tag => tag.text) : []
      }}
      onSubmit={handlePublish}
      validationSchema={insightSchema}
      validateOnChange
      enableReinitialize
    >
      {({ errors, touched, isValid, dirty }) => (
        <InsightForm
          className="mt-5"
          onCancel={gotoInsightView}
          isSaving={isSaving}
          isValid={isValid}
          errorType={errorType}
          errors={errors}
          touched={touched}
          projectId={projectId}
          parentContentId={insight.contentId}
          narratives={narratives}
          setNarratives={setNarratives}
          dirty={dirty || linkedNarrativesChanged}
          onSaveText="Updating..."
          standbyText="Update"
        >
          <h1>Edit Insight</h1>
        </InsightForm>
      )}
    </Formik>
  ) : null;
};

export default EditInsight;
