import React from 'react';
import { Select } from '@pwc/pwc-deals-ui';
import PropTypes from 'prop-types';
import ClearablePill from '../../../../../common/components/Pills/ClearableDisplayPill';
import { keyParser } from '../../../../../common/engine/utilities/linkedAdjustmentsUtilities';

const getLabelFromOptions = (options, selectedValue) => {
  return (
    options.find(x => x.value === selectedValue) || {
      label: null
    }
  ).label;
};

const getTaregtPillText = (documentValue, adjustmentNumber, adjustmentName) => {
  return `${documentValue} / ${adjustmentNumber} ${adjustmentName}`;
};

const DocumentAndAdjustmentPill = ({
  editMode,
  name,
  onDocumentChange,
  onAdjustmentChange,
  onDelete,
  documentOptions,
  adjustmentOptions,
  documentValue,
  adjustmentValue
}) => {
  const selectedDocumentLabel = getLabelFromOptions(
    documentOptions,
    documentValue
  );
  const selectedAdjustmentLabel = getLabelFromOptions(
    adjustmentOptions,
    adjustmentValue
  );
  const adjustmentKey = keyParser(adjustmentValue);
  const pillText = getTaregtPillText(
    selectedDocumentLabel,
    adjustmentKey.number,
    adjustmentKey.name
  );
  const isAdjustmentBroken = !selectedDocumentLabel || !selectedAdjustmentLabel;

  return (
    <div
      data-testid="document-and-adjustment-selection"
      className="document-row"
    >
      <div className="link-to">Link to</div>
      {editMode ? (
        <>
          <Select
            selectTestId={`select-input-target-document-${name}`}
            value={documentValue}
            options={documentOptions}
            onChange={event => onDocumentChange(event.target.value)}
            placeholder="Select analyses"
            maxWidth="240px"
          />
          <Select
            selectTestId={`select-input-target-adjustment-${name}`}
            value={adjustmentValue}
            options={adjustmentOptions}
            onChange={event => onAdjustmentChange(event.target.value)}
            placeholder="Select Adjustment"
            maxWidth="240px"
          />
        </>
      ) : (
        <div
          data-testid="document-and-adjustment-pill"
          className="pill-container"
        >
          <ClearablePill
            onClearClick={onDelete}
            tag={pillText}
            index={`target-document-and-adjustment-pill-${name}`}
            iconName={isAdjustmentBroken ? 'link-remove' : null}
          />
        </div>
      )}
    </div>
  );
};

DocumentAndAdjustmentPill.propTypes = {
  editMode: PropTypes.bool.isRequired,
  name: PropTypes.string.isRequired,
  onDocumentChange: PropTypes.func.isRequired,
  onAdjustmentChange: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  documentOptions: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
  ),
  adjustmentOptions: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
  ),
  documentValue: PropTypes.string,
  adjustmentValue: PropTypes.string
};

DocumentAndAdjustmentPill.defaultProps = {
  documentOptions: [],
  documentValue: '',
  adjustmentOptions: [],
  adjustmentValue: ''
};

export default DocumentAndAdjustmentPill;
