import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { getReportFormatLayoutState } from '../../../../../../../common/redux/reportFormat/reportFormatSlice';
import LayoutTab from './LayoutTab/LayoutTab';
import ReportTabs from './RightPanelTabs/RightPanelTabs';
import DataLibraryTab, {
  DataLibraryLabel
} from './DataLibraryTab/DataLibraryTab';
import './RightPanel.scss';

export const testId = 'right-panel';

const RightPanel = ({
  panelRef,
  handleLayoutTypeChange,
  droppableLocations,
  panelExpanded
}) => {
  const rightPanelTabs = useMemo(
    () => [
      {
        label: <DataLibraryLabel />,
        content: <DataLibraryTab droppableLocations={droppableLocations} />
      },
      {
        label: 'Layout',
        content: <LayoutTab handleLayoutTypeChange={handleLayoutTypeChange} />
      }
    ],
    [handleLayoutTypeChange, droppableLocations]
  );
  const {
    floatingTabsHeight,
    floatingTabsWidth,
    floatingTabsTop
  } = useSelector(getReportFormatLayoutState);

  const componentStyle = useMemo(
    () => ({
      transition: 'all 0.3s ease 0s',
      translateY: 0,
      position: 'absolute',
      top: floatingTabsTop,
      width: floatingTabsWidth
    }),
    [floatingTabsTop, floatingTabsWidth]
  );

  /*
   * Static margin, introduced in PWC-5339
   */
  const margin = 54;
  const sectionHeight = floatingTabsHeight - margin;

  return (
    <div className={testId} id={testId} ref={panelRef} data-testid={testId}>
      <div className="right-panel__children-container" style={componentStyle}>
        <ReportTabs
          tabs={rightPanelTabs}
          sectionHeight={sectionHeight}
          panelExpanded={panelExpanded}
        />
      </div>
    </div>
  );
};

export default RightPanel;
