/* eslint-disable react/forbid-prop-types */
import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import PropTypes from 'prop-types';
import { isLastNPeriodLabel } from '../../../../../common/services/textService';
import AdjustmentControlsColumn from '../AdjustmentControlsColumn';
import FinancialSpacer from '../FinancialSpacer';
import FinancialValue from '../FinancialValue';
import AdjustmentGroupExpansionButton from './AdjustmentGroupExpansionButton';
import { convertTextToId } from './AdjustmentGroupHelpers';
import LinkedAdjustmentIconHeader from './LinkedAdjustmentIconHeader';

const AdjustmentGroupHeader = ({
  documentCode,
  group,
  title,
  expanded,
  onClick,
  totalOverrideValue,
  timePeriods,
  hasLinks,
  hasBrokenLinks,
  isPublishMode
}) => {
  const testId = convertTextToId(title);
  const { totals } = group;
  const isNwcTable = documentCode === Constants.NET_WORKING_CAPITAL;

  return (
    <tr
      className={`adjustment-group group-header${expanded ? ' expanded' : ''}`}
      role="row"
      data-testid={`adjustment-group-header-${testId}`}
    >
      <th role="rowheader" scope="row" className="row-description">
        {isPublishMode && <span>{title}</span>}

        {!isPublishMode && (
          <AdjustmentGroupExpansionButton
            onClick={onClick}
            title={title}
            expanded={expanded}
          />
        )}
      </th>
      {timePeriods.map(timePeriod => {
        const isLastNPeriod = isLastNPeriodLabel(timePeriod);
        const showColumnHighlight = isNwcTable && isLastNPeriod;

        if (expanded) {
          return (
            <FinancialSpacer
              key={timePeriod}
              showColumnHighlight={showColumnHighlight}
            />
          );
        }
        return (
          <FinancialValue
            key={timePeriod}
            text={totalOverrideValue || totals[timePeriod]}
            showColumnHighlight={showColumnHighlight}
          />
        );
      })}

      {!isPublishMode && (
        <AdjustmentControlsColumn>
          <div>&nbsp;</div>
          <LinkedAdjustmentIconHeader
            expanded={expanded}
            hasLinks={hasLinks}
            hasBrokenLinks={hasBrokenLinks}
          />
        </AdjustmentControlsColumn>
      )}
    </tr>
  );
};

AdjustmentGroupHeader.propTypes = {
  documentCode: PropTypes.string.isRequired,
  group: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
  expanded: PropTypes.bool.isRequired,
  onClick: PropTypes.func,
  totalOverrideValue: PropTypes.string,
  timePeriods: PropTypes.arrayOf(PropTypes.string).isRequired,
  hasLinks: PropTypes.bool.isRequired,
  hasBrokenLinks: PropTypes.bool.isRequired,
  isPublishMode: PropTypes.bool
};

AdjustmentGroupHeader.defaultProps = {
  onClick: () => {},
  totalOverrideValue: undefined,
  isPublishMode: false
};

export default AdjustmentGroupHeader;
