import { Icon } from '@pwc/pwc-deals-ui';
import React from 'react';
import {
  getProjectStatus,
  useProjectsStatuses
} from '../../common/hooks/useProjectsStatuses';
import ProjectLoader from '../projects/ProjectLoader';
import ProjectStatePill from '../projectStatePill/ProjectStatePill';
import ProjectStateChangeCards from '../projectStateChangeCards/ProjectStateChangeCards';
import {
  isProjectInPendingState,
  isProjectActive
} from '../../common/helpers/projectStatusesUtils';

const description =
  'You can request to change the status of the project here. Any changes ' +
  ' of the project will apply for all users.';
const projectStatusHeader = 'Current status';
const projectStatusDisabledDescription =
  'Editing functionality is disabled and all users (PwC and client) has read-only access.';
const projectStatusEnabledDescription =
  'Editing functionality is enabled and all users (PwC and client) have permissions based on their role in Junction.';
const resetLink = 'Reset Archive countdown';

const ResetLink = ({ onResetCountdown, isSpinnerVisible, isLinkVisible }) => {
  const refreshIconSize = 24;
  return (
    <div className="reset-link-container">
      {isSpinnerVisible && <ProjectLoader />}
      {isLinkVisible && (
        <>
          <Icon
            name="refresh"
            className="fill icon-orange"
            width={refreshIconSize}
            height={refreshIconSize}
          />
          <button
            type="button"
            data-testid="reset-link"
            className="reset-link"
            onClick={onResetCountdown}
          >
            {resetLink}
          </button>
        </>
      )}
    </div>
  );
};

const ProjectStatus = ({ projectId }) => {
  const projectsStates = useProjectsStatuses();
  const projectState = getProjectStatus(projectsStates, projectId);

  return (
    <>
      <div className="settings-description">{description}</div>
      <div className="separator" />
      <div className="project-settings-form-container">
        <div className="project-current-status-container">
          <span data-testid="status-header">{projectStatusHeader}</span>
          <div className="status-container">
            <ProjectStatePill projectId={projectId} showPendingStatus />
            <ResetLink
              onResetCountdown={() => {}}
              isSpinnerVisible={isProjectInPendingState(projectState)}
              isLinkVisible={false}
            />
          </div>
          <span className="description" data-testid="project-status">
            {isProjectActive(projectState)
              ? projectStatusEnabledDescription
              : projectStatusDisabledDescription}
          </span>
          <ProjectStateChangeCards projectId={projectId} />
        </div>
      </div>
    </>
  );
};

export default ProjectStatus;
