import React from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Icon, Loader } from '@pwc/pwc-deals-ui';
import IngestStructuredAnalysisButton from '../IngestStructuredAnalysisButton/IngestStructuredDocumentButton';
import {
  IngestionThunk,
  clearIngestError,
  getLoadingInProcess,
  getStructuredAnalysisHasError,
  getStructuredAnalysisError,
  getIngestedDocuments
} from '../../../../common/engine/reducers/StructuredAnalysisSlice';
import ModalConfirmation from '../../../../common/components/ModalConfirmation';
import { structuredRoutes } from '../../../../common/services/urlService';
import './structured-analysis-card.scss';

const StructuredAnalysisCard = ({
  projectId,
  documentName,
  documentCode,
  showDetailLink
}) => {
  const loadingDocuments = useSelector(getLoadingInProcess);
  const hasError = useSelector(getStructuredAnalysisHasError);
  const errorMessage = useSelector(getStructuredAnalysisError);
  const ingestedDocuments = useSelector(getIngestedDocuments);
  const dispatch = useDispatch();

  const beginIngest = async () => {
    dispatch(IngestionThunk(projectId, documentCode));
  };

  const handleModalClose = () => dispatch(clearIngestError());

  return (
    <div
      className="analysis-card"
      data-testid={`analysis-card-${documentCode}`}
    >
      <div
        className="analysis-card-header"
        data-testid={`card-header-${documentCode}`}
      >
        {documentName}
      </div>
      <div className="template-placeholder" />
      <div className="button-spinner-group">
        {!ingestedDocuments.includes(documentCode) && (
          <IngestStructuredAnalysisButton
            projectId={projectId}
            documentCode={documentCode}
            onIngestion={beginIngest}
          />
        )}
        {loadingDocuments.includes(documentCode) && (
          <Loader className="small" dataTestId="loading-spinner" />
        )}
      </div>
      {showDetailLink && ingestedDocuments.includes(documentCode) && (
        <Link
          to={`/projects/${projectId}/analyses/${structuredRoutes[documentCode]}`}
        >
          <div className="status-container">
            <Icon
              name="check"
              data-testid={`${documentCode}-confirm-icon`}
              width={14}
              className="fill icon-green"
            />
            <div data-testid={`${documentCode}-link`}>View analysis</div>
            <Icon
              data-testid="structured-analysis-card-arrow"
              name="arrow-right"
              className="outline right"
              width={15}
            />
          </div>
        </Link>
      )}
      {!showDetailLink && ingestedDocuments.includes(documentCode) && (
        <div className="status-container">
          <Icon
            name="check"
            data-testid={`${documentCode}-confirm-icon`}
            width={14}
            className="fill icon-green"
          />
          <div data-testid={`${documentCode}-no-link`}>
            Ingestion Successful
          </div>
        </div>
      )}
      <ModalConfirmation
        show={hasError}
        onCancel={handleModalClose}
        onConfirm={handleModalClose}
        confirmTitle="Failed attempt on import"
        confirmationMessage={errorMessage}
        showCancel={false}
        confirmButtonText="OK"
      />
    </div>
  );
};

StructuredAnalysisCard.propTypes = {
  projectId: PropTypes.string,
  documentName: PropTypes.string,
  documentCode: PropTypes.string
};

StructuredAnalysisCard.defaultProps = {
  projectId: '',
  documentName: '',
  documentCode: ''
};

export default StructuredAnalysisCard;
