import React from 'react';
import PropTypes from 'prop-types';
import { DisplayableContent } from './ConnectedKfaPropTypes';
import { AnalysisTitleLink } from './AnalysisTitleLink';

export const AnalysesTableRow = ({ analyses, hypothesisIndex, projectId }) => {
  return (
    analyses.length > 0 && (
      <tr
        className="kfa-directly-connected-analyses-row"
        data-testid={`analyses-row-${hypothesisIndex}`}
      >
        <td className="kfa-connected-analysis-empty-cell" />
        <td
          className="kfa-connected-analysis-title-cell"
          data-testid={`kfa-connected-analysis-title-cell-${hypothesisIndex}`}
        >
          {analyses.map((analysis, index) => (
            <AnalysisTitleLink
              key={analysis.contentId}
              analysis={analysis}
              projectId={projectId}
              testId={`kfa-connected-analysis-title-${hypothesisIndex}-${index}`}
            />
          ))}
        </td>
      </tr>
    )
  );
};

AnalysesTableRow.propTypes = {
  analyses: PropTypes.arrayOf(PropTypes.shape(DisplayableContent)).isRequired,
  hypothesisIndex: PropTypes.number.isRequired,
  projectId: PropTypes.string.isRequired
};
