import updateEntityVersion from '../../actions/updateEntityVersion';
import {
  getAllDocumentsWithContentForProjectAndViewingType,
  getStructuredAnalysisVersions
} from '../services/structuredAnalysisService';
import { dispatchLibraryItemLinksSet } from './dispatchLibraryItemLinksSet';
import { transformIngestedDataByDocumentCode } from './transformIngestedDataByDocumentCode';
import { dispatchLinkedAdjustmentsForDocument } from './dispatchLinkedAdjustmentsForDocument';
import {
  basisOfPresentationSet,
  descriptionResetToBasisOfPresentation
} from '../reducers/structuredDocumentDescriptionSlice';
import {
  processingFailed,
  processingSucceeded,
  startProcessing
} from '../reducers/structuredAnalysisViewSlice';

const fetchVersionsForDocuments = async (projectId, documents) => {
  const results = await Promise.all(
    documents.map(doc => {
      return getStructuredAnalysisVersions(
        projectId,
        doc.content.contentThreadId
      );
    })
  );
  return results;
};

export const addVersionsToDocumentsWithContent = (
  documentsWithContent,
  versionsForDocuments
) => {
  const documentsWithContentAndVersions = documentsWithContent.map(x => {
    const documentVersions = versionsForDocuments.find(
      y => y.analysisId === x.content.contentThreadId
    );
    return {
      ...x,
      versions: documentVersions ? documentVersions.data : []
    };
  });

  return documentsWithContentAndVersions;
};

export const removeRawCellDataFromDocuments = documents => {
  return documents.map(x => {
    const cloone = { ...x };
    delete cloone.rawCellData; // For redux-dev-tools
    return cloone;
  });
};

const processSingleDocument = async (projectId, document, dispatch) => {
  await dispatchLibraryItemLinksSet(
    true,
    projectId,
    document.code,
    document.content.contentThreadId,
    document.content.versionNumber,
    dispatch
  );
  await dispatchLinkedAdjustmentsForDocument(
    projectId,
    document.code,
    dispatch
  );
  // calc-engine
  await transformIngestedDataByDocumentCode(
    document.code,
    JSON.parse(document.rawCellData),
    document.configurationValues,
    dispatch
  );
};

export const transformAllIngestedDataThunk = (
  projectId,
  documentCodeForReview,
  isViewingAsClient = true
) => async dispatch => {
  dispatch(startProcessing());

  let documentsWithContent = [];
  try {
    documentsWithContent = await getAllDocumentsWithContentForProjectAndViewingType(
      projectId,
      isViewingAsClient
    );
  } catch (error) {
    dispatch(processingFailed());
    return;
  }

  const contentAnalysis = documentsWithContent.find(
    x => x.code === documentCodeForReview
  );

  if (!contentAnalysis) {
    dispatch(processingFailed());
    return;
  }

  const versionsForDocuments = await fetchVersionsForDocuments(
    projectId,
    documentsWithContent
  );

  const documentsWithContentAndVersions = addVersionsToDocumentsWithContent(
    documentsWithContent,
    versionsForDocuments
  );

  await Promise.all(
    documentsWithContentAndVersions.map(async document =>
      processSingleDocument(projectId, document, dispatch)
    )
  );

  const trimmedDocumentsWithContent = removeRawCellDataFromDocuments(
    documentsWithContentAndVersions
  );

  // // TODO: these things are prime refactors
  dispatch(basisOfPresentationSet(contentAnalysis.content.body));
  dispatch(descriptionResetToBasisOfPresentation());
  dispatch(updateEntityVersion(contentAnalysis.content.versionNumber));

  dispatch(
    processingSucceeded({
      documentsWithContent: trimmedDocumentsWithContent
    })
  );
};
