import React from 'react';
import classNames from 'classnames';
import { DateTime } from 'luxon';

import authorizationService from '../../../common/services/security/authorizationService';
import useTabletOrMobile from '../../../common/hooks/useTabletOrMobile';
import FileActions from '../Actions/FileActions/FileActions';
import FileStatus from '../Status/FileStatus';

import './FileListItem.scss';

const FileListItem = ({ file }) => {
  const isClientUser = authorizationService.isClientUser();
  const isTabletOrMobile = useTabletOrMobile();

  return (
    <div
      className={classNames('file-list-item', {
        'file-list-item--three-columns': isClientUser
      })}
    >
      <div className="file-list-item__file-name">{file.fileName}</div>
      {isTabletOrMobile && !isClientUser && <FileStatus file={file} />}
      {isTabletOrMobile && <FileActions file={file} />}
      <div className="file-list-item__date">
        {DateTime.fromSeconds(file.uploadedDateTime).toFormat(
          'HH:mm ZZZZ - dd LLL yyyy'
        )}
      </div>
      {!isClientUser && (
        <>
          <div className="file-list-item__uploaded-by">
            {isTabletOrMobile && 'by '}
            {file.uploadedBy}
          </div>
          {!isTabletOrMobile && <FileStatus file={file} />}
        </>
      )}
      {isTabletOrMobile ? (
        <div className="clearfix" />
      ) : (
        <FileActions file={file} />
      )}
    </div>
  );
};

export default FileListItem;
