import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import userTokenService from '../../common/services/security/userTokenService';
import './ShowForRole.scss';

const ShowForRole = ({
  projectId,
  roles,
  children,
  restrictedForRoleElement = <div data-testid="restricted-for-role" />
}) => {
  const [hasProjectRole, setHasProjectRole] = useState(false);
  const [hasProjectRoleBeenFetched, setHasProjectRoleBeenFetched] = useState(
    false
  );
  useEffect(() => {
    let isMounted = true;
    const projectRole = userTokenService.getProjectTokenProperty(
      projectId,
      'role'
    );
    const hasRole = roles.includes(projectRole);

    if (isMounted) {
      setHasProjectRoleBeenFetched(true);
      setHasProjectRole(hasRole);
    }

    return () => {
      isMounted = false;
    };
  }, [projectId, roles]);

  if (hasProjectRoleBeenFetched) {
    if (hasProjectRole) {
      return children;
    }
    return (
      <div className="restrictedForRoleElement">{restrictedForRoleElement}</div>
    );
  }
  return null;
};

ShowForRole.propTypes = {
  projectId: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired,
  roles: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default ShowForRole;
