import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { PageLastUpdated } from '../PageLastUpdated/PageLastUpdated';
import ShowForRole from '../../../features/security/ShowForRole';
import PillContainer from '../Pills/PillContainer';

const StatusContainer = ({ projectId, content, allowDraftPill, className }) => {
  return (
    <>
      <ShowForRole
        projectId={projectId}
        roles={Constants.USER_ROLE_GROUPS.INTERNAL}
      >
        <PageLastUpdated
          dateModified={content.dateModified}
          modifiedBy={content.modifiedBy}
          version={content.versionNumber}
        />
      </ShowForRole>
      <PillContainer
        projectId={projectId}
        content={content}
        allowDraftPill={allowDraftPill}
        className={className}
      />
    </>
  );
};

export default StatusContainer;
