import PropTypes from 'prop-types';
import React from 'react';
import { useParams } from 'react-router-dom';
import { Button, Icon } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import ApprovalPill from '../../../../../../common/components/Pills/ApprovalPill';
import MoreMenu from '../../../../../../common/components/ContentActions/MoreMenu';
import { DraftPillClientView } from '../../../../../../common/components/Pills/DraftPill';
import VersionStatus from '../../../../../../common/components/Pills/VersionStatus';
import ShowForPermissions from '../../../../../security/ShowForPermissions';
import ShowForRole from '../../../../../security/ShowForRole';
import EnableForProjectState from '../../../../../security/EnableForProjectState';
import './TopicHeader.scss';
import { PageLastUpdated } from '../../../../../../common/components/PageLastUpdated/PageLastUpdated';

const TopicHeader = ({
  onDelete,
  onEdit,
  topic,
  workflowActions,
  viewClient,
  onMove,
  moveOption
}) => {
  const { projectId } = useParams();

  const movePrev = () => {
    onMove(-1);
  };

  const moveNext = () => {
    onMove(1);
  };

  const disableMovePrev = () => {
    return (
      moveOption !== Constants.MOVE_OPTIONS.PREV &&
      moveOption !== Constants.MOVE_OPTIONS.BOTH
    );
  };

  const disableMoveNext = () => {
    return (
      moveOption !== Constants.MOVE_OPTIONS.NEXT &&
      moveOption !== Constants.MOVE_OPTIONS.BOTH
    );
  };

  return (
    <div className="topic-top-bar-container">
      <h3 className="topic-title" data-testid="topic-title">
        {topic.title}
      </h3>
      <div className="pills-section">
        <ShowForRole
          projectId={projectId}
          roles={Constants.USER_ROLE_GROUPS.INTERNAL}
        >
          <VersionStatus content={topic} allowDraftPill />
        </ShowForRole>
        <ShowForRole
          projectId={projectId}
          roles={Constants.USER_ROLE_GROUPS.CLIENT}
        >
          <DraftPillClientView />
        </ShowForRole>
        <ApprovalPill versionStatus={topic.versionStatus} />
      </div>
      <ShowForRole
        projectId={projectId}
        roles={Constants.USER_ROLE_GROUPS.INTERNAL}
      >
        <PageLastUpdated
          dateModified={topic.dateModified}
          modifiedBy={topic.modifiedBy}
          version={topic.versionNumber}
        />
      </ShowForRole>
      <ShowForPermissions
        projectId={projectId}
        permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
      >
        <EnableForProjectState projectId={projectId}>
          {({ isProjectDisabled }) => (
            <>
              <div className="move-buttons-section">
                <button
                  className="topic-move-button"
                  data-testid="topic-move-button-prev"
                  type="button"
                  onClick={movePrev}
                  disabled={isProjectDisabled || disableMovePrev()}
                >
                  <Icon
                    name="desc"
                    width={18}
                    className={`fill ${
                      isProjectDisabled || disableMovePrev()
                        ? 'icon-grey-alt'
                        : 'icon-orange'
                    }`}
                  />
                </button>
                <button
                  className="topic-move-button"
                  data-testid="topic-move-button-next"
                  type="button"
                  onClick={moveNext}
                  disabled={isProjectDisabled || disableMoveNext()}
                >
                  <Icon
                    name="asc"
                    width={18}
                    className={`fill ${
                      isProjectDisabled || disableMoveNext()
                        ? 'icon-grey-alt'
                        : 'icon-orange'
                    }`}
                  />
                </button>
              </div>
              <div className="topic-edit-button">
                <Button
                  data-testid="topic-edit-button"
                  type="button"
                  onClick={onEdit}
                  className="button-block button-small mr-3"
                  disabled={isProjectDisabled || viewClient.isOnClientVersion}
                >
                  Edit
                </Button>
              </div>
              <div className="more-menu">
                <MoreMenu
                  itemId={topic.contentThreadId}
                  onDelete={onDelete}
                  workflowActions={workflowActions}
                  viewClient={viewClient}
                  isDisabled={isProjectDisabled}
                />
              </div>
            </>
          )}
        </EnableForProjectState>
      </ShowForPermissions>
    </div>
  );
};

TopicHeader.propTypes = {
  onDelete: PropTypes.func.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  topic: PropTypes.object.isRequired
};

TopicHeader.defaultProps = {};

export default TopicHeader;
