import React, { useState } from 'react';
import classNames from 'classnames';

import CONSTANTS from '../../../Constants';

import './LayoutFilter.scss';

export const SECTION_AREA_TEXT = {
  ONE: '1',
  TWO: '2',
  THREE: '3',
  FOUR: '4+'
};

export const SECTION_AREAS = [
  {
    text: SECTION_AREA_TEXT.ONE,
    conlumnType: CONSTANTS.COLUMN_TYPE.ONE,
    isSelected: false
  },
  {
    text: SECTION_AREA_TEXT.TWO,
    conlumnType: CONSTANTS.COLUMN_TYPE.TWO,
    isSelected: false
  },
  {
    text: SECTION_AREA_TEXT.THREE,
    conlumnType: CONSTANTS.COLUMN_TYPE.THREE,
    isSelected: false
  },
  {
    text: SECTION_AREA_TEXT.FOUR,
    conlumnType: CONSTANTS.COLUMN_TYPE.FOUR,
    isSelected: false
  },
  {
    text: 'All',
    isSelected: true
  }
];

const LayoutFilter = ({ filterLayouts }) => {
  const [sectionAreaFilters, setSectionAreaFilter] = useState(SECTION_AREAS);

  const resetFilter = () =>
    setSectionAreaFilter(
      sectionAreaFilters.map(filter => ({ ...filter, isSelected: false }))
    );

  const setlectFilter = text => {
    setSectionAreaFilter(
      sectionAreaFilters.map(filter => ({
        ...filter,
        isSelected: text === filter.text
      }))
    );
  };

  const handleFilter = ({ text, conlumnType }) => {
    if (conlumnType) {
      setlectFilter(text);
      filterLayouts(conlumnType);
    } else {
      resetFilter();
      filterLayouts();
      setlectFilter(text);
    }
  };

  return (
    <div>
      <div className="filter-header_div">
        <span>Filter by number of areas</span>
      </div>

      <div id="rs-panel-layout-buttons" className="filter-container_div">
        {sectionAreaFilters.map(area => {
          return (
            <div
              data-testid="rs-panel-layout-filter"
              key={`${area.text}-${area.isSelected}`}
              role="button"
              className={classNames('filter-type', {
                'filter-type--selected-div': area.isSelected
              })}
              onClick={() => handleFilter(area)}
              onKeyDown={() => handleFilter(area)}
              tabIndex={0}
            >
              {area.text}
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default LayoutFilter;
