/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from 'react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';
import { Icon } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';

import UserSnippet from '../../common/components/UserSnippet';
import DealDetails from '../../common/components/DealDetails';
import Separator from '../../common/components/Separator';
import { getUserByRole } from '../project/common/ProjectModel';
import {
  getProjectStatus,
  useProjectsStatuses
} from '../../common/hooks/useProjectsStatuses';
import ShowForRole from '../security/ShowForRole';
import NewAndUpdated from '../project/common/NewAndUpdated';
import { getUserViewStatusCountsForProject } from '../../common/services/projectService';
import userTokenService from '../../common/services/security/userTokenService';
import {
  isProjectActive,
  isProjectReadOnly
} from '../../common/helpers/projectStatusesUtils';
import ProjectStatus from './ProjectStatus';
import Accordion from '../../common/components/Accordion/AccordionOnlyMobile';

const ProjectListRow = ({ project }) => {
  const [counts, setCounts] = useState({ newCount: 0, updatedCount: 0 });
  const [projectToken, setProjectToken] = useState(null);
  const projectsStates = useProjectsStatuses();
  const projectState = getProjectStatus(projectsStates, project.id);

  useEffect(() => {
    const token = userTokenService.getProjectToken(project.id);
    setProjectToken(token);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    (async () => {
      const projectStatusCompleted = 3;
      if (projectToken && project.status === projectStatusCompleted)
        setCounts(await getUserViewStatusCountsForProject(project.id));
    })();
  }, [projectToken, project.id, project.status]);

  const engagementLead = getUserByRole(project, 'Engagement Lead');
  const engagementManager = getUserByRole(project, 'Engagement Manager');
  const isProjectReadable =
    isProjectReadOnly(project.status) || isProjectActive(project.status);

  let projectStatusClass;

  switch (project.status) {
    case 1:
      projectStatusClass = ' new';
      break;
    case 2:
      projectStatusClass = ' in-progress';
      break;
    case 3:
      projectStatusClass = ' completed';
      break;
    case 4:
      projectStatusClass = ' migration-completed';
      break;
    case 5:
      projectStatusClass = ' migration-failed';
      break;
    case 7:
      projectStatusClass = ' sync-failed';
      break;
    case 10:
      projectStatusClass = ' failed';
      break;
    case 11:
      projectStatusClass = ' on-hold';
      break;
    default:
      projectStatusClass = '';
  }

  return (
    <div
      className={classNames('project-panel', {
        'project-panel__readonly': isProjectReadOnly(projectState)
      })}
    >
      <div className="project-panel-row">
        <div className="project-title-description-container">
          <span className={`project-title${projectStatusClass}`}>
            <Link
              to={isProjectReadable ? `/projects/${project.id}` : ''}
              data-testid={`project-${project.id}-link`}
            >
              {project.name}
            </Link>
            <div className="pills-container">
              <ProjectStatus project={project} />
            </div>
          </span>
          <Link
            to={project.status === 3 ? `/projects/${project.id}` : ''}
            className="project-panel-link-arrow"
            data-testid={`project-${project.id}-link-arrow`}
          >
            <Icon
              name="arrow-right"
              width={32}
              className="project-panel-arrow outline icon-orange"
            />
          </Link>
        </div>
      </div>
      <div className="project-description-container">
        <div className="project-description" data-testid="project-description">
          {project.description || ' '}
        </div>
        <div className="project-content">
          {projectToken && (
            <NewAndUpdated
              projectId={project.id}
              newContent={counts.newCount}
              updatedContent={counts.updatedCount}
              shouldRetrieveNotificationCount={isProjectReadable}
            />
          )}
        </div>
      </div>
      {projectToken && (
        <ShowForRole
          projectId={project.id}
          roles={Constants.USER_ROLE_GROUPS.INTERNAL}
        >
          <Separator />
          <Accordion title="Info">
            <div className="row">
              <div className="user-details-container col-md-6 col-sm-12">
                <div>
                  <UserSnippet
                    dataTestId={`project-${project.id}-engagement-lead`}
                    firstName={engagementLead.firstName}
                    lastName={engagementLead.lastName}
                    email={engagementLead.email}
                    position="Engagement Lead"
                  />
                </div>
                <div>
                  <UserSnippet
                    dataTestId={`project-${project.id}-engagement-manager`}
                    firstName={engagementManager.firstName}
                    lastName={engagementManager.lastName}
                    email={engagementManager.email}
                    position="Engagement Manager"
                  />
                </div>
              </div>
              <div className="deal-details-container col-md-6 col-sm-12">
                <div className="detail-section">
                  <DealDetails
                    dataTestId={`project-${project.id}-client-details`}
                    title="Client"
                    content={project.clientName}
                  />
                </div>
                <div className="detail-section">
                  <DealDetails
                    dataTestId={`project-${project.id}-target-details`}
                    title="Target"
                    content={project.target}
                  />
                </div>
                <div className="detail-section">
                  <DealDetails
                    dataTestId={`project-${project.id}-industry-status`}
                    title="Industry"
                    content={project.industry}
                  />
                </div>
              </div>
            </div>
          </Accordion>
        </ShowForRole>
      )}
    </div>
  );
};

export default ProjectListRow;
