import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { resetDivestitureDiagnostic } from '../features/divestitureDiagnostic/divestitureDiagnosticSlice';
import { resetProject } from '../features/project/projectSlice';
import PrivateRoute from '../features/security/PrivateRoute';
import ProjectsPage from '../features/projects/ProjectsPage';
import Access from '../features/security/Access';
import Landing from '../features/security/Landing';
import UnexpectedError from '../common/components/UnexpectedError';
import TermsAndConditions from '../features/termsAndConditions/TermsAndConditions';
import TermsAndConditionsView from '../features/termsAndConditions/TermsAndConditionsView/TermsAndConditionsView';
import { getUserTermsAndConditionsHasAccepted } from '../common/redux/userTermsAndConditions/userTermsAndConditionsSlice';

const NonProjectContentRouter = () => {
  const dispatch = useDispatch();
  dispatch(resetProject());
  dispatch(resetDivestitureDiagnostic());

  const tAndCHasAccepted = useSelector(getUserTermsAndConditionsHasAccepted);

  return (
    <Switch>
      <Route exact path="/" component={Landing} />
      <Route exact path="/access" component={Access} />
      <Route
        exact
        path="/unauthorized"
        render={() => (
          <UnexpectedError
            statusCode="403"
            description="You are not authorized to view this content."
          />
        )}
      />
      <Route
        exact
        path="/notfound"
        render={() => (
          <UnexpectedError statusCode="404" description="Page Not Found." />
        )}
      />
      <PrivateRoute
        exact
        path="/policies/:policyId"
        component={TermsAndConditionsView}
        tAndCHasAccepted={tAndCHasAccepted}
      />
      <PrivateRoute
        exact
        path="/terms-and-conditions"
        component={TermsAndConditions}
        requireTAndCAcceptance={false}
      />
      <PrivateRoute
        exact
        path="/projects"
        component={ProjectsPage}
        tAndCHasAccepted={tAndCHasAccepted}
      />

      <Route
        component={() => (
          <Redirect
            to={{
              pathname: '/'
            }}
          />
        )}
      />
    </Switch>
  );
};

export default NonProjectContentRouter;
