import React from 'react';
import _orderBy from 'lodash/orderBy';
import AnalysisLibraryItem from './AnalysisLibraryItem';

const OneColumnLayout = ({ items, projectId, analysisId }) => {
  return _orderBy(items, [item => item.order]).map(item => (
    <AnalysisLibraryItem
      item={item}
      key={`${item.id}-${item.name}`}
      projectId={projectId}
      analysisId={analysisId}
    />
  ));
};

export default OneColumnLayout;
