import { Constants } from '@pwc/pwc-deals-web-common';
import { RadioGroup } from '@pwc/pwc-deals-ui';
import React, { useCallback, useState } from 'react';
import RichTextEditor from '../../../../../../common/components/RichTextEditor/RichTextEditor';
import TopicContentSelectSearch from '../TopicContentSelectSearch/TopicContentSelectSearch';

const SingleColumnLayout = ({
  column,
  contentType,
  setContentTypes,
  richText,
  setRichTexts,
  hypothesisId,
  setHypothesisArray,
  excelChartId,
  setExcelChartIdArray,
  labelComponent,
  lightModeVariant
}) => {
  const [focused, setFocused] = useState(false);

  const handleContentTypeChange = useCallback(
    event => {
      const { value } = event.target;
      setContentTypes(previousContentTypes => {
        const newContentTypes = [...previousContentTypes];
        newContentTypes[column] = value;
        return newContentTypes;
      });
    },
    [column, setContentTypes]
  );

  const handleRichTextChange = useCallback(
    text => {
      setRichTexts(previousRichTexts => {
        const newRichTexts = [...previousRichTexts];
        newRichTexts[column] = text;
        return newRichTexts;
      });
    },
    [column, setRichTexts]
  );

  const handleHypothesisChange = useCallback(
    id => {
      setHypothesisArray(previousHypotheses => {
        const newHypotheses = [...previousHypotheses];
        newHypotheses[column] = id;
        return newHypotheses;
      });
    },
    [column, setHypothesisArray]
  );

  const handleExcelChartChange = useCallback(
    id => {
      setExcelChartIdArray(previousExcelChartIds => {
        const newExcelChartIds = [...previousExcelChartIds];
        newExcelChartIds[column] = id;
        return newExcelChartIds;
      });
    },
    [column, setExcelChartIdArray]
  );

  const contentTypeField = {
    name: `content-type-column-${column}`,
    onChange: handleContentTypeChange,
    value: contentType
  };

  const contentTypeOptions = Object.keys(Constants.TOPIC_PANEL_TYPES)
    .filter(key => key !== 'UNDEFINED')
    .map(topicPanelType => ({
      label: Constants.TOPIC_PANEL_TYPES[topicPanelType].displayName,
      value: Constants.TOPIC_PANEL_TYPES[topicPanelType].asString
    }));

  return (
    <>
      <h6>{labelComponent}</h6>
      <RadioGroup
        field={contentTypeField}
        options={contentTypeOptions}
        direction="row"
        className="content-type-radio-group"
      />
      {contentType === Constants.TOPIC_PANEL_TYPES.RICH_TEXT.asString && (
        <div className={`${lightModeVariant ? ' light-mode-variant' : ''}`}>
          <RichTextEditor
            richTextData={richText}
            onChange={handleRichTextChange}
            onBlur={(_, editor) => {
              setFocused(false);
              editor.ui.view.toolbar.element.style.display = 'none';
            }}
            onFocus={(_, editor) => {
              setFocused(true);
              editor.ui.view.toolbar.element.style.display = 'block';
            }}
            label="Description"
            isFocused={focused}
          />
        </div>
      )}
      {contentType === Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString && (
        <TopicContentSelectSearch
          handleSelectChange={handleHypothesisChange}
          selectedContentId={hypothesisId}
          contentType={Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString}
          placeholderText="Select Key Focus Area"
        />
      )}
      {contentType === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString && (
        <TopicContentSelectSearch
          handleSelectChange={handleExcelChartChange}
          selectedContentId={excelChartId}
          contentType={Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString}
          placeholderText="Select chart"
        />
      )}
    </>
  );
};

export default SingleColumnLayout;
