import React, { useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { Icon } from '@pwc/pwc-deals-ui';
import './DropArea.scss';
import FileUploaded from './FileUploaded';

const MAX_FILE_SIZE_25MB = 25000000;

const DropArea = ({ onDropAccepted }) => {
  const [fileRejectedError, setFileRejectedError] = useState(null);
  const { getRootProps, getInputProps } = useDropzone({
    disabled: false,
    maxFiles: 1,
    maxSize: MAX_FILE_SIZE_25MB,
    multiple: false,
    onDropAccepted,
    onDropRejected: errorFiles => {
      if (errorFiles && errorFiles.length > 0) {
        const errorFile = errorFiles[0];
        const isFileMaxSizeError =
          errorFile.errors.length > 0 &&
          errorFile.errors[0].code === 'file-too-large';
        setFileRejectedError(
          isFileMaxSizeError
            ? `Failed to upload "${errorFile.file.name}" as file is over 25MB.`
            : `Failed to upload "${errorFile.file.name}"`
        );
      }
    }
  });

  if (fileRejectedError) {
    return (
      <FileUploaded
        fullErrorCopy={fileRejectedError}
        clearFile={() => setFileRejectedError(null)}
        loading={false}
      />
    );
  }

  return (
    <div
      {...getRootProps()}
      className="drop-area__droppable-container droppable-area"
      data-testid="drop-area"
    >
      <input {...getInputProps()} />
      <div className="drop-area__background">
        <div className="drop-area__background__content">
          <Icon name="placeholder-dnd" className="fill icon-grey-medium" />
          <span className="drop-area__background__hint background-text">
            Drag and drop it here, or{' '}
            <span className="ui-button button-link button-theme">
              choose the file
            </span>{' '}
            (max file size is 25MB)
          </span>
        </div>
      </div>
    </div>
  );
};

export default DropArea;
