import React from 'react';
import { useHistory } from 'react-router-dom';
import InfiniteScroll from 'react-infinite-scroll-component';
import { editAnalysisPath2 } from '../../../common/services/urlService';
import AnalysesGridCard from '../AnalysesGridCard/AnalysesGridCard';

const AnalysesGrid = ({
  data,
  onDelete,
  projectId,
  dataLength,
  currentPage,
  next,
  hasMore
}) => {
  const history = useHistory();

  const handleEditAnalysis = analysisId =>
    history.push(editAnalysisPath2(projectId, analysisId));

  const getNextItems = () => {
    const nextPage = currentPage + 1;
    next(nextPage);
  };

  return (
    <div className="analyses-grid-container">
      <div data-testid="list-grid">
        <InfiniteScroll
          dataLength={dataLength}
          hasMore={hasMore}
          next={getNextItems}
          scrollThreshold={1.0}
          className="grid-cards list-grid"
        >
          {data.map((analysis, i) => (
            <AnalysesGridCard
              analysis={analysis}
              projectId={projectId}
              onEditMenuClick={handleEditAnalysis}
              onDeleteMenuClick={() => onDelete(analysis)}
              index={i}
              key={`content-grid-card-${analysis.contentThreadId}`}
            />
          ))}
        </InfiniteScroll>
      </div>
    </div>
  );
};

export default AnalysesGrid;
