import { CommentServiceClient } from '../../common/services/httpClient';

export const getCommentsByEntity = async (
  projectId,
  entityType,
  entityId,
  entityVersion
) => {
  const response = await CommentServiceClient(projectId).get(
    `${projectId}/${entityType}/${entityId}/threads?entityVersion=${entityVersion}`
  );

  return response.data;
};

export const addCommentToEntity = async comment => {
  const response = await CommentServiceClient(comment.projectId).postDeprecated(
    `${comment.projectId}/${comment.entityType}/${comment.entityId}/threads`,
    comment
  );

  return response.data;
};

export const updateComment = async comment => {
  await CommentServiceClient(comment.projectId).put(
    `${comment.projectId}/${comment.entityType}/${comment.entityId}/threads`,
    comment
  );
};

export const deleteCommentThread = async thread => {
  await CommentServiceClient(thread.projectId).delete(
    `${thread.projectId}/${thread.entityType}/${
      thread.entityId
    }/threads/${thread.id || thread.threadId}`
  );
};

export const deleteComment = async comment => {
  await CommentServiceClient(comment.projectId).delete(
    `${comment.projectId}/${comment.entityType}/${comment.entityId}/threads/${comment.threadId}/comments/${comment.commentId}`
  );
};

export const shareCommentThread = async comment => {
  await CommentServiceClient(comment.projectId).patch(
    `${comment.projectId}/${comment.entityType}/${comment.entityId}/threads/${comment.threadId}/share`,
    comment
  );
};

export const resolveCommentThread = async comment => {
  await CommentServiceClient(comment.projectId).patch(
    `${comment.projectId}/${comment.entityType}/${comment.entityId}/threads/${comment.threadId}/resolve`,
    comment
  );
};
