import React, { useEffect, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import './Policy.scss';
import { PerformanceCookiesToggle } from './PolicyInjections/PerformanceCookiesToggle';
import { sanitize } from '../../../common/services/sanitizeService';

const policyInjections = [
  {
    id: 'performance-cookies-section-header',
    child: policy => <PerformanceCookiesToggle policy={policy} />
  }
];

const Policy = ({ policy }) => {
  const ref = useRef();
  const [injections, setInjections] = useState([]);
  useEffect(() => {
    const usableInjections = policyInjections
      .filter(() => policy && policy.body)
      .filter(() => ref && ref.current && ref.current.innerHTML)
      .filter(({ id }) => ref.current.innerHTML.includes(id));
    setInjections(usableInjections);
  }, [policy, ref]);
  return (
    <>
      <h2 className="mb-0">{policy.title}</h2>
      <div
        ref={ref}
        className="policy-container"
        dangerouslySetInnerHTML={{ __html: sanitize(policy.body) }}
      />
      {/* It is important that these come last */}
      {injections.map(({ id, child }) => {
        const parent = document.getElementById(id);
        return parent
          ? ReactDOM.createPortal(
              child(policy.body),
              parent,
              `${id}_${policy.title}`
            )
          : null;
      })}
    </>
  );
};

export default Policy;
