import React from 'react';
import { Link } from 'react-router-dom';
import { sanitize } from '../../../../../../common/services/sanitizeService';
import { viewHypothesisPath } from '../../../../../../common/services/urlService';
import './KeyFocusAreaTopicPanel.scss';

const KeyFocusAreaTopicPanel = ({ projectId, hypothesis }) => {
  return (
    <div
      data-testid="key-focus-area-topic-panel"
      className="key-focus-area-topic-panel topic-panel__kfa"
    >
      <Link
        to={viewHypothesisPath(projectId, hypothesis.contentThreadId)}
        data-testid="hypothesis-link"
        aria-label="View Key Focus Area"
        tabIndex={0}
      >
        <h3
          className="key-focus-area-topic-panel__title"
          data-testid="kfa-topic-panel-title"
        >
          {hypothesis.title}
        </h3>
      </Link>
      {hypothesis.body && (
        <div className="key-focus-area-topic-panel-body">
          <p
            className="key-focus-area-topic-panel-body"
            dangerouslySetInnerHTML={{ __html: sanitize(hypothesis.body) }}
          />
        </div>
      )}
      {hypothesis.conclusion && (
        <div className="key-focus-area-topic-panel-conclusion">
          <h4 className="section-header">PwC View</h4>
          <p
            className="key-focus-area-topic-panel-conclusion"
            dangerouslySetInnerHTML={{
              __html: sanitize(hypothesis.conclusion)
            }}
          />
        </div>
      )}
    </div>
  );
};

export default KeyFocusAreaTopicPanel;
