import React from 'react';
import { Button } from '@pwc/pwc-deals-ui';
import { Link } from 'react-router-dom';
import { addAnalysisTypePath } from '../../common/services/urlService';
import EnableForProjectState from '../security/EnableForProjectState';

const CreateAnalysisLinkButton = ({ projectId }) => {
  const addAnalysisHref = addAnalysisTypePath(projectId);

  return (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <Link to={!isProjectDisabled && addAnalysisHref}>
          <Button disabled={isProjectDisabled} className="mt-3">
            Create Analysis
          </Button>
        </Link>
      )}
    </EnableForProjectState>
  );
};

export default CreateAnalysisLinkButton;
