import React, { useState, useEffect } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { MenuItem } from '@pwc/pwc-deals-ui';
import Button from '@pwc/pwc-deals-ui/build/common/Button';
import {
  useProjectsStatuses,
  getProjectStatusIcon
} from '../../hooks/useProjectsStatuses';
import {
  createProjectPagePath,
  projectOverviewPath
} from '../../services/urlService';
import { getProjectsList } from '../../../features/projects/projectsSlice';
import authorizationService from '../../services/security/authorizationService';
import './ProjectListSideNavButtons.scss';

const ProjectListSideNavButtons = ({ closeSideNav, mobile }) => {
  const location = useLocation();
  const [activeMenuItem, setActiveMenuItem] = useState(location.pathname);
  const projectsStates = useProjectsStatuses();
  const allProjects = useSelector(getProjectsList);
  const ICON_SIZE = 24;

  const history = useHistory();

  useEffect(() => {
    setActiveMenuItem(location.pathname);
  }, [location]);

  const isExactActive = linkPath => {
    return linkPath === activeMenuItem ? 'menu-active' : '';
  };

  const handleClick = navTarget => {
    setActiveMenuItem(navTarget);
    closeSideNav();
  };

  const viewAllProjects = () => {
    history.push('/projects');
    closeSideNav();
  };

  return (
    <>
      {mobile ? (
        <>
          <div className="all-projects-title" data-testid="all-projects-title">
            All&nbsp;
            <span key={process.env.REACT_APP_PLATFORM_NAME}>
              {process.env.REACT_APP_PLATFORM_NAME}
            </span>
            &nbsp;projects
          </div>
          <div className="menu-navigation">
            {allProjects &&
              allProjects.map(currentProject => {
                const projectStatusIcon = getProjectStatusIcon(
                  projectsStates,
                  currentProject.id
                );

                return (
                  <MenuItem
                    navTarget={projectOverviewPath(currentProject.id)}
                    icon="folder"
                    iconClasses="outline icon-grey"
                    extraIcons={projectStatusIcon ? [projectStatusIcon] : []}
                    extraIconsClasses={
                      projectStatusIcon ? ['fill icon-black state-icon'] : []
                    }
                    title={currentProject.name}
                    onClick={() =>
                      handleClick(projectOverviewPath(currentProject.id))
                    }
                    linkClass={isExactActive(
                      projectOverviewPath(currentProject.id)
                    )}
                    key={currentProject.id}
                  />
                );
              })}
            <div className="view-all-projects">
              <Button
                className="button-link button-theme mx-2 mr-4"
                onClick={viewAllProjects}
                type="button"
              >
                View all projects
              </Button>
            </div>
          </div>
        </>
      ) : (
        <div className="menu-navigation">
          {authorizationService.canCreateProjects() && (
            <MenuItem
              navTarget={createProjectPagePath}
              icon="add"
              iconClasses="fill icon-orange"
              iconWidth={ICON_SIZE}
              title="Add Project"
              onClick={() => handleClick(createProjectPagePath)}
              linkClass={isExactActive(createProjectPagePath)}
            />
          )}
        </div>
      )}
    </>
  );
};

export default ProjectListSideNavButtons;
