import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import {
  getFileUploadState,
  openModal,
  closeModal
} from '../../common/redux/fileUpload/fileUploadSlice';
import { getFilesThunk } from '../../common/redux/fileUpload/fileUploadThunk';

import FileHeaders from './List/FileHeaders';
import FileList from './List/FileList';
import FileUploadModal from './Modal/FileUploadModal';
import FileUploader from './Upload/FileUploader';

import './FilesContainer.scss';
import UploadSuccessBanner from './Upload/UploadSuccessBanner/UploadSuccessBanner';

const FilesContainer = () => {
  const { projectId } = useParams();
  const { showModal } = useSelector(getFileUploadState);
  const dispatch = useDispatch();
  const onModalHide = () => dispatch(closeModal());

  useEffect(() => {
    dispatch(getFilesThunk(projectId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="file-list-container">
      <FileUploadModal show={showModal} onHide={onModalHide} />
      <div className="list-container-header">
        <h2>Files</h2>
        <FileUploader
          projectId={projectId}
          openModal={() => dispatch(openModal())}
        />
      </div>
      <UploadSuccessBanner />
      <FileHeaders />
      <FileList />
    </div>
  );
};

export default FilesContainer;
