import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import _orderBy from 'lodash/orderBy';
import _differenceBy from 'lodash/differenceBy';
import _get from 'lodash/get';
import _union from 'lodash/union';
import { Constants } from '@pwc/pwc-deals-web-common';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import classNames from 'classnames';
import {
  contentTypeShape,
  contentShape,
  sortAscending,
  sortDescending,
  contentTypeHypotheses,
  contentTypeInsights,
  contentTypeAnalyses
} from '../../common/components/proptypes/commonProptypes';
import userTokenService from '../../common/services/security/userTokenService';
import {
  editAnalysisPath,
  editHypothesisPath,
  editInsightPath,
  viewAnalysisPath,
  viewHypothesisPath,
  viewInsightPath
} from '../../common/services/urlService';
import { structuredAnalysisLinkToDisplay } from '../../common/engine/utilities/structured-data-utilities';
import EmptyHypothesesCard from '../hypotheses/EmptyHypotheses/EmptyHypothesesCard';
import EmptyInsightsCard from '../insights/EmptyInsights/EmptyInsightsCard';
import EmptyAnalysesCard from '../analyses/EmptyAnalyses/EmptyAnalysesCard';
import ContentListHeader from './ContentListHeader';
import ContentListCard from './ContentListCard';
import ModalConfirmation from '../../common/components/ModalConfirmation';
import { sortKeys } from './contentListSortKeys';
import NoContentForTagsCard from '../../common/components/ContentActions/NoContentForTagsCard/NoContentForTagsCard';
import './ContentList.scss';
import { showModalError } from '../../common/redux/errors/errorsSlice';

const ContentList = ({
  contentType,
  projectId,
  contentItems,
  projectHasContent,
  onDelete,
  onReorder,
  updateSortOrderApiCall,
  isFiltered
}) => {
  const dispatch = useDispatch();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [deleteTarget, setDeleteTarget] = useState(null);
  const [currentSortKey, setCurrentSortKey] = useState(sortKeys.sortOrder);
  const [currentSortDir, setCurrentSortDir] = useState(sortAscending);
  const [isDragAllowed, setIsDragAllowed] = useState(false);
  const [isDragEnabled, setIsDragEnabled] = useState(false);
  const [dragTarget, setDragTarget] = useState(null);
  const [isInternalUser, setIsInternalUser] = useState(false);
  const [sortedContentItems, setSortedContentItems] = useState(
    _orderBy(contentItems, currentSortKey, currentSortDir)
  );
  const [selectedContentItems, setSelectedContentItems] = useState([]);

  useEffect(() => {
    const sorted = _orderBy(
      contentItems,
      [currentSortKey, sortKeys.dateModified],
      [currentSortDir, sortDescending]
    );
    setSortedContentItems(sorted);
    if (currentSortKey !== sortKeys.sortOrder) {
      setSelectedContentItems([]);
    }
  }, [contentItems, currentSortKey, currentSortDir]);

  useEffect(() => {
    if (isFiltered) {
      setSelectedContentItems([]);
    }
  }, [isFiltered]);

  useEffect(() => {
    const internalUserRoles = Constants.USER_ROLE_GROUPS.INTERNAL;

    const projectRole = userTokenService.getProjectTokenProperty(
      projectId,
      'role'
    );
    const permissionAllowsDrag = userTokenService.hasProjectPermission(
      projectId,
      [Constants.USER_PERMISSIONS.MANAGE_CONTENT]
    );
    const sortAllowsDrag = currentSortKey === sortKeys.sortOrder;
    const dragAllowed = permissionAllowsDrag && sortAllowsDrag && !isFiltered;

    setIsDragAllowed(dragAllowed);
    setIsDragEnabled(permissionAllowsDrag);
    setIsInternalUser(internalUserRoles.includes(projectRole));
  }, [currentSortKey, isFiltered, projectId]);

  const handleHideModal = () => {
    setIsModalVisible(false);
    setDeleteTarget(null);
  };

  const handleDeleteMenuClick = contentItem => {
    setIsModalVisible(true);
    setDeleteTarget(contentItem);
  };

  const handleDelete = () => {
    setIsModalVisible(false);
    onDelete(deleteTarget);
  };

  const handleSelectAll = isSelected => {
    if (isSelected) {
      setSelectedContentItems(sortedContentItems.map(_ => _.contentThreadId));
    } else {
      setSelectedContentItems([]);
    }
  };

  const handleSelect = (contentThreadId, isSelected) => {
    if (isSelected) {
      const updatedSelected = _union(selectedContentItems, [contentThreadId]);
      setSelectedContentItems(updatedSelected);
    } else {
      const updatedSelected = selectedContentItems.filter(
        id => id !== contentThreadId
      );
      setSelectedContentItems(updatedSelected);
    }
  };

  const isAscendingSort = () => currentSortDir === sortAscending;

  const handleBeforeCapture = ({ draggableId }) => {
    const dragTargetContentThreadId = parseInt(draggableId, 10);
    setDragTarget(dragTargetContentThreadId);
    if (
      selectedContentItems.length > 0 &&
      !selectedContentItems.includes(dragTargetContentThreadId)
    ) {
      setSelectedContentItems([]);
    }
  };

  const handleDragEnd = dragEnd => {
    setDragTarget(null);

    const hasValidDropTarget = !!dragEnd.destination;
    if (!hasValidDropTarget) {
      return;
    }

    const originalSortedHypotheses = [...sortedContentItems];

    const draggedItemSourceIndex = dragEnd.source.index;
    const draggedItemTargetIndex = dragEnd.destination.index;

    let draggedList;

    if (selectedContentItems.length > 0) {
      const itemsToMove = sortedContentItems.filter(_ =>
        selectedContentItems.includes(_.contentThreadId)
      );
      draggedList = _differenceBy(sortedContentItems, itemsToMove);

      let insertIndex;
      // Drag target is being moved down in the list
      if (draggedItemSourceIndex < draggedItemTargetIndex) {
        const targetContentThreadId =
          sortedContentItems[draggedItemTargetIndex].contentThreadId;
        const targetContentIndexInKeepList = draggedList.findIndex(
          h => h.contentThreadId === targetContentThreadId
        );
        insertIndex =
          draggedItemTargetIndex === 0 ? 0 : targetContentIndexInKeepList + 1;
      }
      // Drag target is being moved up in the list
      else {
        insertIndex = draggedItemTargetIndex;
      }

      draggedList.splice(insertIndex, 0, ...itemsToMove);
    } else {
      draggedList = [...sortedContentItems];
      const [movedItem] = draggedList.splice(draggedItemSourceIndex, 1);
      draggedList.splice(draggedItemTargetIndex, 0, movedItem);
    }

    const indexedList = draggedList.map((item, i) => {
      const order = isAscendingSort() ? i : draggedList.length - 1 - i;
      return {
        ...item,
        sortOrder: order
      };
    });

    setSortedContentItems(indexedList);

    const contentThreadIds = indexedList.map(_ => _.contentThreadId);
    const sortedContentThreadIds = isAscendingSort()
      ? contentThreadIds
      : contentThreadIds.reverse();
    updateSortOrderApiCall(projectId, sortedContentThreadIds)
      .then(() => {
        onReorder(indexedList);
      })
      .catch(e => {
        setSortedContentItems(originalSortedHypotheses);

        const errorCode = _get(e, 'response.data.errors[0].apiErrorCode', null);
        const errorType =
          errorCode === Constants.API_ERROR_CODES.VALIDATION_ERROR
            ? Constants.ModalErrorType.UNABLE_TO_SAVE_SORT_ORDER_STALE_DATA
            : Constants.ModalErrorType.UNABLE_TO_SAVE_SORT_ORDER;
        dispatch(showModalError(errorType));
      });

    setSelectedContentItems([]);
  };

  let emptyCard;
  let viewPathCreator;
  let editPathCreator;
  let linkAriaLabel;
  let deleteModalMessage;
  switch (contentType) {
    case contentTypeHypotheses:
      emptyCard = <EmptyHypothesesCard projectId={projectId} />;
      linkAriaLabel = 'View Key Focus Area';
      viewPathCreator = viewHypothesisPath;
      editPathCreator = editHypothesisPath;
      deleteModalMessage =
        'This Key Focus Area will be permanently deleted and any connections to other content will be removed.';
      break;

    case contentTypeInsights:
      emptyCard = <EmptyInsightsCard projectId={projectId} />;
      linkAriaLabel = 'View Insight';
      viewPathCreator = viewInsightPath;
      editPathCreator = editInsightPath;
      deleteModalMessage =
        'This Insight will be permanently deleted and any connection to other content will be removed.';
      break;

    case contentTypeAnalyses:
      emptyCard = <EmptyAnalysesCard projectId={projectId} />;
      linkAriaLabel = 'View Analysis';
      viewPathCreator = (_, contentThreadId, contentItem) => {
        if (contentItem.isUnStructured) {
          return viewAnalysisPath(projectId, contentThreadId);
        }
        return structuredAnalysisLinkToDisplay(projectId, contentItem.title);
      };
      editPathCreator = editAnalysisPath;
      deleteModalMessage =
        'This Analysis will be permanently deleted and any connections to other content will be removed.';
      break;

    default:
      emptyCard = null;
      linkAriaLabel = '';
      viewPathCreator = () => {};
      editPathCreator = () => {};
      deleteModalMessage = '';
  }

  if (sortedContentItems.length === 0 && projectHasContent) {
    return <NoContentForTagsCard />;
  }

  if (sortedContentItems.length === 0) {
    return emptyCard;
  }

  const areSomeSelected = selectedContentItems.length > 0;
  const areAllSelected =
    sortedContentItems.length === selectedContentItems.length;

  const listClassNames = classNames('content-list', {
    'drag-enabled': isDragEnabled,
    'in-drag-mode': dragTarget !== null
  });

  return (
    <>
      <ModalConfirmation
        show={isModalVisible}
        onCancel={handleHideModal}
        onConfirm={handleDelete}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage={deleteModalMessage}
        confirmButtonText="Yes, delete"
      />

      <div className={listClassNames} data-testid="content-list">
        <ContentListHeader
          projectId={projectId}
          currentSortKey={currentSortKey}
          setCurrentSortKey={setCurrentSortKey}
          setCurrentSortDir={setCurrentSortDir}
          isDragAllowed={isDragAllowed}
          isDragEnabled={isDragEnabled}
          areSomeSelected={areSomeSelected}
          areAllSelected={areAllSelected}
          onSelectAll={handleSelectAll}
        />

        <DragDropContext
          onDragEnd={handleDragEnd}
          onBeforeCapture={handleBeforeCapture}
        >
          <Droppable droppableId="content-list">
            {provided => {
              return (
                <div ref={provided.innerRef} {...provided.droppableProps}>
                  {sortedContentItems.map((item, index) => (
                    <ContentListCard
                      contentType={contentType}
                      contentItem={item}
                      index={index}
                      projectId={projectId}
                      onDeleteMenuClick={handleDeleteMenuClick}
                      onSelect={handleSelect}
                      selectedCount={selectedContentItems.length}
                      areAllSelected={areAllSelected}
                      isSelected={selectedContentItems.includes(
                        item.contentThreadId
                      )}
                      isDragTarget={dragTarget === item.contentThreadId}
                      isDragAllowed={isDragAllowed}
                      isDragEnabled={isDragEnabled}
                      isInternalUser={isInternalUser}
                      viewPathCreator={viewPathCreator}
                      editPathCreator={editPathCreator}
                      linkAriaLabel={linkAriaLabel}
                      key={item.contentThreadId}
                    />
                  ))}
                  {provided.placeholder}
                </div>
              );
            }}
          </Droppable>
        </DragDropContext>
      </div>
    </>
  );
};

ContentList.propTypes = {
  contentType: contentTypeShape.isRequired,
  projectId: PropTypes.string.isRequired,
  contentItems: PropTypes.arrayOf(contentShape),
  projectHasContent: PropTypes.bool.isRequired,
  onDelete: PropTypes.func,
  onReorder: PropTypes.func.isRequired,
  updateSortOrderApiCall: PropTypes.func.isRequired,
  isFiltered: PropTypes.bool
};

ContentList.defaultProps = {
  contentItems: [],
  onDelete: () => {},
  isFiltered: false
};

export default ContentList;
