import { push } from 'connected-react-router';
import { getReportPreview } from '../../../features/analyses/common/AnalysisReportPreview/AnalysisReportService';
import {
  setReportPreviewData,
  setReportPreviewError,
  setReportPreviewLoading
} from './reportFormatSlice';
import {
  postAnalysis,
  putAnalysis
} from '../../../features/analyses/analysesService';
import CONSTANTS from '../../../features/analyses/Freeform/FreeformAnalysisLayout/AddReportDataLayoutContainer/Constants';

let cancelPreviewRequestToken;

export const getReportPreviewThunk = (
  projectId,
  analysisId,
  version
) => async dispatch => {
  dispatch(setReportPreviewLoading({ loading: true }));
  dispatch(setReportPreviewError({ error: false }));
  try {
    const reportPreview = await getReportPreview(
      projectId,
      analysisId,
      version,
      cancelToken => {
        cancelPreviewRequestToken = cancelToken;
      }
    );
    dispatch(setReportPreviewLoading({ loading: false }));
    dispatch(setReportPreviewData({ data: reportPreview }));
  } catch (e) {
    let errorMessage = e.message;
    if (
      cancelPreviewRequestToken.token.reason.message ===
      'preview-request-canceled'
    ) {
      errorMessage = 'Failed to retrieve updated analysis layout preview';
    }
    dispatch(setReportPreviewLoading({ loading: false }));
    dispatch(setReportPreviewError({ error: true, errorMessage }));
  }
};

export const submitAnalysisThunk = (
  projectId,
  analysisId,
  analysisDto,
  baseUrl
) => async dispatch => {
  try {
    dispatch(setReportPreviewError({ error: false }));
    let response;
    if (analysisId) {
      response = await putAnalysis(projectId, analysisId, analysisDto);
    } else {
      response = await postAnalysis(projectId, analysisDto);
    }
    if (response && Number.isInteger(response) && baseUrl) {
      const constructedUrl = `${baseUrl}/${response}/${CONSTANTS.PARTIAL_ROUTE.EDIT_LAYOUT_PREVIEW}`;
      dispatch(push(constructedUrl));
    } else if (!response) {
      dispatch(
        setReportPreviewError({
          error: true,
          errorMessage: `Failed to ${
            analysisId ? 'update' : 'create'
          } analysis layout`
        })
      );
      if (typeof cancelPreviewRequestToken !== typeof undefined) {
        cancelPreviewRequestToken.cancel('preview-request-canceled');
      }
    }
  } catch (e) {
    let errorMessage = e.message;
    if (e.message.includes('400')) {
      errorMessage = `Bad request on ${
        analysisId ? 'updating' : 'creating'
      } analysis layout`;
    }
    dispatch(setReportPreviewLoading({ loading: false }));
    dispatch(setReportPreviewError({ error: true, errorMessage }));
    if (typeof cancelPreviewRequestToken !== typeof undefined) {
      cancelPreviewRequestToken.cancel('preview-request-canceled');
    }
  }
};
