import { matchPath } from 'react-router-dom';
import updateEntityVersion from '../../common/actions/updateEntityVersion';
import { initializeCommentsPanel, resetComments } from './commentsSlice';

import { fetchHypothesisSucceeded } from '../hypotheses/hypothesisSlice';
import { fetchInsightSucceeded } from '../insights/insightSlice';
import { fetchAnalysisSucceeded } from '../analyses/analysisSlice';

export const LOCATION_CHANGE = '@@router/LOCATION_CHANGE';
export const ACTIONS_WITH_VERSION_CHANGE = [
  fetchHypothesisSucceeded.type,
  fetchInsightSucceeded.type,
  fetchAnalysisSucceeded.type
];

export const commentPaths = [
  {
    entityType: 'project',
    path: '/projects/:projectId',
    formatInitCommentsObject({ projectId }) {
      return {
        projectId,
        entityId: projectId,
        entityType: this.entityType,
        versionRequired: false
      };
    }
  },
  {
    entityType: 'hypothesis',
    path: '/projects/:projectId/key-focus-areas/:hypothesisId/view',
    formatInitCommentsObject({ projectId, hypothesisId }) {
      return {
        projectId,
        entityId: hypothesisId,
        entityType: this.entityType,
        versionRequired: true
      };
    }
  },
  {
    entityType: 'insight',
    path: '/projects/:projectId/insights/:insightId/view',
    formatInitCommentsObject({ projectId, insightId }) {
      return {
        projectId,
        entityId: insightId,
        entityType: this.entityType,
        versionRequired: true
      };
    }
  },
  {
    entityType: 'analysis',
    path: '/projects/:projectId/analyses/:analysisId/view',
    formatInitCommentsObject({ projectId, analysisId }) {
      return {
        projectId,
        entityId: analysisId,
        entityType: this.entityType,
        versionRequired: true
      };
    }
  },
  {
    entityType: 'debt-debt-like',
    path: '/projects/:projectId/analyses/debt-debt-like',
    formatInitCommentsObject({ projectId }) {
      return {
        projectId,
        entityId: 1,
        entityType: this.entityType,
        versionRequired: true
      };
    }
  },
  {
    entityType: 'net-working-capital',
    path: '/projects/:projectId/analyses/net-working-capital',
    formatInitCommentsObject({ projectId }) {
      return {
        projectId,
        entityId: 1,
        entityType: this.entityType,
        versionRequired: true
      };
    }
  },
  {
    entityType: 'quality-of-earnings',
    path: '/projects/:projectId/analyses/quality-of-earnings',
    formatInitCommentsObject({ projectId }) {
      return {
        projectId,
        entityId: 1,
        entityType: this.entityType,
        versionRequired: true
      };
    }
  }
];

const commentMiddleware = store => next => action => {
  next(action);

  if (action.type === LOCATION_CHANGE) {
    const { pathname } = action.payload.location;

    const matchedPath = commentPaths.find(({ path }) =>
      matchPath(pathname, {
        path,
        exact: true
      })
    );

    const { showCommentsPanel } = store.getState().comments;

    if (matchedPath !== undefined) {
      const { params } = matchPath(pathname, { path: matchedPath.path });
      const initCommentsObject = matchedPath.formatInitCommentsObject(params);
      next(initializeCommentsPanel(initCommentsObject));
    } else if (showCommentsPanel) {
      next(resetComments());
    }
  }

  if (ACTIONS_WITH_VERSION_CHANGE.includes(action.type)) {
    next(updateEntityVersion(action.payload.versionNumber));
  }
};

export default commentMiddleware;
