import React from 'react';
import { Tooltip, Icon } from '@pwc/pwc-deals-ui';
import { useSelector } from 'react-redux';
import {
  getEngagementUrl,
  getProjectSharePointUrl
} from '../../../project/projectSlice';
import './ViewAnalysisSourceLinks.scss';

const ViewAnalysisSourceLinks = ({ toolTipDirection = 'left' }) => {
  const sharepointUrl = useSelector(getProjectSharePointUrl);
  const engagementUrl = useSelector(getEngagementUrl);

  const toolTipText = `Make sure your PowerPoint ${'\n'}files are saved in SharePoint, ${'\n'}and your Power BI reports are${'\n'} published to Workbench.`;
  return (
    <div className="external-source-links">
      View in{' '}
      <a
        className="external-source-link"
        target="_blank"
        rel="noopener noreferrer"
        data-testid="project-sharepoint-link"
        href={`//${sharepointUrl}/Shared%20Documents/Forms/AllItems.aspx`}
      >
        Sharepoint
      </a>{' '}
      or{' '}
      <a
        className="external-source-link"
        target="_blank"
        rel="noopener noreferrer"
        data-testid="project-workbench-link"
        href={`${engagementUrl}/reports`}
      >
        Workbench
      </a>{' '}
      <Tooltip
        direction={toolTipDirection}
        dataTestId="source-links-tooltip-popup"
        tip={toolTipText}
      >
        <Icon
          name="information"
          width="16"
          className="icon fill icon-yellow source-file-tooltip-icon"
        />
      </Tooltip>
    </div>
  );
};

export default ViewAnalysisSourceLinks;
