import { Constants } from '@pwc/pwc-deals-web-common';

const isEmpty = str => {
  return !str || str.trim() === '';
};

const panelIsValid = ({
  description,
  hypothesisId,
  contentType,
  excelChartId
}) => {
  switch (contentType) {
    case Constants.TOPIC_PANEL_TYPES.RICH_TEXT.asString:
      return !isEmpty(description);
    case Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString:
      return !!parseInt(hypothesisId, 10);
    case Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString:
      return !!parseInt(excelChartId, 10);
    default:
      return false;
  }
};

const topicFormIsValid = ({ title, panels }) => {
  if (isEmpty(title)) {
    return false;
  }

  const hypothesisIds = panels
    .filter(
      panel =>
        panel.contentType ===
        Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString
    )
    .map(panel => panel.hypothesisId);
  const hasDuplicateHypothesisIds =
    hypothesisIds.length !== new Set(hypothesisIds).size;

  const excelChartIds = panels
    .filter(
      panel =>
        panel.contentType === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString
    )
    .map(panel => panel.excelChartId);
  const hasDuplicateExcelChartIds =
    excelChartIds.length !== new Set(excelChartIds).size;

  return (
    !hasDuplicateHypothesisIds &&
    !hasDuplicateExcelChartIds &&
    panels.every(panelIsValid)
  );
};

export default topicFormIsValid;
