import React from 'react';
import { useSelector } from 'react-redux';

import Section from './Section/Section';

import { useSections } from '../../../../FreeformAnalysisForm/FreeformAnalysisFormContext';
import {
  setCurrentSections,
  setNonLaidOutLibraryItems
} from '../../../../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';

import { getReportFormatLayoutState } from '../../../../../../../common/redux/reportFormat/reportFormatSlice';

import {
  /* generateTargetDroppableLocationIds, */ generateTargetDroppableLocationIdsNew
} from '../../../generateTargetDroppableLocationIds';

import './LeftPanel.scss';

const LeftPanel = ({ libraryItemMap, droppableLocations, onAddNewSection }) => {
  const [
    { currentSections = [], nonLaidOutLibraryItems = [] },
    contextDispatch
  ] = useSections();
  const { activeSectionIndex } = useSelector(getReportFormatLayoutState);

  const handleAddNewSection = () => {
    onAddNewSection();
  };

  const handleRemoveSection = sectionOrder => {
    if (currentSections.length === 1) {
      return;
    }
    const sectionToRemove = currentSections.find(s => s.order === sectionOrder);

    const newNonLaidOutLibraryItems = nonLaidOutLibraryItems.concat(
      sectionToRemove.items.map(item => libraryItemMap[item.id])
    );
    setNonLaidOutLibraryItems(newNonLaidOutLibraryItems, contextDispatch);

    const newSections = currentSections
      .filter(s => s.order !== sectionOrder)
      .map((s, i) => {
        return {
          ...s,
          order: i + 1
        };
      });

    setCurrentSections(newSections, contextDispatch);
  };

  return (
    <div className="left-panel" data-testid="left-panel">
      {currentSections.map((section, i, allSections) => {
        const targetDroppableLocationIds = generateTargetDroppableLocationIdsNew(
          section
        );

        return (
          <Section
            key={section.order}
            sectionData={section}
            libraryItemMap={libraryItemMap}
            droppableLocations={droppableLocations}
            onAddSection={handleAddNewSection}
            onRemoveSection={handleRemoveSection}
            lastSection={i === currentSections.length - 1}
            showDeleteButton={allSections.length > 1}
            targetDroppableLocationIds={targetDroppableLocationIds}
            activeSection={activeSectionIndex === i}
          />
        );
      })}
    </div>
  );
};

export default LeftPanel;
