import decode from 'jwt-decode';
import _keys from 'lodash/keys';
import _get from 'lodash/get';
import _some from 'lodash/some';

const applicationTokenKey = 'jwtToken';
const projectTokenKeyPrefix = 'projectJWT';
const projectTokenKeyTemplate = `${projectTokenKeyPrefix}{PROJ}`;

const getProjectToken = projectId => {
  const tokenStorageKey = projectTokenKeyTemplate.replace(
    '{PROJ}',
    btoa(projectId)
  );
  const savedToken = sessionStorage.getItem(tokenStorageKey);
  return savedToken;
};

const isKeyJunctionRelated = key =>
  key.startsWith(projectTokenKeyPrefix) || key === applicationTokenKey;

export const getAllTokenKeys = () =>
  _keys(sessionStorage).filter(isKeyJunctionRelated);

export const getAllTokens = () =>
  getAllTokenKeys().map(key => sessionStorage.getItem(key));

export const clearAllTokens = () =>
  getAllTokenKeys().forEach(key => sessionStorage.removeItem(key));

const getPlaintextToken = projectId => decode(getProjectToken(projectId));

const hasProjectPermission = (projectId, permissionList = []) => {
  const permissions = getPlaintextToken(projectId);
  const permissionKeys = _keys(permissions);

  return _some(permissionKeys, key => permissionList.includes(key));
};

const hasProjectTokenProp = (projectId, prop) => {
  let hasToken = false;
  const tokenStorageKey = projectTokenKeyTemplate.replace(
    '{PROJ}',
    btoa(projectId)
  );
  const savedToken = sessionStorage.getItem(tokenStorageKey);

  if (savedToken) {
    const token = decode(savedToken);

    prop.forEach(p => {
      if (Object.prototype.hasOwnProperty.call(token, p)) {
        hasToken = true;
      }
    });
  }

  return hasToken;
};

const getProjectTokenProperty = (projectId, prop) => {
  const token = getPlaintextToken(projectId);
  return token && token[prop];
};

const hasProjectRoles = (projectId, roles) => {
  const projectRole = getProjectTokenProperty(projectId, 'role');
  return roles.includes(projectRole);
};

const getUserEmail = () => {
  const token = decode(sessionStorage.getItem(applicationTokenKey));
  return _get(token, 'email');
};

const userTokenService = {
  getProjectToken,
  hasProjectPermission,
  hasProjectTokenProp,
  getProjectTokenProperty,
  hasProjectRoles,
  getUserEmail
};

export default userTokenService;
