import { ClassicEditor } from '@pwc/pwc-deals-rich-text-editor';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import EnableForProjectState from '../../../features/security/EnableForProjectState';

import CommentEditor from '../../plugins/CommentEditor';
import CommentPanel from './CommentPanel';
import { getComments } from '../../../features/comments/commentsSlice';
import authorizationService from '../../services/security/authorizationService';

const CommentPanelContainer = ({ projectId }) => {
  const {
    showCommentsPanel,
    entityType,
    entityId,
    entityVersion,
    versionRequired,
    isEntitySharedWithClient
  } = useSelector(getComments);
  const [context, setContext] = useState();

  // For future CKEditor Comment feature
  useEffect(() => {
    let isMounted = true;
    (async () => {
      const result = await ClassicEditor.Context.create();
      if (isMounted) setContext(result);
    })();
    return () => {
      isMounted = false;
    };
  }, []);

  const versionReadyOrNotNeeded = !versionRequired || entityVersion;
  const guardEntityVersion = versionRequired ? entityVersion : -1;

  const isClientUser = authorizationService.isClientUser();

  if (showCommentsPanel && versionReadyOrNotNeeded) {
    return (
      <EnableForProjectState projectId={projectId}>
        {({ isProjectDisabled }) => (
          <div
            className={`comment-panel ${
              isClientUser ? 'current-user-is-client' : 'current-user-is-pwc'
            }`}
          >
            <span hidden data-testid="comment-editor">
              <CommentEditor
                context={context}
                entityType={entityType}
                entityId={entityId}
                entityVersion={guardEntityVersion}
                isEntitySharedWithClient={isEntitySharedWithClient}
                isProjectDisabled={isProjectDisabled}
              />
            </span>
            <CommentPanel projectId={projectId} />
          </div>
        )}
      </EnableForProjectState>
    );
  }
  return null;
};

export default CommentPanelContainer;
