import { Button, Icon } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import classnames from 'classnames';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import EnableForProjectState from '../../../security/EnableForProjectState';
import ShowForPermissions from '../../../security/ShowForPermissions';
import {
  deleteTopic,
  getAllTopics,
  getTopic,
  updateTopicOrder
} from '../../topicService';
import TopicFormModal from './Topic/TopicFormModal/TopicFormModal';
import './ProjectOverviewModuleContainer.scss';
import ProjectOverviewModule from './ProjectOverviewModule';
import ModalConfirmation from '../../../../common/components/ModalConfirmation';
import { showModalError } from '../../../../common/redux/errors/errorsSlice';
import authorizationService from '../../../../common/services/security/authorizationService';

const ProjectOverviewModuleContainer = () => {
  const [showAddTopicModal, setShowAddTopicModal] = useState(false);
  const [isDeleteConfirmationVisible, setDeleteConfirmationVisible] = useState(
    false
  );
  const [selectedTopic, setSelectedTopic] = useState(null);
  const [topics, setTopics] = useState([]);
  const projectOverviewText =
    "Showcase the project's most important information by creating 'Project overview' topics.";

  const { projectId } = useParams();
  const dispatch = useDispatch();
  const isClientUser = authorizationService.isClientUser();

  useEffect(() => {
    if (!showAddTopicModal) {
      getAllTopics(projectId).then(topicList => {
        setTopics(topicList);
      });
    }
  }, [projectId, showAddTopicModal]);

  const handleDeleteTopic = async () => {
    const topicsWithoutDeletedTopic = topics.filter(
      topic => topic.contentThreadId !== selectedTopic.contentThreadId
    );
    setTopics(topicsWithoutDeletedTopic);
    setSelectedTopic(null);
    await deleteTopic(projectId, selectedTopic.contentThreadId);
    setDeleteConfirmationVisible(false);
  };

  const updateTopic = (topic, versionStatus) => {
    getTopic(projectId, topic.contentThreadId, versionStatus).then(
      updatedTopic => {
        const updatedTopics = [...topics];
        const existing = updatedTopics.filter(
          x => x.contentThreadId === topic.contentThreadId
        );
        if (existing.length !== 0) {
          const index = updatedTopics.indexOf(existing[0]);
          updatedTopics[index] = updatedTopic;
          setTopics(updatedTopics);
        }
      }
    );
  };

  const moveTopic = async (topic, offset) => {
    const tmp = [...topics];
    const existing = tmp.filter(
      x => x.contentThreadId === topic.contentThreadId
    );
    if (existing.length !== 0) {
      const index = tmp.indexOf(existing[0]);
      const newIndex = index + offset;
      if (newIndex >= 0 && newIndex < tmp.length) {
        [tmp[index], tmp[newIndex]] = [tmp[newIndex], tmp[index]];
        const newOrder = tmp.map(_ => _.contentThreadId);
        updateTopicOrder(projectId, newOrder)
          .then(() => {
            setTopics(tmp);
          })
          .catch(() => {
            dispatch(
              showModalError(Constants.ModalErrorType.UNABLE_TO_SAVE_CHANGE)
            );
          });
      }
    }
  };

  const getMoveOptionForTopic = index => {
    if (topics.length === 1) {
      return Constants.MOVE_OPTIONS.NONE;
    }

    // eslint-disable-next-line no-nested-ternary
    return index === 0
      ? Constants.MOVE_OPTIONS.NEXT
      : index === topics.length - 1
      ? Constants.MOVE_OPTIONS.PREV
      : Constants.MOVE_OPTIONS.BOTH;
  };

  if (isClientUser && topics && topics.length === 0) {
    return null;
  }

  return (
    <div
      className="project-overview-module-container"
      data-testid="project-overview-module-container"
    >
      <ModalConfirmation
        show={isDeleteConfirmationVisible}
        onCancel={() => setDeleteConfirmationVisible(false)}
        onConfirm={handleDeleteTopic}
        confirmTitle="Confirm Topic Delete"
        confirmationMessage="Proceed with deletion of this Topic?"
      />
      <div
        className="project-overview-header-container"
        data-testid="project-overview-header-container"
      >
        <div className="project-overview-label">
          <Icon
            data-testid="project-overview-icon"
            aria-label="project overview icon"
            name="grid"
            width={32}
            className="outline project-overview-icon"
          />
          <strong>Project Overview Topics</strong>
        </div>
        {topics && topics.length > 0 ? (
          <div>
            <ShowForPermissions
              projectId={projectId}
              permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
            >
              <EnableForProjectState projectId={projectId}>
                {({ isProjectDisabled }) => (
                  <Button
                    className="button-link"
                    onClick={() => setShowAddTopicModal(true)}
                    data-testid="button-add-topic"
                    aria-label="add topic"
                    type="button"
                    icon="add"
                    iconClasses={classnames('fill add-topic-icon', {
                      'icon-grey': isProjectDisabled
                    })}
                    disabled={isProjectDisabled}
                  >
                    Add topic
                  </Button>
                )}
              </EnableForProjectState>
            </ShowForPermissions>
          </div>
        ) : null}
      </div>
      {topics && topics.length > 0 ? (
        <>
          {topics.map((topic, index) => (
            <ProjectOverviewModule
              key={topic.contentThreadId}
              topic={topic}
              projectId={projectId}
              onDelete={() => {
                setSelectedTopic(topic);
                setDeleteConfirmationVisible(true);
              }}
              onEdit={() => {
                setSelectedTopic(topic);
                setShowAddTopicModal(true);
              }}
              onTopicStatusUpdated={updatedTopic => updateTopic(updatedTopic)}
              onViewSpecificVersion={versionStatus => {
                updateTopic(topic, versionStatus);
              }}
              onMove={offset => moveTopic(topic, offset)}
              moveOption={getMoveOptionForTopic(index)}
            />
          ))}
        </>
      ) : (
        <div className="project-overview-module">
          <p className="project-overview-placeholder-text">
            {projectOverviewText}
          </p>
          <ShowForPermissions
            projectId={projectId}
            permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
          >
            <EnableForProjectState projectId={projectId}>
              {({ isProjectDisabled }) => (
                <Button
                  className="add-topic-button"
                  type="submit"
                  data-testid="button-add-topic"
                  aria-label="add topic"
                  onClick={() =>
                    !isProjectDisabled && setShowAddTopicModal(true)
                  }
                  icon="add"
                  iconClasses="fill add-topic-icon"
                  disabled={isProjectDisabled}
                >
                  Add topic
                </Button>
              )}
            </EnableForProjectState>
          </ShowForPermissions>
        </div>
      )}
      {showAddTopicModal && (
        <TopicFormModal
          show={showAddTopicModal}
          handleClose={() => {
            setSelectedTopic(null);
            setShowAddTopicModal(false);
          }}
          topic={selectedTopic}
        />
      )}
    </div>
  );
};

export default ProjectOverviewModuleContainer;
