import React, { useRef, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import PropType from 'prop-types';
import { Button, Icon } from '@pwc/pwc-deals-ui';
import classNames from 'classnames';
import {
  setActiveSectionId,
  setFloatingTabsTop,
  setFloatingTabsHeight,
  getReportFormatLayoutState
} from '../../../../../../../../common/redux/reportFormat/reportFormatSlice';
import { useSections } from '../../../../../FreeformAnalysisForm/FreeformAnalysisFormContext';
import { setSectionToScroll } from '../../../../../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';
import SectionArea from './SectionArea/SectionArea';
import CONSTANTS from '../../../Constants';
import LayoutIcon from '../../../LayoutIcon';
import DeleteButton from './DeleteButton/DeleteButton';
import useTabletOrMobile from '../../../../../../../../common/hooks/useTabletOrMobile';

import './Section.scss';

function Section({
  sectionData,
  libraryItemMap,
  droppableLocations,
  onAddSection,
  onRemoveSection,
  lastSection,
  showDeleteButton,
  targetDroppableLocationIds,
  // activeSection = false, // TODO: will use for styling
  scrollIntoView = true
}) {
  const ref = useRef();
  const [iconType, setIconType] = useState('outline');
  const [isActive, setIsActive] = useState(false);
  const [{ sectionIdToScroll }, useSectionsDispatch] = useSections();
  const isTabletOrMobile = useTabletOrMobile();

  const dispatch = useDispatch();
  const sectionId = sectionData.order - 1;

  const { activeSectionIndex, floatingTabsHeight } = useSelector(
    getReportFormatLayoutState
  );

  const currentHeight = ref.current ? ref.current.offsetHeight : 0;
  const currentTop = ref.current ? ref.current.offsetTop : 0;

  useEffect(() => {
    if (sectionIdToScroll === sectionId && currentHeight) {
      if (scrollIntoView) {
        ref.current.scrollIntoView({
          behavior: 'smooth'
        });
      }
      setSectionToScroll(null, useSectionsDispatch);
    }
  }, [
    currentHeight,
    useSectionsDispatch,
    scrollIntoView,
    sectionId,
    sectionIdToScroll
  ]);

  const handleOnSectionClick = () => {
    /*
     * Bug: PWC-5795
     * If section clicked, floatingTabs needs to be positioned to that section
     */
    if (!isTabletOrMobile && activeSectionIndex !== sectionId) {
      dispatch(setActiveSectionId({ index: sectionId }));
      dispatch(
        setFloatingTabsTop({
          top: currentTop
        })
      );
      if (currentHeight !== floatingTabsHeight) {
        dispatch(
          setFloatingTabsHeight({
            height: currentHeight
          })
        );
      }
    }
  };

  useEffect(() => {
    if (activeSectionIndex === sectionId) {
      if (!isActive) {
        dispatch(
          setFloatingTabsTop({
            top: currentTop
          })
        );
        dispatch(
          setFloatingTabsHeight({
            height: currentHeight
          })
        );
      }

      setIsActive(true);
    } else if (isActive) {
      setIsActive(false);
    }
  }, [
    isActive,
    activeSectionIndex,
    sectionId,
    dispatch,
    currentTop,
    currentHeight
  ]);

  const handleRemoveSection = () => {
    const newSectionIndex = Math.max(0, sectionData.order - 1 - 1);
    dispatch(setActiveSectionId({ index: newSectionIndex }));

    onRemoveSection(sectionData.order);
    setIconType('outline');
  };
  const handleMouseEnter = () => {
    setIconType('fill');
  };
  const handleMouseLeave = () => {
    setIconType('outline');
  };

  const {
    title: layoutTitle,
    description: layoutDescription
  } = CONSTANTS.LAYOUT_TYPE_TO_LAYOUT_TITLE_DESCRIPTION_MAP[
    sectionData.layoutType
  ];

  return (
    // eslint-disable-next-line
    <div
      ref={ref}
      data-section-id={sectionId}
      key={`Section - ${sectionData.order}`}
      className="section"
      data-testid="section"
      onClick={handleOnSectionClick}
    >
      <div className="section__top-row" data-testid="section-top-row">
        <div className="section__top-row__row-1">
          <div className="section__title">{`Section ${sectionData.order}`}</div>
          {isTabletOrMobile && showDeleteButton && (
            <DeleteButton
              handleClick={handleRemoveSection}
              handleMouseEnter={handleMouseEnter}
              handleMouseLeave={handleMouseLeave}
              tooltipPlacement="top"
              tooltip="Remove section"
              iconType={iconType}
            />
          )}
        </div>
        <div className="section__top-row__row-2">
          <div className="section__layout">
            <span className="section__layout__title">{layoutTitle}</span>
            <span className="section__layout__hyphen">-</span>
            <span className="section__layout__description">
              {layoutDescription}
            </span>
          </div>

          <div className="section__delete-button">
            {!isTabletOrMobile && showDeleteButton && (
              <DeleteButton
                handleClick={handleRemoveSection}
                handleMouseEnter={handleMouseEnter}
                handleMouseLeave={handleMouseLeave}
                tooltipPlacement="top"
                tooltip="Remove section"
                iconType={iconType}
              />
            )}
          </div>
        </div>
      </div>
      <div
        className={classNames('section__middle-row', {
          [CONSTANTS.LAYOUT_TYPE_TO_SECTION_CLASSNAME_MAP[
            sectionData.layoutType
          ]]: sectionData.layoutType
        })}
        data-testid="section-middle-row"
      >
        {targetDroppableLocationIds &&
          targetDroppableLocationIds.map(droppableLocationId => {
            const droppableLocation = droppableLocations[droppableLocationId];
            const libraryItems =
              droppableLocation &&
              droppableLocation.libraryItemIds
                .map(id => libraryItemMap[id])
                .filter(libraryItem => !!libraryItem);
            return (
              <React.Fragment key={droppableLocationId}>
                {droppableLocation && (
                  <>
                    {isTabletOrMobile && (
                      <LayoutIcon
                        name={
                          CONSTANTS.LAYOUT_TYPE_TO_ICON_MAP[
                            `${sectionData.layoutType}-${droppableLocation.position}`
                          ]
                        }
                      />
                    )}
                    <SectionArea
                      key={droppableLocationId}
                      areaClass={droppableLocationId}
                      droppableLocation={
                        droppableLocations[droppableLocationId]
                      }
                      libraryItems={libraryItems}
                      libraryItemMap={libraryItemMap}
                      droppableLocations={droppableLocations}
                    />
                  </>
                )}
              </React.Fragment>
            );
          })}
      </div>
      {lastSection && (
        <div className="section__bottom-row" data-testid="section-bottom-row">
          <hr className="section-line" />
          <Button className="button-no-underline" onClick={onAddSection}>
            <Icon
              name="add"
              width={24}
              height={24}
              className="fill icon-orange-theme"
            />
            <span className="section__bottom-row__button-text">
              Add new section
            </span>
          </Button>
          <hr className="section-line" />
        </div>
      )}
    </div>
  );
}

SectionArea.propTypes = {
  sectionData: PropType.shape({
    order: PropType.number,
    layoutType: PropType.number,
    items: PropType.arrayOf(PropType.any)
  }),
  onAddSection: PropType.func,
  onRemoveSection: PropType.func,
  targetDroppableLocationIds: PropType.arrayOf(PropType.string),
  lastSection: PropType.bool,
  activeSection: PropType.bool
};

export default Section;
