import React, { useEffect, useState } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { Button } from '@pwc/pwc-deals-ui';
import authorizationService from '../../../common/services/security/authorizationService';
import userTokenService from '../../../common/services/security/userTokenService';

import './FileUploader.scss';
import EnableForProjectState from '../../security/EnableForProjectState';

const FileUploader = ({ projectId, openModal }) => {
  const isClientUser = authorizationService.isClientUser();
  const [allowUploadByRole, setAllowByRoleUpload] = useState(false);

  useEffect(() => {
    (async () => {
      const isPwcReadOnlyRole = await userTokenService.hasProjectRoles(
        projectId,
        [
          Constants.USER_ROLES.PWC_READONLY_DISPLAY,
          Constants.USER_ROLES.PWC_READONLY
        ]
      );
      if (!isPwcReadOnlyRole) {
        setAllowByRoleUpload(true);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (isClientUser || !allowUploadByRole) return null;
  return (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <div className="file-uploader">
          <Button
            disabled={isProjectDisabled}
            className="btn-large"
            type="button"
            onClick={openModal}
            data-testid="file-uploader-button"
          >
            Upload file...
          </Button>
        </div>
      )}
    </EnableForProjectState>
  );
};

export default FileUploader;
