import { Constants } from '@pwc/pwc-deals-web-common';
import React, { useEffect, useState } from 'react';
import { Button, Icon } from '@pwc/pwc-deals-ui';

import './FileListItemRowActionsButton.scss';
import { useSelector } from 'react-redux';
import authorizationService from '../../../../common/services/security/authorizationService';
import userTokenService from '../../../../common/services/security/userTokenService';

const FileListItemRowActionsButton = ({ ...props }) => {
  const isClientUser = authorizationService.isClientUser();
  const projectId = useSelector(state => state.project && state.project.id);
  const [isReadOnly, setIsReadOnly] = useState(true);

  useEffect(() => {
    (async () => {
      setIsReadOnly(
        await userTokenService.hasProjectRoles(projectId, [
          Constants.USER_ROLES.PWC_READONLY_DISPLAY,
          Constants.USER_ROLES.PWC_READONLY
        ])
      );
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="file-upload-action-button-container">
      <Button
        {...props}
        className="button-link action-button"
        data-testid={
          isClientUser || isReadOnly
            ? 'row-action-unlock'
            : 'row-actions-button'
        }
        aria-label="Action Button"
      >
        <Icon
          name={isClientUser || isReadOnly ? 'locked' : 'more-vert'}
          width={24}
          className="fill icon-orange"
        />
      </Button>
    </div>
  );
};

export default FileListItemRowActionsButton;
