import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Formik } from 'formik';
import { ErrorUtilities } from '@pwc/pwc-deals-web-common';
import HypothesisForm from '../common/HypothesisForm';
import { getHypothesis, putHypothesis } from '../hypothesesService';
import { hypothesisSchema } from '../common/hypothesisModel';
import { viewHypothesisPath } from '../../../common/services/urlService';
import { getNarratives } from '../../narratives/narrativesService';
import useLinkedNarratives from '../../narratives/EditConnectedContent/useLinkedNarratives';
import {
  getLinksFromNarratives,
  getTopicLinksForContent
} from '../../narratives/narrativesUtilities';

const EditHypothesis = () => {
  const { projectId, hypothesisId: hypothesisContentThreadId } = useParams();
  const history = useHistory();

  const [hypothesis, setHypothesis] = useState(null);
  const [narratives, setNarratives] = useState(null);
  const [errorType, setErrorType] = useState();
  const [isSaving, setIsSaving] = useState(false);

  const gotoHypothesisView = () =>
    history.replace(viewHypothesisPath(projectId, hypothesisContentThreadId));

  useEffect(() => {
    getHypothesis(projectId, hypothesisContentThreadId).then(value =>
      setHypothesis({
        ...value,
        body: value.body || '',
        conclusion: value.conclusion || ''
      })
    );
  }, [projectId, hypothesisContentThreadId]);

  useEffect(() => {
    (async () => {
      getNarratives(projectId).then(allContent => {
        setNarratives({
          ...allContent,
          hypotheses: allContent.hypotheses.filter(
            h => h.contentThreadId.toString() !== hypothesisContentThreadId
          )
        });
      });
    })();
  }, [projectId, hypothesisContentThreadId]);

  const handleSave = async values => {
    setIsSaving(true);
    try {
      const links = getLinksFromNarratives(narratives, hypothesis.contentId);
      const topicLinks = getTopicLinksForContent(narratives, hypothesis.links);

      await putHypothesis(projectId, {
        contentThreadId: hypothesis.contentThreadId,
        contentId: hypothesis.contentId,
        title: values.title,
        body: values.body,
        conclusion: values.conclusion,
        impact: values.impact,
        tags: values.tags.map(tag => ({
          text: tag
        })),
        links: [...links, ...topicLinks]
      });
      setErrorType();
      gotoHypothesisView();
    } catch (e) {
      setIsSaving(false);
      setErrorType(ErrorUtilities.getContentErrorType(e));
    }
  };

  const linkedNarrativesChanged = useLinkedNarratives(
    narratives,
    hypothesis ? hypothesis.contentId : null
  );

  return hypothesis ? (
    <Formik
      initialValues={{
        ...hypothesis,
        title: hypothesis.title || '',
        tags: hypothesis.tags ? hypothesis.tags.map(tag => tag.text) : []
      }}
      enableReinitialize
      onSubmit={handleSave}
      validationSchema={hypothesisSchema}
      validateOnChange
    >
      {({ errors, touched, isValid, dirty }) => (
        <HypothesisForm
          className="mt-5"
          onCancel={gotoHypothesisView}
          isSaving={isSaving}
          errorType={errorType}
          errors={errors}
          touched={touched}
          isValid={isValid}
          projectId={projectId}
          parentContentId={hypothesis.contentId}
          narratives={narratives}
          dirty={dirty || linkedNarrativesChanged}
          setNarratives={setNarratives}
          onSaveText="Updating..."
          standbyText="Update"
        >
          <h1>Edit Key Focus Area</h1>
        </HypothesisForm>
      )}
    </Formik>
  ) : null;
};

export default EditHypothesis;
