import { createSlice } from '@reduxjs/toolkit';
import _toNumber from 'lodash/toNumber';
import { clearAllProjectData } from '../../common/engine/actions/clearAllProjectData';

import {
  getProjectById,
  getProjectSettingsById,
  updateProjectSettings
} from '../../common/services/projectService';
import { getReports } from '../analyses/powerBiService';

export const defaultCurrency = 'Dollar';
export const defaultFinancialsMagnitude = 'Millions';
export const defaultFinancialsDecimalCount = 1;

export const initialState = {
  id: null,
  name: null,
  isLoading: false,
  hasError: false,
  showDiagnostic: true,
  errorStatusCode: null,
  powerBiReports: [],
  currency: defaultCurrency,
  financialsMagnitude: defaultFinancialsMagnitude,
  financialsDecimalCount: defaultFinancialsDecimalCount,
  serviceIds: [],
  EngagementUrl: '',
  sharePointSiteUrl: ''
};

const projectSlice = createSlice({
  name: 'project',
  initialState,
  reducers: {
    resetProject: () => initialState,
    fetchProjectBegan: state => ({
      ...state,
      isLoading: true,
      hasError: false,
      errorStatusCode: null
    }),
    fetchProjectEnded: state => ({
      ...state,
      isLoading: false
    }),
    fetchProjectSettingsSucceeded: (state, action) => {
      const {
        currency,
        financialsMagnitude,
        financialsDecimalCount
      } = action.payload;

      const financialsDecimalCountToUse = _toNumber(
        financialsDecimalCount || defaultFinancialsDecimalCount
      );

      return {
        ...state,
        ...action.payload,
        currency: currency || defaultCurrency,
        financialsMagnitude: financialsMagnitude || defaultFinancialsMagnitude,
        financialsDecimalCount: financialsDecimalCountToUse,
        hasError: false,
        errorStatusCode: null
      };
    },
    fetchProjectDetailsSucceeded: (state, action) => {
      return {
        ...state,
        ...action.payload,
        hasError: false,
        errorStatusCode: null
      };
    },
    fetchProjectFailed: (state, action) => ({
      ...state,
      isLoading: false,
      hasError: true,
      errorStatusCode: action.payload
    }),
    powerBiReportsUpdated: (state, action) => ({
      ...state,
      powerBiReports: action.payload
    }),
    fetchUserViewCountsSucceeded: (state, action) => ({
      ...state,
      ...action.payload
    })
  }
});
export default projectSlice.reducer;

// Actions
export const {
  resetProject,
  fetchProjectBegan,
  fetchProjectEnded,
  fetchProjectDetailsSucceeded,
  fetchProjectSettingsSucceeded,
  fetchProjectFailed,
  powerBiReportsUpdated,
  fetchUserViewCountsSucceeded
} = projectSlice.actions;

// Selectors
export const getProjectLoading = state => state.project.isLoading;
export const getProjectError = state => state.project.hasError;
export const getProjectErrorStatusCode = state => state.project.errorStatusCode;
export const getProject = state => state.project;
export const getProjectId = state => state.project.id;
export const getProjectSharePointUrl = state => state.project.sharePointSiteUrl;
export const getEngagementUrl = state => state.project.EngagementUrl;
export const getPowerBiReports = state => state.project.powerBiReports;
export const getProjectFinancials = state => ({
  currency: state.project.currency,
  financialsMagnitude: state.project.financialsMagnitude,
  financialsDecimalCount: state.project.financialsDecimalCount
});

// Thunks
export const loadProjectThunk = projectId => async dispatch => {
  try {
    dispatch(clearAllProjectData());
    dispatch(fetchProjectBegan());
    const projectResponse = await getProjectById(projectId);
    dispatch(fetchProjectDetailsSucceeded(projectResponse));

    if (projectResponse) {
      const settingsResponse = await getProjectSettingsById(projectId);
      dispatch(fetchProjectSettingsSucceeded(settingsResponse));
    }
  } catch (error) {
    dispatch(fetchProjectFailed(error.response ? error.response.status : 500));
  } finally {
    dispatch(fetchProjectEnded());
  }
};

export const loadPowerBiReportsThunk = projectId => async dispatch => {
  const reports = await getReports(projectId);
  dispatch(powerBiReportsUpdated(reports));
};

export const updateProjectSettingsThunk = (
  projectId,
  settings
) => async dispatch => {
  try {
    dispatch(fetchProjectBegan());

    await updateProjectSettings(projectId, settings);

    dispatch(fetchProjectSettingsSucceeded(settings));
  } catch (error) {
    dispatch(fetchProjectFailed(error.response ? error.response.status : 500));
  } finally {
    dispatch(fetchProjectEnded());
  }
};
