import React, { useState, useEffect } from 'react';
import { Formik } from 'formik';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Loader } from '@pwc/pwc-deals-ui';
import { contentTypeHypotheses } from '../../common/components/proptypes/commonProptypes';
import ContentList from '../contentList/ContentList';
import HypothesesGrid from './HypothesesGrid/HypothesesGrid';
import ToggleGridList from '../../common/components/ToggleGridList/ToggleGridList';
import {
  deleteHypothesis,
  getHypothesesWithConnectedData,
  updateHypothesesOrder
} from './hypothesesService';
import './HypothesesListContainer.scss';
import TagEditor from '../tagging/common/TagEditor/TagEditor';
import {
  getUserSettingsSelector,
  setUserSettings
} from '../../common/redux/userSettings/userSettingsSlice';

const HypothesesListContainer = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [isError, setIsError] = useState(false);

  const { projectId } = useParams();
  const [
    kfasWithHierarchicalConnectedContent,
    setKfasWithHierarchicalConnectedContent
  ] = useState([]);
  const [tagFilters, setTagFilters] = useState([]);
  const userSettings = useSelector(getUserSettingsSelector);
  const dispatch = useDispatch();

  useEffect(() => {
    const fetch = () => {
      setIsError(false);
      setIsLoading(true);

      getHypothesesWithConnectedData(projectId)
        .then(projectKfasWithHierarchicalConnectedContent => {
          setKfasWithHierarchicalConnectedContent(
            projectKfasWithHierarchicalConnectedContent
          );
          setIsLoading(false);
        })
        .catch(() => {
          setIsError(true);
          setIsLoading(false);
        });
    };

    fetch();
  }, [projectId]);

  const handleDeleteHypothesis = async hypothesisId => {
    const success = await deleteHypothesis(projectId, hypothesisId);
    if (success) {
      const data = await getHypothesesWithConnectedData(projectId);
      setKfasWithHierarchicalConnectedContent(data);
    }
  };

  const handleReorder = orderedHypotheses => {
    setKfasWithHierarchicalConnectedContent(orderedHypotheses);
  };

  const toggleView = () => {
    dispatch(
      setUserSettings({
        viewGrid: !userSettings.settings.viewGrid
      })
    );
  };

  const updateTags = values => {
    setTagFilters(values);
  };

  const kfasFilteredByTags = kfasWithHierarchicalConnectedContent.filter(
    hypothesis => {
      const hypothesisTags = new Set(
        (hypothesis.tags || []).map(tag => tag.text)
      );
      return tagFilters.length
        ? tagFilters.some(tag => hypothesisTags.has(tag))
        : true;
    }
  );

  return (
    <>
      <div className="hypothesis-list-container">
        <div className="list-container-header">
          <h2 className="list-container-h2">Key Focus Areas</h2>
          {isError && <div>You don&amp;t have access to this content</div>}
          <ToggleGridList
            viewGrid={userSettings.settings.viewGrid}
            onClick={toggleView}
          />
        </div>

        <div className="tag-editor-container">
          <Formik initialValues={{ tags: [] }}>
            {() => (
              <TagEditor
                projectId={projectId}
                onSelect={updateTags}
                tagsRight
                title={false}
                subtitle={false}
                allowCreateTags={false}
                placeholderText="Filter by tag"
              />
            )}
          </Formik>
        </div>

        {isLoading ? (
          <Loader dataTestId="loader" />
        ) : (
          <>
            {userSettings.settings.viewGrid === true ? (
              <HypothesesGrid
                projectId={projectId}
                kfasFilteredByTags={kfasFilteredByTags}
                projectHasKfas={!!kfasWithHierarchicalConnectedContent.length}
                onDelete={handleDeleteHypothesis}
              />
            ) : (
              <ContentList
                contentType={contentTypeHypotheses}
                projectId={projectId}
                contentItems={kfasFilteredByTags}
                projectHasContent={
                  !!kfasWithHierarchicalConnectedContent.length
                }
                onDelete={({ contentThreadId }) =>
                  handleDeleteHypothesis(contentThreadId)
                }
                onReorder={handleReorder}
                updateSortOrderApiCall={updateHypothesesOrder}
                isFiltered={tagFilters.length > 0}
              />
            )}
          </>
        )}
      </div>
    </>
  );
};

export default HypothesesListContainer;
