import { Icon } from '@pwc/pwc-deals-ui';
import React, { useRef, useState } from 'react';
import { noHeadingEditorConfig } from '../RichTextEditor/CKEditorConfig';
import RichTextEditor from '../RichTextEditor/RichTextEditor';
import './RichTextEditorFormikField.scss';

const EDIT_ICON_MARGIN = 12;

const RichTextEditorFormikField = ({
  field: { value, name },
  form: { setFieldValue },
  label,
  dataTestId = 'rte-formik-field-container',
  config = noHeadingEditorConfig,
  lightModeVariant
}) => {
  const [focused, setFocused] = useState(false);
  const labelRef = useRef();

  return (
    <div
      className={`rte-formik-field-2 textarea-container rte-textarea-container ${
        value || focused ? 'label-lifted' : ''
      } ${focused ? 'focused' : ''}${
        lightModeVariant ? ' light-mode-variant' : ''
      }`}
      data-testid={dataTestId}
      id="description-inline"
    >
      <RichTextEditor
        richTextData={value}
        onChange={newValue => {
          setFieldValue(name, newValue);
        }}
        onBlur={() => {
          setFocused(false);
        }}
        onFocus={() => {
          setFocused(true);
        }}
        label={!value ? label : null}
        config={{ ...config, placeholder: null }}
        isFocused={focused}
        labelRef={labelRef}
      />
      <span
        className="description-icon"
        style={{
          left:
            labelRef && labelRef.current
              ? labelRef.current.offsetWidth + EDIT_ICON_MARGIN
              : 0
        }}
      >
        <Icon name="edit" className="fill icon-gray" />
      </span>
    </div>
  );
};

export default RichTextEditorFormikField;
