import { ProgressSteps } from '@pwc/pwc-deals-ui';
import { max } from 'lodash';
import React from 'react';

import { useSections } from '../FreeformAnalysisForm/FreeformAnalysisFormContext';
import {
  setCurrentSections,
  setSectionToScroll
} from '../FreeformAnalysisForm/FreeformAnalysisFormContextUtils';
import { DRAG_AND_DROP_STATE_ACTION } from '../FreeformAnalysisForm/FreeformAnalysisFormUtil/dragAndDropReducer';
import './FreeformAnalysisLayout.scss';
import './FreeformAnalysisLayoutContainer.scss';

import AddReportDataLayoutContainer from './AddReportDataLayoutContainer/AddReportDataLayoutContainer';

const steps = [
  { title: 'first', description: 'Select data' },
  { title: 'second', description: 'Edit layout' }
];

const FreeformAnalysisLayout = ({
  droppableLocations,
  libraryItemMap,
  handleDragEnd,
  setDroppableLocations,
  analysisTitle,
  analysisDescription,
  tags
}) => {
  const [{ currentSections }, dispatch] = useSections();
  const handleAddSection = () => {
    const maxSectionOrder = max(currentSections.map(section => section.order));
    const previousSection = currentSections.find(
      s => s.order === maxSectionOrder
    );
    const newSection = {
      order: maxSectionOrder + 1,
      layoutType: previousSection.layoutType,
      items: []
    };

    setCurrentSections([...currentSections, newSection], dispatch);
    setSectionToScroll(maxSectionOrder, dispatch);
  };

  const updateCurrentPosition = (position, itemId) => {
    dispatch({
      type: DRAG_AND_DROP_STATE_ACTION.ITEM_MOVED_WITHIN_SECTION,
      payload: {
        currentPosition: position,
        draggableWithinSectionsItemId: itemId
      }
    });
  };
  const handleOnDragStart = ({ source, draggableId }) => {
    updateCurrentPosition(source, draggableId);
  };
  const handleOnDragUpdate = ({ destination, draggableId }) => {
    updateCurrentPosition(destination, draggableId);
  };

  return (
    <>
      <h1>Edit Layout</h1>
      <div className="step-indicator-container">
        <ProgressSteps current={2} steps={steps} />
      </div>
      <div className="drag-and-drop-layout">
        <AddReportDataLayoutContainer
          onDragStart={handleOnDragStart}
          onDragUpdate={handleOnDragUpdate}
          onDragEnd={handleDragEnd}
          libraryItemMap={libraryItemMap}
          droppableLocations={droppableLocations}
          onAddNewSection={handleAddSection}
          setDroppableLocations={setDroppableLocations}
          analysisTitle={analysisTitle}
          analysisDescription={analysisDescription}
          tags={tags}
        />
      </div>
    </>
  );
};

export default FreeformAnalysisLayout;
