/* eslint-disable react/forbid-prop-types */
import { Loader, TriPane } from '@pwc/pwc-deals-ui';
import React from 'react';
import PropTypes from 'prop-types';
import { Constants } from '@pwc/pwc-deals-web-common';
import ModalConfirmation from '../../../../../common/components/ModalConfirmation';
import SourceDisplay from '../../../../../common/components/SourceDisplay/SourceDisplay';
import { STRUCTURED_DISPLAY_NAME_BY_CODE } from '../../../../../common/engine/utilities/constants';
import ConnectedContent from '../../../../narratives/ConnectedContent/ConnectedContent';
import AnalysisControlsHeader from '../../../common/AnalysisControlsHeader';
import NotesDisplay from '../../../common/NotesDisplay/NotesDisplay';
import TagList from '../../../common/TagList/TagList';
import ClientManipulationBar from '../../ClientManipulationBar/ClientManipulationBar';
import AnalysisDescription from '../AnalysisDescription/AnalysisDescription';
import StructuredAnalysisErrorCard from '../StructuredAnalysisErrorCard/StructuredAnalysisErrorCard';
import StructuredAnalysisHeader from '../StructuredAnalysisHeader/StructuredAnalysisHeader';
import {
  chartForDocumentCode,
  tableForDocumentCode
} from './structuredComponentToDocumentCodeMap';
import './StructuredAnalysisView.scss';

const StructuredAnalysisView = ({
  projectId,
  documentCode,
  hasError,
  isLoading,
  isConfirmVisible,
  setIsConfirmVisible,
  handleDelete,
  chartKey,
  incrementChartKey,
  analysis,
  workflowActions,
  handleRefresh,
  handleEdit,
  versions,
  viewClient,
  isClientView,
  targetAdjustmentType,
  targetAdjustmentName,
  initialPaneOneAvailable,
  initialPaneTwoAvailable,
  initialPaneThreeAvailable
}) => {
  const TableComponent = tableForDocumentCode(documentCode);
  const ChartComponent = chartForDocumentCode(documentCode);

  if (hasError) {
    return <StructuredAnalysisErrorCard />;
  }

  if (isLoading) {
    return <Loader dataTestId="structured-loading-indicator" />;
  }

  return analysis ? (
    <>
      <ModalConfirmation
        show={isConfirmVisible}
        onCancel={() => setIsConfirmVisible(false)}
        onConfirm={handleDelete}
        confirmTitle={`Confirm ${STRUCTURED_DISPLAY_NAME_BY_CODE[documentCode]} Delete`}
        confirmationMessage="This Analysis will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />

      <TriPane
        onChange={incrementChartKey}
        initialPaneOneAvailable={initialPaneOneAvailable}
        initialPaneTwoAvailable={initialPaneTwoAvailable}
        initialPaneThreeAvailable={initialPaneThreeAvailable}
      >
        <AnalysisControlsHeader
          isShared={analysis.shareWithAll}
          workflowActions={workflowActions}
          onRefresh={handleRefresh}
          onEdit={handleEdit}
          onDelete={() => setIsConfirmVisible(true)}
          versions={versions}
          analysis={analysis}
          viewClient={viewClient}
        >
          <TriPane.Controls />
        </AnalysisControlsHeader>

        <div className="structured-analysis-view">
          <div className="tri-pane-container" data-testid="tri-pane-display">
            <TriPane.HeaderContent>
              <StructuredAnalysisHeader
                title={STRUCTURED_DISPLAY_NAME_BY_CODE[documentCode]}
                projectId={projectId}
                analysis={analysis}
                isOnClientVersion={viewClient.isOnClientVersion}
              />
            </TriPane.HeaderContent>
            <TriPane.PaneOne>
              <div className="scrollable structured-table">
                <TableComponent
                  isClientView={isClientView}
                  contentId={analysis.contentThreadId}
                  contentVersionNumber={analysis.versionNumber}
                  projectId={projectId}
                  targetAdjustmentType={targetAdjustmentType}
                  targetAdjustmentName={targetAdjustmentName}
                />
              </div>
            </TriPane.PaneOne>
            <TriPane.PaneTwo>
              <ChartComponent key={chartKey} />
            </TriPane.PaneTwo>
            <TriPane.PaneThree>
              <AnalysisDescription
                isViewingSharedVersion={
                  analysis.versionStatus ===
                  Constants.VERSION_STATUS.SHARED.value
                }
                lightModeVariant
              />
            </TriPane.PaneThree>
            <TriPane.SourceContent>
              <SourceDisplay sourceText={analysis.source} />
              <NotesDisplay text={analysis.notes} />
              <TagList tags={analysis.tags} testId="analysis-tag-list" />
            </TriPane.SourceContent>
          </div>
        </div>
      </TriPane>
      <ConnectedContent
        classModifier="structured-analysis"
        projectId={projectId}
        contentId={analysis.contentId}
      />
      <ClientManipulationBar />
    </>
  ) : null;
};

StructuredAnalysisView.propTypes = {
  projectId: PropTypes.string.isRequired,
  documentCode: PropTypes.string.isRequired,
  hasError: PropTypes.bool,
  isLoading: PropTypes.bool,
  isConfirmVisible: PropTypes.bool.isRequired,
  setIsConfirmVisible: PropTypes.func.isRequired,
  handleDelete: PropTypes.func.isRequired,
  chartKey: PropTypes.number.isRequired,
  incrementChartKey: PropTypes.func,
  analysis: PropTypes.object,
  workflowActions: PropTypes.array.isRequired,
  handleRefresh: PropTypes.func.isRequired,
  handleEdit: PropTypes.func.isRequired,
  versions: PropTypes.array,
  viewClient: PropTypes.object.isRequired,
  isClientView: PropTypes.bool,
  targetAdjustmentType: PropTypes.string,
  targetAdjustmentName: PropTypes.string,
  initialPaneOneAvailable: PropTypes.bool,
  initialPaneTwoAvailable: PropTypes.bool,
  initialPaneThreeAvailable: PropTypes.bool
};

StructuredAnalysisView.defaultProps = {
  hasError: false,
  isLoading: false,
  analysis: undefined,
  isClientView: false,
  targetAdjustmentType: null,
  targetAdjustmentName: null,
  initialPaneOneAvailable: true,
  initialPaneTwoAvailable: true,
  initialPaneThreeAvailable: true,
  versions: null,
  incrementChartKey: () => {}
};

export default StructuredAnalysisView;
