import { createAction } from '@reduxjs/toolkit';
import { CALC_ENGINE_ACTION_PREFIX } from '../utilities/constants';

export const timePeriodMetadataIngested = createAction(
  `${CALC_ENGINE_ACTION_PREFIX}/timePeriodMetadataIngested`,
  function prepare(documentCode, timePeriodFlags) {
    return {
      payload: { documentCode, timePeriodFlags }
    };
  }
);
