import React from 'react';
import { Link } from 'react-router-dom';
import { Button, Icon } from '@pwc/pwc-deals-ui';
import './EditNarrativeCard.scss';
import { sanitize } from '../../../common/services/sanitizeService';

const EditNarrativeCard = ({
  narrative,
  testId,
  getViewPath,
  parentContentId,
  linkNarrative
}) => {
  const currentNarrative = narrative;
  const contentIsLinked = (narrativeLinks, linkedId) => {
    if (narrativeLinks && narrativeLinks.length) {
      return narrativeLinks.some(
        link =>
          link.id.toString() === linkedId.toString() && link.linked !== false
      );
    }
    return false;
  };

  const link = () => {
    const id = parseInt(parentContentId, 10);
    const contentLink = narrative.links.find(l => l.id === id) || { id };
    const filteredLinks = narrative.links.filter(_ => _.id !== id);
    currentNarrative.links = [
      ...filteredLinks,
      { ...contentLink, linked: true }
    ];
    linkNarrative(currentNarrative);
  };

  const unlink = () => {
    currentNarrative.links.find(
      l => l.id === parseInt(parentContentId, 10)
    ).linked = false;
    linkNarrative(currentNarrative);
  };

  return (
    <div
      data-testid="narrative-card"
      className={`narrative-card ${
        contentIsLinked(narrative.links, parentContentId)
          ? 'narrative-card-connected'
          : 'narrative-card-disconnected'
      }`}
    >
      <Link
        to={getViewPath(narrative.contentThreadId)}
        className="narrative-card-title"
        data-testid={`narrative-card-title-${testId}`}
      >
        {narrative.title}
      </Link>
      {narrative.body && (
        <div
          className="narrative-card-body truncate"
          data-testid={`narrative-card-body-${testId}`}
          dangerouslySetInnerHTML={{ __html: sanitize(narrative.body) }}
        />
      )}
      <div
        className="link-content-buttons"
        data-testid={`narrative-card-link-buttons-${testId}`}
      >
        {contentIsLinked(narrative.links, parentContentId) ? (
          <Button
            type="button"
            className="button-outline button-small mr-4"
            onClick={unlink}
            data-testid="btn-disconnect-content"
          >
            <Icon
              name="hyperlink-remove"
              width={16}
              className="fill icon-orange"
            />
            &nbsp; Disconnect
          </Button>
        ) : (
          <Button
            type="button"
            className="button-block button-small mr-4"
            onClick={link}
            data-testid="btn-connect-content"
          >
            <Icon name="hyperlink" width={16} className="fill" />
            &nbsp; Connect
          </Button>
        )}
        {narrative.shareWithAll && (
          <div className="shared-pill" data-testid="shared-with-client-icon">
            <Icon name="view" width={20} className="fill icon-black" />
          </div>
        )}
      </div>
    </div>
  );
};

export default EditNarrativeCard;
