import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { useSelector } from 'react-redux';
import authorizationService from '../common/services/security/authorizationService';
import ProjectDetails from '../features/project/ViewProject/ProjectDetails';
import AdminProjectList from '../features/admin/AdminProjectList/AdminProjectList';
import CreateProject from '../features/project/CreateProject/CreateProject'; // TODO: Remove use of Beta
import PrivateRoute from '../features/security/PrivateRoute';
import NonProjectContentRouter from './NonProjectContentRouter';
import { getUserTermsAndConditionsHasAccepted } from '../common/redux/userTermsAndConditions/userTermsAndConditionsSlice';

const ContentRouter = () => {
  const tAndCHasAccepted = useSelector(getUserTermsAndConditionsHasAccepted);

  return (
    <Switch>
      {authorizationService.isAdmin() && (
        <PrivateRoute
          exact
          path="/admin"
          component={AdminProjectList}
          tAndCHasAccepted
        />
      )}
      <PrivateRoute
        exact
        path="/projects/create-project"
        component={CreateProject}
        tAndCHasAccepted={tAndCHasAccepted}
      />
      <PrivateRoute
        path="/projects/:projectId"
        component={ProjectDetails}
        tAndCHasAccepted={tAndCHasAccepted}
      />
      <Route>
        <NonProjectContentRouter />
      </Route>
    </Switch>
  );
};

export default ContentRouter;
