import { createReducer } from '@reduxjs/toolkit';
import adjustmentsWithTogglesForDocumentUpdated from '../actions/adjustmentsWithTogglesForDocumentUpdated';
import { extractAdjustmentLines } from '../utilities/extractAdjustmentLines';
import {
  summateAdjustments,
  summateRevenueAdjustments
} from '../utilities/adjustmentSummationUtilities';
import resetCalculationDataForDocument from '../actions/resetCalculationDataForDocument';
import { clearAllProjectData } from '../actions/clearAllProjectData';

const isInvalidPayload = payload =>
  payload === undefined ||
  payload.documentCode === undefined ||
  payload.adjustments === undefined;

const toggledAdjustmentSumsByDocument = createReducer(
  {},
  {
    [adjustmentsWithTogglesForDocumentUpdated]: (state, action) => {
      if (isInvalidPayload(action.payload)) {
        return state;
      }

      const {
        documentCode,
        adjustments: adjustmentsForDocument
      } = action.payload;
      const adjustmentLines = extractAdjustmentLines(adjustmentsForDocument);
      const summedAdjustments = summateAdjustments(adjustmentLines);
      const revenueAdjustments = summateRevenueAdjustments(adjustmentLines);

      return {
        ...state,
        [documentCode]: {
          ...summedAdjustments,
          ...revenueAdjustments
        }
      };
    },
    [resetCalculationDataForDocument]: (state, action) => {
      if (!action.payload || !action.payload.documentCode) {
        return state;
      }
      const { documentCode } = action.payload;
      delete state[documentCode];
      return state;
    },
    [clearAllProjectData]: () => ({})
  }
);

export default toggledAdjustmentSumsByDocument;
