/* eslint-disable react/jsx-wrap-multilines */
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

import EmptyHypothesesCard from '../EmptyHypotheses/EmptyHypothesesCard';
import ModalConfirmation from '../../../common/components/ModalConfirmation';
import { editHypothesisPath } from '../../../common/services/urlService';
import HypothesisGridCard from '../HypothesisGridCard/HypothesisGridCard';
import NoContentForTagsCard from '../../../common/components/ContentActions/NoContentForTagsCard/NoContentForTagsCard';

const HypothesesGrid = ({
  kfasFilteredByTags,
  onDelete,
  projectId,
  projectHasKfas
}) => {
  const history = useHistory();
  const noop = () => {};
  const [
    isDeleteConfirmationVisible,
    setIsDeleteConfirmationVisible
  ] = useState(false);
  const [processDeleteFn, setProcessDeleteFn] = useState(() => noop);
  const handleHideConfirmation = () => setIsDeleteConfirmationVisible(false);

  const handleEditHypothesis = hypothesisId =>
    history.push(editHypothesisPath(projectId, hypothesisId));

  const handleDeleteConfirmation = id => {
    const processConfirmedDeleteForId = () => {
      setIsDeleteConfirmationVisible(false);
      onDelete(id);
    };
    setProcessDeleteFn(() => processConfirmedDeleteForId);
    setIsDeleteConfirmationVisible(true);
  };

  const hasDataForTagFilters = kfasFilteredByTags.length > 0;
  return (
    <>
      <ModalConfirmation
        show={isDeleteConfirmationVisible}
        onCancel={handleHideConfirmation}
        onConfirm={processDeleteFn}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage="This Key Focus Area will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />

      <div className="list-grid list-grid-hypotheses" data-testid="list-grid">
        {!projectHasKfas && <EmptyHypothesesCard projectId={projectId} />}
        {!hasDataForTagFilters && projectHasKfas && <NoContentForTagsCard />}
        {hasDataForTagFilters && (
          <>
            {kfasFilteredByTags.map((h, i) => (
              <HypothesisGridCard
                hypothesis={h}
                projectId={projectId}
                onEditMenuClick={handleEditHypothesis}
                onDeleteMenuClick={handleDeleteConfirmation}
                index={i}
                key={`content-grid-card-${h.contentThreadId}`}
              />
            ))}
          </>
        )}
      </div>
    </>
  );
};

export default HypothesesGrid;
