import React from 'react';
import { DateTime } from 'luxon';
import './PageLastUpdated.scss';

export const PageLastUpdated = ({ dateModified, modifiedBy, version }) => (
  <p className="page-last-updated" data-testid="page-last-updated">
    <span className="text-lastupdated bold">
      {version && `v${version} `}Last updated{' '}
    </span>
    <span>
      <span className="text-timestamp">
        {DateTime.fromISO(dateModified, {
          zone: 'utc'
        })
          .toLocal()
          .toFormat('H:mm ZZZZ - d LLL yyyy')}
      </span>
      {modifiedBy && ` by ${modifiedBy}`}
    </span>
  </p>
);
