import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { Constants } from '@pwc/pwc-deals-web-common';
import CreateHypothesis from './CreateHypothesis/CreateHypothesis';
import EditHypothesis from './EditHypothesis/EditHypothesis';
import ViewHypothesis from './ViewHypothesis/ViewHypothesis';
import HypothesesListContainer from './HypothesesListContainer';
import ProjectPermissionRoute from '../security/ProjectPermissionRoute';
import authorizationService from '../../common/services/security/authorizationService';

const HypothesesContentRouter = () => {
  return (
    <Switch>
      <Route
        exact
        path="/projects/:projectId/key-focus-areas"
        component={HypothesesListContainer}
      />

      <Route
        exact
        path="/projects/:projectId/key-focus-areas/:hypothesisId/view"
        component={ViewHypothesis}
      />

      <ProjectPermissionRoute
        exact
        permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
        isAuthorized={authorizationService.hasValidToken}
        path="/projects/:projectId/key-focus-areas/create"
        component={CreateHypothesis}
      />

      <ProjectPermissionRoute
        exact
        permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
        isAuthorized={authorizationService.hasValidToken}
        path="/projects/:projectId/key-focus-areas/:hypothesisId/edit"
        component={EditHypothesis}
      />
    </Switch>
  );
};

export default HypothesesContentRouter;
