import { models } from 'powerbi-client';
import { getPowerBiTheme } from '../../contentLibrary/IngestLibraryItemModal/getPowerBiTheme';

export const computeContainerDimensions = (height, width, isSinglePage) => {
  const BOTTOM_TOOLBAR_HEIGHT = 36;
  const BOTTOM_SCROLLBAR_HEIGHT = 8;
  const RIGHT_TOOLBAR_WIDTH = 37;

  const computedHeight = isSinglePage
    ? height + BOTTOM_SCROLLBAR_HEIGHT
    : height + BOTTOM_SCROLLBAR_HEIGHT + BOTTOM_TOOLBAR_HEIGHT;

  const computedWidth = isSinglePage ? width : width + RIGHT_TOOLBAR_WIDTH;

  return [computedHeight, computedWidth];
};

export const buildPowerBiReportConfig = ({
  embedToken,
  embedUrl,
  pageName,
  reportId,
  isDarkMode
}) => {
  const config = {
    type: 'report',
    tokenType: models.TokenType.Embed,
    accessToken: embedToken,
    embedUrl,
    id: reportId,
    permissions: models.Permissions.All,
    settings: {
      panes: {
        pageNavigation: {
          visible: !pageName
        },
        filters: {
          expanded: false,
          visible: !pageName
        }
      },
      layoutType: models.LayoutType.Custom,
      customLayout: {
        displayOption: models.DisplayOption.ActualSize
      }
    },
    pageName,
    theme: getPowerBiTheme(isDarkMode)
  };

  return config;
};
