import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Button, Icon } from '@pwc/pwc-deals-ui';
import { v4 as uuidv4 } from 'uuid';
import LinkedAdjustment from './LinkedAdjustment';
import Separator from '../../../../../common/components/Separator';
import './LinkedAdjustments.scss';

const nameBuilder = index => `linkedAdjustments[${index}]`;

const LinkedAdjustments = ({
  documentCode,
  adjustmentOptions,
  formValues,
  targetDocumentOptions,
  setFieldValue
}) => {
  const [
    enableAddNewLinkedAdjustment,
    setEnableAddNewLinkedAdjustment
  ] = useState(false);

  const addNewInProgressLinkedAdjustment = () => {
    setEnableAddNewLinkedAdjustment(true);
  };

  const finishAddingNewLinkedAdjustment = adjustmentToAdd => {
    setFieldValue('linkedAdjustments', [
      ...formValues,
      { newId: uuidv4(), ...adjustmentToAdd }
    ]);

    setEnableAddNewLinkedAdjustment(false);
  };

  const isAddNewAdjustmentLinkDisabled = () =>
    enableAddNewLinkedAdjustment === undefined;

  const onDelete = valueToDelete => {
    const adjustmentsForUpdate = formValues.filter(
      adj => adj.id !== valueToDelete.id || adj.newId !== valueToDelete.newId
    );

    setFieldValue('linkedAdjustments', adjustmentsForUpdate);
  };

  return (
    <>
      <div
        className="linked-adjustment-container"
        data-testid="linked-adjustments-container"
      >
        <div className="linked-adjustments-title">
          <Icon
            name="link"
            width={21}
            className="icon fill connect-content-icon"
          />
          Link Adjustments
        </div>
        {formValues.length === 0 && (
          <div className="linked-adjustment-description helper-text">
            <Icon
              name="information"
              className="fill icon-yellow helper-icon"
              width={18}
              data-testid="no-linked-adjustments"
            />{' '}
            &nbsp;No analysis adjustments have been linked
          </div>
        )}

        {(enableAddNewLinkedAdjustment || formValues.length > 0) && (
          <Separator />
        )}

        {formValues.map((value, index) => (
          // fragment shorthand does not permit specifying a key prop.
          <React.Fragment key={nameBuilder(index)}>
            <LinkedAdjustment
              documentCode={documentCode}
              name={nameBuilder(index)}
              key={value.id || value.newId}
              value={value}
              adjustmentOptions={adjustmentOptions}
              targetDocumentOptions={targetDocumentOptions}
              editMode={false}
              onToggleEditMode={() => undefined}
              onDelete={() => onDelete(value)}
              onChange={updatedValue =>
                finishAddingNewLinkedAdjustment(index, updatedValue)
              }
            />
            <Separator key={`separator-${nameBuilder(index)}`} />
          </React.Fragment>
        ))}
        {enableAddNewLinkedAdjustment && (
          // fragment shorthand does not permit specifying a key prop.
          <React.Fragment key={nameBuilder(formValues.length + 1)}>
            <LinkedAdjustment
              documentCode={documentCode}
              name={nameBuilder(formValues.length + 1)}
              value={undefined}
              adjustmentOptions={adjustmentOptions}
              targetDocumentOptions={targetDocumentOptions}
              editMode
              onToggleEditMode={() => undefined}
              onDelete={() => undefined}
              onChange={updatedValue =>
                finishAddingNewLinkedAdjustment(updatedValue)
              }
            />
            <Separator
              key={`separator-${nameBuilder(formValues.length + 1)}`}
            />
          </React.Fragment>
        )}
      </div>
      <Button
        type="button"
        data-testid="add-new-linked-adjustment"
        className="add-link button-link"
        onClick={() => addNewInProgressLinkedAdjustment()}
        disabled={isAddNewAdjustmentLinkDisabled()}
      >
        <Icon name="add" className="fill icon-orange helper-icon" width={21} />
        <span className="menu-item-title">New Link</span>
      </Button>
    </>
  );
};

LinkedAdjustments.propTypes = {
  documentCode: PropTypes.string.isRequired,
  adjustmentOptions: PropTypes.shape({
    QofE: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
    ),
    NWC: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
    ),
    DDL: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
    )
  }),
  formValues: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      newId: PropTypes.string, // Used to track if a value was added but not Published
      sourceAdjustment: PropTypes.string,
      targetDocument: PropTypes.string
    })
  ),
  targetDocumentOptions: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
  ),
  setFieldValue: PropTypes.func.isRequired
};

LinkedAdjustments.defaultProps = {
  adjustmentOptions: { QofE: [], NWC: [], DDL: [] },
  targetDocumentOptions: [],
  formValues: []
};

export default LinkedAdjustments;
