import React from 'react';
import { Icon } from '@pwc/pwc-deals-ui';
import PropTypes from 'prop-types';

const AdjustmentGroupExpansionButton = ({ title, expanded, onClick }) => (
  <button
    type="button"
    className="adjustment-group__toggle-button"
    onClick={onClick}
    data-testid="adjustment-rollup-toggle-button"
  >
    <span
      aria-hidden="true"
      data-testid="expanded-indicator-icon"
      className={`icon-expanded ${expanded ? 'asc' : 'desc'}`}
    >
      <Icon
        name="desc"
        width="10"
        className="fill icon-orange"
        viewBox="-4 0 32 32"
      />
    </span>
    <span>{title}</span>
  </button>
);

AdjustmentGroupExpansionButton.propTypes = {
  title: PropTypes.string.isRequired,
  expanded: PropTypes.bool.isRequired,
  onClick: PropTypes.func.isRequired
};

export default AdjustmentGroupExpansionButton;
