import React, { useState, Fragment, useEffect, useCallback } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import { xor } from 'lodash';
import { FieldArray, Field, useFormikContext } from 'formik';

import { Icon } from '@pwc/pwc-deals-ui';

import ClearableDisplayPill from '../../../../common/components/Pills/ClearableDisplayPill';
import HiddenInput from '../../../../common/components/HiddenInput/HiddenInput';
import ValidateTag from '../../ValidateTag';
import { getAllTags } from '../../tagService';
import MultiSelectSearch from '../../../../common/components/MultiSelectSearch/MultiSelectSearch';
import './TagEditor.scss';
import { useClickedOutsideOfRef } from '../../../../common/hooks/useClickedOutsideOfRef';
import { fetchAllTags } from '../../../analyses/analysisSlice';

const TAGS_FIELD_NAME = 'tags';

const TagEditor = ({
  projectId,
  onSelect,
  tagsRight,
  title,
  subtitle,
  allowCreateTags,
  placeholderText
}) => {
  const [fetchedTagOptions, setFetchedTagOptions] = useState([]);
  const [tagOptions, setTagOptions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedTagsText, setSelectedTagsText] = useState([]);
  const [search, setSearch] = useState('');
  const [error, setError] = useState();
  const [dropdownOpen, setDropdownOpen] = useState(false);

  const { getFieldMeta, setFieldValue } = useFormikContext();

  const dispatch = useDispatch();

  const setTagFormValues = useCallback(
    tags => {
      setFieldValue(TAGS_FIELD_NAME, tags);
    },
    [setFieldValue]
  );

  const getTagFormValue = useCallback(
    () => getFieldMeta(TAGS_FIELD_NAME).value,
    [getFieldMeta]
  );

  useEffect(() => {
    const tagsFieldValue = getTagFormValue();
    setSelectedTagsText([...(tagsFieldValue || [])]);
  }, [getTagFormValue]);

  useEffect(() => {
    let isMounted = true;

    if (dropdownOpen && loading === true) {
      const fetch = async () => {
        const response = await getAllTags(projectId);

        if (isMounted) {
          setFetchedTagOptions(response);
          dispatch(fetchAllTags({ tags: response }));
          setLoading(false);
        }
      };
      fetch();
    }

    return () => {
      isMounted = false;
    };
  }, [projectId, dropdownOpen, loading, dispatch]);

  useEffect(() => {
    const fetchedTagOptionsByText = fetchedTagOptions.reduce(
      (opts, opt) => ({ ...opts, [opt.text]: opt }),
      {}
    );

    const newTagOptions = selectedTagsText
      .filter(text => !fetchedTagOptionsByText[text])
      .map(text => ({ text }));

    setTagOptions([...fetchedTagOptions, ...newTagOptions]);
  }, [fetchedTagOptions, selectedTagsText]);

  const updateSelectedTags = updatedSelectedTagTexts => {
    const errors = updatedSelectedTagTexts.map(ValidateTag);
    const hasError = errors.some(e => !!e);

    if (hasError) {
      return setError(errors);
    }
    const validatedUpdatedSelectedTagTexts = errors
      .map((err, i) => (err ? null : updatedSelectedTagTexts[i]))
      .flat();
    setSelectedTagsText(validatedUpdatedSelectedTagTexts);
    setTagFormValues(validatedUpdatedSelectedTagTexts);
    setError('');
    setSearch('');
    return validatedUpdatedSelectedTagTexts;
  };

  const removeTag = tagIndex => {
    const updatedSelectedTagTexts = [
      ...selectedTagsText.slice(0, tagIndex),
      ...selectedTagsText.slice(tagIndex + 1)
    ];
    setSelectedTagsText(updatedSelectedTagTexts);
    setTagFormValues(updatedSelectedTagTexts);

    if (onSelect) {
      onSelect(
        updatedSelectedTagTexts,
        !!xor(getTagFormValue(), updatedSelectedTagTexts)
      );
    }
  };

  const handleEscapeKey = ev => {
    if (ev.keyCode === 27) {
      setDropdownOpen(false);
    }
  };

  const onSubmit = updatedSelectedTagTexts => {
    setDropdownOpen(false);
    const validatedUpdatedSelectedTagTexts = updateSelectedTags(
      updatedSelectedTagTexts
    );

    if (onSelect) {
      onSelect(
        validatedUpdatedSelectedTagTexts,
        !!xor(getTagFormValue(), validatedUpdatedSelectedTagTexts)
      );
    }
  };

  const ref = useClickedOutsideOfRef(
    wasClickedOutside =>
      wasClickedOutside && setDropdownOpen(!wasClickedOutside)
  );

  return (
    <div className="tageditor">
      {title && (
        <div className="title">
          <Icon
            name="tag"
            className="fill"
            width={21}
            data-testid="draft-icon"
          />
          Tags (optional)
        </div>
      )}

      {subtitle && (
        <div className="description">
          <Icon
            name="information"
            className="fill icon-yellow"
            width={21}
            data-testid="draft-icon"
          />
          Tag content by report section, category and team, to make it easy to
          find and use.
          <div className="instructions">
            Tags cannot include any special characters. Maximum 30 characters.
          </div>
        </div>
      )}

      <FieldArray
        name="tags"
        render={arrayHelpers => (
          <>
            <div className="a-form-error">{error}</div>
            <div
              className="input-field-container"
              data-tags-right={`${tagsRight}`}
              ref={ref}
            >
              <span className="filter-search-icon">
                <Icon name="search" width={18} className="outline icon-grey" />
              </span>
              <Field
                id="tag-filter-search"
                name="tag"
                className={`input-field ${dropdownOpen ? 'active' : ''}`}
                placeholder={placeholderText}
                error={error}
                autoComplete="off"
                value={search}
                onChange={e => {
                  setSearch(e.target.value);
                }}
                data-testid="text-input-tag-search"
                onFocus={() => {
                  setDropdownOpen(true);
                }}
                onKeyDown={e => handleEscapeKey(e)}
              />

              {dropdownOpen && (
                <MultiSelectSearch
                  isLoading={loading}
                  searchCriteria={search}
                  options={tagOptions}
                  onSubmit={updatedSelectedTagTexts =>
                    onSubmit(updatedSelectedTagTexts, arrayHelpers)
                  }
                  selectedTags={selectedTagsText}
                  allowCreateTags={allowCreateTags}
                  innerIdWhitelist={['tag-filter-search']}
                  closeDropdown={() => setDropdownOpen(false)}
                />
              )}
            </div>
            {selectedTagsText && (
              <div className="pills" data-tags-right={`${tagsRight}`}>
                {selectedTagsText.map((t, index) => (
                  <Fragment key={t}>
                    <Field name={`tag[${index}]`} component={HiddenInput} />
                    <ClearableDisplayPill
                      onClearClick={() => removeTag(index, arrayHelpers)}
                      tag={t}
                      index={index}
                    />
                  </Fragment>
                ))}
              </div>
            )}
          </>
        )}
      />
    </div>
  );
};

TagEditor.propTypes = {
  projectId: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired,
  onSelect: PropTypes.func,
  tagsRight: PropTypes.bool,
  title: PropTypes.bool,
  subtitle: PropTypes.bool,
  allowCreateTags: PropTypes.bool,
  placeholderText: PropTypes.string
};

TagEditor.defaultProps = {
  onSelect: undefined,
  tagsRight: false,
  title: true,
  subtitle: true,
  allowCreateTags: true,
  placeholderText: 'Search or create a new tag'
};

export default TagEditor;
