import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { Constants } from '@pwc/pwc-deals-web-common';
import InsightsListContainer from './InsightsListContainer';
import CreateInsight from './CreateInsight/CreateInsight';
import ViewInsight from './ViewInsight/ViewInsight';
import EditInsight from './EditInsight/EditInsight';
import ProjectPermissionRoute from '../security/ProjectPermissionRoute';
import authorizationService from '../../common/services/security/authorizationService';

const InsightsContentRouter = () => {
  const path = '/projects/:projectId/insights';

  return (
    <Switch>
      <Route exact path={path} component={InsightsListContainer} />

      <Route exact path={`${path}/:insightId/view`} component={ViewInsight} />

      <ProjectPermissionRoute
        exact
        permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
        isAuthorized={authorizationService.hasValidToken}
        path={`${path}/create`}
        component={CreateInsight}
      />

      <ProjectPermissionRoute
        exact
        permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
        isAuthorized={authorizationService.hasValidToken}
        path={`${path}/:insightId/edit`}
        component={EditInsight}
      />
    </Switch>
  );
};

export default InsightsContentRouter;
