import React, { useCallback, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Constants } from '@pwc/pwc-deals-web-common';
import cloneDeep from 'lodash/cloneDeep';
import uniqBy from 'lodash/uniqBy';
import { structuredRoutes } from '../../../common/services/urlService';
import {
  deleteAnalysis,
  getAllAnalyses,
  getPagedAnalyses
} from '../analysesService';
import { deleteStructuredDocument } from '../../../common/engine/services/structuredAnalysisService';
import AnalysesView from './AnalysesView';
import {
  getUserSettingsSelector,
  setUserSettings
} from '../../../common/redux/userSettings/userSettingsSlice';
import useWindowSize from '../../../common/hooks/useWindowSize';

// AnalysesGrid expects each object in the data passed to it
// to have an id for use as a key and a documentCode for
// the delete endpoint
const addFrontendProperties = analysis => {
  switch (analysis.contentSubType) {
    case Constants.structuredDocumentSubtypeCodes[
      Constants.QUALITY_OF_EARNINGS
    ]:
      return {
        ...analysis,
        frontendIdentifier: structuredRoutes[Constants.QUALITY_OF_EARNINGS],
        code: Constants.QUALITY_OF_EARNINGS
      };
    case Constants.structuredDocumentSubtypeCodes[
      Constants.NET_WORKING_CAPITAL
    ]:
      return {
        ...analysis,
        frontendIdentifier: structuredRoutes[Constants.NET_WORKING_CAPITAL],
        code: Constants.NET_WORKING_CAPITAL
      };
    case Constants.structuredDocumentSubtypeCodes[Constants.DEBT_AND_DEBT_LIKE]:
      return {
        ...analysis,
        frontendIdentifier: structuredRoutes[Constants.DEBT_AND_DEBT_LIKE],
        code: Constants.DEBT_AND_DEBT_LIKE
      };
    default:
      return { ...analysis, frontendIdentifier: analysis.contentThreadId };
  }
};

const pageSize = 10;

const AnalysesContainer = () => {
  const routeParams = useParams();
  const { projectId } = routeParams;
  const [combinedAnalyses, setCombinedAnalyses] = useState(null);
  const [itemPage, setItemPage] = useState(0);
  const [itemCount, setItemCount] = useState(null);
  const [isFetching, setIsFetching] = useState(null);
  const userSettings = useSelector(getUserSettingsSelector);
  const dispatch = useDispatch();
  const { height } = useWindowSize();

  const canBeScrolled = useCallback(
    () => height && document.body.scrollHeight > height,
    [height]
  );
  const hasMoreItems = useCallback(
    () => combinedAnalyses && itemCount && combinedAnalyses.length < itemCount,
    [combinedAnalyses, itemCount]
  );

  const compileAnalyses = useCallback(
    (items, rowCount, currentPage) => {
      setItemCount(rowCount);
      setItemPage(currentPage);
      if (currentPage !== 0) {
        const currentItems = combinedAnalyses
          ? cloneDeep(combinedAnalyses)
          : [];
        const newItems = uniqBy(currentItems.concat(items), 'contentThreadId');
        setCombinedAnalyses(newItems.map(addFrontendProperties));
      } else {
        const newItems = uniqBy(items, 'contentThreadId');
        setCombinedAnalyses(newItems.map(addFrontendProperties));
      }
      setIsFetching(false);
    },
    [combinedAnalyses]
  );

  const fetchPagedAnalyses = useCallback(
    async (page = 0) => {
      setIsFetching(true);
      const analysesResponse = await getPagedAnalyses(
        projectId,
        page,
        pageSize
      );
      const { items, rowCount, currentPage } = analysesResponse;
      compileAnalyses(items, rowCount, currentPage);
    },
    [projectId, compileAnalyses]
  );

  const fetchAllAnalyses = useCallback(
    async tagIds => {
      setIsFetching(true);
      const analysesResponse = await getAllAnalyses(projectId, tagIds);
      const { items, currentPage, rowCount } = analysesResponse;
      compileAnalyses(items, rowCount, currentPage);
    },
    [compileAnalyses, projectId]
  );

  useEffect(() => {
    if (userSettings && !userSettings.settings.viewGrid) {
      fetchAllAnalyses();
    } else {
      fetchPagedAnalyses();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (combinedAnalyses && combinedAnalyses.length > 0 && itemCount) {
      const isScrollable = canBeScrolled();
      const hasMore = hasMoreItems();
      if (isScrollable === false && hasMore === true && !isFetching) {
        fetchPagedAnalyses(itemPage + 1);
      }
    }
  }, [
    combinedAnalyses,
    height,
    itemCount,
    itemPage,
    fetchPagedAnalyses,
    canBeScrolled,
    hasMoreItems,
    isFetching
  ]);

  const handleReorder = orderedAnalyses => {
    setCombinedAnalyses(orderedAnalyses);
  };

  const toggleView = () => {
    setCombinedAnalyses(null);
    const {
      settings: { viewGrid }
    } = userSettings;
    const isGridView = !viewGrid;
    dispatch(
      setUserSettings({
        viewGrid: !viewGrid
      })
    );
    if (!isGridView) {
      fetchAllAnalyses();
    } else {
      fetchPagedAnalyses(itemPage);
    }
  };

  const onFilter = tagIds => {
    setCombinedAnalyses(null);
    fetchAllAnalyses(tagIds);
  };

  const handleDeleteAnalysis = async analysis => {
    const { contentThreadId, code, isUnStructured } = analysis;

    const request = isUnStructured
      ? deleteAnalysis(projectId, contentThreadId)
      : deleteStructuredDocument(projectId, code);
    const response = await request;

    if (response.data === true) {
      const itemIndex = combinedAnalyses.findIndex(
        item => item.contentThreadId === contentThreadId
      );
      if (itemIndex > -1) {
        const allItems = cloneDeep(combinedAnalyses);
        allItems.splice(itemIndex, 1);
        setCombinedAnalyses([...allItems]);
      }
    }
  };

  return (
    <div data-testid="analyses-container" className="analyses-container">
      <AnalysesView
        toggleView={toggleView}
        onFilter={onFilter}
        viewGrid={userSettings.settings.viewGrid}
        projectId={projectId}
        dataLength={combinedAnalyses && combinedAnalyses.length}
        currentPage={itemPage}
        pageSize={pageSize}
        hasMore={hasMoreItems()}
        isFetching={isFetching}
        next={fetchPagedAnalyses}
        fetchAllAnalyses={fetchAllAnalyses}
        analyses={combinedAnalyses}
        onDelete={handleDeleteAnalysis}
        onReorder={handleReorder}
      />
    </div>
  );
};

export default AnalysesContainer;
