import { HttpClient } from '@pwc/pwc-deals-web-common';
import authorizationService from './security/authorizationService';
import userTokenService from './security/userTokenService';

export const safeErrors = response => {
  return response && response.data && response.data.errors
    ? response.data.errors
    : [];
};

export const UserAuthClient = new HttpClient(
  process.env.REACT_APP_USER_ACCESS_SERVICE,
  async () => sessionStorage.getItem('jwtToken')
);

export const UserAccessTokenClient = new HttpClient(
  process.env.REACT_APP_USER_ACCESS_SERVICE
);

export const ProjectManagementHttpClient = projectId =>
  new HttpClient(
    process.env.REACT_APP_PROJECT_MANAGEMENT_API_BASE_PATH,
    async () => {
      authorizationService.updateProcessTime();
      return projectId
        ? userTokenService.getProjectToken(projectId)
        : sessionStorage.getItem('jwtToken');
    }
  );

export const ProjectStateManagementHttpClient = projectId =>
  new HttpClient(
    process.env.REACT_APP_PROJECT_STATE_MANAGEMENT_API_BASE_PATH,
    async () => {
      authorizationService.updateProcessTime();
      return projectId
        ? userTokenService.getProjectToken(projectId)
        : sessionStorage.getItem('jwtToken');
    }
  );

export const getProjectTokenBasedOnPath = path => {
  const projectId = path.startsWith('/')
    ? path.split('/')[2]
    : path.split('/')[1];
  return userTokenService.getProjectToken(projectId);
};

export const ProjectHttpClient = new HttpClient(
  process.env.REACT_APP_PROJECT_API_BASE_PATH,
  async path => {
    authorizationService.updateProcessTime();
    return getProjectTokenBasedOnPath(path);
  }
);

