import React from 'react';
import PropTypes from 'prop-types';
import PageTitle from '../../../../../common/components/PageTitle/PageTitle';
import { structuredAnalysisShape } from '../../../../../common/components/proptypes/commonProptypes';
import StatusContainer from '../../../../../common/components/StatusContainer/StatusContainer';

const StructuredAnalysisHeader = ({
  title,
  projectId,
  analysis,
  isOnClientVersion
}) => {
  return (
    <>
      <PageTitle title={title} />
      <StatusContainer
        projectId={projectId}
        content={analysis}
        className="analysis-pill-container"
        allowDraftPill={!isOnClientVersion}
      />
    </>
  );
};

StructuredAnalysisHeader.propTypes = {
  title: PropTypes.string.isRequired,
  projectId: PropTypes.string.isRequired,
  analysis: structuredAnalysisShape.isRequired,
  isOnClientVersion: PropTypes.bool
};

StructuredAnalysisHeader.defaultProps = {
  isOnClientVersion: false
};

export default StructuredAnalysisHeader;
