import React from 'react';
import PropTypes from 'prop-types';
import StructuredIngestionDocumentCard from '../StructuredAnalysisCard/StructuredAnalysisCard';
import EmptyStructuredIngestionDocumentCard from '../EmptyStructuredAnalysis/EmptyStructuredAnalysisCard';
import './StructuredAnalysisTable.scss';

const StructuredAnalysisTable = ({ projectId, structuredDocuments }) => {
  const hasDocuments = !!structuredDocuments.length;
  return (
    <>
      {!hasDocuments && <EmptyStructuredIngestionDocumentCard />}
      {hasDocuments && (
        <div>
          <div
            className="structured-analysis-add-from-template"
            data-testid="structured-analysis-table"
          >
            {structuredDocuments.map(document => (
              <div key={document.code}>
                <StructuredIngestionDocumentCard
                  projectId={projectId}
                  documentName={document.displayName}
                  documentCode={document.code}
                  showDetailLink={document.hasDetailView}
                />
              </div>
            ))}
          </div>
        </div>
      )}
    </>
  );
};

StructuredAnalysisTable.propTypes = {
  projectId: PropTypes.string,
  structuredDocuments: PropTypes.arrayOf(PropTypes.object)
};

StructuredAnalysisTable.defaultProps = {
  projectId: '',
  structuredDocuments: []
};

export default StructuredAnalysisTable;
