import CKEditor from '@ckeditor/ckeditor5-react';
import { ClassicEditor } from '@pwc/pwc-deals-rich-text-editor';
import { CharacterCount } from '@pwc/pwc-deals-ui';
import React, { useState } from 'react';
import editorConfig from './CKEditorConfig';
import './RichTextEditor.scss';

const RichTextEditor = ({
  richTextData,
  onChange,
  onBlur,
  onFocus,
  isFocused,
  label,
  labelRef,
  config = editorConfig,
  showCharactersCounter = false
}) => {
  const focusClass = isFocused ? ' focused' : '';
  const [characters, setCharacters] = useState(0);

  return (
    <div className={`text-input-fieldset fieldset d3-rich-text${focusClass}`}>
      <div
        data-testid="ckEditor"
        className="text-input-container basic allow-overflow"
      >
        {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
        {label && (
          <label
            className="text-input-control-label"
            htmlFor="ck-editor-component"
            ref={labelRef}
          >
            {label}
          </label>
        )}
        <CKEditor
          id="ck-editor-component"
          editor={ClassicEditor}
          config={config}
          data={richTextData}
          maxCharacters={650}
          onInit={ckeditor => {
            setCharacters(ckeditor.plugins.get('WordCount').characters);
          }}
          onChange={(_, editor) => {
            onChange(editor.getData());
            setCharacters(editor.plugins.get('WordCount').characters);
          }}
          onBlur={onBlur}
          onFocus={onFocus}
        />
        {showCharactersCounter && (
          <CharacterCount value={Array(characters).join('*')} maxLength={650} />
        )}
      </div>
    </div>
  );
};

export default RichTextEditor;
