import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams, useHistory } from 'react-router-dom';
import isEmpty from 'lodash/isEmpty';
import { Constants, ErrorUtilities } from '@pwc/pwc-deals-web-common';

import updateEntityVersion from '../../../../common/actions/updateEntityVersion';
import {
  editAnalysisPath2,
  analysesListPath2
} from '../../../../common/services/urlService';
import '../../common/analysis-detail-screen.scss';
import '../../common/analysis-top-bar-container.scss';
import FormLayout from '../../../../common/components/layouts/FormLayout';
import AnalysisControlsHeader from '../../common/AnalysisControlsHeader';
import AnalysisDetailConnectedContent from '../../../narratives/ConnectedContent/AnalysisDetailConnectedContent';
import {
  getAnalysisStatus,
  resetAnalysis,
  selectAnalysisContent,
  getAnalysisIsShared,
  selectAnalysisVersions,
  selectDraftVersion,
  selectSharedVersion
} from '../../analysisSlice';
import {
  loadAnalysisVersions,
  loadAnalysisWithDraftContent,
  loadAnalysisWithSharedContent
} from '../../analysisSliceThunk';
import { deleteAnalysis, updateAnalysisStatus } from '../../analysesService';
import ModalConfirmation from '../../../../common/components/ModalConfirmation';
import { getWorkflowStatus } from '../../../workflow/workflowService';
import TagList from '../../common/TagList/TagList';
import { useApprovalWorkflow } from '../../../../common/hooks/useApprovalWorkflow';
import { getViewClientInfo } from '../../../../common/helpers/viewClientVersion';
import AnalysisDetailScreen from './AnalysisDetailScreen';
import { showMoreMenuError } from '../../../../common/redux/errors/errorsSlice';
import './AnalysisDetailScreenContainer.scss';

const AnalysisDetailScreenContainer = () => {
  const { projectId, analysisId } = useParams();
  const history = useHistory();
  const dispatch = useDispatch();
  const analysisContent = useSelector(selectAnalysisContent);
  const isAnalysisShared = useSelector(getAnalysisIsShared);
  const { isLoading } = useSelector(getAnalysisStatus);
  const [
    isDeleteConfirmationVisible,
    setIsDeleteConfirmationVisible
  ] = useState(false);
  const selectedVersions = useSelector(selectAnalysisVersions);
  const draftVersion = useSelector(selectDraftVersion);
  const sharedVersion = useSelector(selectSharedVersion);

  const handleEdit = () => {
    history.push(editAnalysisPath2(projectId, analysisId));
  };

  const getDraftContent = useCallback(() => {
    const { contentId } = draftVersion;
    dispatch(loadAnalysisWithDraftContent(projectId, contentId));
  }, [dispatch, draftVersion, projectId]);

  const getSharedContent = useCallback(() => {
    const { contentId } = sharedVersion;
    dispatch(loadAnalysisWithSharedContent(projectId, contentId));
  }, [dispatch, sharedVersion, projectId]);

  useEffect(() => {
    function loadStatus() {
      dispatch(loadAnalysisVersions(projectId, analysisId));
    }
    loadStatus();
    return () => dispatch(resetAnalysis());
  }, [dispatch, projectId, analysisId]);

  useEffect(() => {
    if (selectedVersions) {
      if (draftVersion && !isEmpty(draftVersion)) {
        getDraftContent();

        dispatch(updateEntityVersion(draftVersion.versionNumber));
      } else if (sharedVersion && !isEmpty(sharedVersion)) {
        getSharedContent();

        dispatch(updateEntityVersion(sharedVersion.versionNumber));
      }
    }
  }, [
    analysisId,
    draftVersion,
    projectId,
    selectedVersions,
    sharedVersion,
    getDraftContent,
    getSharedContent,
    dispatch
  ]);

  const handleDelete = async () => {
    await deleteAnalysis(projectId, analysisContent.contentThreadId);

    history.push(analysesListPath2(projectId));
  };

  const workflowActions = useApprovalWorkflow(
    getWorkflowStatus,
    async (_projectId, contentId, step) => {
      try {
        await updateAnalysisStatus(_projectId, contentId, step);
      } catch (e) {
        if (ErrorUtilities.hasLibraryItemCorruptedError(e)) {
          dispatch(
            showMoreMenuError({
              itemId: contentId,
              moreMenuErrorType:
                Constants.MoreMenuErrorType.LIBRARY_ITEM_CORRUPTED
            })
          );
        } else {
          dispatch(
            showMoreMenuError({
              itemId: contentId,
              moreMenuErrorType:
                Constants.MoreMenuErrorType.UNABLE_TO_SAVE_CHANGE
            })
          );
        }
        return;
      }

      dispatch(loadAnalysisVersions(projectId, analysisId));
    },
    projectId,
    analysisContent && analysisContent.contentThreadId,
    analysisContent,
    selectedVersions
  );

  const showClientVersion = async () => {
    if (!isLoading && sharedVersion) {
      getSharedContent();

      dispatch(updateEntityVersion(sharedVersion.versionNumber));
    }
  };
  const showDraftVersion = async () => {
    if (!isLoading && draftVersion) {
      getDraftContent();

      dispatch(updateEntityVersion(draftVersion.versionNumber));
    }
  };

  const viewClient = getViewClientInfo(
    showClientVersion,
    showDraftVersion,
    selectedVersions,
    analysisContent && analysisContent.versionNumber,
    analysisContent && analysisContent.versionStatus
  );

  const headerComponent = analysisContent && (
    <>
      <ModalConfirmation
        show={isDeleteConfirmationVisible}
        onCancel={() => setIsDeleteConfirmationVisible(false)}
        onConfirm={handleDelete}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage="This Analysis will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />

      <AnalysisControlsHeader
        isShared={isAnalysisShared}
        workflowActions={workflowActions}
        onEdit={handleEdit}
        onDelete={() => setIsDeleteConfirmationVisible(true)}
        versions={selectedVersions}
        analysis={analysisContent}
        viewClient={viewClient}
      />
    </>
  );

  return (
    analysisContent &&
    analysisContent.contentThreadId && (
      <div className="analysis-details-screen">
        <FormLayout headerComponent={headerComponent}>
          <div
            className="analysis-main-content"
            data-testid="analysis-detail-page"
          >
            <AnalysisDetailScreen
              projectId={projectId}
              analysis={analysisContent}
              versions={selectedVersions}
              viewClient={viewClient}
            />
          </div>

          {analysisContent.tags && analysisContent.tags.length > 0 && (
            <TagList tags={analysisContent.tags} testId="analysis-tag-list" />
          )}
          <AnalysisDetailConnectedContent
            projectId={projectId}
            contentId={analysisContent.contentId}
          />
        </FormLayout>
      </div>
    )
  );
};

export default AnalysisDetailScreenContainer;
