import React, { useEffect, useState } from 'react';
import { Icon } from '@pwc/pwc-deals-ui';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Constants } from '@pwc/pwc-deals-web-common';
import _get from 'lodash/get';

import { getProject } from '../../../projectSlice';
import WelcomeBox from '../WelcomeBox';
import {
  getUserFeatureEnabled,
  saveUserFeatureEnabled
} from '../../../../../common/services/userFeatureToggleService';
import './WelcomeBoxQuickStart.scss';

const iconSize = 24;

const WelcomeBoxQuickStart = () => {
  const [featureEnabled, setFeatureEnabled] = useState(null);
  const project = useSelector(getProject);

  useEffect(() => {
    (async () => {
      if (project.id) {
        const result = await getUserFeatureEnabled(
          project.id,
          Constants.FEATURE_NAMES.WELCOME_BOX
        );
        setFeatureEnabled(_get(result, 'enabled', false));
      }
    })();
  }, [project.id]);

  if (featureEnabled === null || featureEnabled === false) {
    return null;
  }

  const handleDismiss = async () => {
    await saveUserFeatureEnabled(
      project.id,
      Constants.FEATURE_NAMES.WELCOME_BOX,
      false
    );
    setFeatureEnabled(false);
  };

  return (
    <div className="welcome-box-quick-start">
      <WelcomeBox>
        <button
          className="ui-button button-no-appearance"
          aria-label="Close welcome box"
          onClick={handleDismiss}
          type="button"
          data-testid="welcome-box-dismiss-button"
        >
          <Icon
            name="close"
            className="fill icon-white"
            width="18"
            height="18"
          />
        </button>
        <p>
          Start building your project by{' '}
          <span className="nowrap">
            <Icon
              name="add"
              width={iconSize}
              fill="#ffff"
              className="welcome-paragraph-icon fill"
            />{' '}
            adding
          </span>{' '}
          a{' '}
          <span className="nowrap">
            <Icon
              name="hypothesis"
              width={iconSize}
              fill="#ffff"
              className="welcome-paragraph-icon fill"
            />{' '}
            <Link
              to={`${project.id}/key-focus-areas/create`}
              data-testid="hypothesis-welcome-link"
              aria-label="Key Focus Area"
              className="add-link"
            >
              Key Focus Area
            </Link>
          </span>
          ,{' '}
          <span className="nowrap">
            <Icon
              name="insight"
              width={iconSize}
              fill="#ffff"
              className="welcome-paragraph-icon fill"
            />{' '}
            <Link
              to={`${project.id}/insights/create`}
              data-testid="insights-welcome-link"
              aria-label="Insight"
              className="add-link"
            >
              Insight
            </Link>
          </span>{' '}
          or{' '}
          <span className="nowrap">
            <Icon
              name="analysis"
              width={iconSize}
              fill="#ffff"
              className="welcome-paragraph-icon fill"
            />{' '}
            <Link
              to={`${project.id}/analyses/add-type`}
              data-testid="analyses-welcome-link"
              aria-label="Analyses"
              className="add-link"
            >
              Analysis
            </Link>
          </span>
          , then{' '}
          <span className="nowrap">
            <Icon
              name="hyperlink"
              width={iconSize}
              fill="#ffff"
              className="welcome-paragraph-icon fill"
            />{' '}
            connect
          </span>{' '}
          them to demonstrate how our value decisions are informed.
        </p>
      </WelcomeBox>
    </div>
  );
};

export default WelcomeBoxQuickStart;
