/* eslint-disable no-unused-vars */
import { MasterApiClient } from '../../../common/services/httpClient';

export const getAllProjects = async () => {
  const response = await MasterApiClient.get('projects/all');
  return response.data;
};

const updateDeletedStatus = async (projectId, deleted) => {
  const response = await MasterApiClient.patch(
    `projects/${projectId}/deleted`,
    { deleted }
  );
  return response.data;
};

export const hideProject = async projectId => {
  return updateDeletedStatus(projectId, true);
};

export const showProject = async projectId => {
  return updateDeletedStatus(projectId, false);
};
