import { Constants } from '@pwc/pwc-deals-web-common';

export const dealOptions = [
  { label: 'Buy (Acquisitions)', value: 1 },
  { label: 'Sell (Divestitures)', value: 2 }
];

export const defaultRestrictionOption = { label: 'C2', value: 3 };
export const restrictionOptions = [
  { label: 'C1', value: 1 },
  { label: 'C1A', value: 2 },
  defaultRestrictionOption
];

export const financialMagnitudeOptions = [
  { label: 'Thousands', value: 'Thousands' },
  { label: 'Millions', value: 'Millions', default: true }
];

export const currencyOptions = [
  {
    label: 'US Dollar ($)',
    value: 'Dollar',
    default: Constants.TERRITORIES.UNITED_STATES
  },
  {
    label: 'Pound Sterling ()',
    value: 'Pound',
    default: Constants.TERRITORIES.UNITED_KINGDOM
  },
  { label: 'Euro ()', value: 'Euro' }
];

export const financialsDecimalCountOptions = [
  { label: 'Hide', value: '0' },
  { label: 'Show', value: '1', default: true }
];

const codeToDisplayName = (arrayOfObjects, code) => {
  const target = arrayOfObjects.filter(o => o.value === code);
  return target.length !== 0 ? target[0].label : 'Undefined';
};

export const dealCodeToName = code => codeToDisplayName(dealOptions, code);
export const restrictionCodeToName = code =>
  codeToDisplayName(restrictionOptions, code);

export const projectType = {
  buy: { id: 1, text: 'Buy side' },
  sell: { id: 2, text: 'Sell side' }
};
export const getProjectType = type =>
  type === projectType.buy.id ? projectType.buy.text : projectType.sell.text;

export const getDefaultValue = (options, defaultWhere = true) =>
  (options.find(o => o.default === defaultWhere) || options[0]).value;

export const consentLevelOptions = [
  { label: 'None', value: 'None' },
  { label: 'Partial', value: 'Partial' },
  { label: 'Full', value: 'Full' }
];
