import React, { useEffect, useState } from 'react';
import {
  Constants,
  EnvironmentVariableUtilities
} from '@pwc/pwc-deals-web-common';
import { useDispatch, useSelector } from 'react-redux';
import { getProject, loadProjectThunk } from '../project/projectSlice';
import { updateProjectUsers } from '../../common/services/projectService';
import UserEditSettings, { getInitialState } from './UserEditSettings';

const PwCTeamSettings = () => {
  const project = useSelector(getProject);
  const dispatch = useDispatch();
  const [initialValues, setInitialValues] = useState(null);
  const [initialValuesSet, setInitialValuesSet] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [hasError, setHasError] = useState(false);

  useEffect(() => {
    if (!initialValuesSet) {
      if (project.users) {
        setInitialValues(getInitialState(project.users));
        setInitialValuesSet(true);
      } else {
        setInitialValues(getInitialState());
      }
    }
  }, [project, setInitialValues, initialValuesSet, setInitialValuesSet]);

  const fieldConfigs = [
    {
      role: Constants.USER_ROLES.ENGAGEMENT_LEAD,
      title: Constants.USER_ROLES.ENGAGEMENT_LEAD,
      description: `Full access. This is available for Partner and Managing Director level only.`,
      required: true
    },
    {
      role: Constants.USER_ROLES.ENGAGEMENT_MANAGER,
      title: Constants.USER_ROLES.ENGAGEMENT_MANAGER,
      description: `Full access to ${EnvironmentVariableUtilities.platformName}.`,
      required: true
    },
    {
      role: Constants.USER_ROLES.PROJECT_ADMIN,
      title: Constants.USER_ROLES.PROJECT_ADMIN,
      description: `Full access.`,
      allowMany: true
    },
    {
      role: Constants.USER_ROLES.APPROVER,
      title: Constants.USER_ROLES.APPROVER,
      description: `Unable to edit users on ${EnvironmentVariableUtilities.platformName}.`,
      allowMany: true
    },
    {
      role: Constants.USER_ROLES.TEAM_MEMBER,
      title: Constants.USER_ROLES.TEAM_MEMBER,
      description: `Unable to edit users or approve content.`,
      allowMany: true
    },
    {
      role: Constants.USER_ROLES.PWC_READONLY,
      title: Constants.USER_ROLES.PWC_READONLY_DISPLAY,
      description: `Unable to contribute, share, approve, comment, and edit users on ${EnvironmentVariableUtilities.platformName}.`,
      allowMany: true
    }
  ];

  const onSubmit = async newValues => {
    setIsSaving(true);
    try {
      setHasError(false);
      await updateProjectUsers(project.id, newValues);
      await dispatch(loadProjectThunk(project.id));
      setIsSaving(false);
      setInitialValuesSet(false);
    } catch (e) {
      setIsSaving(false);
      setHasError(true);
    }
  };

  const onCancel = resetForm => resetForm();
  return (
    initialValuesSet && (
      <UserEditSettings
        initialValues={initialValues}
        fieldConfigs={fieldConfigs}
        onSubmit={onSubmit}
        onCancel={onCancel}
        submitButtonText="Save"
        isSaving={isSaving}
        hasError={hasError}
      >
        <div data-testid="pwcTeamSettings">
          <h3>Search for PwC team members to invite to the project.</h3>
          <p>
            If you can&apos;t find a team member, please send an email to{' '}
            <a href="mailto:deals-support@pwc.com" className="orange-link">
              deals-support@pwc.com
            </a>
            {'. '}
            Please include the full name and the role you&apos;d like to add.
          </p>
        </div>
      </UserEditSettings>
    )
  );
};
export default PwCTeamSettings;
