import React from 'react';
import classnames from 'classnames';
import PropTypes from 'prop-types';
import { InsightsTableRow } from './InsightsTableRow';
import { AnalysesTableRow } from './AnalysesTableRow';
import {
  DisplayableContent,
  InsightWithConnectedAnalyses
} from './ConnectedKfaPropTypes';
import './AnalysesAndInsightsTable.scss';

export const AnalysesAndInsightsTable = ({
  hypothesis,
  hypothesisIndex,
  projectId
}) => {
  const allInsightConnectedAnalyses = hypothesis.connectedInsights.flatMap(
    insight => insight.connectedAnalyses
  );

  const distinctDirectlyConnectedAnalyses = hypothesis.connectedAnalyses.filter(
    connectedAnalysis => {
      const isAnalysisDisplayedForInsight = allInsightConnectedAnalyses.find(
        analysis =>
          analysis.contentThreadId === connectedAnalysis.contentThreadId &&
          analysis.contentId === connectedAnalysis.contentId
      );

      return !isAnalysisDisplayedForInsight;
    }
  );

  const totalAnalysesCount =
    distinctDirectlyConnectedAnalyses.length +
    allInsightConnectedAnalyses.length;
  const hasConnectedInsights = hypothesis.connectedInsights.length > 0;
  const hasConnectedAnalyses = totalAnalysesCount > 0;
  return (
    <div
      className="analyses-and-insights-details"
      data-testid="analyses-and-insights-details"
    >
      <table
        className="analyses-and-insights-table"
        data-testid="analyses-and-insights-table"
      >
        <thead className="analyses-and-insights-table-header">
          <tr
            data-testid="analyses-and-insights-header-row"
            className="analyses-and-insights-header-row"
          >
            <th
              data-testid="insights-header-cell"
              className="insights-header-cell"
            >
              <span
                data-testid="insights-header"
                className={classnames(
                  'connected-content-heading',
                  'insights-header',
                  hasConnectedInsights ? 'orange' : null
                )}
              >
                Insights
              </span>
            </th>
            <th
              data-testid="analyses-header-cell"
              className="analyses-header-cell"
            >
              <span
                data-testid="analyses-header"
                className={classnames(
                  'connected-content-heading',
                  'analyses-header',
                  hasConnectedAnalyses ? 'orange' : null
                )}
              >
                Analyses
              </span>
            </th>
          </tr>
        </thead>
        <tbody className="analyses-and-insights-table-body">
          {hypothesis.connectedInsights.map((insight, insightIndex) => (
            <InsightsTableRow
              projectId={projectId}
              key={insight.contentThreadId}
              insight={insight}
              hypothesisIndex={hypothesisIndex}
              insightIndex={insightIndex}
            />
          ))}
          <AnalysesTableRow
            projectId={projectId}
            analyses={distinctDirectlyConnectedAnalyses}
            hypothesisIndex={hypothesisIndex}
          />
        </tbody>
      </table>
    </div>
  );
};

AnalysesAndInsightsTable.propTypes = {
  hypothesisIndex: PropTypes.number.isRequired,
  projectId: PropTypes.string.isRequired,
  hypothesis: PropTypes.shape({
    ...DisplayableContent,
    connectedInsights: PropTypes.arrayOf(
      PropTypes.shape(InsightWithConnectedAnalyses)
    ).isRequired,
    connectedAnalyses: PropTypes.arrayOf(PropTypes.shape(DisplayableContent))
      .isRequired
  }).isRequired
};
