import React, { useState, useRef } from 'react';
import { Popover, Overlay } from 'react-bootstrap';
import { Icon, Button } from '@pwc/pwc-deals-ui';
import { useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import useTabletOrMobile from '../../../../common/hooks/useTabletOrMobile';

import { requestFileStatusChange } from '../../../../common/redux/fileUpload/fileUploadThunk';

import './ApprovedFile.scss';

const ShareFilePopover = ({
  target,
  fileName = '',
  isLoading = false,
  show,
  onConfirm = () => {},
  onCancel = () => {}
}) => {
  const ref = useRef();
  const isTabletOrMobile = useTabletOrMobile();

  return (
    <div ref={ref} data-testid="share-file-popover">
      <Overlay
        show={show}
        target={target}
        rootClose
        onHide={onCancel}
        container={ref.current}
        placement={isTabletOrMobile ? 'bottom-end' : 'left-start'}
      >
        <Popover className="share-file-popover">
          <Popover.Content className="popup-menu popup-confirmation share-file-popover">
            <Popover.Title>Share with client team?</Popover.Title>
            <p data-testid="popover-confirmation-message">
              {`This will allow the client team to download and view the file "${fileName}".`}
            </p>
            <div className="confirm-buttons">
              <Button onClick={onCancel} className="button-link mr-3">
                Cancel
              </Button>
              <Button
                onClick={onConfirm}
                className="button-small"
                disabled={isLoading}
              >
                Yes, share now
              </Button>
            </div>
          </Popover.Content>
        </Popover>
      </Overlay>
    </div>
  );
};

const ApprovedFile = ({ file }) => {
  const dispatch = useDispatch();

  const [target, setTarget] = useState();
  const [showShare, setShowShare] = useState(false);

  const { projectId } = useParams();

  const showPopover = e => {
    setTarget(e.target);
    setShowShare(true);
  };

  const hidePopover = () => {
    setShowShare(false);
  };

  return (
    <div>
      <Button
        data-testid="file-approved-btn"
        className="button-menu button-no-underline file-status-button"
        onClick={showPopover}
      >
        <Icon name="view" width={24} className="outline icon-orange" />
        <span>Share with client</span>
      </Button>
      <ShareFilePopover
        target={target}
        onCancel={hidePopover}
        onConfirm={() =>
          dispatch(
            requestFileStatusChange(projectId, file.fileId, 'ShareWithClient')
          )
        }
        fileName={file.fileName}
        show={showShare}
      />
    </div>
  );
};

export default ApprovedFile;
