import React from 'react';
import './DealDetails.scss';

const DealDetails = ({ dataTestId, title, content }) => {
  return (
    <>
      {!content && (
        <div className="deal-details" data-testid={dataTestId}>
          <span className="deal-details-title">{title}</span>
          <span className={`deal-details-content ${title}`}>-</span>
        </div>
      )}
      {content && (
        <div className="deal-details" data-testid={dataTestId}>
          <span className="deal-details-title">{title}</span>
          <span className={`deal-details-content ${title}`}>{content}</span>
        </div>
      )}
    </>
  );
};

export default DealDetails;
