import React from 'react';
import PropTypes from 'prop-types';
import { displayDate } from '../../common/services/textService';

const DisplayDate = ({ dateText }) => (
  <div data-testid="display-date" className="display-date">
    {displayDate(new Date(dateText))}
  </div>
);

DisplayDate.propTypes = {
  dateText: PropTypes.string.isRequired
};

export default DisplayDate;
