import React from 'react';
import {
  viewHypothesisPath,
  viewInsightPath
} from '../../../common/services/urlService';
import Accordion from '../../../common/components/Accordion/Accordion';
import NarrativeSection from '../NarrativeSection/NarrativeSection';
import './AccordionConnectedContent.scss';
import { analysisLinkBuilder } from '../../analyses/common/analysisUrlHelper';

const AccordionConnectedContent = ({
  narratives,
  projectId,
  index,
  showBody = false
}) => {
  const hasInsights = narratives.insights.length > 0;
  const hasAnalyses = narratives.analyses.length > 0;
  const hasKeyFocusAreas = narratives.hypotheses.length > 0;
  const hasNarratives = hasInsights || hasAnalyses || hasKeyFocusAreas;

  return hasNarratives ? (
    <div
      className="accordion-connected-content"
      data-testid={`accordion-connected-content-${index}`}
    >
      <Accordion title="Connected content">
        {hasInsights && (
          <NarrativeSection
            title="Insights"
            narratives={narratives.insights}
            getViewPath={insightId => viewInsightPath(projectId, insightId)}
            index={index}
            showBody={showBody}
            truncate={false}
          />
        )}
        {hasAnalyses && (
          <NarrativeSection
            title="Analyses"
            narratives={narratives.analyses}
            getViewPath={analysisLinkBuilder(narratives.analyses, projectId)}
            index={index}
            showBody={showBody}
            truncate={false}
          />
        )}
        {hasKeyFocusAreas && (
          <NarrativeSection
            title="Key Focus Areas"
            narratives={narratives.hypotheses}
            getViewPath={hypothesisId =>
              viewHypothesisPath(projectId, hypothesisId)
            }
            index={index}
            showBody={showBody}
            truncate={false}
          />
        )}
      </Accordion>
    </div>
  ) : null;
};

export default AccordionConnectedContent;
