import { Button, Icon } from '@pwc/pwc-deals-ui';
import React, { useRef, useState } from 'react';
import { Overlay, Popover } from 'react-bootstrap';
import { Link, useLocation } from 'react-router-dom';
import authorizationService from '../../../services/security/authorizationService';
import { createProjectPagePath } from '../../../services/urlService';
import './AddProjectHeaderMenu.scss';

const AddProjectHeaderMenu = () => {
  const [show, setShow] = useState();
  const [target, setTarget] = useState();
  const ref = useRef(null);
  const { pathname } = useLocation();

  const handleClick = event => {
    setShow(!show);
    setTarget(event.target);
    if (show === true) {
      setTimeout(() => {
        document.body.click();
      }, 200);
    }
  };

  const handleClose = () => {
    setShow(false);
    setTimeout(() => {
      document.body.click();
    }, 200);
  };

  return (
    <>
      <div className="add-project-header-menu" ref={ref}>
        {authorizationService.canCreateProjects() && pathname === '/projects' && (
          <Button
            className="button-avoid-focus add-icon button-link add-popover-button"
            onClick={handleClick}
          >
            <Icon name="add" className="fill icon-orange" width={32} />
          </Button>
        )}
        <Overlay
          show={show}
          target={target}
          placement="bottom"
          rootClose
          onHide={() => {
            setShow(!show);
          }}
          container={ref.current}
          popperConfig={{
            modifiers: [
              {
                name: 'offset',
                options: {
                  offset: [0, 12]
                }
              }
            ]
          }}
        >
          <Popover
            id="popover-add-project"
            ref={ref}
            className="header-add-popover"
          >
            <Popover.Content className="popup-user">
              <Link
                className="dropdown-item"
                to={createProjectPagePath}
                onClick={handleClick}
              >
                <Icon
                  name="folder"
                  width={24}
                  className="outline icon-orange-theme"
                />
                Add Project
              </Link>
            </Popover.Content>
          </Popover>
        </Overlay>
      </div>
      {show && (
        <div
          className="add-header-popover-overlay"
          onClick={handleClose}
          data-testid="add-header-popover-overlay"
          role="presentation"
        />
      )}
    </>
  );
};

export default AddProjectHeaderMenu;
