import React, { useMemo } from 'react';
import ReportTabs from '../RightPanel/RightPanelTabs/RightPanelTabs';
import DataLibraryTab, {
  DataLibraryLabel
} from '../RightPanel/DataLibraryTab/DataLibraryTab';
import LayoutTab from '../RightPanel/LayoutTab/LayoutTab';
import './BottomPanel.scss';

function BottomPanel({
  handleLayoutTypeChange,
  droppableLocations,
  panelRef,
  panelExpanded
}) {
  const bottomPanelTabs = useMemo(
    () => [
      {
        label: <DataLibraryLabel />,
        content: <DataLibraryTab droppableLocations={droppableLocations} />
      },
      {
        label: 'Layout',
        content: <LayoutTab handleLayoutTypeChange={handleLayoutTypeChange} />
      }
    ],
    [handleLayoutTypeChange, droppableLocations]
  );

  return (
    <div className="bottom-panel" data-testid="bottom-panel" ref={panelRef}>
      <ReportTabs tabs={bottomPanelTabs} panelExpanded={panelExpanded} />
    </div>
  );
}

export default BottomPanel;
