import { useCallback, useEffect, useState } from 'react';
import { Constants, ErrorUtilities } from '@pwc/pwc-deals-web-common';
import { useHistory, useParams, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import qs from 'query-string';
import _get from 'lodash/get';
import { getCalcEngineIsProcessing } from '../../../../../common/engine/reducers/calcEngine';
import { getViewClientInfo } from '../../../../../common/helpers/viewClientVersion';
import { useApprovalWorkflow } from '../../../../../common/hooks/useApprovalWorkflow';
import { showMoreMenuError } from '../../../../../common/redux/errors/errorsSlice';
import authorizationService from '../../../../../common/services/security/authorizationService';
import {
  analysesListPath,
  editAnalysisPath,
  structuredRoutes
} from '../../../../../common/services/urlService';
import { getCommentPanelOpened } from '../../../../comments/commentsSlice';
import { getWorkflowStatus } from '../../../../workflow/workflowService';
import {
  deleteStructuredDocument,
  updateStructuredAnalysisStatus
} from '../../../../../common/engine/services/structuredAnalysisService';
import {
  getLoadingInProcess,
  getStructuredAnalysisHasError,
  ReingestDocumentWithDependencies
} from '../../../../../common/engine/reducers/StructuredAnalysisSlice';
import {
  getStructuredAnalysisViewDocumentsWithContentForDocument,
  getStructuredAnalysisViewHasError,
  getStructuredAnalysisViewLoadingInProcess
} from '../../../../../common/engine/reducers/structuredAnalysisViewSlice';
import { clearDataAndtransformAllIngestedStructuredDocumentsThunk } from '../../../../../common/engine/thunks/clearDataAndtransformAllIngestedStructuredDocumentsThunk';

export const useStructuredAnalysisContainer = documentCode => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { projectId } = useParams();
  const searchParams = qs.parse(useLocation().search);
  const targetAdjustmentType = _get(searchParams, 'adjustmentType', null);
  const targetAdjustmentName = _get(searchParams, 'adjustmentName', null);
  const isClientUser = authorizationService.isClientUser();

  const hasIngestionError = useSelector(getStructuredAnalysisHasError);
  const hasError = useSelector(getStructuredAnalysisViewHasError);
  const [hasAlreadyLoadedHack, setHasAlreadyLoadedHack] = useState(false);

  const [isConfirmVisible, setIsConfirmVisible] = useState(false);
  const isCommentPanelOpen = useSelector(getCommentPanelOpened);
  const isProcessing = useSelector(getCalcEngineIsProcessing);
  const reIngestingDocuments = useSelector(getLoadingInProcess);
  const initialViewingAsClient = !!isClientUser;
  const [viewingAsClient, setViewingAsClient] = useState(
    initialViewingAsClient
  );
  const initializingData = useSelector(
    getStructuredAnalysisViewLoadingInProcess
  );

  const analysis = useSelector(
    getStructuredAnalysisViewDocumentsWithContentForDocument(documentCode)
  );

  const [chartKey, setChartKey] = useState(1);
  const incrementChartKey = useCallback(
    () => setChartKey(chartKeyPrevState => chartKeyPrevState + 1),
    [setChartKey]
  );

  useEffect(() => {
    incrementChartKey();
  }, [isCommentPanelOpen, incrementChartKey]);

  const fetchAnalysis = useCallback(async () => {
    await dispatch(
      clearDataAndtransformAllIngestedStructuredDocumentsThunk(
        projectId,
        documentCode,
        viewingAsClient
      )
    );
    setHasAlreadyLoadedHack(true);
  }, [dispatch, documentCode, projectId, viewingAsClient]);

  useEffect(() => {
    fetchAnalysis();
  }, [fetchAnalysis]);

  const showClientVersion = () => {
    if (!isProcessing) {
      setViewingAsClient(true);
    }
  };

  const showDraftVersion = () => {
    if (!isProcessing) {
      setViewingAsClient(false);
    }
  };

  const viewClient = getViewClientInfo(
    showClientVersion,
    showDraftVersion,
    analysis && analysis.versions,
    analysis && analysis.content.versionNumber,
    analysis && analysis.content.versionStatus
  );

  const handleEdit = () => {
    history.push(editAnalysisPath(projectId, structuredRoutes[documentCode]));
  };

  const handleDelete = async () => {
    await deleteStructuredDocument(projectId, documentCode);
    history.push(analysesListPath(projectId));
  };

  const handleRefresh = async () => {
    setViewingAsClient(initialViewingAsClient);
    await dispatch(ReingestDocumentWithDependencies(projectId, documentCode));
    await fetchAnalysis();
  };

  const versions = analysis ? analysis.versions : [];

  const workflowActions = useApprovalWorkflow(
    getWorkflowStatus,
    async (_projectId, contentId, step) => {
      try {
        await updateStructuredAnalysisStatus(_projectId, contentId, step);
      } catch (e) {
        if (ErrorUtilities.hasLibraryItemCorruptedError(e)) {
          dispatch(
            showMoreMenuError({
              itemId: contentId,
              moreMenuErrorType:
                Constants.MoreMenuErrorType.LIBRARY_ITEM_CORRUPTED
            })
          );
        } else {
          dispatch(
            showMoreMenuError({
              itemId: contentId,
              moreMenuErrorType:
                Constants.MoreMenuErrorType.UNABLE_TO_SAVE_CHANGE
            })
          );
        }
        return;
      }
      await fetchAnalysis();
    },
    projectId,
    analysis && analysis.content.contentThreadId,
    analysis && analysis.content,
    versions
  );

  const isClientView = isClientUser || viewClient.isOnClientVersion;
  const hasLoadingError = hasError || hasIngestionError;
  const isLoading =
    !hasAlreadyLoadedHack ||
    initializingData ||
    isProcessing ||
    reIngestingDocuments.length > 0;

  const analysisContent = _get(analysis, 'content', undefined);

  return {
    dispatch,
    projectId,
    targetAdjustmentType,
    targetAdjustmentName,
    hasError: hasLoadingError,
    analysis: analysisContent,
    isConfirmVisible,
    setIsConfirmVisible,
    isLoading,
    versions,
    chartKey,
    incrementChartKey,
    viewClient,
    handleEdit,
    handleDelete,
    handleRefresh,
    workflowActions,
    isClientView
  };
};
