import { ClassicEditor } from '@pwc/pwc-deals-rich-text-editor';
import { UserUtilities } from '@pwc/pwc-deals-web-common';

import {
  toggleShareCommentThreadThunk,
  toggleResolveCommentThreadThunk
} from '../../../features/comments/commentsSlice';

const { CommentThreadView, ButtonView, SwitchButtonView } = ClassicEditor;

export function getModules(thread, user, instantiator) {
  if (UserUtilities.isPractitioner(user, thread.projectId)) {
    return [
      instantiator.createResolveButtonView(thread),
      instantiator.createSharedView(thread)
    ];
  }

  return [instantiator.createResolveButtonView(thread)];
}

export default (
  dispatch,
  commentThreads,
  isEntitySharedWithClient,
  currentUser,
  projectUsers,
  currentEntityVersion,
  isProjectDisabled
) =>
  class CustomCommentThreadView extends CommentThreadView {
    constructor(...args) {
      super(...args);

      // arg[0] (locale) = { uiLanguage, contentLanguage, contentLanguageDirection, uiLanguageDirection, t }
      // arg[1] (comment thread) = { channelId, id, comments, context, id, isReadOnly }
      // arg[2] (user) = { avatar, color, id, name }
      // arg[3] (config) = { CommentView, editorConfig, formatDateTime, maxCommentCharsWhenCollapsed, maxCommentsWhenCollapsed, maxThreadTotalWeight }
      const commentThread = args[1];
      const foundThread = commentThreads.find(
        thread => thread.id === commentThread.id
      );

      // This template definition is partially based on the default comment thread view.
      this.setTemplate({
        tag: 'div',

        attributes: {
          class: [
            'ck-thread',
            this.bindTemplate.if('isActive', 'ck-thread--active')
          ],
          // Needed for managing focus after adding a new comment.
          tabindex: 0
        },

        children: [
          // Adding the top bar element that will hold the dropdown.
          {
            tag: 'div',
            attributes: {
              class: 'ck-thread-top-bar'
            },
            children: foundThread && getModules(foundThread, currentUser, this)
          },
          // The rest of the view is as in the default view.
          {
            tag: 'div',
            attributes: {
              class: 'ck-thread__container'
            },
            children: [this.commentsListView, this.commentThreadInputView]
          }
        ]
      });
    }

    createSharedView(commentThread) {
      const buttonView = new SwitchButtonView(this.locale);

      const { t } = this.locale;

      const buttonLabel = 'Visible to client';

      const threadAuthorId = commentThread.comments[0].authorId;
      const threadAuthor = projectUsers.find(
        user => user.id === threadAuthorId
      );
      const isPracticionerCreatedThread = UserUtilities.isPractitioner(
        threadAuthor,
        commentThread.projectId
      );

      const isEnabled = !commentThread.resolved
        ? isPracticionerCreatedThread && isEntitySharedWithClient
        : false;

      buttonView.set({
        label: buttonLabel,
        isToggleable: !isProjectDisabled,
        tooltip: t(buttonLabel),
        withText: true,
        isOn: commentThread.shareWithAll || false,
        isEnabled: !isProjectDisabled && isEnabled
      });

      buttonView.extendTemplate({
        attributes: {
          class: 'ck-share-toggle'
        }
      });

      // eslint-disable-next-line no-underscore-dangle
      const comment = commentThread.comments[0];

      buttonView.on('execute', () =>
        dispatch(
          toggleShareCommentThreadThunk(
            {
              ...comment,
              shareWithAll: !commentThread.shareWithAll
            },
            currentEntityVersion
          )
        )
      );

      return buttonView;
    }

    createResolveButtonView(commentThread) {
      const buttonView = new ButtonView(this.locale);

      const { t } = this.locale;

      const buttonLabel = commentThread.resolved ? 'Reopen' : 'Resolve';

      buttonView.set({
        label: buttonLabel,
        isToggleable: !isProjectDisabled,
        tooltip: t(buttonLabel),
        withText: true,
        isEnabled: !isProjectDisabled
      });

      buttonView.extendTemplate({
        attributes: {
          class: 'ck-resolve-button'
        }
      });

      // eslint-disable-next-line no-underscore-dangle
      const comment = commentThread.comments[0];

      buttonView.on('execute', () =>
        dispatch(
          toggleResolveCommentThreadThunk(
            {
              ...comment,
              resolved: !commentThread.resolved
            },
            currentEntityVersion
          )
        )
      );

      return buttonView;
    }
  };
