import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import './PageSection.scss';
import { useParams } from 'react-router-dom';
import ShowForRole from '../../../features/security/ShowForRole';
import { sanitize } from '../../services/sanitizeService';

const PageDescription = ({ label, value, sectionId }) => {
  const { projectId } = useParams();

  return value ? (
    <>
      <ShowForRole
        roles={Constants.USER_ROLE_GROUPS.INTERNAL}
        projectId={projectId}
      >
        <div className="section-name">{label}</div>
      </ShowForRole>
      <p
        className="section-text"
        data-testid={`page-${sectionId || label.toLowerCase()}`}
        dangerouslySetInnerHTML={{ __html: sanitize(value) }}
      />
    </>
  ) : null;
};

export default PageDescription;
