import React from 'react';
import PropType from 'prop-types';
import { Droppable } from 'react-beautiful-dnd';
import { Icon } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import classNames from 'classnames';

import DraggableLibraryItemCard from '../../../../../DraggableLibraryItemCard';

import './SectionArea.scss';

function SectionArea({
  areaClass,
  droppableLocation,
  libraryItems,
  libraryItemMap,
  droppableLocations
}) {
  const canLibraryItemBePlacedInSection = libraryItemId => {
    const parsedLibraryItemId = Number.isInteger(parseInt(libraryItemId, 10))
      ? parseInt(libraryItemId, 10)
      : null;
    if (
      parsedLibraryItemId &&
      libraryItemMap[parsedLibraryItemId].type !==
        Constants.LIBRARY_ITEM_TYPES.RICH_TEXT.value &&
      droppableLocation.id.includes('text-overflow')
    ) {
      return false;
    }
    return true;
  };

  const baseStyles = {
    border: '1px dashed var(--color-grey)',
    borderRadius: '2px',
    backgroundColor: 'var(--dragdrop-container-background-hover)'
  };
  const filledBorder = '1px solid var(--color-grey-medium)';
  const draggingOverBorder = '1px dashed #979797';
  const draggingOverBorderError = '2px solid var(--color-red-error-dark-mode)';
  const draggingOverBackgroundColor = 'var(--background-primary)';
  const isAreaFilled = () => {
    return libraryItems.length > 0;
  };

  const getStyle = droppableSnapshot => {
    const {
      isDraggingOver,
      draggingOverWith,
      draggingFromThisWith
    } = droppableSnapshot;
    const returnedStyle = { ...baseStyles };
    if (
      isAreaFilled() &&
      draggingOverWith &&
      draggingOverWith === draggingFromThisWith
    ) {
      Object.assign(returnedStyle, {
        border: draggingOverBorder,
        backgroundColor: draggingOverBackgroundColor
      });
    } else if (
      isAreaFilled() &&
      draggingOverWith &&
      !canLibraryItemBePlacedInSection(draggingOverWith)
    ) {
      Object.assign(returnedStyle, { border: draggingOverBorderError });
    } else if (isAreaFilled()) {
      Object.assign(returnedStyle, { border: filledBorder });
    } else if (
      !isAreaFilled() &&
      isDraggingOver &&
      draggingOverWith &&
      canLibraryItemBePlacedInSection(draggingOverWith)
    ) {
      Object.assign(returnedStyle, {
        border: draggingOverBorder,
        backgroundColor: draggingOverBackgroundColor
      });
    } else if (
      !isAreaFilled() &&
      isDraggingOver &&
      draggingOverWith &&
      !canLibraryItemBePlacedInSection(draggingOverWith)
    ) {
      Object.assign(returnedStyle, { border: draggingOverBorderError });
    }
    // return base styles if area is not filled and not being dragged over
    return returnedStyle;
  };

  const getHint = droppableSnapshot => {
    let hint;
    if (droppableLocation.id.includes('text-overflow')) {
      if (
        !canLibraryItemBePlacedInSection(droppableSnapshot.draggingOverWith)
      ) {
        hint = 'Only text items allowed';
      } else {
        hint = 'Drop one text box item from the data list';
      }
    } else {
      hint = `Drop one item from the data list`;
    }
    return hint;
  };

  return (
    <Droppable
      droppableId={droppableLocation.id}
      direction={droppableLocation.direction}
      key={droppableLocation.id}
      ignoreContainerClipping
    >
      {(provided, snapshot) => {
        const computedStyle = getStyle(snapshot);
        const computedHint = getHint(snapshot);
        return (
          <div
            className={`${areaClass} section-area__droppable-container`}
            data-testid={areaClass}
            ref={provided.innerRef}
            {...provided.droppableProps}
            style={computedStyle}
          >
            <div
              className={classNames('section-area__item-container', {
                'background-left': droppableLocation.id.includes(
                  'text-overflow'
                )
              })}
            >
              {libraryItems.map((libraryItem, index) => {
                return (
                  <DraggableLibraryItemCard
                    key={libraryItem.id}
                    libraryItem={libraryItem}
                    index={index}
                    droppableLocationId={droppableLocation.id}
                    droppableLocations={droppableLocations}
                  />
                );
              })}
              {provided.placeholder}
            </div>
            {libraryItems.length < 1 && (
              <div
                className={classNames('section-area__background', {
                  'background-left': droppableLocation.id.includes(
                    'text-overflow'
                  )
                })}
              >
                <div className="section-area__background__content">
                  <Icon
                    name={
                      // eslint-disable-next-line no-nested-ternary
                      droppableLocation.id.includes('text-overflow')
                        ? !canLibraryItemBePlacedInSection(
                            snapshot.draggingOverWith
                          )
                          ? 'end-process'
                          : 'textbox'
                        : 'placeholder-dnd'
                    }
                    height="36px"
                    width="36px"
                    className={classNames({
                      'outline icon-red-error': !canLibraryItemBePlacedInSection(
                        snapshot.draggingOverWith
                      ),
                      'fill icon-grey-medium': canLibraryItemBePlacedInSection(
                        snapshot.draggingOverWith
                      )
                    })}
                    title={computedHint}
                  />

                  <span
                    className={classNames('section-area__background__hint', {
                      'hint-error': !canLibraryItemBePlacedInSection(
                        snapshot.draggingOverWith
                      )
                    })}
                  >
                    {computedHint}
                  </span>
                </div>
              </div>
            )}
            {droppableLocation.id.includes('text-overflow') && (
              <div className="section-area__background background-right" />
            )}
          </div>
        );
      }}
    </Droppable>
  );
}

SectionArea.propTypes = {
  areaClass: PropType.string
};

SectionArea.defaultProps = {
  areaClass: ''
};

export default SectionArea;
