import React from 'react';
import { TextInput, RadioGroup, Select, Icon, Loader } from '@pwc/pwc-deals-ui';
import PublishButton from '../../../../../common/components/PublishButton';
import Form from '../../common/components/Form';
import CancelButton from '../../common/components/CancelButtonLink';
import FormBottomActionsContainer from '../../common/components/FormBottomActionsContainer';
import {
  dealOptions,
  restrictionOptions,
  currencyOptions,
  financialMagnitudeOptions,
  financialsDecimalCountOptions,
  consentLevelOptions
} from '../../../common/projectOptions';
import SelectMultiple from '../../../../../common/components/SelectMultiple/SelectMultiple';
import { useProjectOptionsFromApi } from '../../../../../common/hooks/useProjectOptionsFromApi';
import './MetaDataStepDisplay.scss';

const MetaDataStepDisplay = ({
  formValues,
  formTouches,
  formErrors,
  isValid,
  onChange,
  onBlur,
  onCancel,
  onNextClick,
  isSaving
}) => {
  const {
    isLoading,
    industryOptions,
    serviceOptions
  } = useProjectOptionsFromApi();

  if (isLoading) {
    return <Loader data-testid="project-options-loading" />;
  }

  return (
    <Form
      onSubmit={onNextClick}
      data-testid="project-create-form"
      className="step-1-form"
    >
      <TextInput
        label="Territory"
        field={{
          name: 'territory',
          value: formValues.territory,
          onChange: () => {}
        }}
        error=""
        basic
        disabled
      />
      <RadioGroup
        label="Deal type"
        field={{
          name: 'dealType',
          value: formValues.dealType,
          onChange
        }}
        options={dealOptions}
        direction="row"
      />
      <br />
      <RadioGroup
        label="Client Type"
        field={{
          name: 'restriction',
          value: formValues.restriction,
          onChange
        }}
        options={restrictionOptions}
        direction="row"
      />
      <br />
      <div className="services-container">
        <SelectMultiple
          matchOnStartsWith={false}
          minimumSearchStringLength={2}
          fieldName="services"
          label="Services / Products"
          placeholder="Search for services and products"
          options={serviceOptions}
          selections={formValues.services}
          onChange={selections =>
            onChange({ target: { name: 'services', value: selections } })
          }
          onBlur={onBlur('services')}
          error={formTouches.services ? formErrors.services : ''}
          noMatchElement={null}
        />
      </div>
      <TextInput
        basic
        label="Project name"
        field={{
          name: 'projectName',
          value: formValues.projectName,
          onChange
        }}
        error={formTouches.projectName ? formErrors.projectName : ''}
        onBlur={onBlur('projectName')}
      />
      <TextInput
        basic
        label="Client name"
        field={{
          name: 'clientName',
          value: formValues.clientName,
          onChange
        }}
        error={formTouches.clientName ? formErrors.clientName : ''}
        onBlur={onBlur('clientName')}
      />
      <TextInput
        basic
        label="Target (optional)"
        field={{
          name: 'target',
          value: formValues.target,
          onChange
        }}
        error=""
        onBlur={onBlur('target')}
      />
      <TextInput
        basic
        label="Description (optional)"
        field={{
          name: 'description',
          value: formValues.description,
          onChange
        }}
        error=""
        onBlur={onBlur('description')}
      />
      <Select
        data-testid="industry-select"
        basic
        options={industryOptions}
        label="Industry (optional)"
        onChange={event =>
          onChange({
            target: { name: 'industryId', value: event.target.value }
          })
        }
        value={formValues.industryId}
        placeholder="Please select the client's industry"
      />
      <br />
      <br />
      <RadioGroup
        label="Data Use Consent Level"
        field={{
          name: 'consentLevel',
          value: formValues.consentLevel,
          onChange
        }}
        options={consentLevelOptions}
        direction="row"
      />
      <br />
      <br />
      <h2>Format details</h2>
      <p
        style={{
          display: 'flex',
          alignItems: 'center'
        }}
      >
        <Icon
          name="information"
          className="fill icon-yellow"
          width={16}
          style={{
            marginRight: '0.5rem'
          }}
        />
        Automated for structured analysis only
      </p>
      <RadioGroup
        label="Currency"
        field={{
          name: 'currency',
          value: formValues.currency,
          onChange
        }}
        options={currencyOptions}
        direction="row"
      />
      <RadioGroup
        label="Number format"
        field={{
          name: 'financialsMagnitude',
          value: formValues.financialsMagnitude,
          onChange
        }}
        options={financialMagnitudeOptions}
        direction="row"
      />
      <RadioGroup
        label="Decimals"
        field={{
          name: 'financialsDecimalCount',
          value: formValues.financialsDecimalCount,
          onChange
        }}
        options={financialsDecimalCountOptions}
        direction="row"
      />
      <br />
      <div className="separator" />
      <FormBottomActionsContainer>
        <CancelButton onClick={onCancel} />
        <PublishButton
          isSaving={isSaving}
          isValid={isValid}
          buttonSize="sm"
          onSaveText="Saving..."
          standbyText="Next"
          buttonPosition="project-details"
        />
      </FormBottomActionsContainer>
    </Form>
  );
};

export default MetaDataStepDisplay;
