import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { Icon } from '@pwc/pwc-deals-ui';
import { Modal } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import {
  getErrorModalState,
  hideModalError
} from '../../redux/errors/errorsSlice';
import './ModalError.scss';

const getModalErrorTitle = type => {
  switch (type) {
    case Constants.ModalErrorType.UNABLE_TO_SAVE_CHANGE:
    case Constants.ModalErrorType.UNABLE_TO_SAVE_SORT_ORDER: {
      return (
        <span className="title-with-icon">
          <Icon name="alert" className="fill icon-yellow" width="24" />
          <span>Unable to save change</span>
        </span>
      );
    }
    case Constants.ModalErrorType.UNABLE_TO_SAVE_SORT_ORDER_STALE_DATA: {
      return (
        <span className="title-with-icon">
          <Icon name="alert" className="fill icon-yellow" width="24" />
          <span>Unable to save order</span>
        </span>
      );
    }
    default: {
      return '';
    }
  }
};

const getModalErrorBody = type => {
  switch (type) {
    case Constants.ModalErrorType.UNABLE_TO_SAVE_CHANGE:
    case Constants.ModalErrorType.UNABLE_TO_SAVE_SORT_ORDER: {
      return (
        <>
          Due to a system error, we are currently unable to save your changes.
          Please try again later. If the issue continues, send an email to{' '}
          <a
            href="mailto:us_junction_support@pwc.com"
            className="support-email-link"
          >
            [us_junction_support@pwc.com]
          </a>
        </>
      );
    }
    case Constants.ModalErrorType.UNABLE_TO_SAVE_SORT_ORDER_STALE_DATA: {
      return 'One or more changes were made to the content since you started re-ordering it. Please refresh the page and try again.';
    }

    default: {
      return '';
    }
  }
};

const ModalError = () => {
  const { show, modalErrorType } = useSelector(getErrorModalState);
  const dispatch = useDispatch();

  const handleModalClose = () => {
    dispatch(hideModalError());
  };

  if (!show) {
    return null;
  }

  return (
    <Modal
      show={show}
      onHide={handleModalClose}
      className="error-modal"
      centered
    >
      <Modal.Header>
        <Modal.Title>
          <>
            {getModalErrorTitle(modalErrorType)}
            <button
              className="ui-button button-no-appearance close-button"
              aria-label="Close topic modal"
              onClick={handleModalClose}
              type="button"
              data-testid="modal-error-close-button"
            >
              <Icon
                name="close"
                className="fill icon-orange-theme"
                width="18"
                height="18"
              />
            </button>
          </>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>{getModalErrorBody(modalErrorType)}</Modal.Body>
    </Modal>
  );
};

export default ModalError;
