/* eslint-disable react/jsx-wrap-multilines */
import React, { useState, useRef } from 'react';
import { string } from 'prop-types';
import { Overlay } from 'react-bootstrap';
import FileListItemPopupMenu from './FileListItemPopupMenu';
import FileListItemRowActionsButton from './FileListItemRowActionsButton';

const FileListItemRowMenu = ({
  itemId,
  direction,
  onUpdateMenuClick,
  filePassword
}) => {
  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);

  const handleButtonClick = handler =>
    handler
      ? () => {
          setShow(false);
          handler(itemId);
        }
      : null;

  const handleClick = event => {
    setTarget(event.target);
    setTimeout(() => {
      setShow(!show);
      if (show) {
        setTimeout(() => {
          document.body.click();
        }, 200);
      }
    }, 100);
  };

  return (
    <div ref={ref} data-testid="file-list-row-options">
      <FileListItemRowActionsButton onClick={handleClick} />
      <Overlay
        show={show}
        target={target}
        placement={direction}
        rootClose
        onHide={() => {
          setShow(!show);
        }}
        container={ref.current}
      >
        <FileListItemPopupMenu
          itemId={itemId}
          onUpdateMenuClick={handleButtonClick(onUpdateMenuClick)}
          filePassword={filePassword}
          dismissMenu={() => setShow(false)}
        />
      </Overlay>
    </div>
  );
};

FileListItemRowMenu.propTypes = {
  direction: string
};

FileListItemRowMenu.defaultProps = {
  direction: 'bottom'
};

export default FileListItemRowMenu;
