/* eslint no-param-reassign:off */

export default function initializeCKEditor(editor, richTextData, childrenData) {
  editor.isReadOnly = false;
  childrenData.forEach(item => {
    editor.execute('registerLibraryItem', {
      id: item.id,
      rendered: item.htmlData
    });
  });
  editor.setData(richTextData);
  editor.isReadOnly = true;
}
