import { DocumentUtilities } from '@pwc/pwc-deals-web-common';
import { createSlice } from '@reduxjs/toolkit';
import _uniq from 'lodash/uniq';
import _get from 'lodash/get';
import { postIngestStructuredDocument } from '../services/structuredAnalysisService';
import { clearAllProjectData } from '../actions/clearAllProjectData';

export const initialState = {
  ingestedDocuments: [],
  loadingInProcess: [],
  errorMessage: ''
};

const structuredAnalysisSlice = createSlice({
  name: 'structuredAnalysis',
  initialState,
  reducers: {
    clearIngestError: (state, action) => ({
      ...state,
      loadingInProcess: state.loadingInProcess.filter(
        item => item !== action.payload.code
      ),
      errorMessage: ''
    }),
    ingestionBegan: (state, action) => ({
      ...state,
      loadingInProcess: [
        ...state.loadingInProcess,
        ...action.payload.map(x => x.code)
      ],
      errorMessage: ''
    }),
    ingestionSucceeded: (state, action) => ({
      ...state,
      ingestedDocuments: _uniq([
        ...state.ingestedDocuments,
        ...action.payload.map(x => x.code)
      ]),
      loadingInProcess: [
        ...state.loadingInProcess.filter(item => {
          return action.payload.find(x => x.code === item) === undefined;
        })
      ],
      errorMessage: ''
    }),
    ingestionFailed: (state, action) => ({
      ...state,
      loadingInProcess: [
        ...state.loadingInProcess.filter(item => {
          return action.payload.codes.find(x => x.code === item) === undefined;
        })
      ],
      errorMessage: action.payload.error
    })
  },
  extraReducers: builder => {
    builder.addCase(clearAllProjectData, () => initialState);
  }
});
export default structuredAnalysisSlice.reducer;

// Actions
export const {
  clearIngestError,
  ingestionBegan,
  ingestionSucceeded,
  ingestionFailed
} = structuredAnalysisSlice.actions;

// Selectors
export const getIngestedDocuments = state =>
  state.structuredAnalysis.ingestedDocuments;
export const getLoadingInProcess = state =>
  state.structuredAnalysis.loadingInProcess;
export const getStructuredAnalysisHasError = state =>
  state.structuredAnalysis.errorMessage !== '';
export const getStructuredAnalysisError = state =>
  state.structuredAnalysis.errorMessage;

// Thunks
export const IngestionThunk = (projectId, documentCode) => async dispatch => {
  try {
    dispatch(ingestionBegan([{ code: documentCode }]));
    await postIngestStructuredDocument(projectId, [documentCode]);
    dispatch(ingestionSucceeded([{ code: documentCode }]));
  } catch (error) {
    const errorMessage = _get(
      error,
      'response.data.errors[0].apiErrorMessage',
      'An error occurred'
    );
    const errorObject = {
      error: errorMessage,
      codes: [{ code: documentCode }]
    };
    dispatch(ingestionFailed(errorObject));
  }
};

export const ReingestDocumentWithDependencies = (
  projectId,
  documentCode
) => async dispatch => {
  const documentWithDependencies = DocumentUtilities.getDocumentWithDependencies(
    documentCode
  );
  const documentsForRefresh = [
    documentWithDependencies.document,
    ...documentWithDependencies.dependencies
  ];
  const codes = documentsForRefresh.map(code => ({ code }));
  try {
    dispatch(ingestionBegan(codes));
    await postIngestStructuredDocument(projectId, documentsForRefresh);
    dispatch(ingestionSucceeded(codes));
  } catch (error) {
    const errorMessage = _get(
      error,
      'response.data.errors[0].apiErrorMessage',
      'An error occurred'
    );
    const errorObject = { error: errorMessage, codes };
    dispatch(ingestionFailed(errorObject));
  }
};
