import React from 'react';
import { Route, Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import userTokenService from '../../common/services/security/userTokenService';

const ProjectPermissionRoute = ({
  isAuthorized,
  permissions,
  component: Component,
  ...rest
}) => (
  <Route
    {...rest}
    render={({ match, location }) => {
      const { projectId } = match.params;
      if (!isAuthorized()) {
        return (
          <Redirect
            to={{
              pathname: '/',
              state: { from: location }
            }}
          />
        );
      }

      if (projectId) {
        const hasPermission = userTokenService.hasProjectTokenProp(
          projectId,
          permissions
        );

        if (hasPermission) {
          return <Component />;
        }
      }

      return (
        <Redirect
          to={{
            pathname: '/unauthorized',
            state: { from: location }
          }}
        />
      );
    }}
  />
);

ProjectPermissionRoute.propTypes = {
  permissions: PropTypes.arrayOf(PropTypes.string)
};

ProjectPermissionRoute.defaultProps = {
  permissions: []
};

export default ProjectPermissionRoute;
