import React, { useState } from 'react';
import { MiniLoader } from '@pwc/pwc-deals-ui';
import {
  viewHypothesisPath,
  viewInsightPath
} from '../../../common/services/urlService';
import ControlledAccordion from '../../../common/components/Accordion/ControlledAccordion';
import { getConnectedContent } from '../ConnectedContentService';
import NarrativeSection from '../NarrativeSection/NarrativeSection';
import './AccordionConnectedContent.scss';
import { analysisLinkBuilder } from '../../analyses/common/analysisUrlHelper';

const ConnectedContentAccordion = ({
  hasConnectedContent,
  projectId,
  contentId,
  index,
  showBody = false
}) => {
  const [connectedContent, setConnectedContent] = useState(null);
  const hasInsights =
    connectedContent &&
    connectedContent.insights &&
    connectedContent.insights.length > 0;
  const hasAnalyses =
    connectedContent &&
    connectedContent.analyses &&
    connectedContent.analyses.length > 0;
  const hasKeyFocusAreas =
    connectedContent &&
    connectedContent.hypotheses &&
    connectedContent.hypotheses.length > 0;
  const isFetchedConnectedContent =
    hasInsights || hasAnalyses || hasKeyFocusAreas;

  const onAccordionExpand = () => {
    const fetchConnectedContent = async () => {
      const fetchedConnectedContent = await getConnectedContent(
        projectId,
        contentId
      );
      setConnectedContent(fetchedConnectedContent);
    };
    if (projectId && contentId) {
      fetchConnectedContent();
    }
  };

  return hasConnectedContent ? (
    <div
      className="accordion-connected-content"
      data-testid={`accordion-connected-content-${index}`}
    >
      <ControlledAccordion
        title="Connected content"
        onExpand={onAccordionExpand}
      >
        {!isFetchedConnectedContent && (
          <div className="accordion-connected-content loading">
            <MiniLoader />
          </div>
        )}
        {hasInsights && (
          <NarrativeSection
            title="Insights"
            narratives={connectedContent.insights}
            getViewPath={insightId => viewInsightPath(projectId, insightId)}
            index={index}
            showBody={showBody}
            truncate={false}
          />
        )}
        {hasAnalyses && (
          <NarrativeSection
            title="Analyses"
            narratives={connectedContent.analyses}
            getViewPath={analysisLinkBuilder(
              connectedContent.analyses,
              projectId
            )}
            index={index}
            showBody={showBody}
            truncate={false}
          />
        )}
        {hasKeyFocusAreas && (
          <NarrativeSection
            title="Key Focus Areas"
            narratives={connectedContent.hypotheses}
            getViewPath={hypothesisId =>
              viewHypothesisPath(projectId, hypothesisId)
            }
            index={index}
            showBody={showBody}
            truncate={false}
          />
        )}
      </ControlledAccordion>
    </div>
  ) : null;
};

export default ConnectedContentAccordion;
