import React from 'react';
import { useSelector } from 'react-redux';
import classNames from 'classnames';

import './FileHeaders.scss';
import { getFilesState } from '../../../common/redux/fileUpload/fileUploadSlice';
import authorizationService from '../../../common/services/security/authorizationService';
import CONSTANTS from '../Constants';

const FileHeaders = () => {
  const { data } = useSelector(getFilesState);
  const isClientUser = authorizationService.isClientUser();

  return (
    <div
      className={classNames('file-headers', {
        'file-headers--disabled': !!data && data.files.length === 0,
        'file-headers--three-columns': isClientUser
      })}
      data-testid="uploadedfiles-fileheaders"
    >
      <div> {CONSTANTS.HEADERS.FILE_NAME}</div>
      <div> {CONSTANTS.HEADERS.DATE_UPLOADED}</div>
      {!isClientUser && (
        <>
          <div> {CONSTANTS.HEADERS.UPLOADED_BY}</div>
          <div> {CONSTANTS.HEADERS.APPROVAL_STATUS}</div>
        </>
      )}
    </div>
  );
};

export default FileHeaders;
