import { Button } from '@pwc/pwc-deals-ui';
import React from 'react';

import './DeleteConfirmationDialog.scss';
import { useDispatch, useSelector } from 'react-redux';
import { removeFile } from '../../../../common/redux/fileUpload/fileUploadThunk';

const DeleteConfirmationDialog = ({ onCloseClick, fileId }) => {
  const dispatch = useDispatch();
  const projectId = useSelector(state => state.project && state.project.id);

  return (
    <div
      className="deleteConfirmation-dialog"
      data-testid="deleteConfirmation-dialog"
    >
      <div>Delete this file?</div>

      <div>This will permanently remove the file from the platform.</div>

      <div>
        <div>
          <Button
            onClick={onCloseClick}
            className="button-outline button-small"
            data-testid="menu-button-show-deleteConfirmation-close"
            disabled={!onCloseClick}
          >
            Cancel
          </Button>
        </div>

        <div>
          <Button
            className="button-small"
            onClick={() => {
              dispatch(removeFile(projectId, fileId));
              onCloseClick();
            }}
          >
            Yes, delete
          </Button>
        </div>
      </div>
    </div>
  );
};

export default DeleteConfirmationDialog;
