import React, { useState } from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { useHistory } from 'react-router-dom';
import { Button, Icon, ProgressSteps } from '@pwc/pwc-deals-ui';
import { postProject } from '../../../common/services/projectService';
import { refreshTokens } from '../../../common/services/security/userAuthService';
import userTokenService from '../../../common/services/security/userTokenService';
import authorizationService from '../../../common/services/security/authorizationService';
import FormLayout from '../../../common/components/layouts/FormLayout';
import MetaDataStep from './steps/MetaDataStep/MetaDataStep';
import TeamMembersStep from './steps/TeamMembersStep/TeamMembersStep';
import SubmitRequestStep from './steps/SubmitRequestStep/SubmitRequestStep';
import RequestError from './steps/RequestError/RequestError';
import RequestSubmittedStep from './steps/RequestSubmittedStep/RequestSubmittedStep';
import './CreateProject.scss';
import {
  currencyOptions,
  financialMagnitudeOptions,
  financialsDecimalCountOptions,
  getDefaultValue
} from '../common/projectOptions';

const CreateProject = () => {
  const history = useHistory();
  const [currentStep, setCurrentStep] = useState(1);
  const [stepOneFormValues, setStepOneFormValues] = useState({
    territory:
      process.env.REACT_APP_TERRITORY || Constants.TERRITORIES.UNITED_STATES,
    services: [],
    currency: getDefaultValue(
      currencyOptions,
      process.env.REACT_APP_TERRITORY || Constants.TERRITORIES.UNITED_STATES
    ),
    financialsMagnitude: getDefaultValue(financialMagnitudeOptions),
    financialsDecimalCount: getDefaultValue(financialsDecimalCountOptions)
  });
  const [stepTwoFormValues, setStepTwoFormValues] = useState({});
  const [hasError, setHasError] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [newProjectId, setNewProjectId] = useState(false);

  const steps = [
    { title: 'first', description: 'Project details' },
    { title: 'second', description: 'Team details' },
    { title: 'third', description: 'Create project' }
  ];

  const onBackClick = () => {
    if (currentStep === 1) {
      history.goBack();
    } else {
      setCurrentStep(currentStep - 1);
    }
  };
  const onCancel = () => {
    history.replace(`/projects/`);
  };

  const onStepOneCompleted = async formValues => {
    setStepOneFormValues(formValues);
    setCurrentStep(2);
  };

  const onStepTwoCompleted = async users => {
    setStepTwoFormValues(users);
    setCurrentStep(3);
  };

  const onStepThreeCompleted = async () => {
    const email = await userTokenService.getUserEmail();
    const defaultSelf = {
      email,
      role: Constants.USER_ROLES.TEAM_MEMBER,
      pendingApproval: false
    };
    const usersWithSelf = stepTwoFormValues.find(u => u.email === email)
      ? stepTwoFormValues
      : [...stepTwoFormValues, defaultSelf];

    const project = {
      territory: stepOneFormValues.territory,
      clientName: stepOneFormValues.clientName,
      industryId: stepOneFormValues.industryId,
      name: stepOneFormValues.projectName,
      restriction: parseInt(stepOneFormValues.restriction, 10),
      serviceIds: stepOneFormValues.services.map(sp => sp.value),
      target: stepOneFormValues.target,
      type: parseInt(stepOneFormValues.dealType, 10),
      currency: stepOneFormValues.currency,
      financialsMagnitude: stepOneFormValues.financialsMagnitude,
      financialsDecimalCount: stepOneFormValues.financialsDecimalCount,
      consentLevel: stepOneFormValues.consentLevel,
      users: usersWithSelf.map(user => ({
        emailAddress: user.email,
        roleName: user.role,
        pendingApproval: user.pendingApproval
      }))
    };

    setIsSaving(true);
    setHasError(false);
    try {
      const projectId = await postProject(project);
      const refreshTokenSet = await refreshTokens();
      authorizationService.setTokens(refreshTokenSet);
      setNewProjectId(projectId);
      setCurrentStep(4);
    } catch (e) {
      setCurrentStep(4);
      setHasError(true);
    } finally {
      setIsSaving(false);
    }
  };

  return (
    <div className="project-create">
      <FormLayout headerComponent={<div />}>
        {currentStep !== 4 && (
          <Button
            className="button-menu back-button mb-3"
            type="button"
            onClick={onBackClick}
            data-testid="back-button"
          >
            <Icon name="left-chevron" width={14} className="fill icon-orange" />
            Back
          </Button>
        )}
        {currentStep === 4 ? (
          <h1 data-testid="project-form-header">
            {hasError ? (
              <span>
                {' '}
                <Icon name="alert" width={30} className="fill icon-orange" />
                &nbsp;Problem creating the project
              </span>
            ) : (
              <span>New project request underway</span>
            )}
          </h1>
        ) : (
          <h1 data-testid="project-form-header">Create a new project</h1>
        )}
        <ProgressSteps current={currentStep} steps={steps} />
        <div className="separator" />
        {currentStep === 1 && (
          <MetaDataStep
            initialValues={stepOneFormValues}
            onCancel={onCancel}
            onStepCompleted={onStepOneCompleted}
          />
        )}{' '}
        {currentStep === 2 && (
          <TeamMembersStep
            initialValues={stepTwoFormValues}
            onCancel={onCancel}
            onStepCompleted={onStepTwoCompleted}
            isSaving={isSaving}
            hasError={hasError}
          />
        )}{' '}
        {currentStep === 3 && (
          <SubmitRequestStep
            onCancel={onCancel}
            onStepCompleted={onStepThreeCompleted}
            isSaving={isSaving}
          />
        )}{' '}
        {currentStep === 4 && hasError === false && (
          <RequestSubmittedStep
            projectId={newProjectId}
            projectName={stepOneFormValues.projectName}
          />
        )}{' '}
        {currentStep === 4 && hasError && (
          <RequestError onTryAgainClick={onStepThreeCompleted} />
        )}
      </FormLayout>
    </div>
  );
};

export default CreateProject;
