import PropTypes from 'prop-types';
import React from 'react';

import './source-display.scss';

const SourceDisplay = ({ sourceText }) => (
  <div className="source-display" data-testid="source-display">
    Source: {sourceText}
  </div>
);

SourceDisplay.propTypes = {
  sourceText: PropTypes.string.isRequired
};

export default SourceDisplay;
