import React, { useState } from 'react';
import { Modal } from 'react-bootstrap';
import StructuredAnalysisViewingToggledDataBanner from '../StructuredAnalysisViewingToggledDataBanner/StructuredAnalysisViewingToggledDataBanner';
import './StructuredAnalysisContainer.scss';

export const withStructuredContainer = WrappedStructuredContainer => () => (
  <StructuredAnalysisContainer>
    <WrappedStructuredContainer />
  </StructuredAnalysisContainer>
);

const ToggledDisclaimerModal = ({ showModal, closeModal }) => {
  const toggledDisclaimerTitle = 'Visual Interactive Disclaimer';
  const toggledDisclaimerMessage =
    'Any adjustments to the output prepared by PwC are at your discretion and for your convenience and general information purposes only. PwC accepts no liability to you or anyone else in connection with the results.';

  return (
    <Modal
      show={showModal}
      onHide={() => closeModal(false)}
      className="disclaimer-modal"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title className="disclaimer-modal-title">
          <h4>{toggledDisclaimerTitle}</h4>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="scrollable disclaimer-modal-body">
        <div
          data-testid="project-disclaimer-content"
          className="project-disclaimer-content"
        >
          {toggledDisclaimerMessage}
        </div>
      </Modal.Body>
    </Modal>
  );
};

const StructuredAnalysisContainer = ({ children }) => {
  const [showModal, openModal] = useState(false);

  return (
    <>
      <StructuredAnalysisViewingToggledDataBanner
        showDisclaimerModal={openModal}
      />
      <ToggledDisclaimerModal showModal={showModal} closeModal={openModal} />
      {children}
    </>
  );
};

export default StructuredAnalysisContainer;
