import React from 'react';
import { Link } from 'react-router-dom';
import Separator from '../../../common/components/Separator';
import ContentActionsRowMenu from '../../../common/components/ContentActions/ContentActionsRowMenu';
import { viewAnalysisPath } from '../../../common/services/urlService';
import ShowForPermissions from '../../security/ShowForPermissions';
import ConnectedContentAccordion from '../../narratives/AccordionConnectedContent/ConnectedContentAccordion';
import { structuredAnalysisLinkToDisplay } from '../../../common/engine/utilities/structured-data-utilities';
import './AnalysesGridCard.scss';
import TagList from '../common/TagList/TagList';
import PillContainer from '../../../common/components/Pills/PillContainer';
import { sanitize } from '../../../common/services/sanitizeService';

const AnalysesGridCard = ({
  analysis,
  projectId,
  onEditMenuClick,
  onDeleteMenuClick,
  index
}) => {
  return (
    <div className="card-container">
      <div className="row">
        <div className="col down">
          <div className="card-title">
            <Link
              to={
                analysis.isUnStructured
                  ? viewAnalysisPath(projectId, analysis.frontendIdentifier)
                  : structuredAnalysisLinkToDisplay(projectId, analysis.title)
              }
              data-testid="analysis-title"
              aria-label="View Analysis"
            >
              <h2>{analysis.displayName || analysis.title}</h2>
            </Link>
          </div>
          <span
            className="card-description"
            data-testid="analysis-description"
            dangerouslySetInnerHTML={{ __html: sanitize(analysis.body) }}
          />

          <ConnectedContentAccordion
            hasConnectedContent={analysis.hasConnectedContent}
            projectId={projectId}
            contentId={analysis.contentId}
            index={index}
          />
        </div>
      </div>

      <Separator />

      <div className="card-action-footer">
        <PillContainer
          content={analysis}
          projectId={projectId}
          allowDraftPill
        />
        <div className="card-actions">
          <ShowForPermissions
            projectId={projectId}
            permissions={['Contribute']}
          >
            <ContentActionsRowMenu
              projectId={projectId}
              itemId={analysis.frontendIdentifier}
              onEditMenuClick={onEditMenuClick}
              onDeleteMenuClick={onDeleteMenuClick}
            />
          </ShowForPermissions>
        </div>
      </div>
      <div className="row">
        {analysis.tags && analysis.tags.length > 0 && (
          <TagList tags={analysis.tags} testId="analysis-tag-list" />
        )}
      </div>
    </div>
  );
};

export default AnalysesGridCard;
