import { createSlice } from '@reduxjs/toolkit';

export const initialState = {
  settings: {
    viewGrid: true
  },
  loading: false,
  loaded: false
};

const userSettingsSlice = createSlice({
  name: 'userSettings',
  initialState,
  reducers: {
    setUserSettings: (state, action) => {
      return {
        ...state,
        settings: { ...state.settings, ...action.payload },
        loading: false,
        loaded: true
      };
    },
    setUserSettingsIsLoading: state => {
      return {
        ...state,
        loading: true
      };
    }
  }
});

// Actions
export const {
  setUserSettings,
  setUserSettingsIsLoading
} = userSettingsSlice.actions;

// Selectors
export const getUserSettingsSelector = state => state.userSettings;
export const getIsDarkModeSelector = state =>
  state.userSettings.settings.darkMode;

export default userSettingsSlice.reducer;
