import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Select } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import { find } from 'lodash';
import { getSharedHypotheses } from '../../../../../hypotheses/hypothesesService';
import { getExcelCharts } from '../../../../../contentLibrary/libraryItemService';
import './TopicContentSelectSearch.scss';
import NarrativesContext from '../../../../../../common/contexts/NarrativesContext';

const TopicContentSelectSearch = ({
  handleSelectChange,
  selectedContentId,
  contentType,
  placeholderText = 'Search and select'
}) => {
  const { hypotheses } = useContext(NarrativesContext) || { hypotheses: [] };
  const [contentOptions, setContentOptions] = useState([]);
  const [search, setSearch] = useState('');
  const { projectId } = useParams();

  useEffect(() => {
    const fetch = async () => {
      if (contentType === Constants.TOPIC_PANEL_TYPES.KEY_FOCUS_AREA.asString) {
        const results = await getSharedHypotheses(projectId);
        const options = results.map(r => {
          const latestHypothesis = find(hypotheses, {
            contentThreadId: r.contentThreadId
          });
          return {
            title: r.title,
            id: latestHypothesis.contentThreadId,
            value: {
              contentId: latestHypothesis.contentId,
              contentThreadId: latestHypothesis.contentThreadId
            }
          };
        });
        setContentOptions(options);
      }
      if (contentType === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString) {
        getExcelCharts(projectId).then(results => {
          const options = results.map(r => {
            return { title: r.name, id: r.id, value: r.id };
          });
          setContentOptions(options);
        });
      }
    };
    fetch();
  }, [projectId, contentType, hypotheses]);

  useEffect(() => {
    if (contentOptions.length === 0) {
      return;
    }

    const selectedOption = contentOptions.find(content => {
      return content.title === search;
    });

    if (selectedOption) {
      handleSelectChange(selectedOption.value);
    } else {
      handleSelectChange(null);
    }
  }, [handleSelectChange, contentOptions, search]);

  useEffect(() => {
    if (contentOptions.length && parseInt(selectedContentId, 10)) {
      const selectedContent = contentOptions.find(
        h => h.id === selectedContentId
      );

      if (selectedContent) {
        setSearch(selectedContent.title);
      }
    }
  }, [selectedContentId, contentOptions]);

  const searchField = {
    name: `topic-${contentType}-search`,
    value: search
  };

  return (
    <div className="topic-text-input-width-limit">
      <Select
        label={
          contentType === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString
            ? 'Chart Name'
            : 'Key Focus Area Name'
        }
        data-testid={`select-topic-${
          contentType === Constants.TOPIC_PANEL_TYPES.EXCEL_CHART.asString
            ? 'chart'
            : 'kfa'
        }`}
        field={searchField}
        placeholder={placeholderText}
        options={contentOptions.map(content => {
          return {
            value: content.title,
            key: content.id.toString()
          };
        })}
        onChange={e => setSearch(e.target.value)}
        value={search}
        basic
      />
    </div>
  );
};

export default TopicContentSelectSearch;
