import React from 'react';
import PropTypes from 'prop-types';
import { TextArea } from '@pwc/pwc-deals-ui';
import './TitleFieldset.scss';

const TitleFieldset = ({
  field,
  label,
  error,
  className,
  basic,
  placeholder,
  disabled,
  onBlur
}) => {
  return (
    <>
      <TextArea
        error={error}
        label={label}
        field={field}
        basic={basic}
        className={className}
        placeholder={placeholder}
        disabled={disabled}
        onBlur={onBlur}
        textareaLineHeight={44}
      />
    </>
  );
};

TitleFieldset.propTypes = {
  label: PropTypes.string,
  error: PropTypes.string
};

TitleFieldset.defaultProps = {
  error: '',
  label: ''
};

export default TitleFieldset;
