import { Loader } from '@pwc/pwc-deals-ui';
import { Constants, ErrorUtilities } from '@pwc/pwc-deals-web-common';
import { Formik } from 'formik';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import { getCalcEngineIsProcessing } from '../../../common/engine/reducers/calcEngine';
import {
  getAdjustmentOptionsForDdl,
  getAdjustmentOptionsForNwc,
  getAdjustmentOptionsForQofE
} from '../../../common/engine/selectors/adjustmentSelectors';
import { getLinkedAdjustmentsByCode } from '../../../common/engine/selectors/linkedAdjustmentsByDocument';
import { clearDataAndtransformAllIngestedStructuredDocumentsThunk } from '../../../common/engine/thunks/clearDataAndtransformAllIngestedStructuredDocumentsThunk';
import { getNarratives } from '../../narratives/narrativesService';
import { getLinksFromNarratives } from '../../narratives/narrativesUtilities';
import AnalysisForm from '../common/AnalysisForm';
import { analysisSchema, emptyAnalysis } from '../common/analysisModel';
import { saveLinkedAdjustments } from '../../../common/engine/services/structuredAnalysisLinkedAdjustmentsService';
import {
  getStructuredAnalysisDocumentsWithData,
  putStructuredAnalyses,
  searchAnalyses
} from '../../../common/engine/services/structuredAnalysisService';
import { getStructuredAnalysisViewLoadingInProcess } from '../../../common/engine/reducers/structuredAnalysisViewSlice';

const EditStructuredAnalysis = ({
  type,
  title,
  getReturnPath,
  showLinkedAdjustments,
  documentCode
}) => {
  const dispatch = useDispatch();
  const { projectId } = useParams();
  const history = useHistory();
  const [analysis, setAnalysis] = useState(emptyAnalysis());
  const [currentTags, setCurrentTags] = useState([]);
  const [tagsUpdated, setTagsUpdated] = useState(false);
  const [
    structuredAnalysisDocumentsWithData,
    setStructuredAnalysisDocumentsWithData
  ] = useState([]);
  const [errorType, setErrorType] = useState();
  const [isSaving, setIsSaving] = useState(false);
  const [narratives, setNarratives] = useState(null);
  const [docsWithDataFetchStatus, setDocsWithDataFetchStatus] = useState('');
  const [analysisFetchStatus, setAnalysisFetchStatus] = useState('');
  const isProcessing = useSelector(getCalcEngineIsProcessing);
  const initializingData = useSelector(
    getStructuredAnalysisViewLoadingInProcess
  );
  const initialLinkedAdjustments = useSelector(
    getLinkedAdjustmentsByCode(documentCode)
  );

  const defaultStatusValue = 300;
  const gotoViewChart = () => history.replace(getReturnPath(projectId));

  const adjustmentOptions = {
    [Constants.QUALITY_OF_EARNINGS]: useSelector(getAdjustmentOptionsForQofE),
    [Constants.NET_WORKING_CAPITAL]: useSelector(getAdjustmentOptionsForNwc),
    [Constants.DEBT_AND_DEBT_LIKE]: useSelector(getAdjustmentOptionsForDdl)
  };

  useEffect(() => {
    if (projectId) {
      setDocsWithDataFetchStatus('loading');
      getStructuredAnalysisDocumentsWithData(projectId).then(
        structuredDocumentsResponse => {
          const targetDocumentOptions = structuredDocumentsResponse
            .filter(
              document =>
                document.hasDetailView === true &&
                document.code !== documentCode
            )
            .map(structuredAnalysisDocument => ({
              value: structuredAnalysisDocument.code,
              label: structuredAnalysisDocument.displayName
            }));

          setStructuredAnalysisDocumentsWithData(() => targetDocumentOptions);
          setDocsWithDataFetchStatus('');
        }
      );
    }
  }, [projectId, documentCode]);

  useEffect(() => {
    const fetch = async () => {
      setAnalysisFetchStatus('loading');
      const response = await searchAnalyses(projectId, type);

      if (!response.length) {
        history.push('/unauthorized');
      } else {
        const returnedAnalysis = response[0];

        returnedAnalysis.title = title;
        setAnalysis(returnedAnalysis);

        if (returnedAnalysis.contentThreadId) {
          const allContent = await getNarratives(projectId);
          setNarratives({
            ...allContent,
            analyses: allContent.analyses.filter(
              i =>
                i.contentThreadId.toString() !==
                returnedAnalysis.contentThreadId.toString()
            )
          });
        }
        setAnalysisFetchStatus('');
      }
    };
    fetch();
  }, [projectId, title, type, history, documentCode, dispatch]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        dispatch(
          clearDataAndtransformAllIngestedStructuredDocumentsThunk(
            projectId,
            documentCode,
            false
          )
        );
        // eslint-disable-next-line no-empty
      } catch (_) {}
    };

    if (projectId) {
      fetchData();
    }
  }, [dispatch, projectId, documentCode]);

  const handleSave = async ({
    body,
    tags,
    linkedAdjustments,
    source,
    notes
  }) => {
    setIsSaving(true);
    try {
      const links = getLinksFromNarratives(narratives, analysis.contentId);
      setCurrentTags(tags);
      setTagsUpdated(true);
      await putStructuredAnalyses(projectId, {
        contentThreadId: analysis.contentThreadId,
        contentId: analysis.contentId,
        contentSubType: analysis.contentSubType,
        title,
        body,
        source,
        notes,
        tags,
        links
      });

      await saveLinkedAdjustments(
        projectId,
        initialLinkedAdjustments,
        linkedAdjustments,
        dispatch
      );

      setErrorType();
      gotoViewChart();
    } catch (e) {
      setIsSaving(false);
      setErrorType(ErrorUtilities.getContentErrorType(e));
    }
  };

  const initialFormTags = (() => {
    const tagsFromAnalysis = (analysis.tags || []).map(tag => tag.text);
    return tagsUpdated ? currentTags : tagsFromAnalysis;
  })();

  const initialFormValues = {
    title: analysis.title,
    body: analysis.body || '',
    status: analysis.status || defaultStatusValue,
    source: analysis.source || '',
    notes: analysis.notes || '',
    tags: initialFormTags,
    linkedAdjustments: [...initialLinkedAdjustments]
  };

  const isLoading =
    docsWithDataFetchStatus !== '' ||
    analysisFetchStatus !== '' ||
    isProcessing ||
    initializingData;

  if (isLoading) {
    return <Loader dataTestId="structured-loading-indicator" />;
  }

  return (
    <Formik
      initialValues={initialFormValues}
      enableReinitialize
      onSubmit={handleSave}
      validationSchema={analysisSchema}
      validateOnChange
    >
      {({ errors, touched, isValid, values, setFieldValue }) => (
        <AnalysisForm
          documentCode={documentCode}
          onCancel={gotoViewChart}
          isSaving={isSaving}
          errorType={errorType}
          errors={errors}
          touched={touched}
          isValid={isValid}
          values={values}
          setFieldValue={setFieldValue}
          canEditTitle={false}
          showLibraryItemPicker={false}
          showLinkedAdjustments={showLinkedAdjustments}
          showNotes
          projectId={projectId}
          parentContentId={analysis.contentId || 0}
          narratives={narratives}
          setNarratives={setNarratives}
          adjustmentOptions={adjustmentOptions}
          onSaveText="Updating..."
          standbyText="Update"
          targetDocumentOptions={structuredAnalysisDocumentsWithData}
        >
          <h1>Edit Analysis</h1>
        </AnalysisForm>
      )}
    </Formik>
  );
};

EditStructuredAnalysis.propTypes = {
  type: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  getReturnPath: PropTypes.func.isRequired,
  showLinkedAdjustments: PropTypes.bool
};

EditStructuredAnalysis.defaultProps = {
  showLinkedAdjustments: false
};
export default EditStructuredAnalysis;
