import React from 'react';
import PropTypes from 'prop-types';
import _orderBy from 'lodash/orderBy';
import { Icon } from '@pwc/pwc-deals-ui';
import './TagList.scss';

const TagList = ({ tags, testId }) => {
  if (tags && tags.length === 0) {
    return null;
  }

  const orderedTags = _orderBy(tags, [tag => tag.text.toLowerCase()], ['asc']);

  return (
    <div className="tags-list-container" data-testid={testId}>
      <div>
        <Icon
          name="tag"
          className="fill tag-icon"
          width={15}
          data-testid="draft-icon"
        />
      </div>
      <div className="tags">
        {orderedTags.map((tag, i) => {
          return (
            // eslint-disable-next-line react/no-array-index-key
            <span key={`${tag.text}-${i}`} data-testid="tag">
              <span className="punctuation">{i ? ', ' : ''}</span>
              <span className="tag-text">{tag.text}</span>
            </span>
          );
        })}
      </div>
    </div>
  );
};

TagList.propTypes = {
  tags: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.number,
      text: PropTypes.string
    })
  ),
  testId: PropTypes.string.isRequired
};

TagList.defaultProps = {
  tags: []
};

export default TagList;
