import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import { useSelector } from 'react-redux';
import { getLoggedInUser } from '../../redux/security/loggedInUser/loggedInUserSelector';
import ShowForPermissions from '../../../features/security/ShowForPermissions';
import ShowForRole from '../../../features/security/ShowForRole';
import './EmptyContentCard.scss';

const EmptyContentCard = ({
  projectId,
  singularName,
  pluralName,
  children
}) => {
  const loggedInUser = useSelector(getLoggedInUser) || { givenName: 'User' };

  const dasherizedPluralName = pluralName.split(' ').join('-');
  const pluralNameCardTestId = `empty-${dasherizedPluralName}-card`;
  const pluralNameCardCreateTestId = `empty-${dasherizedPluralName}-card-create-content`;
  const nothingSharedMessage = `No ${pluralName} have been shared yet`;
  const helloMessage = `Hello ${loggedInUser.givenName},`;
  const createFirstMessage = `Create the first ${singularName} for this project`;

  return (
    <div
      className="empty-content-card content-container"
      data-testid={pluralNameCardTestId}
    >
      <ShowForRole
        projectId={projectId}
        roles={Constants.USER_ROLE_GROUPS.CLIENT}
      >
        <p className="nothing-shared">{nothingSharedMessage}</p>
      </ShowForRole>

      <ShowForPermissions projectId={projectId} permissions={['Contribute']}>
        <div data-testid={pluralNameCardCreateTestId}>
          <h2>{helloMessage}</h2>
          <h2>{createFirstMessage}</h2>
          {children}
        </div>
      </ShowForPermissions>
    </div>
  );
};

export default EmptyContentCard;
