/* eslint-disable no-bitwise */
import React, { useCallback, useState } from 'react';
import { cond, constant, stubTrue } from 'lodash';
import { TextInput, Button, Loader, Icon } from '@pwc/pwc-deals-ui';
import { useUserSearch } from '../../hooks/useUserSearch';
import { useClickedOutsideOfRef } from '../../hooks/useClickedOutsideOfRef';
import './UserSearchSelect.scss';

const nameIsLongEnough = (name, startSearchingLength) =>
  (name || '').trim().length >= startSearchingLength;

const hasAvailableUsers = availableUsers =>
  !availableUsers || availableUsers.length === 0;

const renderLoadingIndicator = constant(
  <div className="user-search-select-loading">
    <Loader dataTestId="user-search-select-loading" />
  </div>
);

const renderErrorMessage = constant(
  <div className="user-search-select-error">
    <span>
      <Icon name="alert" className="fill icon-yellow" />
      We are currently unable to search for users.
    </span>
  </div>
);

const renderNotEnouchCharactersMessage = constant(
  <div className="user-search-select-no-results">
    <span>Enter More Characters To Search</span>
  </div>
);

const renderNoMatchingUsersFoundMessage = constant(
  <div className="user-search-select-no-results">
    <span>No Matching Users Found</span>
  </div>
);

const renderUsersList = (availableUsers, selectUser) => () => (
  <div className="user-search-select-list">
    <ul>
      {availableUsers.map(user => (
        <li key={user.email}>
          <Button
            data-testid="user-search-select-add-user-button"
            onClick={() => selectUser(user)}
            type="button"
          >
            {`${user.firstName} ${user.lastName} (${user.email})`}
          </Button>
        </li>
      ))}
    </ul>
  </div>
);

const getUserSelectConfig = () => {
  return {
    maxResultCount: process.env.SEARCH_USER_MAX_RESULT_COUNT || 10,
    debounceTime: process.env.SEARCH_USER_DELAY_BEFORE_SEND_SEARCH || 250,
    startSearchingLength:
      process.env.SEARCH_USER_MIN_CHARACTERS_TO_SEND_SEARCH || 3
  };
};

export const UserSearchSelect = ({
  role,
  onUserSelected,
  unavailableUsers
}) => {
  const [name, setName] = useState('');

  const roleIdPart = role
    .split(' ')
    .join('-')
    .toLowerCase();

  const config = getUserSelectConfig();

  const {
    usersFound,
    searchUsers,
    clearUsers,
    isLoading,
    hasError
  } = useUserSearch(
    role,
    config.maxResultCount,
    config.debounceTime,
    config.startSearchingLength
  );

  const availableUsers = !usersFound
    ? []
    : usersFound.filter(
        user => !unavailableUsers.some(unUser => unUser.email === user.email)
      );

  const handleTextChange = useCallback(
    event => {
      const text = event.target.value;
      setName(text);
      searchUsers(text);
    },
    [searchUsers]
  );

  const resetSearch = () => {
    setName('');
    clearUsers();
  };

  const selectUser = user => {
    onUserSelected({ ...user, role });
    resetSearch();
  };

  const ref = useClickedOutsideOfRef(
    wasClickedOutside => wasClickedOutside && resetSearch()
  );

  const renderPopup = cond([
    [() => !name, constant(null)],
    [() => isLoading, renderLoadingIndicator],
    [() => hasError, renderErrorMessage],
    [
      () =>
        hasAvailableUsers(availableUsers) &&
        nameIsLongEnough(name, config.startSearchingLength),
      renderNoMatchingUsersFoundMessage
    ],
    [() => hasAvailableUsers(availableUsers), renderNotEnouchCharactersMessage],
    [stubTrue, renderUsersList(availableUsers, selectUser)]
  ]);

  return (
    <div className="user-search-select" ref={ref}>
      <TextInput
        basic
        data-testid={`user-search-select-input-${roleIdPart}`}
        field={{
          name: `user-search-select-input-${roleIdPart}`,
          value: name || '',
          onChange: handleTextChange
        }}
        type="text"
        autoComplete="off"
        iconLeft="search"
        placeholder="Name"
        className={name ? 'has-value' : ''}
      />
      {renderPopup()}
    </div>
  );
};
export default UserSearchSelect;
