import React from 'react';
import { Link } from 'react-router-dom';
import { Constants } from '@pwc/pwc-deals-web-common';

const TermsConditionsLinks = () => {
  return Constants.TERMS_AND_CONDITIONS_LINKS.map(link => (
    <Link className="footer-link" to={`/policies/${link.key}`} key={link.key}>
      {link.text}
    </Link>
  ));
};

export default TermsConditionsLinks;
