import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import featureFlags from '../../../../common/helpers/featureFlags';

import {
  fetchDivestitureDiagnosticResults,
  getDivestitureDiagnosticResults,
  getFetchDivestitureDiagnosticResultsStatus
} from '../../../divestitureDiagnostic/divestitureDiagnosticSlice';
import ShowForPermissions from '../../../security/ShowForPermissions';
import { projectType } from '../../common/projectOptions';
import WelcomeBoxBeginQuestionnaire from '../WelcomeBox/WelcomeBoxBeginQuestionnaire/WelcomeBoxBeginQuestionnaire';
import WelcomeBoxDealComplexity from '../WelcomeBox/WelcomeBoxDealComplexity/WelcomeBoxDealComplexity';
import WelcomeBoxQuickStart from '../WelcomeBox/WelcomeBoxQuickStart/WelcomeBoxQuickStart';

import './DiagnosticContainer.scss';

const DiagnosticContainer = ({ id, type, showDiagnostic }) => {
  const dispatch = useDispatch();

  const diagnosticData = useSelector(getDivestitureDiagnosticResults);

  const isBuySideProject = projectType.buy.id === type;
  const isSellSideProject = projectType.sell.id === type;
  const shouldShowDiagnostic = showDiagnostic && featureFlags.diagnostic;

  const { success: fetchResultsSuccess } = useSelector(
    getFetchDivestitureDiagnosticResultsStatus
  );

  useEffect(() => {
    if (!diagnosticData) {
      dispatch(fetchDivestitureDiagnosticResults(id));
    }
  }, [dispatch, diagnosticData, id]);

  return (
    <div>
      <ShowForPermissions projectId={id} permissions={['Contribute']}>
        {isBuySideProject && !diagnosticData && <WelcomeBoxQuickStart />}
        {isSellSideProject && (!shouldShowDiagnostic || diagnosticData) && (
          <WelcomeBoxQuickStart />
        )}
      </ShowForPermissions>
      {shouldShowDiagnostic && fetchResultsSuccess && (
        <>
          {isSellSideProject && !diagnosticData && (
            <WelcomeBoxBeginQuestionnaire projectId={id} />
          )}
          {isSellSideProject && diagnosticData && (
            <WelcomeBoxDealComplexity projectId={id} />
          )}
        </>
      )}
    </div>
  );
};

export default React.memo(
  DiagnosticContainer,
  (prev, next) =>
    prev.id === next.id &&
    prev.type === next.type &&
    prev.showDiagnostic === next.showDiagnostic
);
