const genDroppableStrings = (length, baseString) => {
  const baseStringLC = baseString.toLowerCase();
  const arr = Array(length)
    .fill(baseStringLC)
    .map((item, index) => {
      return `${item}-layout-area-${index + 1}`;
    });
  return arr;
};

