import decode from 'jwt-decode';
import { TextUtilities } from '@pwc/pwc-deals-web-common';

const jwtTokenKey = 'jwtToken';
const processTimeKey = 'processTime';
const getToken = () => {
  const jwtTokenValue = sessionStorage.getItem(jwtTokenKey);

  if (!jwtTokenValue) return null;

  return decode(jwtTokenValue);
};

const removeToken = () => {
  sessionStorage.clear();
};

const secondsFromExp = () => {
  const jwtToken = getToken();
  return jwtToken && jwtToken.exp - Date.now() / 1000;
};

const hasValidToken = () => {
  const jwtToken = getToken();
  const secondsUntilTokenExpires =
    jwtToken && Math.floor(jwtToken.exp - Date.now() / 1000);

  if (secondsUntilTokenExpires > 0) return true;

  removeToken();
  return false;
};

const setToken = async token => {
  sessionStorage.setItem(jwtTokenKey, token);
};

const storageKeyTemplatePrefix = 'projectJWT';
const storageKeyTemplate = `${storageKeyTemplatePrefix}{PROJ}`;
const setTokens = async tokenSet => {
  sessionStorage.setItem(jwtTokenKey, tokenSet.applicationToken);
  if (tokenSet.projectTokens) {
    tokenSet.projectTokens.forEach(projectToken => {
      const tokenStorageKey = storageKeyTemplate.replace(
        '{PROJ}',
        btoa(projectToken.projectId)
      );
      sessionStorage.setItem(tokenStorageKey, projectToken.token);
    });
  }
};

const updateProcessTime = () => {
  sessionStorage.setItem(processTimeKey, Date.now() / 1000);
};

const isClientUser = email => {
  if (email) return !TextUtilities.isPwCEmail(email);
  const token = getToken();
  if (!token) return true;
  if (!token.email) return true;
  return !TextUtilities.isPwCEmail(token.email);
};

const getEmail = () => {
  const token = getToken();
  return token && token.email ? token.email : null;
};

const getTokenValue = key => {
  const token = getToken();
  return token && token[key] ? token[key] : null;
};

const getExpirationInSeconds = () => {
  const expirationInMinutesEnvVar =
    process.env.REACT_APP_SECURITY_TOKEN_EXPIRATION_MINUTES;

  const paddingInMinutes = 2;

  if (expirationInMinutesEnvVar) {
    const parsedExpirationInMinutes = parseInt(expirationInMinutesEnvVar, 10);
    if (parsedExpirationInMinutes > paddingInMinutes) {
      return (parsedExpirationInMinutes - paddingInMinutes) * 60;
    }
  }

  return (15 - paddingInMinutes) * 60;
};

const canCreateProjects = () => {
  const token = getToken();
  return token && token['Create Project'] !== undefined;
};

const isAdmin = () => {
  const token = getToken();
  return token && token.Admin !== undefined;
};

const getUserTypeClass = email =>
  isClientUser(email) ? 'client-user' : 'pwc-user';

const authorizationService = {
  secondsFromExp,
  hasValidToken,
  setToken,
  setTokens,
  getToken,
  getTokenValue,
  updateProcessTime,
  isClientUser,
  getEmail,
  getExpirationInSeconds,
  canCreateProjects,
  isAdmin,
  getUserTypeClass
};

export default authorizationService;
