import _find from 'lodash/find';
import { structuredAnalysisLinkToDisplay } from '../../../common/engine/utilities/structured-data-utilities';
import { viewAnalysisPath } from '../../../common/services/urlService';

export const getViewPathForAnalysis = (projectId, analysis) => {
  const url = structuredAnalysisLinkToDisplay(projectId, analysis.title);

  return url === analysis.title
    ? viewAnalysisPath(projectId, analysis.contentThreadId)
    : url;
};

export const analysisLinkBuilder = (analyses, projectId) => targetId => {
  const targetAnalysis = _find(analyses, a => a.contentThreadId === targetId);
  return getViewPathForAnalysis(projectId, targetAnalysis);
};
