import { createSelector, createSlice } from '@reduxjs/toolkit';
import { cloneDeep, omit } from 'lodash';
import { Constants } from '@pwc/pwc-deals-web-common';

export const initialState = {
  content: null,
  status: {
    isLoading: false,
    hasError: false,
    errorMessage: null
  },
  sharedStatus: null,
  sections: null,
  versions: null,
  sharedVersion: null,
  draftVersion: null,
  libraryItemsNew: {
    items: []
  },
  libraryItemsShared: {
    items: []
  },
  libraryItemGroups: null,
  tags: null
};

const analysisSlice = createSlice({
  name: 'analysis',
  initialState,
  reducers: {
    resetAnalysis: () => initialState,
    fetchAnalysisBegan: state => ({
      ...state,
      isLoading: true,
      hasError: false,
      errorStatusCode: null
    }),
    fetchAnalysisSucceeded: (state, action) => {
      return {
        ...state,
        ...action.payload,
        isLoading: false,
        hasError: false,
        errorStatusCode: null
      };
    },
    fetchAnalysisFailed: (state, action) => ({
      ...state,
      isLoading: false,
      hasError: true,
      errorStatusCode: action.payload
    }),
    fetchAnalysisDetailBegin: state => {
      state.status.isLoading = true;
      state.status.hasError = false;
      state.status.errorMessage = null;
    },
    fetchAnalysisContent: (state, action) => {
      const {
        content,
        content: { sections }
      } = action.payload;
      const newContent = omit(content, ['sections']);
      state.content = newContent;
      state.sections = sections;
      state.status.isLoading = false;
      state.status.hasError = false;
    },
    fetchAnalysisLibraryItemGroups: (state, action) => {
      const { libraryItemGroups } = action.payload;
      state.libraryItemGroups = libraryItemGroups;
      state.status.isLoading = false;
      state.status.hasError = false;
    },
    fetchAnalysisDetailFailed: (state, action) => {
      const { isLoading, hasError, errorMessage } = action.payload;
      state.status.isLoading = isLoading;
      state.status.hasError = hasError;
      state.status.errorMessage = errorMessage;
    },
    fetchLibraryItemsNew: (state, action) => {
      const { libraryItems } = action.payload;
      state.libraryItemsNew.items = libraryItems;
    },
    updateSharedStatus: (state, action) => {
      state.sharedStatus = { ...action.payload };
    },
    updateVersions: (state, action) => {
      state.versions = [...action.payload];
    },
    updateSectionItems: state => {
      const {
        libraryItemsNew: { items: libraryItems }
      } = state;
      const currentSections = cloneDeep(state.sections);
      if (currentSections && currentSections.length > 0) {
        const newSections = currentSections.map(section => {
          const { items } = section;
          const newItems = items.map(item => {
            const clonedItem = cloneDeep(item);
            const updatedLibItemIndex = libraryItems.findIndex(
              _ => _.id === item.libraryItemId
            );
            if (updatedLibItemIndex > -1) {
              const updatedItem = libraryItems[updatedLibItemIndex];
              return {
                ...clonedItem,
                ...updatedItem
              };
            }
            return {
              ...clonedItem
            };
          });
          return {
            ...section,
            items: newItems
          };
        });
        state.sections = newSections;
      }
    },
    updateSections: (state, action) => {
      const { newSections } = action.payload;
      state.sections = newSections;
    },
    fetchAnalysisVersionsSuccess: (state, action) => {
      const { versions } = action.payload;
      state.versions = versions;
    },
    fetchAllTags: (state, action) => {
      const { tags } = action.payload;
      state.tags = tags;
    }
  }
});
export default analysisSlice.reducer;

// Actions
export const {
  resetAnalysis,
  fetchAnalysisBegan,
  fetchAnalysisSucceeded,
  fetchAnalysisFailed,
  fetchAnalysisDetailBegin,
  fetchAnalysisContent,
  fetchAnalysisLibraryItemGroups,
  fetchAnalysisDetailFailed,
  fetchLibraryItemsNew,
  updateSharedStatus,
  updateVersions,
  updateSectionItems,
  updateSections,
  updateSectionSharedItems,
  fetchAnalysisVersionsSuccess,
  fetchAllTags
} = analysisSlice.actions;

// Selectors
export const getCurrentAnalysis = state => state.analysis;
export const selectAnalysisContent = createSelector(
  getCurrentAnalysis,
  slice => slice.content
);
export const selectAnalysisSections = createSelector(
  getCurrentAnalysis,
  slice => slice.sections
);
export const getLibraryItemsNew = createSelector(
  getCurrentAnalysis,
  slice => slice.libraryItemsNew
);
export const getAnalysisStatus = createSelector(
  getCurrentAnalysis,
  slice => slice.status
);

export const selectAllTags = createSelector(
  getCurrentAnalysis,
  slice => slice.tags
);

export const getAnalysisIsShared = createSelector(
  getCurrentAnalysis,
  slice => slice.content && slice.content.shareWithAll
);

export const selectAnalysisVersions = createSelector(
  getCurrentAnalysis,
  slice => slice.versions
);

export const selectDraftVersion = createSelector(
  selectAnalysisVersions,
  versions =>
    versions &&
    versions.length > 0 &&
    versions.find(
      item => item.versionStatus === Constants.VERSION_STATUS.DRAFT.value
    )
);

export const selectSharedVersion = createSelector(
  selectAnalysisVersions,
  versions =>
    versions &&
    versions.length > 0 &&
    versions.find(
      item => item.versionStatus === Constants.VERSION_STATUS.SHARED.value
    )
);

export const selectSharedStatus = createSelector(
  getCurrentAnalysis,
  slice => slice.sharedStatus
);
