import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import userTokenService from '../../common/services/security/userTokenService';

const ShowForPermissions = ({
  projectId,
  permissions,
  children,
  noPermissionsView = null
}) => {
  const [hasProjectPermission, setHasProjectPermission] = useState(false);

  useEffect(() => {
    const hasPermission = userTokenService.hasProjectPermission(
      projectId,
      permissions
    );
    setHasProjectPermission(hasPermission);
  }, [projectId, permissions, setHasProjectPermission]);

  if (hasProjectPermission) {
    return children;
  }

  return noPermissionsView;
};

ShowForPermissions.propTypes = {
  projectId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  permissions: PropTypes.arrayOf(PropTypes.string)
};

export default ShowForPermissions;
