import {
  getUserSettingsSelector,
  setUserSettings,
  setUserSettingsIsLoading
} from './userSettingsSlice';
import {
  saveUserTheme,
  saveUserPerformanceCookiePreference,
  fetchUserSettings,
  setDoNotProcessForPendo
} from '../../services/userSettingsService';

export const loadUserSettingsThunk = () => async dispatch => {
  dispatch(setUserSettingsIsLoading());
  const getResponse = await fetchUserSettings();
  if (getResponse) {
    dispatch(setUserSettings(getResponse.data));
  }
};

export const changeUserThemeThunk = darkMode => async (dispatch, getState) => {
  const currentUserSettings = getUserSettingsSelector(getState());
  if (
    !currentUserSettings.loading &&
    !!currentUserSettings.settings.darkMode !== darkMode
  ) {
    dispatch(setUserSettingsIsLoading());
    await saveUserTheme(darkMode);
    dispatch(loadUserSettingsThunk());
  }
};

export const changeUserPerformanceCookiePreferenceThunk = (
  allowPerformanceCookies,
  doNotProcessRequests
) => async (dispatch, getState) => {
  const currentUserSettings = getUserSettingsSelector(getState());
  if (
    !currentUserSettings.loading &&
    !!currentUserSettings.settings.allowPerformanceCookies !==
      allowPerformanceCookies
  ) {
    dispatch(setUserSettingsIsLoading());
    await Promise.all([
      saveUserPerformanceCookiePreference(allowPerformanceCookies),
      setDoNotProcessForPendo(doNotProcessRequests)
    ]);
    dispatch(loadUserSettingsThunk());
  }
};
