import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import Footer from '../common/components/Footer/Footer';
import Header from '../common/components/Header/Header';
import SideNav from '../common/components/SideNav/SideNav';
import Pendo from '../features/userAnalytics/Pendo';
import ContentRouter from './ContentRouter';
import UnauthenticatedRoute from './UnauthenticatedRoute';
import { getMobileSidebarOpened } from '../features/appSettings/appSettingsSlice';
import { getCommentPanelOpened } from '../features/comments/commentsSlice';
import { getUserSettingsSelector } from '../common/redux/userSettings/userSettingsSlice';
import { loadUserSettingsThunk } from '../common/redux/userSettings/userSettingsThunk';
import authorizationService from '../common/services/security/authorizationService';
import ModalError from '../common/components/ModalError/ModalError';
import './AppLayout.scss';

const AppLayout = () => {
  let themeClass = localStorage.getItem('pwc-theme');
  const { pathname } = useLocation();
  const dispatch = useDispatch();
  const userSettings = useSelector(getUserSettingsSelector);
  if (userSettings.settings.darkMode !== undefined) {
    themeClass = userSettings.settings.darkMode ? 'theme-dark' : 'theme-light';
  }

  const hasValidToken = authorizationService.hasValidToken();
  useEffect(() => {
    if (hasValidToken && !userSettings.loaded && !userSettings.loading) {
      dispatch(loadUserSettingsThunk());
    }
  }, [hasValidToken, userSettings, dispatch]);

  const projectId = useSelector(state => state.project && state.project.id);
  const mobileSidebarOpened = useSelector(getMobileSidebarOpened);
  const commentPanelOpened = useSelector(getCommentPanelOpened);
  const freezeClass = mobileSidebarOpened || commentPanelOpened ? 'freeze' : '';

  useEffect(() => {
    const htmlElement = document.getElementsByTagName('html')[0];

    if (mobileSidebarOpened) htmlElement.classList.add('mobile-sidenav-open');
    else htmlElement.classList.remove('mobile-sidenav-open');
  }, [mobileSidebarOpened]);

  return (
    <div className={`app-layout ${freezeClass}`}>
      <Helmet>
        <body className={`no-outline ${themeClass}`} />
      </Helmet>
      <Pendo />
      <SideNav />
      <main>
        <Header projectId={projectId} />
        <div id="main-content" className="sr-only">
          Main Content
        </div>
        {pathname.toLowerCase() !== '/unauthenticated' && <ContentRouter />}
        {pathname.toLowerCase() === '/unauthenticated' && (
          <UnauthenticatedRoute />
        )}
        <Footer />
      </main>
      <ModalError />
    </div>
  );
};
export default AppLayout;
