import React, { useCallback, useRef, useState } from 'react';
import { Overlay, Popover } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux';
import { Button, Toggle } from '@pwc/pwc-deals-ui';
import { getLoggedInUser } from '../../redux/security/loggedInUser/loggedInUserSelector';
import './UserInitialSnippet.scss';
import { changeUserThemeThunk } from '../../redux/userSettings/userSettingsThunk';
import { getUserSettingsSelector } from '../../redux/userSettings/userSettingsSlice';
import LogOut from '../LogOut/LogOut';
import authorizationService from '../../services/security/authorizationService';

const UserInitialsSnippet = ({ ...props }) => {
  const loggedInUser = useSelector(getLoggedInUser);
  const userSettings = useSelector(getUserSettingsSelector);
  const dispatch = useDispatch();

  const darkMode = !!userSettings.settings.darkMode;

  const toggleDarkMode = useCallback(
    newTheme => {
      dispatch(changeUserThemeThunk(newTheme));
      localStorage.setItem(
        'pwc-theme',
        newTheme ? 'theme-dark' : 'theme-light'
      );
    },
    [dispatch]
  );

  const [show, setShow] = useState(false);
  const [target, setTarget] = useState(null);
  const ref = useRef(null);

  const handleClick = event => {
    setShow(!show);
    setTarget(event.target);
    if (show === true) {
      setTimeout(() => {
        document.body.click();
      }, 200);
    }
  };

  const userTypeClass = authorizationService.getUserTypeClass();
  return loggedInUser ? (
    <div className="usersnippet-initials" data-testid="user-initials" ref={ref}>
      <Button
        {...props}
        className={`usersnippet-initials-text button-no-appearance ${userTypeClass}`}
        data-testid="user-initials-button"
        onClick={handleClick}
      >
        {loggedInUser.initials}
      </Button>
      <Overlay
        show={show}
        target={target}
        placement="bottom"
        rootClose
        onHide={() => {
          setShow(!show);
        }}
        container={ref.current}
        darkMode={!!userSettings.settings.darkMode}
      >
        <Popover
          id="popover-user"
          ref={ref}
          {...props}
          data-testid="popup-menu-user"
          className={darkMode ? 'theme-dark' : 'theme-light'}
        >
          <Popover.Content className="popup-user">
            <div className="user-profile-dropdown-menu-section">
              <p data-testid="popup-user-fullname-message">
                Welcome {loggedInUser.name}
              </p>
            </div>
            <div className="user-profile-dropdown-menu-section">
              <Toggle
                checked={!!darkMode}
                onClick={() => toggleDarkMode(!darkMode)}
                name="dark-mode-toggle"
                data-testid="dark-mode-toggle"
              >
                Dark Mode
              </Toggle>
            </div>
            <div className="user-profile-dropdown-menu-section">
              <LogOut />
            </div>
          </Popover.Content>
        </Popover>
      </Overlay>
    </div>
  ) : null;
};

export default UserInitialsSnippet;
