import { ReportingApiClient } from '../../../../common/services/httpClient';

const baseServicePath = (projectId, contentId, versionStatus) =>
  ['projects', projectId, 'preview', 'content', contentId, versionStatus].join(
    '/'
  );

export const getReportPreview = async (
  projectId,
  contentId,
  version,
  cancelRequestFn = undefined
) => {
  const response = await ReportingApiClient.get(
    baseServicePath(projectId, contentId, version),
    undefined,
    undefined,
    cancelRequestFn
  );

  return response.data.previewImage;
};
