import React from 'react';
import { PropTypes } from 'prop-types';
import ReadOnlyRichTextEditor from '../../../../../common/components/RichTextEditor/ReadOnlyRichTextEditor';
import { sanitize } from '../../../../../common/services/sanitizeService';

const NonEditableTextEditor = ({ richTextData, childrenData }) => {
  // Shared rich text library items have their child items already pre-rendered and embedded into the richTextData html markup.
  // CKEditor does not like embedded svgs and charts so this was a work-around
  const useCKEditor = childrenData.length > 0;
  return !useCKEditor ? (
    <div
      data-testid="description-with-innerhtml"
      className="description-body"
      dangerouslySetInnerHTML={{ __html: sanitize(richTextData) }}
    />
  ) : (
    <div className="description-body">
      <ReadOnlyRichTextEditor
        richTextData={richTextData}
        childrenData={childrenData}
      />
    </div>
  );
};

NonEditableTextEditor.propTypes = {
  richTextData: PropTypes.string,
  childrenData: PropTypes.arrayOf(PropTypes.shape({}))
};

NonEditableTextEditor.defaultProps = {
  richTextData: '',
  childrenData: []
};

export default NonEditableTextEditor;
