/* eslint-disable react/forbid-prop-types */
import React from 'react';
import { Constants } from '@pwc/pwc-deals-web-common';
import PropTypes from 'prop-types';
import { isLastNPeriodLabel } from '../../../../../common/services/textService';
import AdjustmentControlsColumn from '../AdjustmentControlsColumn';
import FinancialValue from '../FinancialValue';

const AdjustmentGroupSummary = ({
  totals,
  totalOverrideValue,
  timePeriods,
  documentCode,
  isPublishMode
}) => {
  const isNwcTable = documentCode === Constants.NET_WORKING_CAPITAL;
  return (
    <tr role="row" className="group-summary" data-testid="group-summary">
      <th role="rowheader" scope="row" className="row-description">
        Total
      </th>
      {timePeriods.map(timePeriod => {
        const isLastNPeriod = isLastNPeriodLabel(timePeriod);
        const showColumnHighlight = isNwcTable && isLastNPeriod;

        return (
          <FinancialValue
            key={timePeriod}
            text={totalOverrideValue || totals[timePeriod]}
            showColumnHighlight={showColumnHighlight}
          />
        );
      })}
      {!isPublishMode && <AdjustmentControlsColumn />}
    </tr>
  );
};

AdjustmentGroupSummary.propTypes = {
  totals: PropTypes.object.isRequired,
  totalOverrideValue: PropTypes.string,
  timePeriods: PropTypes.arrayOf(PropTypes.string).isRequired,
  documentCode: PropTypes.string.isRequired,
  isPublishMode: PropTypes.bool
};

AdjustmentGroupSummary.defaultProps = {
  totalOverrideValue: '',
  isPublishMode: false
};

export default AdjustmentGroupSummary;
