export const DEFAULT_TITLE = 'Junction';

const makeTitle = getStr => args => `${getStr(args)} | ${DEFAULT_TITLE}`;

const makeProject = getStr => args => `${getStr(args)} - ${args.projectName}`;

const makeProjectTitle = getStr => args => makeTitle(makeProject(getStr))(args);

