const useMatchedRoute = route => ({
  ...route,
  match: useRouteMatch(route.path)
});

const useMatchedRoutes = () => ROUTES.map(useMatchedRoute);

export const useExactMatchedRoute = () =>
  useMatchedRoutes().filter(r => (r.match || {}).isExact)[0];

export const useCustomRouteNames = () => {
  const project = useSelector(getProject);
  const hypothesis = useSelector(getCurrentHypothesis);
  const analysis = useSelector(selectAnalysisContent);
  const insight = useSelector(getCurrentInsight);
  const tile = useSelector(previewPageSelector);
  return {
    projectName: project.name,
    hypothesisTitle: hypothesis.title,
    analysisTitle: analysis && analysis.title,
    insightTitle: insight.title,
    tileTitle: tile.title
  };
};
