import { Constants } from '@pwc/pwc-deals-web-common';
import React, { useEffect, useState } from 'react';
import { Popover } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import MenuButtonDelete from '../../../../common/components/MenuButtonDelete';
import useTabletOrMobile from '../../../../common/hooks/useTabletOrMobile';
import authorizationService from '../../../../common/services/security/authorizationService';
import FileDownload from '../FileDownload/FileDownload';
import userTokenService from '../../../../common/services/security/userTokenService';

import DeleteConfirmationDialog from './DeleteConfirmationDialog';
import MenuButtonShowPassword from './MenuButtonShowPassword';
import MenuButtonUpdate from './MenuButtonUpdate';
import ShowPasswordDialog from './ShowPasswordDialog';
import './FileListItemPopupMenu.scss';

const FileListItemPopupMenu = React.forwardRef(
  ({ itemId, onUpdateMenuClick, filePassword, dismissMenu, ...props }, ref) => {
    const isClientUser = authorizationService.isClientUser();
    const projectId = useSelector(state => state.project && state.project.id);
    const [showPasswordDialog, setShowPasswordDialog] = useState(true);
    const isTabletOrMobile = useTabletOrMobile();
    const [isReadOnlyUser, setIsReadOnlyUser] = useState(true);

    useEffect(() => {
      (async () => {
        const readOnly = await userTokenService.hasProjectRoles(projectId, [
          Constants.USER_ROLES.PWC_READONLY_DISPLAY,
          Constants.USER_ROLES.PWC_READONLY
        ]);
        setIsReadOnlyUser(readOnly);

        setShowPasswordDialog(isClientUser || readOnly);
      })();
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    const [
      showDeleteConfirmationDialog,
      setShowDeleteConfirmationDialog
    ] = useState(false);

    const displayOrShowPasswordDialog = show => {
      setShowPasswordDialog(show);
      if (!show) {
        dismissMenu();
      }
    };
    const displayOrShowDeleteConfirmationDialog = show => {
      setShowDeleteConfirmationDialog(show);
      if (!show) {
        dismissMenu();
      }
    };

    const confirmationClassNameIfNeeded =
      showPasswordDialog || showDeleteConfirmationDialog
        ? ' file-confirmation-popover'
        : '';

    props.className += confirmationClassNameIfNeeded;

    return (
      <Popover
        ref={ref}
        id="file-actions-popup"
        data-testid={`content-actions-popup-menu-${itemId}`}
        {...props}
      >
        <Popover.Content
          className={`popup-menu${confirmationClassNameIfNeeded}`}
        >
          {/* eslint-disable-next-line no-nested-ternary */}
          {showPasswordDialog ? (
            <ShowPasswordDialog
              password={filePassword}
              onCloseClick={() => displayOrShowPasswordDialog(false)}
            />
          ) : showDeleteConfirmationDialog ? (
            <DeleteConfirmationDialog
              onCloseClick={() => {
                displayOrShowDeleteConfirmationDialog(false);
              }}
              fileId={itemId}
            />
          ) : (
            <>
              {!isClientUser && !isReadOnlyUser && (
                <FileDownload fileId={itemId} showText />
              )}
              {!isTabletOrMobile && (
                <MenuButtonUpdate onClick={onUpdateMenuClick} />
              )}
              {!isClientUser && (
                <MenuButtonShowPassword
                  onClick={() => displayOrShowPasswordDialog(true)}
                />
              )}
              <MenuButtonDelete
                onClick={() => displayOrShowDeleteConfirmationDialog(true)}
              />
            </>
          )}
        </Popover.Content>
      </Popover>
    );
  }
);

export default FileListItemPopupMenu;
