import { chain as _chain, isEmpty as _isEmpty } from 'lodash';
import { useSelector } from 'react-redux';
import { getProjectsList } from '../../features/projects/projectsSlice';
import {
  isProjectArchived,
  isProjectInactive
} from '../helpers/projectStatusesUtils';

export const getProjectStatus = (projectStatuses, projectId) => {
  return (
    projectId && projectStatuses[projectId] && projectStatuses[projectId].status
  );
};

export const getProjectStatusIcon = (projectStatuses, projectId) => {
  return projectId && projectStatuses[projectId]
    ? projectStatuses[projectId].icon
    : '';
};

export const useProjectsStatuses = () => {
  const projectList = useSelector(getProjectsList);
  if (!_isEmpty(projectList)) {
    return _chain(projectList)
      .filter(project => project && project.id && project.status)
      .keyBy(project => project.id)
      .mapValues(project => {
        let icon = '';
        if (isProjectInactive(project.status)) {
          icon = 'pause';
        } else if (isProjectArchived(project.status)) {
          icon = 'archive';
        }

        return {
          status: project.status,
          icon
        };
      })
      .value();
  }
  return {};
};
