import {
  getUserNotificationSettings,
  saveUserNotificationSettings
} from '../../services/userNotificationSettingsService';
import {
  getUserNotificationSettingsSelector,
  setSaveErrorUserNotificationSettings,
  setUserNotificationSettings,
  setUserNotificationSettingsIsLoading
} from './userNotificationSettingsSlice';

export const loadUserNotificationSettingsThunk = projectId => async dispatch => {
  const response = await getUserNotificationSettings(projectId);
  if (response) {
    dispatch(setUserNotificationSettings(projectId, response.data));
  }
};

export const saveUserNotificationSettingsThunk = (
  projectId,
  receiveEmails
) => async (dispatch, getState) => {
  const currentUserNotificationSettings = getUserNotificationSettingsSelector(
    projectId
  )(getState());
  if (
    !currentUserNotificationSettings.loading &&
    !!currentUserNotificationSettings.settings.receiveEmails !== receiveEmails
  ) {
    dispatch(setUserNotificationSettingsIsLoading(projectId));
    try {
      await saveUserNotificationSettings(projectId, receiveEmails);
    } catch (_error) {
      dispatch(setSaveErrorUserNotificationSettings(projectId));
      return;
    }
    dispatch(loadUserNotificationSettingsThunk(projectId));
  }
};
