import _ from 'lodash';
import { createSlice } from '@reduxjs/toolkit';
import updateEntityVersion from '../../common/actions/updateEntityVersion';
import {
  getCommentsByEntity,
  updateComment,
  shareCommentThread,
  resolveCommentThread
} from './commentService';

const updateValues = (oldComment, newComment) => {
  const copy = { ...oldComment };
  Object.keys(copy).forEach(key => {
    copy[key] =
      typeof newComment[key] === 'undefined'
        ? oldComment[key]
        : newComment[key];
  });

  return copy;
};

export const initialState = {
  threads: [],
  viewingStatus: 'unresolved',
  isLoading: false,
  hasLoaded: false,
  hasError: false,
  hasCommented: false,
  errorStatusCode: null,
  projectId: undefined,
  entityType: '',
  entityId: undefined,
  showCommentsPanel: false,
  commentPanelOpened: false,
  entityVersion: undefined,
  versionRequired: undefined,
  isEntitySharedWithClient: true
};

const commentsSlice = createSlice({
  name: 'comments',
  initialState,
  reducers: {
    resetComments: () => initialState,
    fetchCommentsBegan: state => ({
      ...state,
      isLoading: true,
      hasLoaded: false,
      hasError: false,
      errorStatusCode: null
    }),
    fetchCommentsSucceeded: (state, action) => {
      const { entityType, entityId } =
        action.payload.length && action.payload[0];
      const isForSameThread =
        entityType === state.entityType && entityId === +state.entityId;
      const threadWithoutComments = action.payload.length === 0;
      const loadCommentThreads = isForSameThread || threadWithoutComments;
      return {
        ...state,
        threads: loadCommentThreads ? action.payload : [],
        isLoading: false,
        hasLoaded: true,
        hasError: false,
        errorStatusCode: null
      };
    },
    fetchCommentsFailed: (state, action) => ({
      ...state,
      isLoading: false,
      hasLoaded: false,
      hasError: true,
      errorStatusCode: action.payload
    }),
    setHasCommented: state => ({
      ...state,
      hasCommented: true
    }),
    viewUnresolvedComments: state => ({
      ...state,
      viewingStatus: 'unresolved'
    }),
    viewResolvedComments: state => ({
      ...state,
      viewingStatus: 'resolved'
    }),
    initializeCommentsPanel: (_state, action) => {
      const {
        projectId,
        entityType,
        entityId,
        versionRequired
      } = action.payload;
      return {
        ...initialState,
        projectId,
        entityType,
        entityId,
        versionRequired,
        showCommentsPanel: true
      };
    },
    toggleCommentPanelOpened: state => ({
      ...state,
      commentPanelOpened: !state.commentPanelOpened
    }),
    entitySharedStatusUpdateSuccess: (state, action) => ({
      ...state,
      isEntitySharedWithClient: action.payload
    })
  },
  extraReducers: builder => {
    builder.addCase(updateEntityVersion, (state, action) => ({
      ...state,
      entityVersion: action.payload
    }));
  }
});
export default commentsSlice.reducer;

// Actions
export const {
  resetComments,
  fetchCommentsBegan,
  fetchCommentsSucceeded,
  fetchCommentsFailed,
  setHasCommented,
  viewUnresolvedComments,
  viewResolvedComments,
  initializeCommentsPanel,
  toggleCommentPanelOpened,
  updateCommentPanel,
  entitySharedStatusUpdateSuccess
} = commentsSlice.actions;

// Selectors
export const getComments = state => state.comments;
export const getCommentsHasLoaded = state => state.comments.hasLoaded;
export const getCommentsIsLoading = state => state.comments.isLoading;
export const getHasCommented = state => state.comments.hasCommented;
export const getViewingStatus = state => state.comments.viewingStatus;
export const getCurrentCommentThreads = state => state.comments.threads || [];
export const getCommentPanelOpened = state => state.comments.commentPanelOpened;
export const getCurrentComments = state =>
  _.map(state.comments.threads, 'comments').flat();

// Thunks
export const loadCommentsThunk = (
  projectId,
  entityType,
  entityId,
  entityVersion
) => async (dispatch, getState) => {
  try {
    const state = getState();
    if (!state.comments.isLoading) {
      dispatch(fetchCommentsBegan());

      const response = await getCommentsByEntity(
        projectId,
        entityType,
        entityId,
        entityVersion
      );

      dispatch(fetchCommentsSucceeded(response));
    }
  } catch (error) {
    dispatch(fetchCommentsFailed(error.response ? error.response.status : 500));
  }
};

export const updateCommentThunk = comment => async (dispatch, getState) => {
  try {
    const state = getState();
    const existingComment = getCurrentComments(state).find(
      currentComment =>
        currentComment.commentId === comment.commentId ||
        currentComment.commentId === comment.id
    );

    const oldCopy = { ...existingComment };
    const updatedComment = updateValues(existingComment, comment);

    if (!_.isEqual(oldCopy, updatedComment)) {
      await updateComment(updatedComment);
    }

    const { projectId, entityType, entityId, entityVersion } = state.comments;

    dispatch(loadCommentsThunk(projectId, entityType, entityId, entityVersion));
  } catch (error) {
    dispatch(fetchCommentsFailed(error.response ? error.response.status : 500));
  }
};

export const setViewingStateThunk = viewingState => async (
  dispatch,
  getState
) => {
  if (viewingState === 'resolved') dispatch(viewResolvedComments());
  else dispatch(viewUnresolvedComments());

  const state = getState();
  const { projectId, entityType, entityId, entityVersion } = state.comments;

  dispatch(loadCommentsThunk(projectId, entityType, entityId, entityVersion));
};

export const toggleShareCommentThreadThunk = (
  comment,
  currentEntityVersion
) => async dispatch => {
  try {
    await shareCommentThread(comment);

    const { projectId, entityType, entityId } = comment;

    dispatch(fetchCommentsBegan());
    const response = await getCommentsByEntity(
      projectId,
      entityType,
      entityId,
      currentEntityVersion
    );
    dispatch(fetchCommentsSucceeded(response));
  } catch (error) {
    dispatch(fetchCommentsFailed(error.response ? error.response.status : 500));
  }
};

export const toggleResolveCommentThreadThunk = (
  comment,
  currentEntityVersion
) => async dispatch => {
  try {
    await resolveCommentThread(comment);

    const { projectId, entityType, entityId } = comment;

    dispatch(fetchCommentsBegan());
    const response = await getCommentsByEntity(
      projectId,
      entityType,
      entityId,
      currentEntityVersion
    );
    dispatch(fetchCommentsSucceeded(response));
  } catch (error) {
    dispatch(fetchCommentsFailed(error.response ? error.response.status : 500));
  }
};
