import { useEffect, useState } from 'react';
import { getProjectOptions } from '../services/projectService';

const getIndustryOptions = (projectOptions = {}) =>
  (projectOptions.industry || []).map(({ id, name }) => ({
    value: `${id}`,
    label: name
  }));

const getServiceOptions = (projectOptions = {}) =>
  (projectOptions.service || []).map(({ id, name }) => ({
    value: `${id}`,
    label: name
  }));

export const useProjectOptionsFromApi = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [hasError, setHasError] = useState(false);
  const [projectOptions, setProjectOptions] = useState(undefined);

  useEffect(() => {
    setIsLoading(true);
    getProjectOptions()
      .then(opts => {
        setProjectOptions(opts);
        setHasError(false);
        setIsLoading(false);
      })
      .catch(() => {
        setHasError(true);
        setIsLoading(false);
      });
  }, [setIsLoading, setProjectOptions]);

  return {
    isLoading,
    hasError,
    industryOptions: getIndustryOptions(projectOptions),
    serviceOptions: getServiceOptions(projectOptions)
  };
};
