import { Icon, MiniLoader } from '@pwc/pwc-deals-ui';
import classNames from 'classnames';
import React from 'react';
import './FilesUploaded.scss';

const FileUploaded = ({ file, clearFile, loading, error, fullErrorCopy }) => {
  const hasError = !!error || !!fullErrorCopy;
  const onKeyDown = e => {
    if (e.keyCode === 32) {
      e.preventDefault();
      clearFile();
    }
  };

  return (
    <div className="files">
      <h6>File to upload</h6>
      {(file || fullErrorCopy) && (
        <div className={classNames('file', { 'file-upload-error': hasError })}>
          {loading && <MiniLoader />}
          {hasError && (
            <Icon
              name="alert"
              className="icon-error fill"
              width={24}
              onClick={clearFile}
              tabIndex={0}
              onKeyDown={onKeyDown}
            />
          )}
          <Icon
            name="circle-cross"
            className="action-icon outline icon-orange-theme with-hover"
            width={24}
            onClick={clearFile}
            tabIndex={0}
            onKeyDown={onKeyDown}
          />
          <span>
            {fullErrorCopy ||
              (error ? `Failed to upload: "${file.name}"` : file.name)}
          </span>
        </div>
      )}
    </div>
  );
};

export default FileUploaded;
