import {
  LibraryItemsPowerBiHttpClient,
  ProjectHttpClient
} from '../../common/services/httpClient';

export const getReports = async projectId => {
  const response = await ProjectHttpClient.get(
    `projects/${projectId}/powerBiReports`
  );
  return response.data;
};

export const getReportEmbedData = async (
  projectId,
  libraryItemId,
  isShared
) => {
  const shared = isShared || false;

  const response = await ProjectHttpClient.get(
    `projects/${projectId}/powerBiReports/search?libraryItemId=${libraryItemId}&shared=${shared}`
  );
  return response.data;
};

export const getReportEmbedDataByReportId = async (projectId, reportId) => {
  const response = await ProjectHttpClient.get(
    `projects/${projectId}/powerBiReports/${reportId}`
  );
  return response.data;
};

export const getPowerBiReportData = async (projectId, reportData) => {
  const response = await LibraryItemsPowerBiHttpClient.post(
    `/projects/${projectId}`,
    reportData
  );
  return response.data;
};
