// Archiving
export const archiveProject = () => async () => {};

export const requestArchiveProject = (projectId, userId) =>
  archiveProject(projectId, userId, true);

export const approveRequestArchiveProject = (projectId, userId) =>
  service.approveRequestArchiveProject(projectId, userId);

export const declineRequestArchiveProject = (projectId, userId) =>
  service.declineRequestArchiveProject(projectId, userId);

export const cancelRequestArchiveProject = (projectId, userId) =>
  service.cancelRequestArchiveProject(projectId, userId);

export const unarchiveProject = () => async () => {};

export const requestUnarchiveProject = (projectId, userId) =>
  unarchiveProject(projectId, userId, true);

export const approveRequestUnarchiveProject = () =>
  service.approveRequestUnarchiveProject();

export const declineRequestUnarchiveProject = (projectId, userId) =>
  service.declineRequestUnarchiveProject(projectId, userId);

export const cancelRequestUnarchiveProject = () =>
  service.cancelRequestUnarchiveProject();
