import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';

const EditLayoutTab = ({
  activeTab,
  label,
  onClick,
  disabled,
  setTabByIndex,
  tabNumber,
  numOfTabs
}) => {
  let className = 'edit-layout-tab-list-item';
  const tabRef = useRef(null);

  if (activeTab === label) {
    className += ' edit-layout-tab-list-active';
  }

  if (disabled) {
    className += ' edit-layout-tab-list-inactive';
  }

  useEffect(() => {
    if (activeTab && activeTab === tabRef.current.getAttribute('aria-label')) {
      tabRef.current.focus();
    }
  }, [activeTab]);

  const setNextTab = index => {
    let nextTabIndex = null;
    if (index + 1 < numOfTabs) {
      nextTabIndex = index + 1;
    } else if (index + 1 >= numOfTabs) {
      nextTabIndex = 0;
    }
    setTabByIndex(nextTabIndex);
  };

  const setPreviousTab = index => {
    let previousTabIndex = null;
    if (index - 1 >= 0) {
      previousTabIndex = index - 1;
    } else if (index - 1 < 0) {
      previousTabIndex = numOfTabs - 1;
    }
    setTabByIndex(previousTabIndex);
  };

  const handleKeyDown = e => {
    if (e.keyCode === 39 && e.key === 'ArrowRight') {
      setNextTab(tabNumber);
    } else if (e.keyCode === 37 && e.key === 'ArrowLeft') {
      setPreviousTab(tabNumber);
    }
  };

  return (
    <li
      className={className}
      role="tab"
      aria-label={label}
      aria-selected={activeTab === label}
      tabIndex={activeTab === label ? 0 : -1}
      onClick={disabled ? () => {} : () => onClick(label)}
      onKeyDown={handleKeyDown}
      data-testid={label ? `${label.replace(' ', '')}Tab` : ''}
      ref={tabRef}
    >
      {`${label}`}
    </li>
  );
};

EditLayoutTab.propTypes = {
  activeTab: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  tabNumber: PropTypes.number,
  numOfTabs: PropTypes.number,
  onClick: PropTypes.func.isRequired,
  setTabByIndex: PropTypes.func,
  disabled: PropTypes.bool
};

EditLayoutTab.defaultProps = {
  tabNumber: 1,
  numOfTabs: 1,
  setTabByIndex: () => {},
  disabled: false
};

export default EditLayoutTab;
