import { Button } from '@pwc/pwc-deals-ui';
import { Constants } from '@pwc/pwc-deals-web-common';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import MoreMenu from '../../../common/components/ContentActions/MoreMenu';
import FormLayout from '../../../common/components/layouts/FormLayout';
import ModalConfirmation from '../../../common/components/ModalConfirmation';
import PageContentLabel from '../../../common/components/PageContentLabel/PageContentLabel';
import PageSection from '../../../common/components/PageSection';
import PageTitle from '../../../common/components/PageTitle/PageTitle';
import { getViewClientInfo } from '../../../common/helpers/viewClientVersion';
import { useApprovalWorkflow } from '../../../common/hooks/useApprovalWorkflow';
import {
  editHypothesisPath,
  hypothesesListPath
} from '../../../common/services/urlService';
import TagList from '../../analyses/common/TagList/TagList';
import { entitySharedStatusUpdateSuccess } from '../../comments/commentsSlice';
import ConnectedContent from '../../narratives/ConnectedContent/ConnectedContent';
import EnableForProjectState from '../../security/EnableForProjectState';
import ShowForPermissions from '../../security/ShowForPermissions';
import ShowForRole from '../../security/ShowForRole';
import { getWorkflowStatus } from '../../workflow/workflowService';
import {
  deleteHypothesis,
  getHypothesisVersions,
  updateHypothesisStatus
} from '../hypothesesService';
import {
  getCurrentHypothesis,
  getHypothesisLoading,
  loadHypothesisThunk,
  resetHypothesis
} from '../hypothesisSlice';
import './view-hypothesis.scss';
import { showMoreMenuError } from '../../../common/redux/errors/errorsSlice';
import StatusContainer from '../../../common/components/StatusContainer/StatusContainer';

const ViewHypothesis = () => {
  const { projectId, hypothesisId: hypothesisContentThreadId } = useParams();
  const [versions, setVersions] = useState(null);
  const dispatch = useDispatch();
  const history = useHistory();

  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);
  const hypothesis = useSelector(getCurrentHypothesis);
  const isLoading = useSelector(getHypothesisLoading);

  useEffect(() => {
    if (hypothesis && !hypothesis.hasError) {
      getHypothesisVersions(projectId, hypothesisContentThreadId).then(data =>
        setVersions(data)
      );
    }
  }, [hypothesis, projectId, hypothesisContentThreadId]);

  const handleDeleteHypothesis = async () => {
    setShowDeleteConfirmation(false);
    const success = await deleteHypothesis(
      projectId,
      hypothesisContentThreadId
    );
    if (success) {
      history.replace(hypothesesListPath(projectId));
    }
  };

  useEffect(() => {
    dispatch(loadHypothesisThunk(projectId, hypothesisContentThreadId));

    return () => dispatch(resetHypothesis());
  }, [dispatch, projectId, hypothesisContentThreadId]);

  const gotoEditHypothesis = () =>
    history.push(editHypothesisPath(projectId, hypothesisContentThreadId));

  const workflowActions = useApprovalWorkflow(
    getWorkflowStatus,
    async (_projectId, contentId, step) => {
      try {
        await updateHypothesisStatus(_projectId, `${contentId}`, step);
      } catch (e) {
        dispatch(
          showMoreMenuError({
            itemId: contentId,
            moreMenuErrorType: Constants.MoreMenuErrorType.UNABLE_TO_SAVE_CHANGE
          })
        );
        return;
      }
      dispatch(loadHypothesisThunk(projectId, hypothesisContentThreadId));
    },
    projectId,
    hypothesisContentThreadId,
    hypothesis,
    versions
  );

  const showClientVersion = () => {
    if (!isLoading)
      dispatch(
        loadHypothesisThunk(
          projectId,
          hypothesisContentThreadId,
          Constants.VERSION_STATUS.SHARED.value
        )
      );
  };
  const showDraftVersion = () => {
    if (!isLoading)
      dispatch(loadHypothesisThunk(projectId, hypothesisContentThreadId));
  };

  const viewClient = getViewClientInfo(
    showClientVersion,
    showDraftVersion,
    versions,
    hypothesis && hypothesis.versionNumber,
    hypothesis && hypothesis.versionStatus
  );

  useEffect(() => {
    if (hypothesis) {
      dispatch(entitySharedStatusUpdateSuccess(hypothesis.shareWithAll));
    }
  }, [dispatch, hypothesis]);

  const headerComponent = (
    <>
      {hypothesis && (
        <div className="hypothesis-top-bar-container">
          <div className="hypothesis-top-bar">
            <div />
            <div>
              <ShowForPermissions
                projectId={projectId}
                permissions={[Constants.USER_PERMISSIONS.CONTRIBUTE]}
              >
                <EnableForProjectState projectId={projectId}>
                  {({ isProjectDisabled }) => (
                    <Button
                      data-testid="edit-button"
                      type="button"
                      onClick={gotoEditHypothesis}
                      className="button-block button-small mr-3"
                      disabled={
                        isProjectDisabled || viewClient.isOnClientVersion
                      }
                    >
                      Edit
                    </Button>
                  )}
                </EnableForProjectState>
                <MoreMenu
                  itemId={hypothesisContentThreadId}
                  onDelete={() => setShowDeleteConfirmation(true)}
                  workflowActions={workflowActions}
                  viewClient={viewClient}
                />
              </ShowForPermissions>
            </div>
          </div>
        </div>
      )}
      <ModalConfirmation
        show={showDeleteConfirmation}
        onCancel={() => setShowDeleteConfirmation(false)}
        onConfirm={handleDeleteHypothesis}
        confirmTitle="Are you sure you want to delete?"
        confirmationMessage="This Key Focus Area will be permanently deleted and any connections to other content will be removed."
        confirmButtonText="Yes, delete"
      />
    </>
  );

  return (
    projectId && (
      <FormLayout headerComponent={headerComponent}>
        <div
          className="hypothesis-main-content"
          data-testid="hypothesis-detail-page"
        >
          <PageContentLabel cssLabel="key-focus-area" label="Key Focus Area" />
          <StatusContainer
            projectId={projectId}
            content={hypothesis}
            allowDraftPill={!viewClient.isOnClientVersion}
          />
          <ShowForRole
            projectId={projectId}
            roles={Constants.USER_ROLE_GROUPS.INTERNAL}
          >
            <div className="section-name">Title</div>
          </ShowForRole>

          <PageTitle title={hypothesis.title} />

          <PageSection label="Description" value={hypothesis.body} />

          <ShowForRole
            projectId={projectId}
            roles={Constants.USER_ROLE_GROUPS.CLIENT}
          >
            {hypothesis.conclusion ? (
              <div
                className="client-view-conclusion"
                data-testid="client-view-conclusion"
              >
                <div className="section-name">PwC View</div>
                <PageSection
                  sectionId="conclusion"
                  label="PwC View"
                  value={hypothesis.conclusion}
                />
              </div>
            ) : null}
          </ShowForRole>
          <ShowForRole
            projectId={projectId}
            roles={Constants.USER_ROLE_GROUPS.INTERNAL}
          >
            <PageSection
              sectionId="conclusion"
              label="PwC View"
              value={hypothesis.conclusion}
            />
          </ShowForRole>
        </div>

        <ConnectedContent
          projectId={projectId}
          contentId={hypothesis.contentId}
        />
        {hypothesis.tags && hypothesis.tags.length > 0 && (
          <TagList tags={hypothesis.tags} testId="hypothesis-tag-list" />
        )}
      </FormLayout>
    )
  );
};

export default ViewHypothesis;
