import React from 'react';
import EditNarrativeSection from '../EditNarrativeSection/EditNarrativeSection';
import {
  viewHypothesisPath,
  viewInsightPath
} from '../../../common/services/urlService';
import { analysisLinkBuilder } from '../../analyses/common/analysisUrlHelper';

const EditConnectedContentSection = ({
  projectId,
  parentContentId,
  narratives,
  allNarratives,
  setNarratives,
  testId = 'connected-content',
  title = undefined,
  search
}) => {
  const setHypotheses = newHypotheses => {
    setNarratives({ ...narratives, hypotheses: newHypotheses });
  };

  const setInsights = newInsights => {
    setNarratives({ ...narratives, insights: newInsights });
  };

  const setAnalyses = newAnalyses => {
    setNarratives({ ...narratives, analyses: newAnalyses });
  };

  const hasNarratives = narratives && Object.keys(narratives).length > 0;

  return (
    hasNarratives && (
      <>
        <div
          className="connected-content edit-connected-content"
          data-testid={testId}
        >
          {title && title.length > 0 && <p>{title}</p>}
          <div className="linked-narratives" data-testid="linked-narratives">
            <EditNarrativeSection
              title="Key Focus Areas"
              narratives={narratives.hypotheses}
              allNarratives={allNarratives.hypotheses}
              getViewPath={hypothesisId =>
                viewHypothesisPath(projectId, hypothesisId)
              }
              setNarratives={setHypotheses}
              parentContentId={parentContentId}
              projectId={projectId}
              edit
              search={search}
            />
            <EditNarrativeSection
              title="Insights"
              narratives={narratives.insights}
              allNarratives={allNarratives.insights}
              getViewPath={insightId => viewInsightPath(projectId, insightId)}
              setNarratives={setInsights}
              parentContentId={parentContentId}
              projectId={projectId}
              edit
              search={search}
            />
            <EditNarrativeSection
              title="Analyses"
              narratives={narratives.analyses}
              allNarratives={allNarratives.analyses}
              getViewPath={analysisLinkBuilder(narratives.analyses, projectId)}
              setNarratives={setAnalyses}
              parentContentId={parentContentId}
              projectId={projectId}
              edit
              search={search}
            />
          </div>
        </div>
      </>
    )
  );
};

export default EditConnectedContentSection;
