import { Constants } from '@pwc/pwc-deals-web-common';
import React, { useEffect, useState } from 'react';
import { Icon, Button, MiniLoader } from '@pwc/pwc-deals-ui';
import { useSelector } from 'react-redux';
import { downloadFile } from '../../../../common/services/fileLibraryService';
import authorizationService from '../../../../common/services/security/authorizationService';
import userTokenService from '../../../../common/services/security/userTokenService';

const fileTypeMap = {
  pdf: 'application/pdf',
  doc: 'application/msword',
  docx:
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
  ppt: 'application/vnd.ms-powerpoint',
  pptx:
    'application/vnd.openxmlformats-officedocument.presentationml.presentation',
  xls: 'application/vnd.ms-excel',
  xlsx: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
};

const parseFileStream = fileStream => {
  const byteCharacters = atob(fileStream);
  const byteArrays = [];

  for (let offset = 0; offset < byteCharacters.length; offset += 512) {
    const slice = byteCharacters.slice(offset, offset + 512);

    const byteNumbers = new Array(slice.length);
    // eslint-disable-next-line no-plusplus
    for (let i = 0; i < slice.length; i++) {
      byteNumbers[i] = slice.charCodeAt(i);
    }

    const byteArray = new Uint8Array(byteNumbers);
    byteArrays.push(byteArray);
  }

  return byteArrays;
};

const FileDownload = ({ fileId, showText }) => {
  const [isDownloading, setIsDownloading] = useState();

  const projectId = useSelector(state => state.project && state.project.id);

  const [isReadOnlyUser, setIsReadOnlyUser] = useState(true);
  useEffect(() => {
    (async () => {
      setIsReadOnlyUser(
        await userTokenService.hasProjectRoles(projectId, [
          Constants.USER_ROLES.PWC_READONLY_DISPLAY,
          Constants.USER_ROLES.PWC_READONLY
        ])
      );
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleDownload = async () => {
    if (isDownloading) {
      return;
    }

    setIsDownloading(true);

    const { fileStream, fileName } = await downloadFile(projectId, fileId);
    const fileExtension = fileTypeMap[fileName.split('.').pop()];

    const blob = new Blob(parseFileStream(fileStream), {
      type: fileExtension || 'application/zip'
    });

    const link = document.createElement('a');

    link.href = window.URL.createObjectURL(blob);
    link.download = fileExtension ? fileName : `${fileName}.zip`;
    link.click();

    setIsDownloading(false);
  };

  const isClientUser = authorizationService.isClientUser();

  return (
    <Button
      className={
        (showText
          ? 'ui-button button-menu'
          : 'ui-button button-no-underline icon-button') +
        (isClientUser || isReadOnlyUser ? '' : ' file-download')
      }
      onClick={handleDownload}
      data-testid="file-download"
      aria-label="download"
    >
      {isDownloading ? (
        <MiniLoader data-testid="file-download-loader" />
      ) : (
        <Icon
          name="download-alt"
          className={
            showText ? 'outline icon-orange' : 'fill icon-orange-theme'
          }
          data-testid="file-download-icon"
          {...(showText ? { width: 21 } : {})}
        />
      )}
      {showText ? 'Download' : null}
    </Button>
  );
};

export default FileDownload;
