import React from 'react';
import PropTypes from 'prop-types';
import './EditLayoutTabs.scss';
import EditLayoutTab from './EditLayoutTab';

const EditLayoutTabs = ({ defaultTab, children, onChange, setTabByIndex }) => {
  const validChildren = children.filter(c => c);

  return (
    <>
      {defaultTab && (
        <div className="edit-layout-tabs">
          <ul
            className="edit-layout-tab-list"
            role="tablist"
            aria-label="Tab panel of layout"
            tabIndex={0}
          >
            {validChildren.map((child, index) => {
              const { label, disabled } = child.props;

              return (
                <EditLayoutTab
                  activeTab={defaultTab}
                  key={label}
                  label={label}
                  tabNumber={index}
                  numOfTabs={validChildren.length}
                  onClick={onChange}
                  setTabByIndex={setTabByIndex}
                  disabled={disabled}
                />
              );
            })}
          </ul>
        </div>
      )}
    </>
  );
};

EditLayoutTabs.propTypes = {
  defaultTab: PropTypes.string.isRequired,
  children: PropTypes.instanceOf(Array).isRequired,
  onChange: PropTypes.func.isRequired,
  setTabByIndex: PropTypes.func
};

EditLayoutTabs.defaultProps = {
  setTabByIndex: () => {}
};
export default EditLayoutTabs;
