import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { Icon } from '@pwc/pwc-deals-ui';
import Separator from '../../../common/components/Separator';
import {
  addAnalysisFromTemplatePath,
  createAnalysisPath
} from '../../../common/services/urlService';
import './AddAnalysisType.scss';
import ViewAnalysisSourceLinks from '../common/ViewAnalysisSourceLinks/ViewAnalysisSourceLinks';
import PageTitle from '../../../common/components/PageTitle/PageTitle';

const AddAnalysisType = () => {
  const { projectId } = useParams();
  const addAnalysisFromTemplateHref = addAnalysisFromTemplatePath(projectId);
  const createFreeformAnalysisHref = createAnalysisPath(projectId);

  return (
    <div className="add-analysis-type-container">
      <PageTitle title="Add an Analysis" size="h1" />

      <p>There are two ways to create analyses:</p>

      <div className="card-container">
        <section className="card" data-testid="analysis-type-card">
          <div className="title">
            <Icon name="from-template" width={20} />
            From template
          </div>
          <div className="card-content">
            Add common analyses to generate the tables and charts. Populate the
            relevant data fields in the Excel template on SharePoint.
          </div>
          <Link
            to={addAnalysisFromTemplateHref}
            data-testid="add-from-template-link"
            className="add-from-template-button"
          >
            <Icon name="add" width={18} />
            Start from template
          </Link>
        </section>

        <section className="card">
          <div className="title">
            <Icon name="free-form" width={20} />
            Free-form
          </div>
          <div className="card-content">
            Choose pre-ingested data or ingest your own using the Excel add-in.
            You can also import data from Power BI and PowerPoint.
          </div>
          <Link
            to={createFreeformAnalysisHref}
            data-testid="add-freeform-link"
            className="add-from-template-button"
          >
            <Icon name="add" width={18} />
            Start free-form
          </Link>
        </section>
      </div>

      <Separator />
      <ViewAnalysisSourceLinks />
    </div>
  );
};

export default AddAnalysisType;
