import classNames from 'classnames';
import React from 'react';
import { Button, Icon } from '@pwc/pwc-deals-ui';

import './RowActionsButton.scss';

const RowActionsButton = ({ isDisabled, ...rest }) => {
  return (
    <div className="action-button-container">
      <Button
        {...rest}
        className="button-link action-button action-button"
        data-testid="row-actions-button"
        aria-label="Action Button"
        disabled={isDisabled}
      >
        <Icon
          name="more-vert"
          width={24}
          className={classNames('fill', {
            'icon-orange': !isDisabled,
            'icon-grey': isDisabled
          })}
        />
      </Button>
    </div>
  );
};

export default RowActionsButton;
