import PropTypes from 'prop-types';
import React from 'react';
import { Icon } from '@pwc/pwc-deals-ui';
import classnames from 'classnames';
import './LinkedAdjustmentLinkIcon.scss';

export const LinkedAdjustmentLinkIcon = ({
  interactive,
  isSelected,
  isBroken
}) => (
  <div className="linked-adjustment-icon-container">
    <Icon
      name={isBroken ? 'link-remove' : 'link'}
      width="18px"
      className={classnames('linked-adjustment-icon', {
        'icon-gray': !interactive,
        'icon-orange': interactive && !isBroken,
        'icon-red': interactive && isBroken,
        fill: isSelected,
        outline: !isSelected,
        interactive
      })}
    />
  </div>
);

LinkedAdjustmentLinkIcon.propTypes = {
  interactive: PropTypes.bool,
  isSelected: PropTypes.bool,
  isBroken: PropTypes.bool
};

LinkedAdjustmentLinkIcon.defaultProps = {
  interactive: true,
  isSelected: false,
  isBroken: false
};
