import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { InsightWithConnectedAnalyses } from './ConnectedKfaPropTypes';
import { sanitize } from '../../../common/services/sanitizeService';
import { viewInsightPath } from '../../../common/services/urlService';
import { AnalysisTitleLink } from './AnalysisTitleLink';

export const InsightsTableRow = ({
  projectId,
  insight,
  hypothesisIndex,
  insightIndex
}) => {
  const insightUrl = viewInsightPath(projectId, insight.contentThreadId);
  return (
    <tr
      className="insights-table-row"
      key={insight.contentThreadId}
      data-testid={`insight-table-row-${insight.contentThreadId}`}
    >
      <td
        className="connected-insight-cell"
        data-testid="connected-insight-cell"
      >
        <h4
          className="kfa-connected-insight-title"
          data-testid="kfa-connected-insight-heading"
        >
          <Link
            to={insightUrl}
            key={insight.contentId}
            className="kfa-connected-insight-title-link"
            data-testid={`kfa-connected-insight-title-${hypothesisIndex}-${insightIndex}`}
          >
            {insight.title}
          </Link>
        </h4>
        {insight.body && (
          <div
            className="kfa-connected-insight-body"
            data-testid={`kfa-connected-insight-body-${hypothesisIndex}-${insightIndex}`}
            dangerouslySetInnerHTML={{
              __html: sanitize(insight.body)
            }}
          />
        )}
      </td>
      <td
        className="kfa-connected-analysis-title-cell analyses-connected-to-insight-cell"
        data-testid="analyses-connected-to-insight-cell"
      >
        {insight.connectedAnalyses.map((analysis, index) => (
          <AnalysisTitleLink
            key={analysis.contentId}
            analysis={analysis}
            projectId={projectId}
            testId={`kfa-connected-insight-analysis-title-${hypothesisIndex}-${insightIndex}-${index}`}
          />
        ))}
      </td>
    </tr>
  );
};

InsightsTableRow.propTypes = {
  insight: PropTypes.shape(InsightWithConnectedAnalyses).isRequired,
  hypothesisIndex: PropTypes.number.isRequired,
  insightIndex: PropTypes.number.isRequired,
  projectId: PropTypes.string.isRequired
};
