import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ProjectListRow from './ProjectListRow';
import './ProjectList.scss';
import { getProjectsList, loadProjectsThunk } from './projectsSlice';

const ProjectList = () => {
  const dispatch = useDispatch();
  const allProjects = useSelector(getProjectsList);

  useEffect(() => {
    dispatch(loadProjectsThunk());
  }, [dispatch]);

  return (
    <div data-testid="project-container">
      <h2>Projects</h2>
      <div className="project-container">
        {allProjects &&
          allProjects.map(value => (
            <ProjectListRow key={value.id} project={value} />
          ))}
      </div>
    </div>
  );
};

export default ProjectList;
