import { libraryItemLinksSet } from '../reducers/structuredDocumentDescriptionSlice';
import { getLibraryItemLinksByContentVersion } from '../services/structuredAnalysisService';

export const dispatchLibraryItemLinksSet = async (
  fetchLibraryItems,
  projectId,
  documentCode,
  contentId,
  contentVersionNumber,
  dispatch
) => {
  if (fetchLibraryItems) {
    const libraryItemLinks = await getLibraryItemLinksByContentVersion(
      projectId,
      documentCode,
      contentId,
      contentVersionNumber
    );
    dispatch(libraryItemLinksSet({ libraryItemLinks, documentCode }));
  } else {
    dispatch(libraryItemLinksSet({ libraryItemLinks: [], documentCode }));
  }
};
