import { Button } from '@pwc/pwc-deals-ui';
import classnames from 'classnames';
import React from 'react';

const StateChangeCard = ({ flow, isDisabled, onAccept, onCancel }) => {
  return (
    <div className="project-state-change-card-container" disabled={isDisabled}>
      <div
        className={classnames('state-change-card--header', {
          'state-change-card--header__disabled': isDisabled
        })}
        data-testid="state-change-card-header"
        /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
        tabIndex={0}
      >
        {flow.header}
      </div>
      <div className="state-change-card--text-container">
        <div
          className={classnames('state-change-card--text-area', {
            'state-change-card--text-area__disabled': isDisabled
          })}
          data-testid="state-change-card-text"
          /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
          tabIndex={0}
        >
          {flow.text}
        </div>
        {flow.secondaryText && (
          <div
            className={classnames('state-change-card--text-area', {
              'state-change-card--text-area__disabled': isDisabled
            })}
            data-testid="state-change-card-secondary-text"
            /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
            tabIndex={0}
          >
            {flow.secondaryText}
          </div>
        )}
      </div>
      <div className="state-change-card--button-container">
        {flow.leftButton && (
          <Button
            className="ui-button button-link form-cancel-button"
            onClick={onCancel}
            data-testid="state-change-card-cancel-button"
          >
            {flow.leftButton}
          </Button>
        )}
        <Button
          disabled={isDisabled}
          onClick={onAccept}
          data-testid="state-change-card-approve-button"
        >
          {flow.rightButton}
        </Button>
      </div>
    </div>
  );
};

export default StateChangeCard;
