import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { getUserSettingsSelector } from '../../common/redux/userSettings/userSettingsSlice';
import authorizationService from '../../common/services/security/authorizationService';
import pendoService from './pendoService';

export const PendoStarter = ({ children }) => {
  const userSettings = useSelector(getUserSettingsSelector);

  useEffect(() => {
    if (authorizationService.hasValidToken() && userSettings.loaded) {
      if (userSettings.settings.allowPerformanceCookies) {
        (() =>
          new Promise(res => {
            const isInstalled = pendoService.isInstalled();
            if (isInstalled) {
              res();
              return;
            }
            const checkingInitializedInterval = setInterval(() => {
              if (pendoService.isInstalled()) {
                clearInterval(checkingInitializedInterval);
                res();
              }
            }, 200);
          }).then(() => {
            if (!pendoService.isStarted()) {
              pendoService.start();
            }
          }))();
      }
    }
  }, [userSettings]);
  return children;
};
