import {
  notificationSlice,
  notificationSliceMiddleware
} from '@pwc/pwc-deals-notification-ui';
import { reportSlice } from '@pwc/pwc-deals-report-ui';
import { configureStore } from '@reduxjs/toolkit';
import { connectRouter, routerMiddleware } from 'connected-react-router';
import { createBrowserHistory } from 'history';
import { combineReducers } from 'redux';
import thunk from 'redux-thunk';

import processingMiddleware from '../common/engine/middleware/processingMiddleware';
import rowIngestionMiddleware from '../common/engine/middleware/rowIngestionMiddleware';
import schedulerMiddleware from '../common/engine/middleware/schedulerMiddleware';
import toggleMiddleware from '../common/engine/middleware/toggleMiddleware';
import adjustmentsByDocument from '../common/engine/reducers/adjustmentsByDocument';
import adjustmentSumsByDocument from '../common/engine/reducers/adjustmentSumsByDocument';
import adjustmentTogglesByDocument from '../common/engine/reducers/adjustmentTogglesByDocument';
import calcEngine from '../common/engine/reducers/calcEngine';
import factCalculations from '../common/engine/reducers/factCalculations';
import longDescription from '../common/engine/reducers/longDescription';
import timePeriodMetadataByDocument from '../common/engine/reducers/timePeriodMetadataByDocument';
import timePeriods from '../common/engine/reducers/timePeriods';
import toggledAdjustmentSumsByDocument from '../common/engine/reducers/toggledAdjustmentSumsByDocument';
import loggedInUserSlice from '../common/redux/security/loggedInUser/loggedInUserSlice';
import termsAndConditionsSlice from '../common/redux/termsAndConditions/termsAndConditionsSlice';
import userSettingsSlice from '../common/redux/userSettings/userSettingsSlice';
import userNotificationSettingsSlice from '../common/redux/userNotificationSettings/userNotificationSettingsSlice';
import userTermsAndConditionsSlice from '../common/redux/userTermsAndConditions/userTermsAndConditionsSlice';
import linkedAdjustmentsByDocument from '../common/engine/reducers/linkedAdjustmentsByDocument';
import errorsSlice from '../common/redux/errors/errorsSlice';

import analysisSlice from '../features/analyses/analysisSlice';
import structuredDocumentDescriptionSlice from '../common/engine/reducers/structuredDocumentDescriptionSlice';
import { structuredAnalysisViewReducer } from '../common/engine/reducers/structuredAnalysisViewSlice';
import structuredAnalysisSlice from '../common/engine/reducers/StructuredAnalysisSlice';
import appSettings from '../features/appSettings/appSettingsSlice';
import commentMiddleware from '../features/comments/commentMiddleware';
import commentsSlice from '../features/comments/commentsSlice';
import divestitureDiagnosticSlice from '../features/divestitureDiagnostic/divestitureDiagnosticSlice';
import hypothesisSlice from '../features/hypotheses/hypothesisSlice';
import insightSlice from '../features/insights/insightSlice';
import projectSlice from '../features/project/projectSlice';
import projectsSlice from '../features/projects/projectsSlice';
import projectDisclaimersSlice from '../features/projectDisclaimer/projectDisclaimerSlice';
import httpMiddleware from '../Survey/httpMiddleware';
import surveySlice from '../Survey/surveySlice';
import footerSlice from '../common/redux/termsAndConditions/footerSlice';
import reportFormatSlice from '../common/redux/reportFormat/reportFormatSlice';
import fileUploadSlice from '../common/redux/fileUpload/fileUploadSlice';
import projectStatusChangeSlice from '../features/projectStateChangeCards/projectStatusChangeSlice';

export const history = createBrowserHistory();

const commonReducers = {
  structuredAnalysis: structuredAnalysisSlice,
  loggedInUser: loggedInUserSlice,
  userTermsAndConditions: userTermsAndConditionsSlice,
  userSettings: userSettingsSlice,
  userNotificationSettings: userNotificationSettingsSlice,
  termsAndConditions: termsAndConditionsSlice,
  reportFormat: reportFormatSlice,
  fileUpload: fileUploadSlice,
  footer: footerSlice,
  longDescriptionsByCode: longDescription,
  adjustmentsByDocument,
  adjustmentTogglesByDocument,
  adjustmentSumsByDocument,
  toggledAdjustmentSumsByDocument,
  factCalculations,
  timePeriodMetadataByDocument,
  timePeriodsByCode: timePeriods,
  calcEngine,
  linkedAdjustmentsByDocument,
  errors: errorsSlice
};

const commonMiddleware = [
  processingMiddleware,
  schedulerMiddleware,
  rowIngestionMiddleware,
  toggleMiddleware
];

export const rootReducer = combineReducers({
  router: connectRouter(history),
  project: projectSlice,
  projects: projectsSlice,
  structuredDocumentDescription: structuredDocumentDescriptionSlice,
  structuredAnalysisView: structuredAnalysisViewReducer,
  hypothesis: hypothesisSlice,
  insight: insightSlice,
  analysis: analysisSlice,
  comments: commentsSlice,
  report: reportSlice,
  notification: notificationSlice,
  projectDisclaimers: projectDisclaimersSlice,
  projectApprovals: projectStatusChangeSlice,
  appSettings,
  survey: surveySlice,
  divestitureDiagnostic: divestitureDiagnosticSlice,
  ...commonReducers
});

export const createStore = () =>
  configureStore({
    reducer: rootReducer,
    middleware: [
      routerMiddleware(history),
      thunk,
      ...commonMiddleware,
      commentMiddleware,
      notificationSliceMiddleware,
      httpMiddleware
    ],
    devTools: true
  });
