import { Button, Loader, Icon } from '@pwc/pwc-deals-ui';
import { PropTypes } from 'prop-types';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import RichTextEditor from '../../../../../common/components/RichTextEditor/RichTextEditor';
import {
  getAnalysisDescription,
  saveDocumentDescriptionThunk
} from '../../../../../common/engine/reducers/structuredDocumentDescriptionSlice';
import NonEditableTextEditor from './NonEditableTextEditor';
import { getProjectId } from '../../../../project/projectSlice';

import ShowForPermissions from '../../../../security/ShowForPermissions';
import OutsideClickHandler from '../../../../../common/components/OutsideClickHandler';
import './AnalysisDescription.scss';

const AnalysisDescription = ({ isViewingSharedVersion, lightModeVariant }) => {
  const dispatch = useDispatch();
  const { header, subheader, content, childrenData, loading } = useSelector(
    getAnalysisDescription
  );
  const projectId = useSelector(getProjectId);
  const [editMode, setEditMode] = useState(false);

  const hasRichText = content.richText ? content.richText.length > 0 : false;

  const [richText, setRichText] = useState(hasRichText ? content.richText : '');
  const [focused, setFocused] = useState(false);

  const persistRichText = () => {
    dispatch(saveDocumentDescriptionThunk(richText));
    setEditMode(false);
  };

  const shouldShowEditButton =
    !loading && !editMode && !isViewingSharedVersion && hasRichText;

  const getComponentBody = () => {
    if (loading) {
      return <Loader dataTestId="analysis-description-loader" />;
    }

    if (!editMode) {
      return (
        <NonEditableTextEditor
          richTextData={content.richText || content.plainText}
          childrenData={childrenData}
        />
      );
    }
    return (
      <>
        <div className={`${lightModeVariant ? ' light-mode-variant' : ''}`}>
          <RichTextEditor
            richTextData={content.richText}
            onChange={setRichText}
            label="Adjustment Description"
            onBlur={(_, editor) => {
              setFocused(false);

              editor.ui.view.toolbar.element.style.display = 'none';
            }}
            onFocus={(_, editor) => {
              setFocused(true);

              editor.ui.view.toolbar.element.style.display = 'block';
            }}
            isFocused={focused}
          />
        </div>
        <div className="edit-button-container">
          <Button
            data-testid="cancel-button"
            className="button-link mx-2 mr-4"
            onClick={() => setEditMode(false)}
          >
            Cancel
          </Button>
          <Button
            data-testid="save-button"
            className="button-block button-small"
            onClick={persistRichText}
          >
            Update
          </Button>
        </div>
      </>
    );
  };

  return (
    <OutsideClickHandler onClick={() => setEditMode(false)}>
      <div id="analysisDescription" aria-live="polite">
        <div className="description-header" role="heading" aria-level="3">
          {header}
        </div>
        {subheader && (
          <div className="description-subheader" role="heading" aria-level="4">
            <div>{subheader}</div>
            <ShowForPermissions
              projectId={projectId}
              permissions={['Contribute']}
            >
              {shouldShowEditButton && (
                <Button
                  className="button-menu"
                  onClick={() => setEditMode(true)}
                  data-testid="icon-button-edit"
                >
                  <Icon
                    name="pencil"
                    width="16"
                    className="sr2 fill icon-orange"
                  />
                </Button>
              )}
            </ShowForPermissions>
          </div>
        )}
        {getComponentBody()}
      </div>
    </OutsideClickHandler>
  );
};

AnalysisDescription.propTypes = {
  isViewingSharedVersion: PropTypes.bool.isRequired
};

export default AnalysisDescription;
