import React from 'react';
import { Button } from '@pwc/pwc-deals-ui';
import { Link } from 'react-router-dom';
import { createHypothesisPath } from '../../common/services/urlService';
import EnableForProjectState from '../security/EnableForProjectState';

const CreateHypothesisLinkButton = ({ projectId }) => {
  const addHypothesisHref = createHypothesisPath(projectId);

  return (
    <EnableForProjectState projectId={projectId}>
      {({ isProjectDisabled }) => (
        <Link to={addHypothesisHref}>
          <Button className="mt-3" disabled={isProjectDisabled}>
            Create Key Focus Area
          </Button>
        </Link>
      )}
    </EnableForProjectState>
  );
};

export default CreateHypothesisLinkButton;
