import { createSelector, createSlice } from '@reduxjs/toolkit';
import { cloneDeep } from 'lodash';

export const initialState = {
  files: {
    loading: null,
    error: null,
    data: null
  },
  upload: {
    /**
     * to open or close the FileUploadModal
     */
    showModal: false,
    loading: false,
    error: null,
    successMessage: null,
    /**
     * to send the fileId in case we are updating a file (not uploading a new one)
     */
    uploadedFileId: null,
    uploadedFileName: null
  }
};

const fileUploadSlice = createSlice({
  name: 'fileUpload',
  initialState,
  reducers: {
    setFilesData: (state, action) => {
      const { data } = action.payload;
      state.files.data = cloneDeep(data);
      state.files.error = false;
      state.files.loading = false;
    },
    setFilesDataLoading: (state, action) => {
      const { loading } = action.payload;
      state.files.loading = loading;
      state.files.error = false;
    },
    setFilesDataError: (state, action) => {
      const { error } = action.payload;
      state.files.loading = false;
      state.files.error = error;
    },
    setFileUploadLoading: (state, action) => {
      const { loading } = action.payload;
      state.upload.loading = loading;
      state.upload.error = null;
    },
    setFileUploadError: (state, action) => {
      const { error } = action.payload;
      state.upload.loading = false;
      state.upload.error = error;
    },
    openModal: (state, action) => {
      const { fileId, fileName } = action.payload || { fileId: null };
      state.upload.showModal = true;
      state.upload.uploadedFileId = fileId;
      state.upload.uploadedFileName = fileName;
    },
    closeModal: state => {
      state.upload.showModal = false;
    },
    showSuccessMessage: (state, action) => {
      state.upload.showModal = false;
      state.upload.successMessage = action.payload;
    },
    hideSuccessMessage: state => {
      state.upload.successMessage = null;
    }
  }
});

// Actions
export const {
  setFilesData,
  setFilesDataLoading,
  setFilesDataError,
  setFileUploadLoading,
  setFileUploadError,
  openModal,
  closeModal,
  showSuccessMessage,
  hideSuccessMessage
} = fileUploadSlice.actions;

// Selectors
const getFileUploadSlice = state => state.fileUpload;

export const getFilesState = createSelector(
  getFileUploadSlice,
  slice => slice.files
);
export const getFileUploadState = createSelector(
  getFileUploadSlice,
  slice => slice.upload
);

export default fileUploadSlice.reducer;
