import React from 'react';
import { Icon } from '@pwc/pwc-deals-ui';
import './SharedPill.scss';

const SharedPill = ({ shared, version }) => {
  const versionText = `v${version} Draft shared `;

  return (
    <>
      {(shared || version) && (
        <div className="shared-pill" data-testid="shared-with-client-pill">
          <Icon name="view" width={20} className="fill icon-black" />
          <span className="text-shared-with-client">
            {versionText}
            <span className="hide-for-mobile">with client</span>
          </span>
        </div>
      )}
    </>
  );
};

export default SharedPill;
