import React from 'react';
import { Select } from '@pwc/pwc-deals-ui';
import PropTypes from 'prop-types';
import ClearableDisplayPill from '../../../../../common/components/Pills/ClearableDisplayPill';
import { keyParser } from '../../../../../common/engine/utilities/linkedAdjustmentsUtilities';

const getLabelFromOptions = (options, selectedValue) => {
  return (
    options.find(x => x.value === selectedValue) || {
      label: null
    }
  ).label;
};

const getSourcePillText = (adjustmentNumber, adjustmentName) => {
  return `${adjustmentNumber} ${adjustmentName}`;
};

const SelectablePill = ({
  editMode,
  name,
  onChange,
  onDelete,
  options,
  value,
  placeholder
}) => {
  const selectedLabel = getLabelFromOptions(options, value);

  const adjustmentKey = keyParser(value);
  const pillText = getSourcePillText(adjustmentKey.number, adjustmentKey.name);
  const isAdjustmentBroken = !selectedLabel;

  return editMode ? (
    <Select
      selectTestId={`select-input-${name}`}
      value={value}
      options={options}
      onChange={event => onChange(event.target.value)}
      placeholder={placeholder}
      maxWidth="240px"
    />
  ) : (
    <div className="pill-container">
      <ClearableDisplayPill
        onClearClick={onDelete}
        tag={pillText}
        index={name}
        iconName={isAdjustmentBroken ? 'link-remove' : null}
      />
    </div>
  );
};

SelectablePill.propTypes = {
  editMode: PropTypes.bool.isRequired,
  name: PropTypes.string.isRequired,
  onDelete: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  options: PropTypes.arrayOf(
    PropTypes.shape({ label: PropTypes.string, value: PropTypes.string })
  ),
  value: PropTypes.string,
  placeholder: PropTypes.string
};

SelectablePill.defaultProps = {
  options: [],
  value: '',
  placeholder: ''
};

export default SelectablePill;
