import { Icon } from '@pwc/pwc-deals-ui';
import React from 'react';
import './PasswordSection.scss';

const PasswordSection = ({ password, setPassword }) => {
  const onChange = ({ target: { value } }) => setPassword(value);

  return (
    <div className="password-wrapper">
      <h6>Password</h6>
      <div className="content">
        <Icon name="information" className="fill icon-yellow" width={16} />
        <div>
          Add a password to the file. If the file already has one, you can enter
          it here so that all users can discover the password using the Show
          password feature in Junction.
        </div>
      </div>
      <input
        className="input-field"
        onChange={onChange}
        value={password}
        type="text"
      />
    </div>
  );
};

export default PasswordSection;
