import { createSelector, createSlice } from '@reduxjs/toolkit';
import { clearAllProjectData } from '../actions/clearAllProjectData';
import resetCalculationDataForDocument from '../actions/resetCalculationDataForDocument';

export const initialState = {
  loadingInProcess: false,
  hasError: false,
  structuredDocuments: []
};

const structuredAnalysisViewSlice = createSlice({
  name: 'structuredAnalysisViewSlice',
  initialState,
  reducers: {
    startProcessing: (_state, _action) => ({
      loadingInProcess: true,
      hasError: false,
      structuredDocuments: []
    }),
    processingSucceeded: (state, action) => ({
      ...state,
      loadingInProcess: false,
      hasError: false,
      structuredDocuments: action.payload.documentsWithContent
    }),
    processingFailed: (_state, _action) => ({
      loadingInProcess: false,
      hasError: true,
      structuredDocuments: []
    })
  },

  extraReducers: builder => {
    builder.addCase(clearAllProjectData, () => initialState);
    builder.addCase(resetCalculationDataForDocument, (state, action) => {
      if (!action.payload || !action.payload.documentCode) {
        return state;
      }
      const { documentCode } = action.payload;
      const updatedDocuments = state.structuredDocuments.filter(
        x => x.code !== documentCode
      );

      return { ...state, structuredDocuments: updatedDocuments };
    });
  }
});

export const {
  reducer: structuredAnalysisViewReducer
} = structuredAnalysisViewSlice;

// Actions
export const {
  startProcessing,
  processingSucceeded,
  processingFailed
} = structuredAnalysisViewSlice.actions;

// Selectors
export const getStructuredAnalysisViewLoadingInProcess = state =>
  state.structuredAnalysisView.loadingInProcess;

export const getStructuredAnalysisViewHasError = state =>
  state.structuredAnalysisView.hasError;

const getStructuredAnalysisViewDocumentsWithContent = state =>
  state.structuredAnalysisView.structuredDocuments;

export const getStructuredAnalysisViewDocumentsWithContentForDocument = documentCode =>
  createSelector(
    getStructuredAnalysisViewDocumentsWithContent,
    structuredDocuments => {
      const documentsForCode = structuredDocuments.find(
        x => x.code === documentCode
      );
      return documentsForCode;
    }
  );
